import { SFSData } from "../../../../network/sfs/constans/SFSData";
import { SFSEvent } from "../../../../network/sfs/constans/SFSEvent";
var BaseProxy = require('BaseProxy');
var GameConfig = require('GameConfig');
var LobbyMessage = require('LobbyMessage');
var Utility = require('Utility');
var i18n = require('i18n');

export default class LobbyProxy extends BaseProxy { 
    static get NAME() {
        return 'LobbyProxy';
    }

    static errData = {
        ROOM_BET_NOT_EXIST: 1000,
        FREE_CURRENCY_NOT_ENOUGH: 1001,
        ROOM_NOT_EXIST: 1002
    }

    onRegister() {
        BaseProxy.prototype.onRegister.call(this);
        this.dataUser = this.facade.retrieveProxy('UserProxy');
        this.channel = this.facade.retrieveProxy('ChannelProxy');
        this.sfsProxy = this.facade.retrieveProxy('SFSGameProxy');
        this.sfsProxy.messages.push(this.onMessage.bind(this));
    }


    /////////////////////////////////////////////////////////////////
    ////////////////////SEVER REPONSE//////////////////////////////
    ////////////////////////////////////////////////////////////////
    onMessage(event) {
        var params = event.params;
        switch (event.cmd) {
            case SFSEvent.GET_ROOM_LIST:
                this.onUpdateRoomList(params);
                break;
            case SFSEvent.ADD_ROOM:
                this.onAddRoom(params);
                break;
            case SFSEvent.REMOVE_ROOM:
                this.onRemoveRoom(params);
                break;
            case SFSEvent.ERROR:
                this.onError(params);
                break;
        }
    }

    onUpdateRoomList(params) {
        var responseParams = params;
        var sfsRoomList = responseParams.getSFSArray("roomList");
        var roomList = [];
        for (var i = 0; i < sfsRoomList.size(); i++) {
            var sfsVO = sfsRoomList.getSFSObject(i);
            var vo = Utility.convertSFSObjectToObject(sfsVO);
            roomList.push(vo);
        }
        this.sendNotification(LobbyMessage.RECEIVE_UPDATE_ROOM_LIST, { roomList: roomList });
    }

    onAddRoom(evtParams) {

    }

    onRemoveRoom(evtParams) {
    }

    onError(params) {
        var responseParams = params;
        var code = responseParams.get('code');
        switch (code) {
            case this.errData.ROOM_BET_NOT_EXIST:
                this.sendNotification(LobbyMessage.SHOW_ALERT, { content: i18n.t('C0048') });
                this.sendNotification(LobbyMessage.HIDE_LOADING);
                break;
            case this.errData.FREE_CURRENCY_NOT_ENOUGH:
                //var freeBalance = responseParams.get('code');
                var txt = (GameConfig.CURRENT_MODE === "MONEY") ? i18n.t("C0044") : i18n.t("C0045");
                this.openAlertEnoughMoney(Utility.setText(i18n.t("C0010"), [txt, txt]));
                this.sendNotification(LobbyMessage.HIDE_LOADING);
                break;
            case this.errData.ROOM_NOT_EXIST:
                this.sendNotification(LobbyMessage.SHOW_ALERT, { content: i18n.t('C0049') });
                this.sendNotification(LobbyMessage.HIDE_LOADING);
                break;
        }
    }

    /////////////////////////////////////////////////////////////////
    ////////////////////SEND TO SERVER//////////////////////////////
    ////////////////////////////////////////////////////////////////
    sendExtensionRequest(command, sfo) {
        sfo.putUtfString(SFSData.USER_NAME, this.dataUser.mySelf.userName);
        var room = this.sfs.lastJoinedRoom;
        var request = new SFS2X.ExtensionRequest(command, sfo, room);
        this.sfsProxy.send(request);
    }

    sendCreateGame(params) {
        var sfo = new SFS2X.SFSObject();
        var room = new SFS2X.SFSObject();
        var sfsArray = new SFS2X.SFSArray();
        room.putUtfString(SFSData.NAME, SFSData.ROOM_BET);
        room.putUtfString(SFSData.VALUE, params.roomBet.toString());
        sfsArray.addSFSObject(room);

        sfo.putSFSArray(SFSData.VARIABLES, sfsArray);
        sfo.putUtfString(SFSData.ROOM_BET, params.roomBet.toString());
        sfo.putUtfString(SFSData.MAX_USER, params.maxPlayer.toString());
        sfo.putUtfString(SFSData.PASSWORD, params.password.toString());
        sfo.putUtfString(SFSData.USER_NAME, this.dataUser.mySelf.userName);
        var request = new SFS2X.ExtensionRequest(SFSEvent.CREATE_ROOM, sfo, null);
        this.sfsProxy.send(request);
    }

    sendJoinZone() {
        var mySelf = this.dataUser.mySelf;
        var zoneName = this.channel.getCurZone();
        this.sfsProxy.joinZone(GameConfig.SOCKET_IP, GameConfig.SOCKET_PORT, mySelf.userName, mySelf.password, zoneName, mySelf.token);
        this.sfsProxy.startGetRoomList();
    }

    sendJoinGame(params) {
        var sfo = new SFS2X.SFSObject();
        sfo.putUtfString(SFSData.ROOM_ID, params.tableId.toString());

        var request = new SFS2X.ExtensionRequest(SFSEvent.JOIN_ROOM, sfo, null);
        this.sfsProxy.send(request);
    }

    sendQuickJoinGame(params) {
        var sfo = new SFS2X.SFSObject();
        var room = new SFS2X.SFSObject();
        var sfsArray = new SFS2X.SFSArray();
        room.putUtfString(SFSData.NAME, SFSData.ROOM_BET);
        room.putUtfString(SFSData.VALUE, '1000');
        sfsArray.addSFSObject(room);

        sfo.putSFSArray(SFSData.VARIABLES, sfsArray);
        sfo.putUtfString(SFSData.ROOM_BET, '1000');
        sfo.putUtfString(SFSData.MAX_USER, params.maxPlayer.toString());
        sfo.putUtfString(SFSData.USER_NAME, this.dataUser.mySelf.userName);
        var request = new SFS2X.ExtensionRequest(SFSEvent.QUICK_JOIN, sfo, null);
        this.sfsProxy.send(request);
    }

    sendRefreshMoney() {
        var sfo = new SFS2X.SFSObject();
        sfo.putUtfString(SFSData.COMMAND, SFSData.REFRESH_MONEY);
        sfo.putUtfString(SFSData.USER_NAME, this.dataUser.mySelf.userName);
        var room = null;
        var request = new SFS2X.ExtensionRequest(SFSEvent.REFRESH_MONEY, sfo, room);
        this.sfsProxy.send(request);
    }

    sendChangeMode(params) {

    }

    sendDisconnectChannel() {
        this.sfsProxy.onDisconnect();
    }

}
