var BaseCommand = require('BaseCommand');
var SlotMessage = require('SlotMessage');
var LobbyMessage = require('LobbyMessage');
var SoundGameMessage = require('SoundGameMessage');
var Slot20VO = require('Slot20VO');
var SlotMCVO = require('SlotMCVO');
var SlotMCProxy = require('SlotMCProxy');
var UserProxy = require('UserProxy');
var LocalStorage = require('LocalStorage');
var i18n = require('i18n');

export default class ReceiveSpinMCCommand extends BaseCommand {
  execute(notification) {
    BaseCommand.prototype.execute.call(this, notification);
    this.gameVO = notification.getBody().data;
    // this.gameVO.session++;
    if(this.gameVO.typeResult === Slot20VO.ERROR) {
      this.gameVO.isSpining = false;
      this.gameVO.autoSpin = false;
      this.sendNotification(SlotMessage.ON_SHOW_MESSAGE_MASTER_CHEF, {message: this.gameVO.errorMessage});
      this.sendNotification(SlotMessage.ON_SET_AUTO_SPIN_MASTER_CHEF, {autoSpin: false});
      this.sendNotification(SlotMessage.ON_RESET_BTN_SPIN_MASTER_CHEF);
    }else{
      this.dataUser = this.facade.retrieveProxy(UserProxy.NAME);
      if(this.gameVO.isChoiThu === false) {
        this.dataUser.mySelf.money -= this.gameVO.getTotalBet();
        this.sendNotification(LobbyMessage.ON_UPDATE_MY_INFO);
      }
      this.sendNotification(SlotMessage.ON_SPIN_AND_RESULT_MASTER_CHEF, {items:this.gameVO.items, onComplete: this.onComplete.bind(this)});
      // if( LocalStorage.getSound() === "true") {
      //   this.sendNotification(LobbyMessage.PLAY_SOUND_EFFECT, SoundGameMessage.SPIN_MASTER_CHEF_SOUND);
      // }
    }

    setTimeout(function(session){
      if(this.gameVO.session !== session) return;
      this.gameVO.isSpining = false;
      this.sendNotification(SlotMessage.ON_RESET_BTN_SPIN_MASTER_CHEF);
    }.bind(this), 5000,this.gameVO.session);
  }

  onComplete() {
    if (this.gameVO.isChoiThu) {
      this.sendNotification(SlotMessage.ON_UPDATE_MONEY_PLAY_TRY_MASTER_CHEF, this.gameVO);
    }
    else {
      this.dataUser.mySelf.money = this.gameVO.money;
      this.sendNotification(LobbyMessage.ON_UPDATE_MY_INFO);
    }

    if (this.gameVO.multiplier > 0)
      this.sendNotification(SlotMessage.ON_UPDATE_MULTIPLIER_MASTER_CHEF, this.gameVO.multiplier);
    if (this.gameVO.tPoint > 0)
      this.sendNotification(SlotMessage.ON_UPDATE_TICH_LUY_MASTER_CHEF, this.gameVO.mutiplier);

    this.checkHasWild(function(){
      this.checkHasMinigame(function(){
        this.checkHasFreeSpin(function(){
          this.checkHasJackpot(function(){
            this.checkHasbigWin(function(){
              this.checkHasNomalWin(function(){
                this.checkHasAutoSpin(function(){
                  this.displayOneLine();
                }.bind(this));
              }.bind(this));
            }.bind(this));
          }.bind(this));
        }.bind(this));
      }.bind(this));
    }.bind(this));
  }

  displayOneLine() {
    this.sendNotification(SlotMessage.ON_CHECK_GAME_X2_MASTER_CHEF);

    var timeDeay = 1.5;
    var wins = this.gameVO.wins;
    var session = this.gameVO.session;
    if(this.gameVO.typeResult !== Slot20VO.JACKPOT_WIN) this.sendNotification(SlotMessage.ON_HIDE_EFFECT_WIN_MASTER_CHEF);
    if (wins.length === 0)  return;
    this.displayLine(0, wins, session, 0);
  }

  displayLine (delay, wins, session, index) {
    TweenLite.delayedCall(delay, function(data, session){
      if(this.gameVO.session !== session) {
        this.sendNotification(SlotMessage.ON_HIDE_ALL_LINE_MASTER_CHEF);
        return;
      }
      this.sendNotification(SlotMessage.ON_HIDE_ALL_EFFECT_ITEM_WIN_MASTER_CHEF);
      this.sendNotification(SlotMessage.ON_SHOW_LINE_WIN_MASTER_CHEF, {wins: [data]});
      this.sendNotification(SlotMessage.ON_SHOW_EFFECT_ITEM_WIN_MASTER_CHEF, {wins: [data]});
      index ++;
      if(wins.length === index) index = 0;
      this.displayLine(1.5, wins, session, index);
    }.bind(this),[wins[index], session]);
  }

  checkHasWild(callBack) {
    var posWild = [];
    for (var i = 0; i < 15; i++) {
      if (this.gameVO.items[i] === SlotMCVO.ITEM_WILD_ID) {
        posWild.push(i % 5);
      }
    }
    if (posWild.length > 0) {
      setTimeout(function(posWild){
        this.sendNotification(SlotMessage.ON_SHOW_WILD_MASTER_CHEF, posWild);
      }.bind(this), 1, posWild);
      this.sendNotification(LobbyMessage.PLAY_SOUND_EFFECT, SoundGameMessage.WILD_APPEAR_MASTER_CHEF);
      TweenLite.delayedCall(1, function () {
        callBack.call();
      });
    }
    else
      callBack.call();
  }

  checkHasAutoSpin(callBack) {
    if(this.gameVO.autoSpin || this.gameVO.freeSpin > 0) {
      // if(this.gameVO.autoSpin) {
      //   this.gameVO.curNumAutoSpin--;
      //   this.sendNotification(SlotMessage.ON_SET_NUM_AUTO_SPIN_MASTER_CHEF, {num: this.gameVO.curNumAutoSpin});
      // }
      this.sendNotification(SlotMessage.ON_HIDE_ALL_LINE_MASTER_CHEF);
      this.sendNotification(SlotMessage.ON_HIDE_EFFECT_WIN_MASTER_CHEF);
      this.sendNotification(SlotMessage.SEND_SPIN_MASTER_CHEF , {data: this.gameVO.prevDataSpin});
    }else{
      callBack.call();
    }
  }

  checkHasNomalWin(callBack) {
    this.sendNotification(SlotMessage.ON_HIDE_ALL_EFFECT_ITEM_WIN_MASTER_CHEF);
    this.gameVO.isSpining = false;
    this.sendNotification(SlotMessage.ON_RESET_BTN_SPIN_MASTER_CHEF);
    if (this.gameVO.isHasTPoint) {
      var wins = [];
      for (var i = 0; i < this.gameVO.wins.length; i ++) {
        if (this.gameVO.wins[i].itemId === SlotMCVO.ITEM_FREE_SPIN_ID)
        wins.push(this.gameVO.wins[i]);
      }
      this.sendNotification(SlotMessage.ON_SHOW_EFFECT_TPOINT_MASTER_CHEF, wins);
    }

    if(this.gameVO.typeResult === Slot20VO.NORMAL_WIN) {
      this.sendNotification(SlotMessage.ON_SHOW_LINE_WIN_MASTER_CHEF, {wins: this.gameVO.wins});
      this.sendNotification(SlotMessage.ON_SHOW_EFFECT_WIN_MASTER_CHEF, {gameVO: this.gameVO});
      this.sendNotification(LobbyMessage.PLAY_SOUND_EFFECT, SoundGameMessage.WIN_MASTER_CHEF);
      if(this.gameVO.wins.length > 0) {
        setTimeout(function(session){
          if(this.gameVO.session !== session) return;
          callBack.call();
        }.bind(this), 3500*this.gameVO.timeSpin,this.gameVO.session);
      }else{
        callBack.call();
      }
    }else{
      callBack.call();
    }
  }

  checkHasbigWin(callBack) {
    this.sendNotification(SlotMessage.ON_HIDE_ALL_EFFECT_ITEM_WIN_MASTER_CHEF);
    if(this.gameVO.typeResult === Slot20VO.BIG_WIN) {
      this.sendNotification(SlotMessage.ON_SHOW_LINE_WIN_MASTER_CHEF, {wins: this.gameVO.wins});
      this.sendNotification(SlotMessage.ON_SHOW_EFFECT_WIN_MASTER_CHEF, {gameVO: this.gameVO});
      this.sendNotification(LobbyMessage.PLAY_SOUND_EFFECT, SoundGameMessage.BIG_WIN_MASTER_CHEF);

      setTimeout(function(){
        callBack.call();
      }, 7000*this.gameVO.timeSpin);
    }else{
      callBack.call();
    }
  }

  checkHasJackpot(callBack) {
    this.sendNotification(SlotMessage.ON_HIDE_ALL_EFFECT_ITEM_WIN_MASTER_CHEF);
    if(this.gameVO.typeResult === Slot20VO.JACKPOT_WIN) {
      this.sendNotification(SlotMessage.ON_PLAY_ANIM_JACKPOT_MASTER_CHEF);
      setTimeout(function(){
        this.sendNotification(SlotMessage.ON_SHOW_LINE_WIN_MASTER_CHEF, {wins: this.gameVO.wins});
        this.sendNotification(SlotMessage.ON_SHOW_EFFECT_WIN_MASTER_CHEF, {gameVO: this.gameVO});
        this.sendNotification(LobbyMessage.PLAY_SOUND_EFFECT, SoundGameMessage.JACKPOT_MASTER_CHEF);

         setTimeout(function(){
          callBack.call();
        }, 6000);
      }.bind(this), 2000);
    }else{
      callBack.call();
    }
  }

  checkHasMinigame(callBack) {
    if(this.gameVO.isMinigame) {
      this.sendNotification(SlotMessage.ON_HIDE_ALL_EFFECT_ITEM_WIN_MASTER_CHEF);
      // this.sendNotification(SlotMessage.ON_PLAY_ANIM_BONUS_MASTER_CHEF);
      setTimeout(function(){
        this.sendNotification(SlotMessage.ON_SHOW_MINIGAME, {gameVO: this.gameVO, playComplete:function(){
          setTimeout(function(){
            callBack.call();
          }, 500);
        }});
      }.bind(this), 500);
    }else{
      callBack.call();
    }
  }

  checkHasFreeSpin(callBack) {
    if(this.gameVO.isFreeSpin) {
      this.sendNotification(SlotMessage.ON_HIDE_ALL_EFFECT_ITEM_WIN_MASTER_CHEF);
      // this.sendNotification(SlotMessage.ON_PLAY_ANIM_SCATTER_MASTER_CHEF);
      setTimeout(function(){
          this.sendNotification(SlotMessage.ON_SHOW_EFFECT_FREE_SPIN_MASTER_CHEF, {gameVO: this.gameVO});
          this.sendNotification(LobbyMessage.PLAY_SOUND_EFFECT, SoundGameMessage.FREE_SPIN_TAM_QUOC);
          setTimeout(function(){
            this.sendNotification(SlotMessage.ON_HIDE_EFFECT_WIN_MASTER_CHEF);
            this.sendNotification(SlotMessage.ON_SHOW_FREE_SPIN_MASTER_CHEF);
            setTimeout(function(){
              callBack.call();
            }, 0);
          }.bind(this), 2000);
      }.bind(this),0);

    }else {
        this.sendNotification(SlotMessage.ON_SHOW_FREE_SPIN_MASTER_CHEF);
        callBack.call();
    }
  }
}
