var Base64 = require('Base64');
var GameConfig = require('GameConfig');
var Alert = require('Alert');
var i18n = require('i18n');
var CustomAction = require('CustomAction');
cc.Class({
    extends: cc.Component,

    properties: {
        clientKey: "",
        upStatus: "",
        isTest:true,
        isDebug: true,
        progressBar: cc.ProgressBar,
        alert: Alert
    },

    // use this for initialization
    onLoad: function () {
        var params = {
            clientKey: this.clientKey,
            upStatus: this.upStatus
        };

        var url = "";
        // cc.debug.setDisplayStats(false);

        if(window.configApp){
            GameConfig.IS_TEST = window.configApp.isTest;
            GameConfig.IS_DEBUG = window.configApp.isDebug;
            url = window.configApp.url;
        }else{
            GameConfig.IS_TEST = this.isTest;
            GameConfig.IS_DEBUG = this.isDebug;
            // url = "http://18.136.223.57:8028/cfapi/startappconfig";
            url = "http://113.160.59.18:8028/cfapi/startappconfig";
            if(GameConfig.IS_TEST){
                GameConfig.IS_SEND_CARD = true;
            }else{
                GameConfig.IS_SEND_CARD = false;
            }
        }

        this.onPosData(url, params, "POST");
    },

    onReponseStartAppConfig: function (params) {
        GameConfig.WEB_RESOURCE = params.data.partnerConfig.configInfo.resourceConfig.resourceDomain + "/";
        GameConfig.WEB_SERVICE = params.data.partnerConfig.apiInfo.webApiDomain + "/";
        try{
            GameConfig.SOCKET_IP = params.data.partnerConfig.apiInfo.gameServerIp.split(':')[0];
            GameConfig.SOCKET_PORT = params.data.partnerConfig.apiInfo.gameServerIp.split(':')[1];
            // GameConfig.SOCKET_IP = "18.189.171.155";
            // GameConfig.SOCKET_PORT = 8004;

        }catch(err){

        }

        GameConfig.POLICY = params.data.partnerConfig.configInfo.policyUrl;
        if (params.data.partnerConfig.hotlines[CustomAction.ACTION_HOTLINE_1] !== undefined) {
            GameConfig.HOT_LINE = params.data.partnerConfig.hotlines[CustomAction.ACTION_HOTLINE_1].name;
        }
        this.progressBar.progress = 0.1;
        cc.loader.onProgress = function (completedCount, totalCount, item) {
            var percent = 0;
            if (totalCount > 0) {
                percent = completedCount / totalCount;
            }
            if (this.progressBar && percent !== 0) this.progressBar.progress = (percent <= 0.1) ? 0.1 : percent;
        }.bind(this);

        cc.director.preloadScene('LVCScene', function (err) {
            cc.director.loadScene('LVCScene');
        }.bind(this));

        GameConfig.KEY_RAND = params.data.keyRand;
        GameConfig.DISPLAY_NAME = GameConfig.KEY_RAND;
    },

    onPosData: function (url, params, method, isShowLoad) {
        var xhr = new XMLHttpRequest();
        xhr.open(method, url);
        xhr.onreadystatechange = function () {
            if (xhr.readyState === 4 && (xhr.status >= 200 && xhr.status <= 207)) {
                var jsonData = Base64.decode(xhr.responseText);
                jsonData = JSON.parse(jsonData);

                if (GameConfig.IS_DEBUG) {
                    console.log("%c[HTTP RECEIVE][" + url + "]: " + new Date().toLocaleString(), "color:orange");
                    console.log(this.JSONstringify(jsonData));
                }

                this.onReponseStartAppConfig({data: jsonData, sendData: params});
            }
            if (xhr.status < 200 || xhr.status > 207) {
                this.alert.showAlert({
                    title: i18n.t('C0042'),
                    content: i18n.t('C0043'),
                    callback: this.onLoad.bind(this)
                });
            }
        }.bind(this);

        if (GameConfig.IS_DEBUG) {
            console.log("%c[HTTP SEND][" + url + "]: " + new Date().toLocaleString(), "color:orange");
            console.log(this.JSONstringify(params));

        }
        xhr.setRequestHeader("Content-Type", "application/x-www-form-urlencoded");
        xhr.setRequestHeader("Accept", "application/json");
        if (params) {
            var urlParams = Object.keys(params).map(function (key) {
                return params[key];
            }).join('|');
            urlParams = "dt=" + Base64.encode(urlParams);
            xhr.send(urlParams);

        } else {
            xhr.send();
        }
    },

    JSONstringify: function (json) {
        if (typeof json != 'string') {
            json = JSON.stringify(json, undefined, '\t');
        }

        var
            arr = [],
            _string = 'color:green',
            _number = 'color:darkorange',
            _boolean = 'color:blue',
            _null = 'color:magenta',
            _key = 'color:white';

        json = json.replace(/("(\\u[a-zA-Z0-9]{4}|\\[^u]|[^\\"])*"(\s*:)?|\b(true|false|null)\b|-?\d+(?:\.\d*)?(?:[eE][+\-]?\d+)?)/g, function (match) {
            var style = _number;
            if (/^"/.test(match)) {
                if (/:$/.test(match)) {
                    style = _key;
                } else {
                    style = _string;
                }
            } else if (/true|false/.test(match)) {
                style = _boolean;
            } else if (/null/.test(match)) {
                style = _null;
            }
            arr.push(style);
            arr.push('');
            return '%c' + match + '%c';
        });

        arr.unshift(json);

        console.log.apply(console, arr);
    },
});
