var BaseScene = require('BaseScene');
var ListJackpotSceneMediator = require('ListJackpotSceneMediator');
var Utility = require('Utility');
var Runnin2gLabel = require('Runnin2gLabel');
var CustomAction = require('CustomAction');

cc.Class({
    extends: BaseScene,

    ctor: function () {
        this.logic = null;
        this.itemGameList = [];
        this.isFirstRequest = true;
        this.timeDelay = 5;
        this.mucCuoc = 100;
    },

    properties: {
        listItem: cc.Node,
        itemGame: cc.Node,
        iconMinigame: [cc.SpriteFrame]
    },

    onLoad: function () {
        ListJackpotSceneMediator.getInstance.init(this);
        this.scroll = this.node.getChildByName("scrollview");
        this.listItem.removeAllChildren();

        this.hide();
        this.schedule(this.requestUpdate.bind(this), this.timeDelay, cc.macro.REPEAT_FOREVER, 0);
    },

    requestUpdate: function () {
        if (!this.isShow || !this.node.active) {
            return;
        }
        else {
            this.onSendGetListJackpot();
        }
    },

    show: function () {
        this.isShow = true;
        BaseScene.prototype.show.call(this);
        this.onSendGetListJackpot({mucCuoc: 100});

        this.node.stopAllActions();
        // this.node.active = true;
        TweenLite.to(this.node,0.3, {scaleY: 1});
    },

    hide: function () {
        this.isShow = false;
        this.node.stopAllActions();
        // this.node.active = false;
        TweenLite.to(this.node,0.3, {scaleY: 0});

    },

    showHide: function () {
        if (this.isShow) {
            this.hide();
        }
        else {
            this.show();
        }
    },

    updateListJackpot: function () {
        for (var i = 0; i < this.logic.listJackpot.length; i++) {

            var goalValue = 0;
            if (this.mucCuoc === 100) {
                goalValue = this.logic.listJackpot[i].jp100;
            }
            else if (this.mucCuoc === 1000) {
                goalValue = this.logic.listJackpot[i].jp1000;
            }
            else if (this.mucCuoc === 10000) {
                goalValue = this.logic.listJackpot[i].jp10000;
            }

            if (!this.itemGameList[i] || (this.itemGameList[i].code !== this.logic.listJackpot[i].code)) {
                if (this.itemGameList[i]) {
                    this.itemGameList[i].removeFromParent();
                    this.itemGameList[i] = null;
                }

                var item = cc.instantiate(this.itemGame);
                item.getComponent(CustomAction).updateCode(this.logic.listJackpot[i].code);
                item.getChildByName("lbJackpot").getComponent(Runnin2gLabel).string = Utility.formatCurrency(this.logic.listJackpot[i].jackpot);
                if(this.logic.listJackpot[i].act === "0x0024") {
                    item.getChildByName("iconGame").getComponent(cc.Sprite).spriteFrame = this.iconMinigame[0];
                }
                else if(this.logic.listJackpot[i].act === "0x0018"){
                    item.getChildByName("iconGame").getComponent(cc.Sprite).spriteFrame = this.iconMinigame[1];
                }

                var node = item.getChildByName("lbJackpot");

                Utility.tweenRunNumber(node, goalValue, 2);
                item.lastValue = this.logic.listJackpot[i].jackpot;
                switch (this.logic.listJackpot[i].gameName) {
                    case "tamquoc":
                        item.getChildByName("lbTitleGame").getComponent(cc.Label).string = 'Tam quốc';
                        break;
                    case "songoku":
                        item.getChildByName("lbTitleGame").getComponent(cc.Label).string = 'Nhà hàng';
                        break;
                    case "slotbom":
                        item.getChildByName("lbTitleGame").getComponent(cc.Label).string = 'Bom tấn';
                        break;
                    case "aicap":
                        item.getChildByName("lbTitleGame").getComponent(cc.Label).string = 'Thần đèn';
                        break;
                    default:
                        item.getChildByName("lbTitleGame").getComponent(cc.Label).string = this.logic.listJackpot[i].gameName;
                        break;

                }

                item.code = this.logic.listJackpot[i].code;
                this.itemGameList[i] = item;
                this.listItem.addChild(item);
            }
            else {
                var node = this.itemGameList[i].getChildByName("lbJackpot");
                Utility.tweenRunNumber(node, goalValue, 2);
            }
        }

        this.isFirstRequest = false;
    },

    activeBtnRoom1: function () {
        this.mucCuoc = 100;
        this.onClickBtnRoom();
    },

    activeBtnRoom2: function () {
        this.mucCuoc = 1000;
        this.onClickBtnRoom();
    },

    activeBtnRoom3: function () {
        this.mucCuoc = 10000;
        this.onClickBtnRoom();
    }
});

