var BaseCommand = require('BaseCommand');
var LobbyMessage = require('LobbyMessage');
var ListJackpotProxy = require('ListJackpotProxy');

export default class ReceiveGetListJackpotCommand extends BaseCommand {
    execute(notification) {
        BaseCommand.prototype.execute.call(this, notification);
        if (this.isError(notification.getBody().data)) return;
        var params = notification.getBody().data;
        var configProxy = this.facade.retrieveProxy('ConfigProxy');
        var listJackpotProxy = this.facade.retrieveProxy(ListJackpotProxy.NAME);
        listJackpotProxy.update(params);
        configProxy.updateConfigDataList(params.dataList);
        this.sendNotification(LobbyMessage.ON_UPDATE_LIST_JACKPOT);
    }
}