var BauCuaVO = require('BauCuaVO');
var Utility = require('Utility');
var i18n = require('i18n');
var LocalStorage = require('LocalStorage');
cc.Class({
    extends: cc.Component,

    properties: {
        toggleNan: cc.Toggle,
        iconDice: [cc.SpriteFrame],
        iconCau: [cc.SpriteFrame],
        resultDice: [cc.Sprite],
        itemCau: cc.Node,
        listCau: cc.Node
    },

    onLoad () {
        this.gameState = null;
        this.gate = [];
        this.isBetAllow = false;

        this.bettingPanel = this.node.getChildByName("BettingPanel");
        this.fixedBetPanel = this.bettingPanel.getChildByName("FixedBetPanel");
        this.unfixedBetPanel = this.bettingPanel.getChildByName("UnfixedBetPanel");

        this.resultPanel = this.node.getChildByName("ResultPanel");
        this.showScorePanel = this.resultPanel.getChildByName("showScore");
        this.diceAnim = this.resultPanel.getChildByName("diceAnim");

        this.bowl = this.node.getChildByName("bowl");
        this.bowl.active = false;
        this.lbSession = this.node.getChildByName("lbSession").getComponent(cc.Label);

        this.lbTime = this.node.getChildByName("lbTime").getComponent(cc.Label);
        this.lbTime.string = '';
        this.countdownWaiting = this.node.getChildByName("countdownbox");
        this.lbTimeLeft = this.countdownWaiting.getChildByName("lbTimeLeft").getComponent(cc.Label);
        this.messagePanel = this.node.getChildByName("messagePanel");
        this.lbMessage = this.messagePanel.getChildByName("lbMessage").getComponent(cc.Label);
        this.soiCauPanel = this.node.getChildByName("SoiCauPanel");
        this.bettingPanel.active = false;

        var gatePanel = this.node.getChildByName("GatePanel");
        for (var i = 1; i <= 6; i++) {
            this.gate[i-1] = gatePanel.getChildByName("Gate" + i);
            this.gate[i-1].spine = this.gate[i-1].getChildByName('mask').getChildByName('spine').getComponent(sp.Skeleton);
        }
        this.resetGame();
    },

    buildUI() {
    },

    onOpenSoicau: function () {
        if (this.soiCauPanel.scale === 1)
            this.soiCauPanel.runAction(cc.scaleTo(0.2,0,1));
        if (this.soiCauPanel.scaleX === 0)
            this.soiCauPanel.runAction(cc.scaleTo(0.2,1,1));
    },

    onUpdateTimer: function(time) {
        this.lbTimeLeft.string = (time < 10) ? "00:0" + time : "00:" + time;
        // this.lbTime.string = (time < 10) ? "00:0" + time : "00:" + time;
        // this.lbScore.string =  time;
    },

    onUpdateGameState: function(gameState, session) {
        // var time = new Date() / 1000;
        if (session) this.session = session;
        this.lbSession.string = "#" + this.session;
        switch (gameState) {
            case "bet":
                this.resetGame();
                this.enableBetting(true);
                this.onShowMessage(i18n.t("T0009"));
                break;

            case "repay":
                this.enableBetting(false);
                this.onShowMessage(i18n.t("T0010"));
                // this.lbTime.node.color = cc.Color.RED;
                break;

            case "waiting":
                // this.enableBetting(false);
                // this.resultPanel.active = true;
                // this.lbScore.node.active = true;
                // this.imgScore.active = true;
                // this.lbTime.node.active = false;
                // this.countdownWaiting.active = false;
                // this.isNan = this.toggleNan.isChecked;
                break;
            case "preGameOver":
                this.enableBetting(false);
                this.onShowMessage(i18n.t("T0010"));
                this.countPreGameOver();
                break;
            case "gameOver":
                this.enableBetting(false);
                this.resultPanel.active = true;
                // this.lbScore.node.active = true;
                // this.imgScore.active = true;
                this.lbTime.node.active = false;
                // this.animCounting.node.active = false;
                // this.countdownWaiting.active = false;
                this.isNan = this.toggleNan.isChecked;
                break;
            default:
        }
        this.gameState = gameState;

    },

    countPreGameOver: function () {
        this.timePreGame = 5;
        // this.animCounting.node.active = true;
        // this.animCounting.setAnimation(0, 'idle-fast', true);
        // this.lbTime.node.color = cc.Color.RED;
        this.lbTime.string = "0" + this.timePreGame;
        this.timePre = setInterval(function () {
            this.timePreGame--;
            this.lbTime.string = "0" + this.timePreGame;
            if (this.timePreGame === 0)
                clearInterval(this.timePre);
        }.bind(this), 1000);
    },

    onBetBauCua: function (data) {
            // this.lbPlayerBetTai.string = Utility.formatCurrency(data.value + parseInt(this.lbPlayerBetTai.string.replace(/\./g, "")));

        this.gate[data.index].bet = this.gate[data.index].bet + data.value;
        this.gate[data.index].getChildByName('txtAlreadyBet').getComponent(cc.Label).string = Utility.formatCurrency2(this.gate[data.index].bet);
        this.gate[data.index].getChildByName('bc_dat_so').active = true;

        var betData = [];
        for (var i = 0; i < this.gate.length; i++) {
            betData.push(this.gate[i].bet);
        }
        LocalStorage.setBetDataBauCua({session: this.session, betData: betData});
    },

    onShowMessage: function (message) {
        this.messagePanel.stopAllActions();
        this.messagePanel.opacity = 255;
        this.messagePanel.scale = 1;
        if (this.bettingPanel.active)
            this.messagePanel.y = -212;
        else
            this.messagePanel.y = -56;
        this.lbMessage.string = message;

        var callFunc = cc.callFunc(function() {
            TweenLite.from(this.messagePanel, 0.2, {scaleX: 0, scaleY: 0});
        }.bind(this));

        this.messagePanel.runAction(cc.sequence(callFunc , cc.delayTime(4) , cc.fadeOut(0.5)));
    },

    onUpdatePlayingInfo: function (data) {
        // this.session = data.session;
        // this.lbSession.string = "#" + this.session;

        for (var i = 0; i < 6; i++) {
            if (data.betting[i] !== '0') {
                this.gate[i].getChildByName('bc_dat_so').active = true;
                this.gate[i].getChildByName('txtAlreadyBet').getComponent(cc.Label).string = Utility.formatCurrency(data.betting[i]);
                this.gate[i].bet = parseInt(data.betting[i]);
            }
        }
        // if (data.history.length > 100)
        //     this.chainDataTotal = data.history.slice(-100);
        // else
        //     this.chainDataTotal = data.history;

        // this.onUpdateGameState(data.gameState, data.session);
        // this.onUpdateHistory();
        // this.effectUpdateBoardBet(this.lbTotalTai.node);
        // this.effectUpdateBoardBet(this.lbTotalXiu.node);
    },

    updateSoiCau: function (data) {
        var arr = Utility.convertSFSArray(data.cau);
        this.listCau.removeAllChildren();
        for (var i = arr.length - 1; i >= 0 ; i--) {
            var item = cc.instantiate(this.itemCau);
            for (var j = 0; j < 3; j++) {
                item.getChildByName('icon_' + (j+1)).getComponent(cc.Sprite).spriteFrame = this.iconCau[parseInt(arr[i][j])];
            }
            this.listCau.addChild(item);
        }
    },

    onUpdateBoardBet: function (session, betting, time) {
        this.session = session;
        this.lbSession.string = "#" + this.session;

        for (let i = 0; i < 6; i++) {
            this.gate[i].getChildByName('txtTotalBet').getComponent(cc.Label).string = Utility.formatCurrency(betting[i]);
        }
        if (time > 0 && !this.isBetAllow) {
            this.isBetAllow = true;
            this.lbTime.node.active = true;
            for (let i = 0; i < 6; i++){
                this.gate[i].getChildByName('txtMyBet').active = true;
            }
        }
        if (time <=0 && this.isBetAllow) {
            for (let i = 0; i < 6; i++){
                this.gate[i].getChildByName('bc_dat_on').active = false;
                if (this.gate[i].bet === 0) {
                    this.gate[i].getChildByName('bc_dat_so').active = false;
                    this.gate[i].getChildByName('txtMyBet').active = false;
                }
            }
            this.isBetAllow = false;
            this.bettingPanel.runAction(cc.scaleTo(0.2,1,0));
        }

        if(time < 10) {
            this.lbTime.string = "0" + time;
        }
        else {
            this.lbTime.string = "" + time;
        }

    },

    selectGate: function (event, index) {
        if (!this.isBetAllow) {
            this.onShowMessage('Chưa đến phiên đặt');
            return;
        }

        if (event.target == this.gateSelected && this.bettingPanel.active) return;
        index = parseInt(index);
        if (this.gateSelected) {
            if (this.gateSelected.bet > 0) {
                this.gateSelected.getChildByName('txtAlreadyBet').active = true;
            }
            else
                this.gateSelected.getChildByName('bc_dat_so').active = false;

            this.gateSelected.getChildByName('txtMyBet').getComponent(cc.Label).string = '';
        }
        this.gateSelected = this.gate[index];
        this.index = index;
        this.gateSelected.getChildByName('bc_dat_so').active = true;
        this.gateSelected.getChildByName('txtMyBet').getComponent(cc.Label).string = '0';
        this.gateSelected.getChildByName('txtAlreadyBet').active = false;

        this.bettingPanel.active = true;
        this.bettingPanel.scaleY = 0;
        this.bettingPanel.runAction(cc.scaleTo(0.2,1,1));
        this.fixedBetPanel.active = true;
        this.unfixedBetPanel.active = false;
        this.betValue = 0;
    },

    ////////////////////////////////////////////////////////////////////////////
    onHandlerTouchSoKhac: function() {
        this.fixedBetPanel.active = !this.fixedBetPanel.active;
        this.unfixedBetPanel.active = !this.unfixedBetPanel.active;
        this.betValue = 0;
        this.gateSelected.getChildByName('txtMyBet').getComponent(cc.Label).string = '';
        this.gateSelected.getChildByName('txtAlreadyBet').active = true;
    },

    onHandlerTouchBetOk: function() {
        this.bettingPanel.active = false;
        if(this.betValue !== 0 && this.index !== undefined) {
            var params = {};
            params.type = 0;
            params.bet = this.betValue;
            params.index = this.index;
            this.node.emit('ACTIVE_SEND_BET', params);
        }
        // this.lbBetValueTai.string = "0";
        // this.lbBetValueXiu.string = "0";
        // this.editBoxTai.string = "";
        // this.editBoxXiu.string = "";
        this.betValue = 0;
        this.gateSelected.getChildByName('txtMyBet').getComponent(cc.Label).string = '';
        this.gateSelected.getChildByName('txtAlreadyBet').active = true;
        this.gateSelected.getChildByName('bc_dat_so').active = (this.gateSelected.bet > 0);
    },
    onHandlerTouchBetCancel: function() {
        this.bettingPanel.active = false;
        this.gateSelected.getChildByName('txtMyBet').getComponent(cc.Label).string = '';
        if (this.gateSelected.bet > 0) {
            this.gateSelected.getChildByName('txtAlreadyBet').active = true;
        }
        else
            this.gateSelected.getChildByName('bc_dat_so').active = false;
    },

    onHandlerBettingEvent: function(event) {
        var number = event.target._name;
        if (this.fixedBetPanel.active) {
            this.betValue = this.betValue + parseInt(number);
        }
        else {
            if(this.betValue === 0 && (number === "0" || number === "000" || number === "btnRemove"))
                return;
            if(number === "000")
                this.betValue = this.betValue * 1000;
            else if(number === "btnRemove")
                this.betValue = Math.floor(this.betValue/10);
            else
                this.betValue = this.betValue * 10 +  parseInt(number);
        }
        if(this.betValue > 100000000)
            this.betValue = 100000000;

        this.gateSelected.getChildByName('txtMyBet').getComponent(cc.Label).string = Utility.formatCurrency2(this.betValue);
    },

    onHandlerDatLai: function(event, type) {
        var betData = LocalStorage.getBetDataBauCua();
        if (!betData || (this.session !== betData.session + 1)) {
            this.onShowMessage('Không có dữ liệu phiên trước');
        }
        else {
            var params = {};
            params.type = parseInt(type);
            params.bet = betData.betData[i];
            this.node.emit('ACTIVE_SEND_BET', params);
        }
    },

    onHandlerRankBtn: function() {
        this.node.emit('ACTIVE_RANK');
    },

    onHandlerMyHistoryBtn: function() {
        this.node.emit('ACTIVE_HISTORY');
    },

    onHandlerGuideBtn: function() {
        this.node.emit('ACTIVE_GUIDE');
    },

    onHandlerEventBtn: function() {
        var params = {};
        // params.date = new Date();
        // params.isWin = 1;
        this.root.onActiveEvent();
    },

    ////////////////////////////////////////////////////////////////////////////
    ////////////////////////////////////////////////////////////////////////////
    ////////////////////////////////////////////////////////////////////////////

    onShowResultDice: function(result) {
        this.resultPanel.active = true;
        this.lbTime.node.active = false;
        this.diceAnim.active = true;
        this.diceAnim.getComponent(sp.Skeleton).setAnimation(0, 'active', false);

        TweenLite.delayedCall(2.5, function () {
            this.showResult(result);
        }.bind(this));
    },


    showResult: function (result) {

        this.result = result;
        this.showScorePanel.active = true;
        // this.lbScore.string = this.score;
        if(this.toggleNan.isChecked) {
            this.bowl.active = true;
        }
        else {
            this.showEffectResult(result);
        }
        this.timeLeft = 16;
        this.timeStick = setInterval(function () {
            this.onUpdateTimer(this.timeLeft);
            this.timeLeft--;
            // if (this.timeLeft <= 5 && !this.imgScore.active) {
            //     this.bowl.active = false;
            //     this.showEffectResult();
            // }
            // if (this.timeLeft === 1) {
            //     this.chainDataTotal.shift();
            //     var item = {};
            //     item.result = dices.dice1+","+dices.dice2+","+dices.dice3;
            //     item.id = this.session;
            //     this.chainDataTotal.push(item);
            //     this.root.onUpdateMyHistory(this.chainDataTotal);
            // }
            if (this.timeLeft === 0) {
                clearInterval(this.timeStick);
                this.resetGame();
            }
        }.bind(this), 1000);
    },

    showEffectResult: function () {
        if (!this.result)  return;
        this.countdownWaiting.active = true;
        for (var i = 0; i < 6; i++) {
            this.gate[i].getChildByName('bc_circle_off').active = true;
            this.gate[i].getChildByName('bc_circle_on').active = false;
            this.gate[i].spine.paused = true;
        }
        for (var j = 0; j < 3; j++) {
            this.resultDice[j].spriteFrame = this.iconDice[parseInt(this.result[j])];
            this.gate[parseInt(this.result[j])].getChildByName('bc_eff').active = true;
            this.gate[parseInt(this.result[j])].getChildByName('bc_eff').getComponent('RotationObject').onStart();
            this.gate[parseInt(this.result[j])].getChildByName('bc_circle_off').active = false;
            this.gate[parseInt(this.result[j])].getChildByName('bc_circle_on').active = true;
            this.gate[parseInt(this.result[j])].spine.paused = false;
        }

    },

    runEffect: function (node) {
        var fadeIn = cc.fadeIn(0.15);
        var fadeOut = cc.fadeOut(0.15);
        node.runAction(cc.sequence(fadeIn, fadeOut).repeatForever());
    },

    effectUpdateBoardBet: function (node) {
        var callFunc1 = cc.callFunc(function() {
            TweenLite.to(node, 0.2, {scaleX: 1.2, scaleY: 1.2});
        }.bind(this));

        var callFunc2 = cc.callFunc(function() {
            TweenLite.to(node, 0.2, {scaleX: 1, scaleY: 1});
        }.bind(this));
        node.runAction(cc.sequence(callFunc1, cc.delayTime(0.2), callFunc2));
    },

    updateMoney: function (data) {
        var lbUpdateMoney = new cc.Node();
        lbUpdateMoney.addComponent(cc.Label);
        lbUpdateMoney.getComponent(cc.Label).fontSize = 40;

        if(data.plusMoney > 0) {
            lbUpdateMoney.color = cc.Color.GREEN;
            lbUpdateMoney.getComponent(cc.Label).string = '+' + Utility.formatCurrency(data.plusMoney);
        }

        this.node.addChild(lbUpdateMoney);
        lbUpdateMoney.x = 92;
        lbUpdateMoney.y = 83;
        var move = cc.moveTo(3,cc.v2(lbUpdateMoney.x,
            lbUpdateMoney.y+60));

        lbUpdateMoney.runAction(cc.sequence(move, cc.callFunc(function () {
            lbUpdateMoney.destroy();
        })));
    },

    resetGame: function () {
        this.countdownWaiting.active = false;
        // if (this.timeStick) clearInterval(this.timeStick);
        // if (this.timePre) clearInterval(this.timePre);
        // this.lbTime.node.color = cc.Color.BLACK;//cc.Color.BLUE;
        // this.lbScore.node.active = false;
        this.showScorePanel.active = false;
        this.resultPanel.active = false;
        this.bowl.active = false;
        this.node.emit('ACTIVE_SEND_SOI_CAU');

        for (var i = 0; i < 6; i++) {
            this.gate[i].getChildByName('txtTotalBet').getComponent(cc.Label).string = '0';
            this.gate[i].getChildByName('txtAlreadyBet').getComponent(cc.Label).string = '';
            this.gate[i].getChildByName('txtMyBet').getComponent(cc.Label).string = '';
            this.gate[i].getChildByName('bc_circle_off').active = false;
            this.gate[i].getChildByName('bc_circle_on').active = true;
            this.gate[i].getChildByName('bc_dat_on').active = true;
            // this.gate[i].getChildByName('bc_dat_on').active = true;
            this.gate[i].getChildByName('bc_dat_so').active = false;
            this.gate[i].getChildByName('bc_eff').active = false;
            this.gate[i].getChildByName('bc_eff').rotation = 0;
            this.gate[i].getChildByName('bc_eff').getComponent('RotationObject').onStop();
            this.gate[i].spine.paused = false;
            this.gate[i].bet = 0;
        }

    },

});
