var BaseGameCommand = require('BaseGameCommand');
var SlotMessage = require('SlotMessage');
var SlotFBProxy = require('SlotFBProxy');

export default class SetupSlotFBCommand extends BaseGameCommand {
  execute(notification) {
    BaseGameCommand.prototype.execute.call(this, notification);
    this.gameMediator =  notification.getBody().gameMediator;
    this.gameProxy = notification.getBody().gameProxy;
    SlotFBProxy.proxy = this.gameProxy;

    this.facade.registerCommand(SlotMessage.INIT_SLOT_FOOTBALL, require('InitSlotFBCommand'));
    this.facade.registerCommand(SlotMessage.SEND_SPIN_FOOTBALL, require('SendSpinFBCommand'));
    this.facade.registerCommand(SlotMessage.SEND_GET_RANK_FOOTBALL, require('SendGetRankFBCommand'));
    this.facade.registerCommand(SlotMessage.SEND_GET_MY_HISTORY_FOOTBALL, require('SendGetMyHistoryFBCommand'));
    this.facade.registerCommand(SlotMessage.SEND_LEAVE_ROOM_FOOTBALL, require('SendLeaveRoomFBCommand'));
    this.facade.registerCommand(SlotMessage.SEND_ORDER_SPIN_FOOTBALL, require('SendOrderSpinFBCommand'));

    this.facade.registerCommand(SlotMessage.RECEIVE_SPIN_FOOTBALL, require('ReceiveSpinFBCommand'));
    this.facade.registerCommand(SlotMessage.RECEIVE_UPDATE_JACKPOT_FOOTBALL, require('ReceiveUpdateJackPotFBCommand'));
    this.facade.registerCommand(SlotMessage.RECEIVE_GET_RANK_FOOTBALL, require('ReceiveGetRankFBCommand'));
    this.facade.registerCommand(SlotMessage.RECEIVE_GET_MY_HISTORY_FOOTBALL, require('ReceiveGetMyHistoryFBCommand'));
    this.facade.registerMediator(this.gameMediator);
  }
}
