var MiniPokerIcon = require('MiniPokerIcon');
var Utility = require('Utility');
var i18n = require('i18n');

cc.Class({
    extends: cc.Component,

    properties: {
      bgMiniPoker: cc.Node,
      menuTop: cc.Node,
      menuBottom: cc.Node,
      offy: 180,
      totalRow: 1,
      totalColl: 5,
      numItem: 52,
      lbJackPot: cc.Label,
      toggleAutoSpin: cc.Toggle,
      toggleFastSpin: cc.Toggle,
      groupBet:cc.Node,
      selectToggle:cc.Node,
      selectTypeToggle:cc.Node,
      wheelsNode: cc.Node,
      wheelsX3Node: cc.Node,
      wheels: [cc.Node],
      wheelsX3: [cc.Node],
      cardAtlas: cc.SpriteAtlas,
      btnSpin:cc.Button,
      animSpin: sp.Skeleton,
    },
    onLoad () {
      this.totalItem = 30;
      this.matrixId = [];
      this.matrixX3Id = [];
      this.matrixItem = [];
      this.matrixX3Item = [];
      this.posX = 0;
      this.posY = 0;
      this.timeSpin = 1;
      this.posRow = 0;
      this.isAutoSpin = false;
      this.resultItems = [];
      this.onSpinComplete = null;
      this.onSpinCompleteX3 = null;
      this.initMatrix();
      this.initMatrixX3();
    },

    buildUI(gameVO) {
      this.gameVO = gameVO;
      this.btnSpin.interactable = true;
    },

    onHandlerChangeStyle(target) {
        if (this.checkAutoSpin()) {
            this.selectTypeToggle.getComponent(cc.Toggle).check();
            return;
        }
        if (target.node.name === 'x1') {
            if (this.gameVO.spinType === 1) return;
            this.gameVO.spinType = 1;
            this.bgMiniPoker.getChildByName('bgx1').active = true;
            this.bgMiniPoker.getChildByName('bgx3').active = false;
            this.wheelsNode.active = true;
            this.wheelsX3Node.active = false;
            this.menuTop.y = 0;
            this.menuBottom.y = 0;
            this.groupBet.getChildByName('selectBet1').getChildByName('100').getComponent(cc.Label).string = '100';
            this.groupBet.getChildByName('selectBet2').getChildByName('1000').getComponent(cc.Label).string = '1K';
            this.groupBet.getChildByName('selectBet3').getChildByName('10000').getComponent(cc.Label).string = '10K';
        }
        else {
            if (this.gameVO.spinType === 3) return;
            this.gameVO.spinType = 3;
            this.bgMiniPoker.getChildByName('bgx1').active = false;
            this.bgMiniPoker.getChildByName('bgx3').active = true;
            this.wheelsNode.active = false;
            this.wheelsX3Node.active = true;
            this.menuTop.y = 95;
            this.menuBottom.y = -82;
            this.groupBet.getChildByName('selectBet1').getChildByName('100').getComponent(cc.Label).string = '300';
            this.groupBet.getChildByName('selectBet2').getChildByName('1000').getComponent(cc.Label).string = '3K';
            this.groupBet.getChildByName('selectBet3').getChildByName('10000').getComponent(cc.Label).string = '30K';
        }
        this.selectTypeToggle = target;
    },

    onHandlerSpin() {
      if(this.checkAutoSpin()) return;
        this.timeSpin = 1;
        this.node.emit('ACTIVE_SEND_SPIN');
        this.btnSpin.interactable = false;
    },

    resetBtnSpin() {
        this.btnSpin.interactable = true;
    },

    onHandlerAutoSpin() {
        this.gameVO.autoSpin = this.toggleAutoSpin.isChecked;
        if(this.gameVO.autoSpin === true) {
            this.node.emit('ACTIVE_SEND_SPIN');
        }
    },

    resetButtonAutoSpin () {
        this.toggleAutoSpin.uncheck();
    },

    onHandlerSieuToc() {
        this.timeSpin = (this.toggleFastSpin.isChecked === true) ? 0.5 : 1;
    },

    onHandlerFastSpin() {
        if(this.checkAutoSpin()) return;
        this.timeSpin = 0.3;
        this.node.emit('ACTIVE_SEND_SPIN');
    },

    onHandlerLeaveGame() {
        this.toggleAutoSpin.uncheck();
        this.toggleFastSpin.uncheck();
        this.node.emit('ACTIVE_LEAVE_GAME');
    },

    onHandlerRank() {
      this.node.emit('ACTIVE_RANK');
    },

    onHandlerHistory() {
      this.node.emit('ACTIVE_HISTORY');
    },

    onHandlerGuide() {
      this.node.emit('ACTIVE_GUIDE');
    },

    onHanderChooseBet(event, currentBetIndex) {
      if(event === this.selectToggle) return;
      if(this.checkAutoSpin()) {
        this.selectToggle.getComponent(cc.Toggle).check();
        return;
      }
      this.gameVO.updateCurrentBetIndex(parseInt(currentBetIndex));
      this.selectToggle = event;
      this.onUpdateJackpot(this.gameVO.getCurrentJackpot())
    },

    onSpinAndResult(data) {
      this.onSpinComplete = data.onComplete;
      // TweenMax.killAll(false, false, true);
      var items = data.items;
      var result = [];
      result.matrix = [];
      for(var i = 0; i < items.length; i+=this.totalColl) {
        var arr = [];
        for(var j = 0; j < this.totalColl; j++) {
          var itemId = parseInt(items[i + j]);
          arr.push(itemId);
        }
        result.matrix.unshift(arr);
      }
      this.setResult(result);
      this.runSpin();
      this.animSpin.setAnimation(0,'active', false);
    },

    onSpinAndResultX3(data) {
        this.onSpinCompleteX3 = data.onComplete;
        // TweenMax.killAll(false, false, true);
        var items = data.items;
        var result = [];
        result.matrix = [];
        for(var i = 0; i < items.length; i++) {
            var arr = [];
            for(var j = 0; j < this.totalColl; j++) {
                var itemId = parseInt(items[i][j]);
                arr.push(itemId);
            }
            result.matrix.push(arr);
        }
        this.setResultX3(result);
        this.runSpinX3();
        this.animSpin.setAnimation(0,'active', false);
    },

    onSetAutoSpin(autoSpin) {
      if(autoSpin){
        this.toggleAutoSpin.check();
      }else{
        this.toggleAutoSpin.uncheck();
        this.toggleAutoSpin.uncheck();
      }
    },

    onUpdateJackpot(roomJackPot) {
        Utility.tweenRunNumber(this.lbJackPot.node, roomJackPot, 1.5);
    },

    //////////////////////////////////////////////////////////////////
    //////////////////////////////////////////////////////////////////
    //////////////////////////////////////////////////////////////////

    setResult(result){
      for(var i = 1; i < this.totalItem; i++){
          for(var j = 0; j < this.totalColl; j++){
              if(i >= this.totalItem - 1) {
                  this.matrixId[i][j] = result.matrix[i - this.totalItem + 1][j];
              } else {
                  var itemId = Utility.getRandomBetween(1, this.numItem);
                  this.matrixId[i][j] = itemId;
              }
          }
      }
      this.resultItems = [];
      for(var i = 1; i < this.totalItem; i++){
          for(j = 0; j < this.totalColl; j++){
              var itemId = this.matrixId[i][j];
              var item = this.matrixItem[(i - this.posRow + this.totalItem) % this.totalItem][j];
              item.sprite.spriteFrame = this.getCardTexture(itemId);
              item.itemId = itemId;
              if(i >= this.totalItem - 1) {
                this.resultItems.push(item);
              }
          }
      }
    },

    setResultX3: function (result) {
        for(var i = 3; i < this.totalItem; i++){
            for(var j = 0; j < this.totalColl; j++){
                if(i >= this.totalItem - 3) {
                    this.matrixX3Id[i][j] = result.matrix[i - this.totalItem + 3][j];
                } else {
                    var itemId = Utility.getRandomBetween(1, this.numItem);
                    this.matrixX3Id[i][j] = itemId;
                }
            }
        }
        this.resultItems = [];
        for(var i = 3; i < this.totalItem; i++){
            for(j = 0; j < this.totalColl; j++){
                var itemId = this.matrixX3Id[i][j];
                var item = this.matrixX3Item[(i - this.posRow + this.totalItem) % this.totalItem][j];
                item.sprite.spriteFrame = this.getCardTexture(itemId);
                item.itemId = itemId;
                if(i >= this.totalItem - 3) {
                    this.resultItems.push(item);
                }
            }
        }
    },

    runSpin: function() {
      this.totalRow = 1;
      for(var j = 0; j < 5; j++){
          for(var i = 0; i < this.totalItem; i++){
              var item = this.matrixItem[i][j];
              var actionBy = cc.moveBy(1.6 * this.timeSpin, 0, -this.offy * (this.totalItem - 1));
              var data = {};
              data.item = item;
              data.i =  i;
              data.posRow = this.posRow;
              var callBack = function(sender, data){
                  data.item.y = this.posY + ((data.i + data.posRow + this.totalRow + this.totalItem) % this.totalItem) * this.offy - 20;
              }.bind(this);

              item.runAction(cc.sequence(cc.delayTime(0.2 * this.timeSpin * j), actionBy.easing(cc.easeIn(1.5))
          , cc.callFunc(callBack, this, data), cc.moveBy(0.3,cc.v2(0,20))));
          }
      }
      this.node.runAction(cc.sequence(cc.delayTime(2 * this.timeSpin), cc.callFunc(this.runSpinComplete, this)));
      this.posRow = (this.posRow + this.totalRow + this.totalItem) % this.totalItem;
    },

    runSpinX3: function () {
        this.totalRow = 3;
        for(var j = 0; j < 5; j++){
            for(var i = 0; i < this.totalItem; i++){
                var item = this.matrixX3Item[i][j];
                var actionBy = cc.moveBy(1.6 * this.timeSpin, 0, -this.offy * (this.totalItem - 3));
                var data = {};
                data.item = item;
                data.i =  i;
                data.posRow = this.posRow;
                var callBack = function(sender, data){
                    data.item.y = this.posY + ((data.i + data.posRow + this.totalRow + this.totalItem) % this.totalItem) * this.offy - 20;
                }.bind(this);

                item.runAction(cc.sequence(cc.delayTime(0.2 * this.timeSpin * j), actionBy.easing(cc.easeIn(1.5))
                    , cc.callFunc(callBack, this, data), cc.moveBy(0.3,cc.v2(0,20))));
            }
        }
        this.node.runAction(cc.sequence(cc.delayTime(2 * this.timeSpin), cc.callFunc(this.runSpinCompleteX3, this)));
        this.posRow = (this.posRow + this.totalRow + this.totalItem) % this.totalItem;
    },

    runSpinComplete: function(){
      console.log('GamePanelMiniPoker ---- runSpinComplete' );

      this.isSpining = false;
      if(this.onSpinComplete !== null) {
        this.onSpinComplete.call();
        this.onSpinComplete = null;
      }
    },

    runSpinCompleteX3: function() {
        this.isSpining = false;
        if(this.onSpinCompleteX3 !== null) {
            this.onSpinCompleteX3.call();
            this.onSpinCompleteX3 = null;
        }
    },

    initMatrix() {
      var rect = this.getCardTexture(0);
      for(var i =0; i < this.totalItem; i++){
          this.matrixId.push([]);
          for(var j = 0; j < this.totalColl; j++){
              var random = Utility.getRandomBetween(0, this.numItem);
              this.matrixId[i].push(random);
          }
      }

      for(var i =0; i < this.totalItem; i++){
          this.matrixItem.push([]);
          for(var j = 0; j < this.totalColl; j++){
              var itemId =  this.matrixId[i][j];
              var node = MiniPokerIcon.create(itemId, this.getCardTexture(itemId));
              // node.scale = 0.8;
              node.setAnchorPoint(0.5, 0);
              this.matrixItem[i].push(node);
              this.wheels[j].addChild(node);
              node.x = this.posX + this.wheels[j].width * 0.5;
              node.y = this.posY + i * this.offy;
          }
      }

      for(var j = 0; j < this.totalColl; j++){
        this.wheels[j].x += this.wheels[j].width * 0.5;
        this.wheels[j].y += rect.height * 0.5;
      }
    },

    initMatrixX3() {
        var rect = this.getCardTexture(0);
        for(var i =0; i < this.totalItem; i++){
            this.matrixX3Id.push([]);
            for(var j = 0; j < this.totalColl; j++){
                var random = Utility.getRandomBetween(0, this.numItem);
                this.matrixX3Id[i].push(random);
            }
        }

        for(var i =0; i < this.totalItem; i++){
            this.matrixX3Item.push([]);
            for(var j = 0; j < this.totalColl; j++){
                var itemId =  this.matrixX3Id[i][j];
                var node = MiniPokerIcon.create(itemId, this.getCardTexture(itemId));
                // node.scale = 0.8;
                node.setAnchorPoint(0.5, 0);
                this.matrixX3Item[i].push(node);
                this.wheelsX3[j].addChild(node);
                node.x = this.posX + this.wheelsX3[j].width * 0.5;
                node.y = this.posY + i * this.offy;
            }
        }

        for(var j = 0; j < this.totalColl; j++){
            this.wheelsX3[j].x += this.wheelsX3[j].width * 0.5;
            this.wheelsX3[j].y += rect.height * 0.5;
        }
    },

    checkAutoSpin() {
      if(this.gameVO.autoSpin) {
        this.node.emit('ACTIVE_SHOW_MESSAGE', {message: i18n.t("T0013")});
        return true;
      }
      return false;
    },

    getCardTexture: function(cardId) {
      var name = (cardId < 10) ? "cards_0" + cardId : "cards_" + cardId;
      var texture = this.cardAtlas.getSpriteFrame(name);
      return texture;
    },

});
