var BaseMediator = require('BaseMediator');
var LobbyMessage = require('LobbyMessage');
var GameMessage = require('GameMessage');
var UserProxy = require('UserProxy');
var Constants = require('Constants');
var CustomAction = require('CustomAction');
var MiniGameMessage = require('MiniGameMessage');
var LocalStorage = require('LocalStorage');

class ChatTaiXiuMediator extends BaseMediator {
    static get NAME() {
        return 'ChatTaiXiuMediator';
    }

    static get getInstance() {
        if (this.instance === undefined) {
            this.instance = new ChatTaiXiuMediator();
        }
        return this.instance;
    }

    onRegister() {
        this.dataUser = this.facade.retrieveProxy(UserProxy.NAME);
        this.fbProxy = this.facade.retrieveProxy('FacebookProxy');
        this.event = this.facade.retrieveProxy('EventProxy');
    }

    /** @override */
    listNotificationInterests() {
        return [
            LobbyMessage.SHOW_TOP_MENU,
            LobbyMessage.HIDE_TOP_MENU,
            LobbyMessage.ON_UPDATE_MY_INFO,
            LobbyMessage.SHOW_CHANNEL_SCENE,
            LobbyMessage.ON_UPDATE_CHANGE_AVATAR
        ];
    }

    /** @override */
    handleNotification(notification) {
        switch (notification.getName()) {
            case LobbyMessage.SHOW_TOP_MENU:
                this.view.show();
                this.view.onUpdateUserInfo(this.dataUser.mySelf);
                break;
            case LobbyMessage.HIDE_TOP_MENU:
                this.view.hide();
                break;
            case LobbyMessage.SHOW_CHANNEL_SCENE:
                this.view.show();
                break;
            case LobbyMessage.ON_UPDATE_MY_INFO:
                this.view.onUpdateUserInfo(this.dataUser.mySelf);
                break;
            case LobbyMessage.ON_UPDATE_CHANGE_AVATAR:
                this.view.onUpdateChangeAvatar(this.dataUser.mySelf);
                break;
            default:
                break;
        }
    }

    addHanlers() {
        this.view.activeBack = this.activeBack.bind(this);
        this.view.activeShowEvent = this.activeShowEvent.bind(this);
        this.view.activeShowGift = this.activeShowGift.bind(this);
        this.view.activeShowGiftCode = this.activeShowGiftCode.bind(this);
        this.view.activeShowShop = this.activeShowShop.bind(this);
        this.view.activeShowTransfer = this.activeShowTransfer.bind(this);
        this.view.activeShowRecharge = this.activeShowRecharge.bind(this);
        this.view.activeShowSetting = this.activeShowSetting.bind(this);
        this.view.activeShowMail = this.activeShowMail.bind(this);
        this.view.activeShowAgent = this.activeShowAgent.bind(this);
        this.view.activeShowUserProfile = this.activeShowUserProfile.bind(this);
        this.view.activeDailyEvent = this.activeDailyEvent.bind(this);
        this.view.activeShowSupportMail = this.activeShowSupportMail.bind(this);
        this.view.activeShowFanpage = this.activeShowFanpage.bind(this);
        this.view.activeShowHelp = this.activeShowHelp.bind(this);
        this.view.activeShowSecurity = this.activeShowSecurity.bind(this);
        this.view.activeShowHistory = this.activeShowHistory.bind(this);
        this.view.activeShowVip = this.activeShowVip.bind(this);
        this.view.activeDailyMission = this.activeDailyMission.bind(this);

    }

    activeShowEvent() {
        this.sendNotification(LobbyMessage.SHOW_FLY_ALERT, {content: "Tính năng đang được phát triển !"});
        // this.sendNotification(LobbyMessage.SHOW_EVENT_SCENE);
    }

    activeShowGift() {
        this.sendNotification(LobbyMessage.SHOW_GIFT_SCENE);
    }

    activeShowGiftCode() {
        if (!this.isLogin("C0052")) {
            return;
        }
        this.sendNotification(LobbyMessage.SHOW_GIFTCODE_SCENE);
    }

    activeShowShop(tabId) {
        this.sendNotification(LobbyMessage.SHOW_SHOP_SCENE, tabId);
    }

    activeShowTransfer() {
        this.sendNotification(LobbyMessage.SHOW_TAB_IN_SHOP_SCENE, {tabId: CustomAction.ACTION_SHOP_TRANSFEDERS});
    }

    activeShowRecharge() {
        this.sendNotification(LobbyMessage.SHOW_RECHARGE_SCENE);
    }

    activeShowSetting() {
        this.sendNotification(LobbyMessage.SHOW_SETTING_SCENE);
    }

    activeShowMail() {
        if (!this.isLogin("C0052")) {
            return;
        }
        this.sendNotification(LobbyMessage.SHOW_USER_PROFILE_SCENE, {tabId: 2});
        // this.sendNotification(LobbyMessage.SEND_GET_MAIL_LIST);
    }

    activeShowSupportMail() {
        if (!this.isLogin("C0052")) {
            return;
        }
        this.sendNotification(LobbyMessage.SHOW_SUPPORT_MAIL_SCENE);
    }

    activeShowFanpage() {
        if (!this.isLogin("C0052")) {
            return;
        }
    }

    activeShowHelp() {
        if (!this.isLogin("C0052")) {
            return;
        }
    }

    activeShowUserProfile() {
        this.sendNotification(LobbyMessage.SHOW_USER_PROFILE_SCENE);
        this.sendNotification(LobbyMessage.SEND_GET_USER_INFO_DETAIL);
    }

    activeShowAgent() {
        this.sendNotification(LobbyMessage.SHOW_AGENT_SCENE);
    }

    activeDailyEvent() {
        if (this.event.eventDailyList.length !== 0)
            this.sendNotification(LobbyMessage.SHOW_STARTGAME_POPUP_SCENE);
    }

    activeShowSecurity() {
        this.sendNotification(LobbyMessage.SHOW_USER_PROFILE_SCENE, {tabId: 3});
    }

    activeShowHistory() {
        this.sendNotification(LobbyMessage.SHOW_HISTORY_TRANFER_SCENE);
    }

    activeShowVip() {
        this.sendNotification(LobbyMessage.SHOW_VIP_POINT_SCENE);
    }

    activeDailyMission () {
        this.sendNotification(LobbyMessage.SHOW_FLY_ALERT, {content: "Tính năng đang được phát triển !"});
    }

    activeBack() {
        switch (Constants.CURRENT_SCENE) {
            case Constants.GAME_SELECT:
                this.sendNotification(LobbyMessage.SHOW_LOGIN_SCENE);
                this.sendNotification(LobbyMessage.SHOW_SELECT_GAME_SCENE);
                this.sendNotification(LobbyMessage.SHOW_EVENT_BANNER_SCENE);
                this.sendNotification(LobbyMessage.HIDE_TOP_MENU);
                this.sendNotification(LobbyMessage.HIDE_BOTTOM_MENU);
                this.sendNotification(LobbyMessage.ON_RESET_MYSELF);
                this.sendNotification(LobbyMessage.UPDATE_LOGIN_SCENE, { userName: "", password: "" });
                this.sendNotification(MiniGameMessage.DISCONNECT_ALL_MINIGAME);
                LocalStorage.setIsSocial(-1);
                // LocalStorage.setAutoLoginNormal("0");
                this.fbProxy.logout();
                break;
            case Constants.GAME_CHANNEL:
                this.sendNotification(LobbyMessage.SHOW_SELECT_GAME_SCENE);
                this.sendNotification(LobbyMessage.SHOW_EVENT_BANNER_SCENE);
                this.sendNotification(LobbyMessage.HIDE_CHANNEL_SCENE);
                this.sendNotification(LobbyMessage.HIDE_CHAT_SCENE);
                this.sendNotification(LobbyMessage.SEND_LEAVE_CHANNEL);
                this.sendNotification(GameMessage.DESTROY_GAME);
                break;
        }
    }
}

module.exports = ChatTaiXiuMediator;
