var BaseCommand = require('BaseCommand');
var SlotAlaProxy = require('SlotAlaProxy');
var SlotMessage = require('SlotMessage');
var LobbyMessage = require('LobbyMessage');
var i18n = require('i18n');

export default class SendSpinAlaCommand extends BaseCommand {
  execute(notification) {
    BaseCommand.prototype.execute.call(this, notification);
    var params = notification.getBody();
    var gameVO = SlotAlaProxy.proxy.gameVO;

    if(gameVO.isChoiThu){
      this.sendNotification(SlotMessage.ON_HIDE_ALL_LINE_ALADIN);
      this.sendNotification(SlotMessage.ON_HIDE_WILD_ALADIN);
      gameVO.isSpining = true;
      gameVO.updateChoiThuData();
      TweenLite.delayedCall(0.2, function () {
        this.sendNotification(SlotMessage.RECEIVE_SPIN_ALADIN, {data: gameVO} );
      }.bind(this));
      return;
    }

    var lines = params.data.lines.slice (1 , params.data.lines.length - 1).split(',');
    var money = lines.length * params.data.bet;

    if (this.checkEnoughMoney(money)) {
      this.sendNotification(SlotMessage.ON_RESET_BUTTON_ALADIN);
      return;
    }

    if(gameVO.isSpining === true) return this.sendNotification(SlotMessage.ON_SHOW_MESSAGE_ALADIN, {message: i18n.t("T0012")});

    gameVO.isSpining = true;
    gameVO.prevDataSpin = params.data;
    if(gameVO.autoSpin && gameVO.curNumAutoSpin === 0) {
      gameVO.autoSpin = false;
      this.sendNotification(SlotMessage.ON_SET_AUTO_SPIN_ALADIN, {isAuto: false});
    }
    this.sendNotification(SlotMessage.ON_HIDE_ALL_LINE_ALADIN);

    this.gameProxy = SlotAlaProxy.proxy;
      // for(var i = 0; i < 100; i++) {
      //   this.gameProxy.sendSpin(params.data);
      // }
    var sendData = {
        lines: params.data.lines,
        bet: params.data.bet,
        gid: params.data.roomId
    };
    this.sendNotification(SlotMessage.ON_HIDE_WILD_ALADIN);
    this.gameProxy.sendSpin(sendData);

    var session = gameVO.session;

    setTimeout(function(session, gameVO){
      if(gameVO.session !== session && !gameVO.isChoiThu) return;
      gameVO.isSpining = false;
      this.sendNotification(SlotMessage.ON_RESET_BTN_SPIN_ALADIN);
    }.bind(this), 5000,session, gameVO);

  }
}
