cc.Class({
    extends: cc.Component,

    properties: {
        top: false,
       topSpace: 0,
        left: false,
        leftSpace: 0,
        right: false,
        rightSpace: 0,
        bottom: false,
        bottomSpace: 0,

        hozCenter: false,
        hozCenterSpace: 0,
        verCenter: false,
        verCenterSpace: 0,
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad() {
        console.log("11111");
        this.fameSize = new cc.Vec2(1000000, 1000000);
    },

    start() {

    },

    update(dt) {
        if (this.fameSize.width === cc.view.getFrameSize().width && this.fameSize.height === cc.view.getFrameSize().height){
            return;
        }
        this.fameSize = cc.view.getFrameSize();
        var size = cc.view.getDesignResolutionSize();
        var scaleX = cc.view.getScaleX();
        var scaleY = cc.view.getScaleY();
        if (this.top) {
            this.node.y = size.height / 2 + (cc.view.getViewportRect().y - this.topSpace) / scaleY;
        }

        if (this.bottom) {
            this.node.y = -size.height / 2 - (cc.view.getViewportRect().y - this.bottomSpace) / scaleY;
        }

        if (this.left) {
            this.node.x = -size.width / 2 - (cc.view.getViewportRect().x - this.leftSpace) / scaleX; 
        }

        if (this.right) {
            this.node.x = size.width / 2 + (cc.view.getViewportRect().x - this.rightSpace) / scaleX;
        }

        if (this.hozCenter) {
            this.node.x = hozCenterSpace;
        }

        if (this.verCenterSpace) {
            this.node.y = verCenterSpace;
        }
    },
});
