var LobbyEvent = require('LobbyEvent');
var Utility = require('Utility');
cc.Class({
    extends: cc.Component,

    properties: {
        pageControl: cc.Node,
        mailDetailForm: cc.Node,
        mailListScrollview: cc.Node,
        mailListScrollviewContent: cc.Node
    },

    onLoad: function () {
        this.mailDetailForm.active = false;

        this.itemRate = this.mailListScrollviewContent.children[0];
        this.mailListScrollviewContent.removeAllChildren();
        this.currentPage = 0;
        this.totalPages = 0;

        var btn_mail_back = this.mailDetailForm.getChildByName('btn_mail_back');
        btn_mail_back.on(cc.Node.EventType.TOUCH_START, this.onBackToMailList, this);

        var btn_next_page = this.pageControl.getChildByName('btn_next_page');
        btn_next_page.on(cc.Node.EventType.TOUCH_START, this.onNextPage, this);

        var btn_back_page = this.pageControl.getChildByName('btn_back_page');
        btn_back_page.on(cc.Node.EventType.TOUCH_START, this.onBackPage, this);
    },

    onNextPage: function () {
        if (parseInt(this.currentPage) < parseInt(this.totalPages))
            this.node.emit(LobbyEvent.GET_MAIL_LIST, {pageIndex: parseInt(this.currentPage) + 1});
    },

    onBackPage: function () {
        if (parseInt(this.currentPage) > 1)
            this.node.emit(LobbyEvent.GET_MAIL_LIST, {pageIndex: parseInt(this.currentPage) - 1});
    },

    updateDeleteMail: function () {
        this.node.emit(LobbyEvent.GET_MAIL_LIST, {pageIndex: parseInt(this.currentPage)});
    },

    onGetMailList:function () {
        this.node.emit(LobbyEvent.GET_MAIL_LIST, {pageIndex: parseInt(this.currentPage)});
    },

    updateMailList: function (mailList) {
        var data = mailList.dataList;
        this.mailListScrollviewContent.removeAllChildren();

        var item, txt_title, txt_sender, txt_time, btn_view, btn_delete, txt_page, mcGroup;

        txt_page = this.pageControl.getChildByName("txt_page").getComponent(cc.Label);
        txt_page.string = mailList.currentPage + '/' + mailList.totalPages;
        this.currentPage = mailList.currentPage;
        this.totalPages = mailList.totalPages;

        if (this.totalPages === 0) this.pageControl.active = false;
        else this.pageControl.active = true;


        for (var i = 0; i < data.length; i++) {
            item = cc.instantiate(this.itemRate);
            mcGroup = item.getChildByName("mcGroup");
            txt_title = mcGroup.getChildByName("txt_title").getComponent(cc.Label);
            txt_sender = mcGroup.getChildByName("txt_sender").getComponent(cc.Label);
            txt_time = mcGroup.getChildByName("txt_time").getComponent(cc.Label);
            btn_delete = item.getChildByName("btn_delete");

            mcGroup.on(cc.Node.EventType.TOUCH_START, this.onViewMail.bind(this, data[i], this));
            btn_delete.on(cc.Node.EventType.TOUCH_START, this.onDeleteMail.bind(this, data[i], this));

            btn_delete.active = (data[i].isForAll === 1)?false:true;
            txt_title.string = data[i].title;
            txt_sender.string = data[i].sender;
            txt_time.string = data[i].createdDate;
            this.mailListScrollviewContent.addChild(item);
        }
    },

    updateMailDetail: function (mailDetail) {
        this.pageControl.active = false;
        this.mailDetailForm.active = true;
        this.mailListScrollview.active = false;

        var item, txt_title, txt_sender, txt_time;

        txt_title = this.mailDetailForm.getChildByName('txt_title').getComponent(cc.Label);
        txt_sender = this.mailDetailForm.getChildByName('txt_sender').getComponent(cc.Label);
        txt_time = this.mailDetailForm.getChildByName('txt_time').getComponent(cc.Label);

        txt_title.string = mailDetail.title;
        txt_sender.string = mailDetail.sender;
        txt_time.string = mailDetail.createdDate;

        var contentScrollview = this.mailDetailForm.getChildByName('contentScrollview');
        var view = contentScrollview.getChildByName('view');
        var content = view.getChildByName('content');
        item = content.children[0];
        item.getComponent(cc.RichText).string = Utility.convertToRtf(mailDetail.content);
    },

    onViewMail: function (mail) {
        this.node.emit(LobbyEvent.GET_MAIL_DETAIL, {
            operatorId: mail.operatorId,
            isForAll: mail.isForAll
        });
    },

    onBackToMailList: function () {
        this.pageControl.active = true;
        this.mailDetailForm.active = false;
        this.mailListScrollview.active = true;
    },

    onDeleteMail: function (mail) {
        this.node.emit(LobbyEvent.DELETE_MAIL, {
            listOperatorId: [mail.operatorId].toString()
        });
    }
});
