var BasePopup = require('BasePopup');
var Utility = require('Utility');

cc.Class({
    extends: BasePopup,

    properties: {
        content: cc.Node,
        itemTemp:cc.Node,
        historyDetail: cc.Node,
        itemDetail: cc.Node,
        contentDetail: cc.Node,
        spriteFrame: [cc.SpriteFrame]
    },

    onLoad () {
    },

    show: function () {
        BasePopup.prototype.show.call(this);
        this.content.active = true;
        this.content.removeAllChildren();
        this.historyDetail.active = false;
        TweenLite.delayedCall(0.2, function () {
            this.node.emit('ACTIVE_HISTORY_SHOW_PAGE');
        }.bind(this));
    },

    hide: function () {
        BasePopup.prototype.hide.call(this);
        this.content.removeAllChildren();
    },

    updateHistory: function (data) {
        this.content.removeAllChildren();
        for(let i=0 ; i<data.length ; i++){
            var item = cc.instantiate(this.itemTemp);
            item.getChildByName("txtSession").getComponent(cc.Label).string = '#' + data[i].id;
            item.getChildByName("txtTime").getComponent(cc.Label).string = Utility.js_yyyy_mm_dd_hh_mm_ss(data[i].date);
            item.getChildByName("txtBetValue").getComponent(cc.Label).string = Utility.formatCurrency(data[i].bet);
            item.getChildByName("txtPrize").getComponent(cc.Label).string = Utility.formatCurrency(data[i].pay);
            item.getChildByName("btnDetail").itag = data[i];
            this.content.addChild(item);
        }
    },

    onHandlerShowHistoryDetail: function (event) {
        this.historyDetail.active = true;
        this.content.active = false;
        var hisDetail = event.target.itag;

        this.historyDetail.getChildByName('txtSession').getComponent(cc.Label).string = hisDetail.id;
        this.historyDetail.getChildByName('txtWin').getComponent(cc.Label).string = Utility.formatCurrency(hisDetail.pay);
        this.contentDetail.removeAllChildren();
        if (!hisDetail.slot_data) return;
        for (var i = 0; i < hisDetail.slot_data.length; i++) {
            var item = cc.instantiate(this.itemDetail);
            item.getChildByName('sprite').getComponent(cc.Sprite).spriteFrame = this.spriteFrame[hisDetail.slot_data[i]];
            this.contentDetail.addChild(item);
        }
    },

    onHandleHideHistoryDetail: function () {
        this.historyDetail.active = false;
        this.content.active = true;
    }
});
