var BaseCommand = require('BaseCommand');
var SlotFBProxy = require('SlotFBProxy');
var SlotMessage = require('SlotMessage');

export default class ReceiveUpdateJackPotFBCommand extends BaseCommand {
  execute(notification) {
    BaseCommand.prototype.execute.call(this, notification);
    var params = notification.getBody();
    var gameVO = SlotFBProxy.proxy.gameVO;
    this.sendNotification(SlotMessage.ON_UPDATE_JACKPOT_FOOTBALL, {roomJackPot: gameVO.getCurrentJackpot(), listRoomJackPot: gameVO.getListRoomJackpot()});
  }
}
