var BasePopup = require('BasePopup');

cc.Class({
    extends: BasePopup,

    properties: {
        pageView: cc.PageView
    },
    
    onLoad: function () {
        this.pageIndex = 0;
    },

    show: function () {
        BasePopup.prototype.show.call(this);
        this.pageView.scrollToPage(0);
        this.pageIndex = 0;
    },
    
    onHandlerNextPage: function () {
        this.pageIndex++;
        if (this.pageIndex === this.pageView.getPages().length)
            this.pageIndex = 0;
        this.pageView.scrollToPage(this.pageIndex);
    },
    
    onHandlerPreviousPage: function () {
        this.pageIndex--;
        if (this.pageIndex  < 0)
            this.pageIndex = this.pageView.getPages().length - 1;
        this.pageView.scrollToPage(this.pageIndex);
    }
});
