var BaseGameCommand = require('BaseGameCommand');
var SlotmachineConfig = require('SlotmachineConfig');
var LobbyMessage = require('LobbyMessage');
var SlotMessage = require('SlotMessage');
var i18n = require('i18n');
var Constants = require('Constants');

export default class SendJoinSlot20Command extends BaseGameCommand {
    execute(notification) {
        BaseGameCommand.prototype.execute.call(this, notification);
        // if (!this.isLogin(i18n.t("C0047"))) return;
        this.mySelf = this.facade.retrieveProxy('UserProxy').mySelf;
        this.msgInit = null;
        this.gameProxy = null;
        this.zoneName = "";
        var params = notification.getBody();
        switch (params.game) {
            case Constants.SLOT20_LUCKY_CAFE:
                this.gameProxy = this.facade.retrieveProxy("SlotLCProxy");
                this.zoneName = SlotmachineConfig.SLOT20_LC_SCENE;
                this.msgInit = SlotMessage.INIT_SLOT_LUCKY_CAFE;
                this.loadGame("slotmachine/TamQuoc/SlotLCScene", params.target);
                break;
            case Constants.SLOT20_KEO_NGOT:
                this.gameProxy = this.facade.retrieveProxy("SlotKNProxy");
                this.zoneName = SlotmachineConfig.SLOT20_KEO_NGOT_SCENE;
                this.msgInit = SlotMessage.INIT_SLOT_KEO_NGOT;
                this.loadGame("slotmachine/KeoNgot/SlotKNScene", params.target);
                break;    
            case Constants.SLOT20_MASTER_CHEF:
                this.gameProxy = this.facade.retrieveProxy("SlotMCProxy");
                this.zoneName = SlotmachineConfig.SLOT20_MC_SCENE;
                this.msgInit = SlotMessage.INIT_SLOT_MASTER_CHEF;
                this.loadGame("slotmachine/MasterChef/SlotMCScene", params.target);
                break;
            case Constants.SLOT25_ALADIN:
                this.gameProxy = this.facade.retrieveProxy("SlotAlaProxy");
                this.zoneName = SlotmachineConfig.SLOT25_ALADIN_SCENE;
                this.msgInit = SlotMessage.INIT_SLOT_ALADIN;
                this.loadGame("slotmachine/Aladin/SlotAlaScene", params.target);
                break;
            case Constants.SLOT30_FOOTBALL:
                this.gameProxy = this.facade.retrieveProxy("SlotFBProxy");
                this.zoneName = SlotmachineConfig.SLOT30_FOOTBALL_SCENE;
                this.msgInit = SlotMessage.INIT_SLOT_FOOTBALL;
                this.loadGame("slotmachine/Football/SlotFBScene", params.target);
                break;
        }
        // this.sendNotification(LobbyMessage.SHOW_LOADING);
    }

    loadGame(name, target, params, msgInitGame) {
        if (!this.gameProxy.isLoadDone) {
            this.showLoading(target, true);
            this.sendNotification(LobbyMessage.ON_LOAD_SLOT, {name: name, target: target,
                onComplete: function () {
                    this.gameProxy.isLoadDone = true;
                    this.showLoading(target, false);
                    this.sendRefreshMoney();
                    this.gameProxy.sendJoinGame({
                        userJoin: this.mySelf.uid,
                        zoneName: this.zoneName
                    });
                }.bind(this)
            });
        } else {
            // this.sendNotification(this.msgInit);
            this.sendRefreshMoney();
            this.gameProxy.sendJoinGame({
                userJoin: this.mySelf.uid,
                zoneName: this.zoneName
            });
        }
    }

    showLoading(target, isShow) {
        target.getChildByName('loading').active = isShow;
        if (isShow)
            target.getChildByName('loading').getChildByName('loader_light').getComponent('RotationObject').onStart();
        else
            target.getChildByName('loading').getChildByName('loader_light').getComponent('RotationObject').onStop();
    }
}
