var BaseCommand = require('BaseCommand');
var MiniGameMessage = require('MiniGameMessage');

export default class ReceiveChatTaiXiuCommand extends BaseCommand {
  execute(notification) {
    BaseCommand.prototype.execute.call(this, notification);
    var displayName = notification.getBody().displayName;
    var message = notification.getBody().message;
    if(message === "") return;

    this.sendNotification(MiniGameMessage.ON_UPDATE_CHAT_TAIXIU, {displayName: displayName, message:message});
  }
}
