var BasePopup = require('BasePopup');
var TabMobileCard = require('TabMobileCard');
var TabOTP = require('TabOTP');
var TabSMS9029 = require('TabSMS9029');
var TabBank = require('TabBank');
var TabMain = require('TabMain');
var TabInApp = require('TabInApp');
var LobbyEvent = require('LobbyEvent');
var RechargeSceneMediator = require('RechargeSceneMediator');

cc.Class({
    extends: BasePopup,

    properties: {
        tabMain: TabMain,
        tabCard: TabMobileCard,
        tabOTP: TabOTP,
        tabSMS: TabSMS9029,
        tabBank: TabBank,
        tabInApp: TabInApp
    },

    // use this for initialization
    onLoad: function () {
        RechargeSceneMediator.getInstance.init(this);
        this.addEventListeners();
        this.hide();
    },

    addEventListeners: function () {
        this.tabCard.node.on(LobbyEvent.MOBILE_CARD_RECHARGE, this.onHandlerMobileCardRecharge, this);
        this.tabOTP.node.on(LobbyEvent.OTP_CONFIRM, this.onHandlerOTPConfirm, this);
        this.tabOTP.node.on(LobbyEvent.OTP_RECHARGE, this.onHandlerOTPRecharge, this);
        this.tabBank.node.on(LobbyEvent.BANK_RECHARGE, this.onHandlerBankRecharge, this);
    },

    removeEventListeners: function () {
        this.tabCard.node.off(LobbyEvent.MOBILE_CARD_RECHARGE, this.onHandlerMobileCardRecharge, this);
        this.tabOTP.node.off(LobbyEvent.OTP_CONFIRM, this.onHandlerOTPConfirm, this);
        this.tabOTP.node.off(LobbyEvent.OTP_RECHARGE, this.onHandlerOTPRecharge, this);
        this.tabBank.node.off(LobbyEvent.BANK_RECHARGE, this.onHandlerBankRecharge, this);
    },

    onUpdateMySelf: function (mySelf) {
        this.tabCard.updateMySelf(mySelf);
        this.tabOTP.updateMySelf(mySelf);
        this.tabSMS.updateMySelf(mySelf);
        this.tabBank.updateMySelf(mySelf);
    },

    onUpdateRechargeInfo: function (recharge) {
        this.tabCard.updateCardTelcoList(recharge.cardData.cardTelcoList);
        this.tabCard.updateCardRateList(recharge.cardData.cardRateList);
        this.tabOTP.updateOtpValueList(recharge.otpData.otpValueList);
        this.tabOTP.updateSmsOtpRateList(recharge.otpData.smsOtpRateList);
        this.tabOTP.updateTextNote(recharge.sms9029Data.note);
        this.tabSMS.updateSms9029RateList(recharge.sms9029Data.sms9029RateList);
        this.tabSMS.updateSms9029ValueList(recharge.sms9029Data.sms9029ValueList);
        this.tabSMS.updateSmsTelcoList(recharge.sms9029Data.smsTelcoList);
        this.tabSMS.updateSmsCommand(recharge.sms9029Data.command);
        this.tabSMS.updateTextNote(recharge.sms9029Data.note);

        this.tabBank.updateBankDescription(recharge.bankData.bankDescription);
    },

    onShowTab: function (tabId) {
        this.tabMain.openTab(tabId);
    },

    onHandlerBankRecharge: function (params) {
        this.activeBankRecharge(params.detail);
    },


    onHandlerTransferMoneyToChip: function (params) {
        this.activeTransferMoneyToChip(params.detail);
    },

    onHandlerOTPRecharge: function (params) {
        this.activeOTPRecharge(params.detail);
    },

    onHandlerMobileCardRecharge: function (params) {
        this.activeMobileCardRecharge(params);
    },

    onHandlerOTPConfirm: function (params) {
        this.activeOTPConfirm(params.detail);
    },

    onHandlerTransferdesGoldAgent: function (params) {
        this.activeTransferdesGoldAgent(params);
    },

    onHandlerShowInfo: function () {
        this.activeShowInfo();
    },

    onResetMobileCardRecharge: function () {
        this.tabCard.reset();
    },

    onShowConfirmOTP: function () {
        this.tabOTP.onShowConfirmOTP();
    },

    onComfirmOTPFail: function () {
        this.tabOTP.onComfirmOTPFail();
    },

    onComfirmOTPSucess: function () {
        this.tabOTP.onComfirmOTPSucess();
    }
});

