var Utility = require('Utility');
cc.Class({
    extends: cc.Component,

    properties: {
        txtPrize: cc.Label,
        txtWinMoney: cc.Label,
        txtTime: cc.Label,
        txtTurn: cc.Label,
        mcTotalWin: cc.Node,
        // mcTurn1: cc.Node,
        // mcTurn2: cc.Node,
        // mcTurn3: cc.Node,
        // mcTurn4: cc.Node,
        // winTemp: cc.Node,
        mcSocolaGroup: cc.Node,
        minigameStep2: cc.Node,
        items: [cc.SpriteFrame]
    },

    onLoad () {
        this.isLoad = true;
        this.minigameStep2.active = false;
        this.totalCountTime = 15;
        // this.mcX1 = this.winTemp.getChildByName('mcX1');
        // this.mcX2 = this.winTemp.getChildByName('mcX2');
        // this.mcX3 = this.winTemp.getChildByName('mcX3');
        // this.mcX5 = this.winTemp.getChildByName('mcX5');
        // this.mcMoc = this.winTemp.getChildByName('mcMoc');
        this.timerCount = this.totalCountTime;
        this.isPlaying = false;
        this.isAuto = false;
        this.listResultNode = [];
        this.curPos = new cc.Vec2(this.node.x, this.node.y);
        this.node.active = false;
        this.reset();
    },

    onStartGame(data, onComplete) {
        this.show();
        setTimeout(function(){
            this.reset();
            this.show();
            this.onComplete = onComplete;
            this.isPlaying = true;
            this.dataResult = data;
            this.prize = 0;
            this.multiplier = this.dataResult.multiplier;
            this.totalPrize = this.dataResult.bonusPrize;
            this.timerCount = this.totalCountTime;
            this.arrResult = this.dataResult.bonusData;
            this.totalTurn = this.arrResult.length + 1;

            // this.arrResult.shift();
            // this.txtTurn.string = this.totalTurn;
            if(this.timer) clearInterval(this.timer);
            this.timer = setInterval(this.updateTimer.bind(this), 1000);
        }.bind(this), 1000);
    },

    show() {
        // if(this.curPos) {
        //   this.node.x = this.curPos.x;
        //   this.node.y = this.curPos.y;
        // }
        this.node.active = true;
        TweenLite.to(this.node, 0.5, { y: this.curPos.y });
    },

    hide() {
        this.node.active = false;
        TweenLite.to(this.node, 0.5, { y: -800 });
    },

    onHandlerAutoChoose() {
        if(this.isAuto) return;
        this.isAuto = true;
        clearInterval(this.timer);
        for(var i = 0; i < 12; i++) {
            TweenLite.delayedCall(i * 1, function (data) {
                if(!this.isPlaying) return;
                var btChocolate = this.getRandomChocolate();
                this.onChooseSocola(btChocolate);
            }.bind(this));
        }
    },

    updateTimer() {
        if(!this.isPlaying) return;
        this.timerCount--;
        this.txtTime.string = this.timerCount;
        if(this.timerCount === 0) {
            var btChocolate = this.getRandomChocolate();
            this.onChooseSocola(btChocolate);
            this.timerCount = this.totalCountTime;
        }
    },

    getRandomChocolate() {
        var num = Utility.randomNumber(0, 23);
        for(var i = num; i < 24; i++) {
            var bt = this.mcSocolaGroup.children[i];
            if(bt && bt.active === true) return bt;
            if(i === 24) i = 0;
        }
    },

    onChooseSocola(mcTarget) {
        if(!this.isPlaying) return;
        mcTarget.getComponent(cc.Button).interactable = false;
        // mcTarget.getComponent(cc.Sprite).spriteFrame = this.items[1];
        var mcResult = null;
        // if(this.arrResult[this.curResult] === "0") mcResult = cc.instantiate(this.mcMoc);
        // if(this.arrResult[this.curResult] === "1") mcResult = cc.instantiate(this.mcX1);
        // if(this.arrResult[this.curResult] === "2") mcResult = cc.instantiate(this.mcX2);
        // if(this.arrResult[this.curResult] === "3") mcResult = cc.instantiate(this.mcX3);
        // if(this.arrResult[this.curResult] === "5") mcResult = cc.instantiate(this.mcX5);

        // mcResult.x = mcTarget.x;
        // mcResult.y = mcTarget.y;
        this.timerCount = this.totalCountTime;
        // this.mcSocolaGroup.addChild(mcResult);
        // this.listResultNode.push(mcResult);
        this.prize += parseInt(this.arrResult[this.curResult]) ;
        Utility.tweenRunNumber(this.txtPrize.node, this.prize, 1);
        // TweenLite.from(mcResult, 0.3, {scale:0});
        // if(this.curResult === 3) this.activeTurn(this.mcTurn2, true);
        // if(this.curResult === 6) this.activeTurn(this.mcTurn3, true);
        // if(this.curResult === 9) this.activeTurn(this.mcTurn4, true);

        // mcTarget.getChildByName('imgGift').getComponent(cc.Sprite).spriteFrame = this.items[2];

        mcTarget.getChildByName('icon_den').active = false;
        mcTarget.getChildByName('txtMoney').active = true;
        mcTarget.getChildByName('txtMoney').getComponent(cc.Label).string = Utility.formatCurrency(this.arrResult[this.curResult]);
        this.curResult++;
        // this.txtTurn.string = this.totalTurn - this.curResult;

        if(this.curResult === this.totalTurn) {
            this.isPlaying = false;
            mcTarget.getChildByName('icon_game_over').active = true;

            TweenLite.delayedCall(1, function () {
                this.showMinigameStep2();
            }.bind(this))
        }

    },

    onHandlerCloseGame() {
        this.hide();
        TweenLite.delayedCall(1, function(){
            if(this.onComplete) this.onComplete.call();
            this.onComplete = null;
            this.reset();
        }.bind(this));
    },

    handlerChooseSocola(event) {
        var mcTarget = event.currentTarget;
        this.onChooseSocola(mcTarget);
    },

    showMinigameStep2() {
        this.minigameStep2.active = true;

    },

    handlerSelectGenie(event) {
        var item = event.target.getChildByName('item');
        item.active = true;
        item.getComponent(cc.Sprite).spriteFrame = this.items[this.multiplier-1];
        for(let i = 1; i <= 3; i++) {
            var btnSelect = this.minigameStep2.getChildByName('btnSelect' + i);
            btnSelect.getComponent(cc.Button).interactable = false;
        }
        TweenLite.delayedCall(1, function(){
            this.finishGame();
        }.bind(this));
    },

    finishGame() {
        for(let i = 1; i <= 3; i++) {
            var btnSelect = this.minigameStep2.getChildByName('btnSelect' + i);
            btnSelect.getComponent(cc.Button).interactable = false;
        }
        this.mcTotalWin.active = true;
        this.txtWinMoney.string = 0;
        TweenLite.from(this.mcTotalWin, 0.3, {scale:0});
        Utility.tweenRunNumber(this.txtWinMoney.node, this.totalPrize, 1);
        // Utility.tweenRunNumber(this.txtPrize.node, this.dataResult.v, 1.5);
        setTimeout(function(session){
            this.onHandlerCloseGame();
        }.bind(this), 4000);
    },

    reset() {
        this.minigameStep2.active = false;
        this.txtTime.string = this.totalCountTime;
        this.curResult = 0;
        this.mcTotalWin.active = false;
        this.isAuto = false;
        this.onComplete = null;
        // this.activeTurn(this.mcTurn1, true);
        // this.activeTurn(this.mcTurn2, false);
        // this.activeTurn(this.mcTurn3, false);
        // this.activeTurn(this.mcTurn4, false);
        this.txtPrize.string = "0";
        if(this.listResultNode) {
            for(let i=0;i<this.listResultNode.length; i++) {
                this.listResultNode[i].removeFromParent();
            }
        }

        for(let i=0;i<this.mcSocolaGroup.children.length; i++) {
            if(this.mcSocolaGroup.children[i]) {
                // this.mcSocolaGroup.children[i].getComponent(cc.Sprite).spriteFrame = this.items[0];
                this.mcSocolaGroup.children[i].getComponent(cc.Button).interactable = true;
                this.mcSocolaGroup.children[i].getChildByName('txtMoney').active = false;
                this.mcSocolaGroup.children[i].getChildByName('icon_game_over').active = false;
                this.mcSocolaGroup.children[i].getChildByName('icon_den').active = true;
            }
        }

        for(let i = 1; i <= 3; i++) {
            var btnSelect = this.minigameStep2.getChildByName('btnSelect' + i);
            btnSelect.getChildByName('item').active = false;
            btnSelect.getComponent(cc.Button).interactable = true;
        }
    },

    activeTurn(mcTurn, isActive) {
        var mcOn = mcTurn.getChildByName('LuotChon');
        var mcOff = mcTurn.getChildByName('LuotChonD');

        mcOn.active = isActive;
        mcOff.active = !isActive;
    }
});
