var BaseProxy = require("BaseProxy");
var ListJackpotVO = require("ListJackpotVO");


export default class LitJackpotProxy extends BaseProxy {
    static get NAME() {
        return 'LitJackpotProxy';
    }

    onRegister() {
        BaseProxy.prototype.onRegister.call(this);
        this.vo = new ListJackpotVO();
    }

    update(data) {
        this.vo.update(data);
    }

    getMucCuoc() {
        return this.vo.getMucCuoc();
    }

    getLogic() {
        return this.vo;
    }
}