var BaseCommand = require('BaseCommand');
var LobbyMessage = require('LobbyMessage');

export default class ReceiveResetPhoneCommand extends BaseCommand {
    execute(notification) {
        BaseCommand.prototype.execute.call(this, notification);
        this.sendNotification(LobbyMessage.HIDE_LOADING);
        if (this.isError(notification.getBody().data))
            return;
        else
            this.sendNotification(LobbyMessage.SHOW_FLY_ALERT, { content: notification.getBody().data.message });

        var params = notification.getBody().data;
        this.sendNotification(LobbyMessage.ON_UPDATE_RESET_PHONE);
    }
}