var BaseCommand = require('BaseCommand');
var Constants = require('Constants');
var SlotKNProxy = require('SlotKNProxy');
export default class SendGetRankKNCommand extends BaseCommand {
  execute(notification) {
    BaseCommand.prototype.execute.call(this, notification);
    var params = notification.getBody().params;

    var sendData = {
      type: params.type,
      gid: params.bet
    };
    this.gameProxy = SlotKNProxy.proxy;
    this.gameProxy.sendGetRank(sendData);
  }
}
