import { SFSData } from "../../../../network/sfs/constans/SFSData";
import { SFSEvent } from "../../../../network/sfs/constans/SFSEvent";

var MiniPokerVO = require("MiniPokerVO");
var MinigameProxy = require('MinigameProxy');
var MiniGameMessage = require('MiniGameMessage');
var Utility = require('Utility');
var Constants = require('Constants');

export default class MiniPokerProxy extends MinigameProxy {
    static get NAME() {
        return 'MiniPokerProxy';
    }

    onRegister() {
        MinigameProxy.prototype.onRegister.call(this);
        this.gameVO = new MiniPokerVO();
        this.gameVO.onRegister();
    }

    getLogic() {
        return this.logic;
    }

    /////////////////////////////////////////////////////////////////
    ////////////////////SEVER REPONSE//////////////////////////////
    ////////////////////////////////////////////////////////////////
    onMessage(event) {
        MinigameProxy.prototype.onMessage.call(this, event);
        var data = null;
        if (typeof event.params === "object") {
            data = Utility.convertSFSObjectToObject(event.params);
        }

        switch (event.cmd) {
            case SFSEvent.USER_JOIN_MINIGAME:
                this.sendNotification(MiniGameMessage.RECEIVE_JOIN_MINIGAME, { curMiniGame: Constants.MINIGAME_MINI_POKER, data: data });
                break;
            case SFSEvent.USER_DISCONNECT:
                this.onUserDis(data);
                break;
            case SFSEvent.SPIN:
                this.onUpdateResult(data);
                break;
            case SFSEvent.SPINX3:
                this.onUpdateResultX3(data);
                break;
            case SFSEvent.UPDATE_MONEY:
                this.sendNotification(MiniGameMessage.RECEIVE_UPDATE_MONEY_MINI_POKER, data);
                break;
            case SFSEvent.UPDATE_POT:
                this.sendNotification(MiniGameMessage.RECEIVE_UPDATE_JACKPOT_MINI_POKER, data);
                break;
            case SFSEvent.GET_HISTORY:
                this.onShowHistoryMiniPoker(data);
                break;
            case SFSEvent.GET_TOP:
                this.onShowRankMiniPoker(data);
                break;
            default:
        }
    }

    onUserDis() {
        //this.sendNotification(MiniGameMessage.RECEIVE_DISCONNECT_MINI_POKER, {});
    }

    onUpdateResult(data) {
        if (data.success) {
            var mySelf = this.facade.retrieveProxy('UserProxy').mySelf;
            data.result = Utility.convertSFSObjectToObject(data.result);
            data.result.cards = Utility.convertServerToClientCardsMiniPoker(data.result.cards);
            // data.result.prize = data.result.money;
            data.result.money = mySelf.money + parseInt(data.result.prize);
        }
        this.gameVO.updateData(data);
        this.sendNotification(MiniGameMessage.RECEIVE_SPIN_MINI_POKER, {data: this.gameVO});
    }

    onUpdateResultX3(data) {
        data.result1 = Utility.convertSFSObjectToObject(data.result1);
        data.result2 = Utility.convertSFSObjectToObject(data.result2);
        data.result3 = Utility.convertSFSObjectToObject(data.result3);

        data.result1.cards = Utility.convertServerToClientCardsMiniPoker(data.result1.cards);
        data.result2.cards = Utility.convertServerToClientCardsMiniPoker(data.result2.cards);
        data.result3.cards = Utility.convertServerToClientCardsMiniPoker(data.result3.cards);

        this.gameVO.updateDataX3(data);
        this.sendNotification(MiniGameMessage.RECEIVE_SPIN_MINI_POKER, {data: this.gameVO});
    }

    onShowHistoryMiniPoker(data) {
        this.sendNotification(MiniGameMessage.ON_UPDATE_HISTORY_MINIGAME_MINI_POKER, data);
    }

    onShowRankMiniPoker(data) {
        this.sendNotification(MiniGameMessage.ON_UPDATE_RANK_MINIGAME_MINI_POKER, data);
    }

    /////////////////////////////////////////////////////////////////
    ////////////////////SEND TO SERVER//////////////////////////////
    ////////////////////////////////////////////////////////////////


    sendSpin(params) {
        var sfo = new SFS2X.SFSObject();
        sfo.putUtfString(SFSData.VALUE, params.value);
        if (params.type === 1)
            this.sendExtensionRequest(SFSEvent.SPIN, sfo);
        else
            this.sendExtensionRequest(SFSEvent.SPINX3, sfo);
    }

    sendGetRank() {
        // params = params.params;
        var page = 1;
        var type = 1;
        var sfsob = new SFS2X.SFSObject();
        sfsob.putInt(SFSData.PAGE, page);
        sfsob.putInt(SFSData.MONEY_TYPE, type);
        this.sendExtensionRequest("getTop", sfsob);
    }

    sendGetHistory() {
        // params = params.params;
        var page = 1;
        var type = 1;
        var sfsob = new SFS2X.SFSObject();
        sfsob.putInt(SFSData.PAGE, page);
        sfsob.putInt(SFSData.MONEY_TYPE, type);
        this.sendExtensionRequest("getHistory", sfsob);
    }
}
