var BaseGameCommand = require('BaseGameCommand');
var MiniGameMessage = require('MiniGameMessage');
var MiniGameConfig = require('MiniGameConfig');
var i18n = require('i18n');
var Constants = require('Constants');

export default class SendJoinMiniGameCommand extends BaseGameCommand {
  execute(notification) {
    BaseGameCommand.prototype.execute.call(this, notification);
    // if (!this.isLogin(i18n.t("C0047"))) return;
    this.mySelf = this.facade.retrieveProxy('UserProxy').mySelf;
    this.msgInit = null;
    this.gameProxy = null;
    this.zoneName = "";
    var params = notification.getBody();
    switch (params.game) {
      case Constants.MINIGAME_TAI_XIU:
        this.gameProxy = this.facade.retrieveProxy("TaiXiuProxy");
        this.zoneName = MiniGameConfig.ZONE_TAIXIU;
        this.msgInit = MiniGameMessage.INIT_TAI_XIU;
        this.loadMiniGame("TaiXiuScene", params.target);
        break;
      case Constants.MINIGAME_SLOT3X3:
        this.gameProxy = this.facade.retrieveProxy("Slot3x3Proxy");
        this.zoneName = MiniGameConfig.ZONE_SLOT3X3;
        this.msgInit = MiniGameMessage.INIT_SLOT3X3;
        this.loadMiniGame("Slot3x3Scene", params.target);
        break;
      case Constants.MINIGAME_BAU_CUA:
        this.gameProxy = this.facade.retrieveProxy("BauCuaProxy");
        this.zoneName = MiniGameConfig.ZONE_BAU_CUA;
        this.msgInit = MiniGameMessage.INIT_BAU_CUA;
        this.loadMiniGame("BauCuaScene", params.target);
        break;
      case Constants.MINIGAME_MINI_POKER:
        this.gameProxy = this.facade.retrieveProxy("MiniPokerProxy");
        this.zoneName = MiniGameConfig.ZONE_MINIPOKER;
        this.msgInit = MiniGameMessage.INIT_MINI_POKER;
        this.loadMiniGame("MiniPokerScene", params.target);
        break;
      case Constants.MINIGAME_TO_NHO:
        this.gameProxy = this.facade.retrieveProxy("ToNhoProxy");
        this.zoneName = MiniGameConfig.ZONE_TO_NHO;
        this.msgInit = MiniGameMessage.INIT_TO_NHO;
        this.loadMiniGame("ToNhoScene", params.target);
        break;
      case Constants.MINIGAME_VONG_QUAY:
        this.gameProxy = this.facade.retrieveProxy("VongQuayProxy");
        this.zoneName = MiniGameConfig.ZONE_VONG_QUAY;
        this.msgInit = MiniGameMessage.INIT_VONG_QUAY;
        this.loadMiniGame("VongQuayScene", params.target);
        break;
      case Constants.MINIGAME_SLOT_BT:
        this.gameProxy = this.facade.retrieveProxy("SlotBTProxy");
        this.zoneName = MiniGameConfig.ZONE_SLOT_BT;
        this.msgInit = MiniGameMessage.INIT_SLOT_BT;
        this.loadMiniGame("SlotBTScene", params.target);
        break;  
    }
  }

  loadMiniGame(name, target, params, msgInitGame) {
    if (!this.gameProxy.isLoadDone) {
        if (this.gameProxy.isLoadProcess)  return;
        this.gameProxy.isLoadProcess = true;
        this.showLoading(target, true);
      this.sendNotification(MiniGameMessage.ON_LOAD_MINIGAME, {name: name, target: target, onComplete: function () {
          this.gameProxy.isLoadDone = true;
          this.showLoading(target, false);
          this.sendRefreshMoney();
          this.gameProxy.sendJoinGame({
            userJoin: this.mySelf.uid,
            zoneName: this.zoneName
          });
        }.bind(this)
      });
    } else {
      this.sendNotification(this.msgInit);
      this.sendRefreshMoney();
      this.gameProxy.sendJoinGame({
        userJoin: this.mySelf.uid,
        zoneName: this.zoneName
      });
    }
  }

  showLoading(target, isShow) {
    target.getChildByName('loading').active = isShow;
    if (isShow)
      target.getChildByName('loading').getChildByName('loader_light').getComponent('RotationObject').onStart();
    else
      target.getChildByName('loading').getChildByName('loader_light').getComponent('RotationObject').onStop();
  }
}
