import { SFSData } from "../../../../network/sfs/constans/SFSData";
import { SFSEvent } from "../../../../network/sfs/constans/SFSEvent";

var MinigameProxy = require("MinigameProxy");
var MiniGameMessage = require('MiniGameMessage');
var Constants = require('Constants');
var Utility = require('Utility');

export default class BauCuaProxy extends MinigameProxy {
    static get NAME() {
        return 'BauCuaProxy';
    }

    /////////////////////////////////////////////////////////////////
    ////////////////////SEVER REPONSE//////////////////////////////
    ////////////////////////////////////////////////////////////////
    onMessage(event) {
        MinigameProxy.prototype.onMessage.call(this, event);
        var data = null;
        if (typeof event.params === "object") {
            data = Utility.convertSFSObjectToObject(event.params);
        }

        switch (event.cmd) {
            case SFSEvent.USER_JOIN_MINIGAME:
                this.sendNotification(MiniGameMessage.RECEIVE_JOIN_MINIGAME, { curMiniGame: Constants.MINIGAME_BAU_CUA, data: data });
                break;
            case SFSEvent.GET_USER_INFO:
                this.sendNotification(MiniGameMessage.RECEIVE_GET_PLAYING_INFO_BAU_CUA, { curMiniGame: Constants.MINIGAME_BAU_CUA, data: data });
                break;
            case SFSEvent.UPDATE_GAME_STATE:
                this.sendNotification(MiniGameMessage.RECEIVE_UPDATE_GAME_STATE_BAU_CUA, { curMiniGame: Constants.MINIGAME_BAU_CUA, data: data });
                break;
            case SFSEvent.UPDATE_BOARD_BC:
                this.sendNotification(MiniGameMessage.RECEIVE_UPDATE_BOARD_BET_BAU_CUA, { curMiniGame: Constants.MINIGAME_BAU_CUA, data: data });
                break;
            case SFSEvent.GAME_OVER:
                this.sendNotification(MiniGameMessage.RECEIVE_GAME_OVER_BAU_CUA, { curMiniGame: Constants.MINIGAME_BAU_CUA, data: data });
                break;
            case SFSEvent.UPDATE_MONEY:
                this.sendNotification(MiniGameMessage.RECEIVE_UPDATE_MONEY_BAU_CUA, { curMiniGame: Constants.MINIGAME_BAU_CUA, data: data });
                break;
            case SFSEvent.BET_BC:
                this.sendNotification(MiniGameMessage.RECEIVE_BET_BAU_CUA, { curMiniGame: Constants.MINIGAME_BAU_CUA, data: data });
                break;
            case SFSEvent.SOI_CAU_BC:
                this.sendNotification(MiniGameMessage.ON_UPDATE_SOI_CAU_BAU_CUA, data);
                break;
            case SFSEvent.GET_HISTORY:
                this.sendNotification(MiniGameMessage.ON_UPDATE_HISTORY_BAU_CUA, data );
                break;
            case SFSEvent.GET_TOP:
                this.sendNotification(MiniGameMessage.ON_UPDATE_TOP_BAU_CUA, data);
                break;
            default:
        }
    }

    /////////////////////////////////////////////////////////////////
    ////////////////////SEND TO SERVER//////////////////////////////
    ////////////////////////////////////////////////////////////////

    sendBet(params) {
        var sfo = new SFS2X.SFSObject();
        sfo.putUtfString('bet', params.value);
        sfo.putInt('index', params.index);

        this.sendExtensionRequest('userBet', sfo);
    }

    sendGetRank(params) {
        var type = 0;
        var sfsob = new SFS2X.SFSObject();
        sfsob.putInt(SFSData.MONEY_TYPE, type);
        this.sendExtensionRequest(SFSEvent.GET_TOP, sfsob);
    }

    sendGetHistory(params) {
        var type = 0;
        var sfsob = new SFS2X.SFSObject();
        sfsob.putInt(SFSData.MONEY_TYPE, type);
        this.sendExtensionRequest(SFSEvent.GET_HISTORY, sfsob);
    }

    sendGetSoiCau() {
        var sfsob = new SFS2X.SFSObject();
        this.sendExtensionRequest(SFSEvent.SOI_CAU_BC, sfsob);
    }

    sendExtensionRequest(command, sfo) {
        if (this.sfs === null)
            return;
        sfo.putUtfString(SFSData.USER_NAME, this.userName);
        var request = new SFS2X.ExtensionRequest(command, sfo);
        this.send(request);
    }

}
