var BaseGameCommand = require('BaseGameCommand');
var SlotMessage = require('SlotMessage');
var SlotAlaProxy = require('SlotAlaProxy');

export default class SetupSlotAlaCommand extends BaseGameCommand {
  execute(notification) {
    BaseGameCommand.prototype.execute.call(this, notification);
    this.gameMediator =  notification.getBody().gameMediator;
    this.gameProxy = notification.getBody().gameProxy;
    SlotAlaProxy.proxy = this.gameProxy;

    this.facade.registerCommand(SlotMessage.INIT_SLOT_ALADIN, require('InitSlotAlaCommand'));
    this.facade.registerCommand(SlotMessage.SEND_SPIN_ALADIN, require('SendSpinAlaCommand'));
    this.facade.registerCommand(SlotMessage.SEND_GET_RANK_ALADIN, require('SendGetRankAlaCommand'));
    this.facade.registerCommand(SlotMessage.SEND_GET_MY_HISTORY_ALADIN, require('SendGetMyHistoryAlaCommand'));
    this.facade.registerCommand(SlotMessage.SEND_LEAVE_ROOM_ALADIN, require('SendLeaveRoomAlaCommand'));
    this.facade.registerCommand(SlotMessage.SEND_ORDER_SPIN_ALADIN, require('SendOrderSpinAlaCommand'));

    this.facade.registerCommand(SlotMessage.RECEIVE_SPIN_ALADIN, require('ReceiveSpinAlaCommand'));
    this.facade.registerCommand(SlotMessage.RECEIVE_UPDATE_JACKPOT_ALADIN, require('ReceiveUpdateJackPotAlaCommand'));
    this.facade.registerCommand(SlotMessage.RECEIVE_GET_RANK_ALADIN, require('ReceiveGetRankAlaCommand'));
    this.facade.registerCommand(SlotMessage.RECEIVE_GET_MY_HISTORY_ALADIN, require('ReceiveGetMyHistoryAlaCommand'));
    this.facade.registerMediator(this.gameMediator);
  }
}
