var BaseCommand = require('BaseCommand');
var TaiXiuProxy = require('TaiXiuProxy');
var MiniGameMessage = require('MiniGameMessage');

export default class SetupTaiXiuCommand extends BaseCommand {
  execute(notification) {
    BaseCommand.prototype.execute.call(this, notification);
    this.gameProxy = new TaiXiuProxy();

    //register command
    this.facade.registerCommand(MiniGameMessage.INIT_TAI_XIU, require('InitTaiXiuCommand'));
    this.facade.registerCommand(MiniGameMessage.SEND_BET_TAIXIU, require('SendBetTaiXiuCommand'));
    this.facade.registerCommand(MiniGameMessage.SEND_CHANGE_MONEY_TYPE_TAIXIU, require('SendChangeMoneyTypeTaiXiuCommand'));
    this.facade.registerCommand(MiniGameMessage.SEND_GET_DETAIL_SESSION_TAI_XIU, require('SendGetDetailSessionTaiXiuCommand'));
    this.facade.registerCommand(MiniGameMessage.SEND_CHAT_TAIXIU, require('SendChatTaiXiuCommand'));
    this.facade.registerCommand(MiniGameMessage.SEND_GET_RANK_TAIXIU, require('SendGetRankTaiXiuCommand'));
    this.facade.registerCommand(MiniGameMessage.SEND_GET_HISTORY_TAIXIU, require('SendGetHistoryTaiXiuCommand'));
    this.facade.registerCommand(MiniGameMessage.SEND_GET_EVENT_TAIXIU, require('SendGetEventTaiXiuCommand'));

    this.facade.registerCommand(MiniGameMessage.RECEIVE_GET_PLAYING_INFO_TAIXIU, require('ReceiveGetPlayingInfoTaiXiuCommand'));
    this.facade.registerCommand(MiniGameMessage.RECEIVE_UPDATE_BOARD_BET_TAIXIU, require('ReceiveUpdateBoardBetTaiXiuCommand'));
    this.facade.registerCommand(MiniGameMessage.RECEIVE_UPDATE_GAME_STATE_TAIXIU, require('ReceiveUpdateGameStateTaiXiuCommand'));
    this.facade.registerCommand(MiniGameMessage.RECEIVE_REPAY_TAIXIU, require('ReceiveRepayTaiXiuCommand'));
    this.facade.registerCommand(MiniGameMessage.RECEIVE_GAME_OVER_TAIXIU, require('ReceiveGameOverTaiXiuCommand'));
    this.facade.registerCommand(MiniGameMessage.RECEIVE_UPDATE_MONEY_TAIXIU, require('ReceiveUpdateMoneyTaiXiuCommand'));
    this.facade.registerCommand(MiniGameMessage.RECEIVE_BET_TAIXIU, require('ReceiveBetTaiXiuCommand'));
    this.facade.registerCommand(MiniGameMessage.RECEIVE_CHAT_TAIXIU, require('ReceiveChatTaiXiuCommand'));
    this.facade.registerCommand(MiniGameMessage.RECEIVE_GET_RANK_TAIXIU, require('ReceiveGetRankTaiXiuCommand'));
    this.facade.registerCommand(MiniGameMessage.RECEIVE_GET_HISTORY_TAIXIU, require('ReceiveGetHistoryTaiXiuCommand'));
    this.facade.registerCommand(MiniGameMessage.RECEIVE_CHANGE_MONEY_TYPE_TAIXIU, require('ReceiveChangeMoneyTypeTaiXiuCommand'));

    //register proxy
    this.facade.registerProxy(this.gameProxy);

    //register mediator
    this.facade.registerMediator(require('TaiXiuSceneMediator').getInstance);
    // this.facade.registerMediator(require('DetailSessionTaiXiuMediator').getInstance);
    // this.facade.registerMediator(require('ChatTaiXiuMediator').getInstance);
  }
}
