var LobbyEvent = require('LobbyEvent');
var Utility = require('Utility');
cc.Class({
    extends: cc.Component,

    properties: {
        mailDetailPanel: cc.Node,
        content: cc.Node,
        itemTemp: cc.Node,
        txtMailDetail: cc.RichText
    },

    // use this for initialization
    onLoad: function () {
    },

    updateMailList: function (data) {
        // if ()
        this.content.removeAllChildren();
        for(var i=0 ; i<data.length ; i++){
            var item = cc.instantiate(this.itemTemp);
            item.active = true;
            item.operatorId = data[i].operatorId;
            item.getChildByName("bgLine").active = (i % 2 === 0);
            // item.getChildByName("id").getComponent(cc.Label).string = data[i].id;
            item.getChildByName("txtTime").getComponent(cc.Label).string = data[i].createdDate;
            item.getChildByName("txtTitle").getComponent(cc.Label).string = data[i].title.toUpperCase();
            item.getChildByName("txtSender").getComponent(cc.Label).string = data[i].sender;
            // item.getChildByName("txtStatus").getComponent(cc.Label).string = data[i].statusText;
            this.content.addChild(item);
        }
    },

    onSendGetMailDetail: function (event) {
        var operatorId = event.target.operatorId;
        this.node.emit('SEND_GET_MAIL_DETAIL', operatorId);
    },

    onDeleteMail: function (event) {
        event.target.getParent().destroy();
        var operatorId = event.target.getParent().operatorId;
        this.node.emit('SEND_DELETE_MAIL', operatorId);
    },

    showMailDetail(content) {
        this.mailDetailPanel.active = true;
        this.txtMailDetail.string = content;

    },

    closeMailDetail() {
        this.mailDetailPanel.active = false;
    }

});
