import { SFSData } from "../../../../network/sfs/constans/SFSData";
import { SFSEvent } from "../../../../network/sfs/constans/SFSEvent";

var MinigameProxy = require("MinigameProxy");
var MiniGameMessage = require('MiniGameMessage');
var Constants = require('Constants');
var Utility = require('Utility');

export default class TaiXiuProxy extends MinigameProxy {
    static get NAME() {
        return 'TaiXiuProxy';
    }

    /////////////////////////////////////////////////////////////////
    ////////////////////SEVER REPONSE//////////////////////////////
    ////////////////////////////////////////////////////////////////
    onMessage(event) {
        MinigameProxy.prototype.onMessage.call(this, event);
        var data = null;
        if (typeof event.params === "object") {
            data = Utility.convertSFSObjectToObject(event.params);
        }

        switch (event.cmd) {
            case SFSEvent.USER_JOIN_MINIGAME:
                this.sendNotification(MiniGameMessage.RECEIVE_JOIN_MINIGAME, { curMiniGame: Constants.MINIGAME_TAI_XIU, data: data });
                break;
            case SFSEvent.GET_PLAYING_INFO:
                this.sendNotification(MiniGameMessage.RECEIVE_GET_PLAYING_INFO_TAIXIU, { curMiniGame: Constants.MINIGAME_TAI_XIU, data: data });
                break;
            case SFSEvent.UPDATE_GAME_STATE:
                this.sendNotification(MiniGameMessage.RECEIVE_UPDATE_GAME_STATE_TAIXIU, { curMiniGame: Constants.MINIGAME_TAI_XIU, data: data });
                break;
            case SFSEvent.UPDATE_BOARD_BET:
                this.sendNotification(MiniGameMessage.RECEIVE_UPDATE_BOARD_BET_TAIXIU, { curMiniGame: Constants.MINIGAME_TAI_XIU, data: data });
                break;
            case SFSEvent.REPAY:
                this.sendNotification(MiniGameMessage.RECEIVE_REPAY_TAIXIU, { data: data });
                break;
            case SFSEvent.GAME_OVER:
                this.sendNotification(MiniGameMessage.RECEIVE_GAME_OVER_TAIXIU, { curMiniGame: Constants.MINIGAME_TAI_XIU, data: data });
                break;
            case SFSEvent.UPDATE_MONEY:
                this.sendNotification(MiniGameMessage.RECEIVE_UPDATE_MONEY_TAIXIU, { curMiniGame: Constants.MINIGAME_TAI_XIU, data: data });
                break;
            case SFSEvent.BET:
                this.sendNotification(MiniGameMessage.RECEIVE_BET_TAIXIU, { curMiniGame: Constants.MINIGAME_TAI_XIU, data: data });
                break;
            case SFSEvent.CHANGE_MONEY_TYPE:
                this.sendNotification(MiniGameMessage.RECEIVE_CHANGE_MONEY_TYPE_TAIXIU, data);
                break;
            case SFSEvent.GET_HISTORY:
                this.sendNotification(MiniGameMessage.ON_UPDATE_HISTORY_TAI_XIU, data );
                break;
            case SFSEvent.GET_TOP:
                this.sendNotification(MiniGameMessage.ON_UPDATE_TOP_TAI_XIU, data);
                break;
            case SFSEvent.GET_DETAIL_SESSION:
                this.sendNotification(MiniGameMessage.ON_UPDATE_MY_DETAIL_SESSION_TAI_XIU, data);
                break;
            case SFSEvent.GET_TOP_EVENT:
                this.sendNotification(MiniGameMessage.ON_UPDATE_TOP_EVENT_TAIXIU, data);
                break;
            default:
        }
    }

    onPublicMessage(event) {
        MinigameProxy.prototype.onPublicMessage.call(this, event);
        var data = null;
        if (typeof event.data === "object") {
            data = Utility.convertSFSObjectToObject(event.data);
        }
        switch (event.message) {
            case SFSEvent.PUBLIC_CHAT:
                if (data.userName === "") return;
                this.sendNotification(MiniGameMessage.RECEIVE_CHAT_TAIXIU, data);
                break;
            default:
        }
    }

    /////////////////////////////////////////////////////////////////
    ////////////////////SEND TO SERVER//////////////////////////////
    ////////////////////////////////////////////////////////////////

    sendBet(params) {
        var sfo = new SFS2X.SFSObject();
        sfo.putUtfString(SFSData.VALUE, params.value);
        sfo.putInt(SFSData.TYPE, params.type);
        sfo.putInt(SFSData.MONEY_TYPE, params.moneyType);

        this.sendExtensionRequest(SFSEvent.BET, sfo);
    }

    sendChangeMoneyType(params) {
        var sfo = new SFS2X.SFSObject();
        sfo.putInt(SFSData.MONEY_TYPE, params.params.moneyType);
        this.sendExtensionRequest(SFSEvent.CHANGE_MONEY_TYPE, sfo);
    }

    sendGetDetailSession(params) {
        var session = params.session;
        var page = params.page;
        var type = params.moneyType;
        var sfsob = new SFS2X.SFSObject();
        sfsob.putInt(SFSData.PAGE, page);
        // sfsob.putInt(SFSData.MONEY_TYPE, type);
        sfsob.putLong(SFSData.SID, session);
        this.sendExtensionRequest(SFSEvent.GET_DETAIL_SESSION, sfsob);
    }

    sendChat(params) {
        var sfo = new SFS2X.SFSObject();
        sfo.putUtfString(SFSData.COMMAND, SFSData.PUBLIC_CHAT);
        sfo.putUtfString(SFSData.MESSAGE, params.message);
        sfo.putUtfString(SFSData.DISPLAY_NAME, params.displayName);
        sfo.putUtfString(SFSData.USER_NAME, this.dataUser.mySelf.uid);
        var room = this.sfs.lastJoinedRoom;
        var request = new SFS2X.PublicMessageRequest(SFSEvent.PUBLIC_CHAT, sfo, room);
        this.send(request);
    }

    sendGetRank(params) {
        var page = params.page;
        var type = 0;
        var sfsob = new SFS2X.SFSObject();
        sfsob.putInt(SFSData.PAGE, page);
        sfsob.putInt(SFSData.MONEY_TYPE, type);
        this.sendExtensionRequest(SFSEvent.GET_TOP, sfsob);
    }

    sendGetHistory(params) {
        var page = params.page;
        var type = 0;
        var sfsob = new SFS2X.SFSObject();
        sfsob.putInt(SFSData.PAGE, page);
        sfsob.putInt(SFSData.MONEY_TYPE, type);
        this.sendExtensionRequest(SFSEvent.GET_HISTORY, sfsob);
    }

    sendGetEvent(params) {
        var type = params.type;
        var isWin = params.isWin;
        var indexDate = params.indexDate;
        var sfsob = new SFS2X.SFSObject();
        sfsob.putInt(SFSData.TYPE, type);
        sfsob.putBool(SFSData.IS_WIN, isWin);
        sfsob.putInt(SFSData.INDEX_DATE, indexDate);
        this.sendExtensionRequest(SFSEvent.GET_TOP_EVENT, sfsob);
    }
}
