var BaseCommand = require('BaseCommand');
var MiniGameMessage = require('MiniGameMessage');
var MiniPokerProxy = require('MiniPokerProxy');
var MiniPokerSceneMediator = require('MiniPokerSceneMediator');

export default class SetupMiniPokerCommand extends BaseCommand {
  execute(notification) {
    BaseCommand.prototype.execute.call(this, notification);
    if (!this.gameProxy) {
      this.gameProxy = new MiniPokerProxy();
    }

    this.facade.registerCommand(MiniGameMessage.INIT_MINI_POKER, require('InitMiniPokerCommand'));

    this.facade.registerCommand(MiniGameMessage.SEND_SPIN_MINI_POKER, require('SendSpinMiniPokerCommand'));
    this.facade.registerCommand(MiniGameMessage.SEND_LEAVE_ROOM_MINI_POKER, require('SendSpinMiniPokerCommand'));
    this.facade.registerCommand(MiniGameMessage.SEND_GET_RANK_MINI_POKER, require('SendGetRankMiniPokerCommand'));
    this.facade.registerCommand(MiniGameMessage.SEND_GET_HISTORY_MINI_POKER, require('SendGetHistoryMiniPokerCommand'));

    this.facade.registerCommand(MiniGameMessage.RECEIVE_SPIN_MINI_POKER, require('ReceiveSpinMiniPokerCommand'));
    this.facade.registerCommand(MiniGameMessage.RECEIVE_UPDATE_JACKPOT_MINI_POKER, require('ReceiveUpdateJackPotMiniPokerCommand'));
    this.facade.registerCommand(MiniGameMessage.RECEIVE_UPDATE_MONEY_MINI_POKER, require('ReceiveUpdateMoneyMinipokerCommand'));
    this.facade.registerCommand(MiniGameMessage.RECEIVE_GET_RANK_MINI_POKER, require('ReceiveGetRankMiniPokerCommand'));
    this.facade.registerCommand(MiniGameMessage.RECEIVE_GET_HISTORY_MINI_POKER, require('ReceiveGetHistoryMiniPokerCommand'));

    //register proxy
    this.facade.registerProxy(this.gameProxy);

    //register mediator
    this.facade.registerMediator(MiniPokerSceneMediator.getInstance);
  }
}
