var BaseScene = require('BaseScene');
var Constants = require('Constants');
var SelectGameSceneMediator = require('SelectGameSceneMediator');
var Utility = require('Utility');

var SelectGameScene = cc.Class({
    extends: BaseScene,

    properties: {
        listButton: [cc.Button],
        modeDisplay: 1,
        iconTaiXiu: cc.Node,
        iconSlot3x3: cc.Node,
        iconBomTan: cc.Node,
        iconMiniPoker: cc.Node,
        iconTamQuoc: cc.Node,
        iconMasterChef: cc.Node,
        iconAladin: cc.Node,
        iconCandy: cc.Node,
        timeDelay: 5,
        mcTopHu: cc.Node,
    },

    // use this for initialization
    onLoad: function () {
        SelectGameSceneMediator.getInstance.init(this);
        this.onSendGetListJackpot();
        // this.schedule(this.requestUpdate.bind(this), this.timeDelay, cc.macro.REPEAT_FOREVER, 0);
        setInterval(this.requestUpdate.bind(this), 5000);
    },

    activeJackpot: function () {
        TweenLite.to(this.mcTopHu, 0.3, {x:-565, y:230});
        this.activeListJackpot();
    },

    changeMode: function () {
        this.modeDisplay = (this.modeDisplay + 1) % 2; // hien thi minigame
        this.requestUpdate();
    },

    hanlerEventSelectGameTLMN: function () {
        this.onJoinGameChannel(Constants.GAME_TLMN);
    },

    hanlerEventSelectGameTLMNSolo: function () {
        this.onJoinGameChannel(Constants.GAME_TLMN_SOLO);
    },

    hanlerEventSelectGamePhom: function () {
        this.onJoinGameChannel(Constants.GAME_PHOM);
    },

    hanlerEventSelectGameBinh: function () {
        this.onJoinGameChannel(Constants.GAME_BINH);
    },

    hanlerEventSelectGameSam: function () {
        this.onJoinGameChannel(Constants.GAME_SAM);
    },

    hanlerEventSelectGameSamSolo: function () {
        this.onJoinGameChannel(Constants.GAME_SAM_SOLO);
    },

    hanlerEventSelectGameXocDia: function () {
        this.onJoinGameChannel(Constants.GAME_XOCDIA);
    },

    hanlerEventSelectGameXiTo: function () {
        //this.onJoinGameChannel(Constants.GAME_XITO);
    },

    hanlerEventSelectGameLieng: function () {
        //this.onJoinGameChannel(Constants.GAME_LIENG);
    },

    hanlerEventSelectGameTaiXiu: function (event) {
        this.onJoinMiniGame({game: Constants.MINIGAME_TAI_XIU, target: event.target});
    },

    hanlerEventSelectGamePokego: function (event) {
        this.onJoinMiniGame({game: Constants.MINIGAME_SLOT3X3, target: event.target});
    },

    hanlerEventSelectGameBomTan: function (event) {
        this.onJoinMiniGame({game: Constants.MINIGAME_SLOT_BT, target: event.target});
    },

    hanlerEventSelectGameMiniPoker: function (event) {
        this.onJoinMiniGame({game: Constants.MINIGAME_MINI_POKER, target: event.target});
    },

    hanlerEventSelectGameBauCua: function (event) {
        this.onJoinMiniGame({game: Constants.MINIGAME_BAU_CUA, target: event.target});
    },

    hanlerEventSelectGameVongQuay: function () {
        this.onJoinMiniGame(Constants.MINIGAME_VONG_QUAY);
    },

    hanlerEventSelectSlot20LuckyCafe: function (event) {
        this.onJoinSlot({game: Constants.SLOT20_LUCKY_CAFE, target: event.target});
    },

    hanlerEventSelectSlot20KeoNgot: function (event) {
        this.onJoinSlot({game: Constants.SLOT20_KEO_NGOT, target: event.target});
    },

    hanlerEventSelectSlot20MasterChef: function (event) {
        this.onJoinSlot({game: Constants.SLOT20_MASTER_CHEF, target: event.target});
    },

    hanlerEventSelectSlotAladin: function (event) {
        this.onJoinSlot({game: Constants.SLOT25_ALADIN, target: event.target});
    },

    hanlerEventSelectSlotFootball: function (event) {
        this.onJoinSlot({game: Constants.SLOT30_FOOTBALL, target: event.target});
    },

    requestUpdate: function () {
        this.onSendGetListJackpot();
    },

    updateListJackpot: function (listJackpot) {
        for (var i = 0; i < listJackpot.length; i++) {
            var btn = null;
            switch (listJackpot[i].code) {
                case 'tamquoc':
                    btn = this.iconTamQuoc;
                    break;
                case 'songoku':
                    btn = this.iconMasterChef;
                    break;
                case 'aicap':
                    btn = this.iconAladin;
                    break;
                case 'candy':
                    btn = this.iconCandy;
                    break;
            }

            var goalValue = 0;
            var name = "";


            if (btn) {
                goalValue = listJackpot[i].jp100;
                name = "jp100";

                var node = btn.getChildByName(name);
                Utility.tweenRunNumber(node, goalValue, 3);


                goalValue = listJackpot[i].jp1000;
                name = "jp1000";

                node = btn.getChildByName(name);
                Utility.tweenRunNumber(node, goalValue, 3);


                goalValue = listJackpot[i].jp10000;
                name = "jp10000";

                node = btn.getChildByName(name);
                Utility.tweenRunNumber(node, goalValue, 3);
            }
        }
    },

    clickMinigame: function (data) {
        switch (data) {
            case Constants.MINIGAME_TAI_XIU:
                this.hanlerEventSelectGameTaiXiu(this.iconTaiXiu.getComponent(cc.Button));
                break;
            case Constants.MINIGAME_MINI_POKER:
                this.hanlerEventSelectGameMiniPoker(this.iconMiniPoker.getComponent(cc.Button));
                break;
            case Constants.MINIGAME_SLOT_BT:
                this.hanlerEventSelectGameBomTan(this.iconBomTan.getComponent(cc.Button));
                break;
            case Constants.MINIGAME_SLOT3X3:
                this.hanlerEventSelectGamePokego(this.iconSlot3x3.getComponent(cc.Button));
                break;
        }
    }
});

