var BaseCommand = require('BaseCommand');
var LobbyMessage = require('LobbyMessage');

export default class SendUseGiftCommand extends BaseCommand {
    execute(notification) {
        BaseCommand.prototype.execute.call(this, notification);
        var http = this.facade.retrieveProxy('HttpRequestProxy');
        var config = this.facade.retrieveProxy('ConfigProxy').config;
        var mySelf = this.facade.retrieveProxy('UserProxy').mySelf;

        var sendData = {
            userId: mySelf.uid,
            userName: mySelf.acountName,
            securityCode: config.securityCode
        };

        http.getGiftList(LobbyMessage.RECEIVE_GET_GIFT_LIST, sendData);
    }
}