var BaseCommand = require('BaseCommand');
var BauCuaProxy = require('BauCuaProxy');
var MiniGameMessage = require('MiniGameMessage');

export default class SetupBauCuaCommand extends BaseCommand {
    execute(notification) {
        BaseCommand.prototype.execute.call(this, notification);
        this.gameProxy = new BauCuaProxy();

        //register command
        this.facade.registerCommand(MiniGameMessage.INIT_BAU_CUA, require('InitBauCuaCommand'));
        this.facade.registerCommand(MiniGameMessage.SEND_BET_BAU_CUA, require('SendBetBauCuaCommand'));
        this.facade.registerCommand(MiniGameMessage.SEND_SOI_CAU_BAU_CUA, require('SendSoiCauBauCuaCommand'));
        this.facade.registerCommand(MiniGameMessage.SEND_GET_RANK_BAU_CUA, require('SendGetRankBauCuaCommand'));
        this.facade.registerCommand(MiniGameMessage.SEND_GET_HISTORY_BAU_CUA, require('SendGetHistoryBauCuaCommand'));
        //
        this.facade.registerCommand(MiniGameMessage.RECEIVE_GET_PLAYING_INFO_BAU_CUA, require('ReceiveGetPlayingInfoBauCuaCommand'));
        this.facade.registerCommand(MiniGameMessage.RECEIVE_UPDATE_BOARD_BET_BAU_CUA, require('ReceiveUpdateBoardBetBauCuaCommand'));
        this.facade.registerCommand(MiniGameMessage.RECEIVE_GAME_OVER_BAU_CUA, require('ReceiveGameOverBauCuaCommand'));
        this.facade.registerCommand(MiniGameMessage.RECEIVE_UPDATE_MONEY_BAU_CUA, require('ReceiveUpdateMoneyBauCuaCommand'));
        this.facade.registerCommand(MiniGameMessage.RECEIVE_BET_BAU_CUA, require('ReceiveBetBauCuaCommand'));
        this.facade.registerCommand(MiniGameMessage.RECEIVE_GET_RANK_BAU_CUA, require('ReceiveGetRankBauCuaCommand'));
        this.facade.registerCommand(MiniGameMessage.RECEIVE_GET_HISTORY_BAU_CUA, require('ReceiveGetHistoryBauCuaCommand'));

        //register proxy
        this.facade.registerProxy(this.gameProxy);

        //register mediator
        this.facade.registerMediator(require('BauCuaSceneMediator').getInstance);
    }
}
