var BaseCommand = require('BaseCommand');
var MiniGameMessage = require('MiniGameMessage');
var LobbyMessage = require('LobbyMessage');
var i18n = require('i18n');

export default class SendSpinSlotBTCommand extends BaseCommand {
  execute(notification) {
    BaseCommand.prototype.execute.call(this, notification);
    var params = notification.getBody();
    var gameVO = this.facade.retrieveProxy('SlotBTProxy').gameVO;

    var mySelf = this.facade.retrieveProxy('UserProxy').mySelf;
    var money = params.data.bet;
    if (money > mySelf.money) {
      this.sendNotification(LobbyMessage.SHOW_FLY_ALERT, { content: i18n.t("T0002") });
      this.sendNotification(MiniGameMessage.ON_RESET_BUTTON_SLOT_BT);
      return;
    }

    if(gameVO.isSpining === true){
      this.sendNotification(MiniGameMessage.ON_SHOW_MESSAGE_SLOT_BT, {message: i18n.t("T0012")});
      return;
    }
    gameVO.isSpining = true;
    gameVO.prevDataSpin = params.data;

    var sendData = {
      lines: params.data.lines,
      bet: params.data.bet,
      gid: params.data.roomId
    }
    this.gameProxy = this.facade.retrieveProxy("SlotBTProxy");
    this.gameProxy.sendSpin(sendData);
    this.sendNotification(MiniGameMessage.ON_HIDE_ALL_LINE_SLOT_BT);

    var session = gameVO.session;
      setTimeout(function(session, gameVO){
          if(gameVO.session !== session) return;
          gameVO.isSpining = false;
          this.sendNotification(MiniGameMessage.ON_RESET_BTN_SPIN_SLOT_BT);
      }.bind(this), 5000,session, gameVO);
  }
}
