var BaseCommand = require('BaseCommand');
var LobbyMessage = require('LobbyMessage');
var Utility = require('Utility');
var i18n = require('i18n');

export default class SendRegisterCommand extends BaseCommand {
    execute(notification) {
        BaseCommand.prototype.execute.call(this, notification);
        var params = notification.getBody();
        var userName = params.userName;
        var password = params.password;
        var displayName = params.displayName;
        var rePassword = params.repassword;
        var captcha = params.captcha;

        if (this.isError(userName, password, rePassword, displayName)) return;

        var http = this.facade.retrieveProxy('HttpRequestProxy');
        var config = this.facade.retrieveProxy('ConfigProxy').config;

        var ls = cc.sys.localStorage;
        config.deviceId = (ls.getItem("deviceId")) ? ls.getItem("deviceId") : Utility.randomNumber(0, 100000000000) + Utility.randomNumber(0, 100000000000);

        var sendData = {
            userName: userName,
            displayName: "",
            password: password,
            rePassword: rePassword,
            deviceId: config.deviceId,
            clientKey: config.clientKey,
            keyRand: displayName,
            captcha: captcha
        };
        http.register(LobbyMessage.RECEIVE_REGISTER, sendData);
    }

    isError(userName, password, rePassword, displayName) {
        if (userName === "" || password === "" || displayName === "" || rePassword === "") {
            this.sendNotification(LobbyMessage.SHOW_FLY_ALERT, { content: i18n.t("C0005") });
            return true;
        }

        if (password !== rePassword) {
            this.sendNotification(LobbyMessage.SHOW_FLY_ALERT, { content: i18n.t("C0006") });
            return true;
        }

        if (password.length < 6) {
            this.sendNotification(LobbyMessage.SHOW_FLY_ALERT, { content: i18n.t("C0007") });
            return true;
        }

        return false;
    }
}
