var BaseMediator = require('BaseMediator');
var LobbyMessage = require('LobbyMessage');

class HelpAndRulesSceneMediator extends BaseMediator {
    static get NAME() {
        return 'HelpAndRulesSceneMediator';
    }

    static get getInstance() {
        if (this.instance === undefined) {
            this.instance = new HelpAndRulesSceneMediator();
        }
        return this.instance;
    }

    onRegister() {
    }

    /** @override */
    listNotificationInterests() {
        return [
            LobbyMessage.SHOW_HELP_AND_RULES_SCENE,
            LobbyMessage.HIDE_HELP_AND_RULES_SCENE
        ];
    }

    /** @override */
    handleNotification(notification) {
        BaseMediator.prototype.handleNotification.call(this);
        switch (notification.getName()) {
            case LobbyMessage.SHOW_HELP_AND_RULES_SCENE:
                this.view.show();
                break;
            case LobbyMessage.HIDE_HELP_AND_RULES_SCENE:
                this.view.hide();
                break;
            default:
                break;
        }
    }

    addHanlers() {
        BaseMediator.prototype.addHanlers.call(this);
    }

}

module.exports = HelpAndRulesSceneMediator;
