var BaseScene = require('BaseScene');
var RegisterSceneMediator = require('RegisterSceneMediator');
var i18n = require('i18n');
var Captcha = require('Captcha');
var GameConfig = require('GameConfig');

cc.Class({
    extends: BaseScene,

    properties: {
        txtUserName: cc.EditBox,
        txtDisplayName: cc.EditBox,
        txtPassword: cc.EditBox,
        txtRepassword: cc.EditBox,
        txtError: cc.Label,
        txtCaptcha: cc.Node,
        ctlCaptcha: Captcha,
        toggleTerm: cc.Toggle
    },

    // use this for initialization
    onLoad: function () {
        RegisterSceneMediator.getInstance.init(this);
        // this.hide();
    },

    show: function () {
        // BaseScene.prototype.show.call(this);
        // this.txtUserName.string = "";
        // this.txtPassword.string = "";
        // this.txtRepassword.string = "";
        // this.txtDisplayName.string = "";
    },

    refreshCaptcha() {
        this.ctlCaptcha.refeshCaptcha();
    },

    handlerLoginFacebook: function () {
        this.activeLoginFB();
    },

    handlerEventRegister: function () {
        // if (this.toggleTerm.isChecked === false) {
        //     this.showAlert(i18n.t("C0037")).bind(this);
        //     return;
        // }

        var captcha = this.txtCaptcha.getChildByName("txtInput").getComponent(cc.EditBox);

        var data = {
            userName: this.txtUserName.string,
            password: this.txtPassword.string,
            repassword: this.txtRepassword.string,
            displayName: GameConfig.DISPLAY_NAME,
            captcha: captcha.string
        };

        this.activeRegister(data);
    },

    handlerEventShowLogin: function () {
        this.activeShowLoginScene();
    },

    onHandleSupportClick: function () {
        var url = GameConfig.POLICY;
        this.activeShowWebViewPopup(url);
    }

});
