var BaseGameCommand = require('BaseGameCommand');
var SFSSubMesseage = require('SFSSubMesseage');
var SFSMessage = require('SFSMessage');

export default class SendLeaveGameCommand extends BaseGameCommand {
    execute(notification) {
        BaseGameCommand.prototype.execute.call(this, notification);

        var tableVO = this.gameProxy.tableVO;
        if (tableVO.registerLeave) {
            this.gameProxy.sendLeaveGame();
        } else {
            this.gameProxy.sendCancelRegisterQuit();
        }
    }
}
