var BaseVO = require("BaseVO");

export default class Slot3x3VO extends BaseVO{
    static get ERROR() { return 0 }
    static get NORMAL_WIN() { return 1 }
    static get BIG_WIN() { return 2 }
    static get JACKPOT_WIN() { return 3 }

    onRegister() {
      this.session = 0;
      this.bet = 0;
      this.money = 0;
      this.prize = 0;
      this.jackpot = 0;
      this.wins = [];
      this.items = [];
      this.timeSpin = 1;
      this.isSpining = false;
      this.prevDataSpin = false;
      this.autoSpin = false;
      this.errorMessage = "";
      this.typeResult = -1;
      this.selectRoomId = 1;
      this.freeSpin = 0;

      this.roomJackpot1 = 0;
      this.roomJackpot2 = 0;
      this.roomJackpot3 = 0;

      this.linesBet = '11111111111111111111';
      this.listBet = [0, 100, 1000, 10000];
      this.listRoom = [0, 1, 2, 3, 4]
    }

    reset(){
      this.bet = 0;
      this.money = 0;
      this.prize = 0;
      this.jackpot = 0;
      this.wins = [];
      this.items = [];
      this.errorMessage = "";
      this.typeResult = -1;
    }

    updateData(data) {
      this.reset();
      if (data.success === false) {
        this.typeResult = Slot3x3VO.ERROR;
        this.errorMessage = data.m;
      }else{
        this.session = data.data.id;
        this.bet = data.gid;
        this.money = data.data.money;
        this.prize = data.data.prize;
        // this.jackpot = data.data.jackpot;
        this.wins = [];
        if (data.data.rewardLine) {
          for (var i = 0; i < data.data.rewardLine.length; i++) {
            var o = data.data.rewardLine[i];
            //jackpot
            if (o.rewardId === 1) this.jackpot = o.value;
            var iw = {
              line: o.lineId,
              itemId: o.rewardId,
              money: o.value
            }
            this.wins.push(iw);
          }
        }
        
        this.items = data.data.slots;

        // if(this.jackpot > 0) this.typeResult = Slot3x3VO.JACKPOT_WIN;
        // else if(this.prize >= this.listBet[this.bet] * 70) this.typeResult = Slot3x3VO.BIG_WIN;
        // else
        this.typeResult = Slot3x3VO.NORMAL_WIN;
      }

    }

    setLinesBet(value) {
      this.linesBet = value;
    }

    getCountBet() {
      var arr = this.linesBet.split('');
      var count = 0;
      for(var i=0;i<this.linesBet.length;i++){
          if(this.linesBet[i] === "1") count++;
      }
      return count;
    }

    getLinesBet() {
      var arr = this.linesBet.split('');
      var result = [];
      for (var i = 0; i < arr.length; i++) {
        if (arr[i] === "1") result.push(i + 1);
      }

      return result.toString()
    }

    getTotalBet() {
      var count = this.getCountBet();
      return count * this.listBet[this.selectRoomId];
    }

    getCurentBet() {
      return this.listBet[this.selectRoomId];
    }

    updateSelectRoomId(selectRoomId) {
      this.selectRoomId = selectRoomId;
    }

    updatejackpot(data) {
      this.roomJackpot1 = data[0].jackpot;
      this.roomJackpot2 = data[1].jackpot;
      this.roomJackpot3 = data[2].jackpot;
    }

    getCurrentJackpot() {
      var roomJackPot = 0;
      if(this.selectRoomId === 1) roomJackPot = this.roomJackpot1;
      if(this.selectRoomId === 2) roomJackPot = this.roomJackpot2;
      if(this.selectRoomId === 3) roomJackPot = this.roomJackpot3;
      return roomJackPot;
    }
}
