var BaseCommand = require('BaseCommand');
var LobbyMessage = require('LobbyMessage');
var i18n = require('i18n');

export default class SendMobileCardRechargeCommand extends BaseCommand {
    execute(notification) {
        BaseCommand.prototype.execute.call(this, notification);
        var data = notification.getBody();
        if (this.isError(data.numCard, data.numSerial, data.partnerCode)) return;

        var http = this.facade.retrieveProxy('HttpRequestProxy');
        var config = this.facade.retrieveProxy('ConfigProxy').config;
        var mySelf = this.facade.retrieveProxy('UserProxy').mySelf;

        var sendData = {
            displayName: mySelf.displayName,
            token: mySelf.token,
            provider: data.partnerCode,
            serial: data.numSerial,
            code: data.numCard,
            clientKey: config.clientKey

        };

        http.getChargemoney(LobbyMessage.RECEIVE_MOBILE_CARD_RECHARGE, sendData);
    }

    isError(numCard, numSerial, partnerCode) {
        if (!numCard || numCard === "") {
            this.sendNotification(LobbyMessage.SHOW_FLY_ALERT, { content: i18n.t("C0012") });
            return true;
        }

        if (!numSerial || numSerial === "") {
            this.sendNotification(LobbyMessage.SHOW_FLY_ALERT, { content: i18n.t("C0013") });
            return true;
        }

        if (partnerCode === null) {
            this.sendNotification(LobbyMessage.SHOW_FLY_ALERT, { content: i18n.t("C0050") });
            return true;
        }

        return false;
    }
}
