cc.Class({
    extends: cc.Component,

    properties: {
        iconGameContainer: cc.Node,
        listIconShow: [cc.Node]
    },

    onLoad: function () {

    },

    onHandlerChooseMode: function () {
        var i, icon, customAction;
        for (i = 0; i < this.iconGameContainer.children.length; i++) {
            icon = this.iconGameContainer.children[i];
            icon.active = false;
        }

        for (i = 0; i < this.listIconShow.length; i++) {
            icon = this.listIconShow[i];
            customAction = icon.getComponent("CustomAction");
            if(customAction){
                var configProxy = window.facade.retrieveProxy('ConfigProxy');
                var code = configProxy.configIngame.configData[customAction.code];
                if(!code || code.visible  !== "0"){
                    icon.active = true;
                }
            }else if (icon) {
                icon.active = true;
            }
        }
    }

});
