var Utility = require('Utility');
var Slot20VO = require('Slot20VO');

cc.Class({
    extends: cc.Component,

    properties: {
      wildEffect: cc.Node,
      lbPrizeMoney: cc.Label,
      mcJackpot:cc.Node,
      mcBigWin:cc.Node,
      mcNormalWin:cc.Node,
      mcFreeSpin:cc.Node,
      mcMinigame:cc.Node,
    },

    onLoad() {
      this.hide();
    },

    show() {
      // this.node.active = true;
    },

    hide() {
      // this.node.active = false;
      this.mcJackpot.active = false;
      this.mcBigWin.active = false;
      this.mcNormalWin.active = false;
      this.mcFreeSpin.active = false;
      this.mcMinigame.active = false;
    },

    onShowEffectWin (gameVO) {
        this.gameVO = gameVO;
        switch (gameVO.typeResult){
           case Slot20VO.NORMAL_WIN:
               this.showNormalWin(gameVO.prize);
               break;
           case Slot20VO.BIG_WIN:
               this.showBigWin(gameVO.prize);
               break;
           case Slot20VO.JACKPOT_WIN:
               this.showNoHu(gameVO.jackpot);
               break;
           default:
               break;
       }
    },

    onShowEffectFreeSpin(data) {
      this.hide();
      this.show();
      // var lbFreeSpin = this.mcFreeSpin.getChildByName('lbFreeSpin').getComponent(cc.Label);
      this.mcFreeSpin.active = true;
      // lbFreeSpin.string = "0";
      // Utility.tweenRunNumber(lbFreeSpin.node, data.gameVO.freeSpin, 0.5);
      TweenLite.from(this.mcFreeSpin, 0.3,{scaleX:0, scaleY:0});
    },

    showWild(pos) {
      this.hide();
      this.show();
      for (var i = 0; i < pos.length; i++) {
          var nodeWild = this.wildEffect.getChildByName('nodeWild' + pos[i]);
          var spine = nodeWild.getChildByName('skeleton');
          spine.y = 0;
          nodeWild.active = true;
          spine.runAction(cc.moveTo(0.5,cc.p(0,-367)).easing(cc.easeElasticOut(0.3)));
      }
    },

    hideWild() {
        for (var i = 1; i <= 3; i++) {
            this.wildEffect.getChildByName('nodeWild' + i).active = false;
        }
    },

    onShowMinigame(gameVO) {
      this.hide();
      this.show();
      this.mcMinigame.active = true;
      TweenLite.from(this.mcMinigame, 0.3,{scaleX:0, scaleY:0});
    },

    showNormalWin(prize) {
      this.hide();
      this.show();
      this.mcNormalWin.active = true;
      var partical = this.mcNormalWin.getChildByName('partical').getComponent(cc.ParticleSystem);
      partical.resetSystem();
      this.lbPrizeMoney.string = "0";
      Utility.tweenRunNumber(this.lbPrizeMoney.node, prize, 1*this.gameVO.timeSpin);
    },

    showBigWin(prize) {
      this.hide();
      this.show();
      this.mcBigWin.active = true;

      var spine = this.mcBigWin.getChildByName('skeleton').getComponent(sp.Skeleton);
      spine.setAnimation(0, 'active', false);
      spine.addAnimation(0, 'idle', true, 0);
      var partical = this.mcBigWin.getChildByName('partical').getComponent(cc.ParticleSystem);
      partical.resetSystem();

      var lbBigWin = this.mcBigWin.getChildByName('lbBigWin').getComponent(cc.Label);
      this.lbPrizeMoney.string = "0";
      lbBigWin.string = "0";
      Utility.tweenRunNumber(this.lbPrizeMoney.node, prize, 1*this.gameVO.timeSpin);
      Utility.tweenRunNumber(lbBigWin.node, prize, 1*this.gameVO.timeSpin);
      TweenLite.from(this.mcBigWin.getChildByName('bg'), 0.3,{opacity:0});
      TweenLite.from(spine.node, 0.3,{scaleX:0, scaleY:0});
    },

    showNoHu(prize) {
      this.hide();
      this.show();
      var lbJackpot = this.mcJackpot.getChildByName('lbJackpot').getComponent(cc.Label);
      var spine = this.mcJackpot.getChildByName('skeleton').getComponent(sp.Skeleton);
      spine.setAnimation(0, 'active', false);
      spine.addAnimation(0, 'idle', true, 0);
      var partical = this.mcJackpot.getChildByName('partical').getComponent(cc.ParticleSystem);
      partical.resetSystem();
      this.mcJackpot.active = true;
      this.lbPrizeMoney.string = "0";
      Utility.tweenRunNumber(this.lbPrizeMoney.node, prize, 1.5);
      Utility.tweenRunNumber(lbJackpot.node, prize, 1.5);
      TweenLite.from(spine.node.getChildByName('bg'), 0.3,{opacity:0});
        // TweenLite.from(this.mcJackpot, 0.3,{scaleX:0, scaleY:0});
    },
});
