var BaseScene = require('BaseScene');
var TopMenuSceneMediator = require('TopMenuSceneMediator');
var GameConfig = require('GameConfig');
var Avatar = require('Avatar');
var Utility = require('Utility');

cc.Class({
    extends: BaseScene,

    properties: {
        groupTop:cc.Node,
        groupBottom:cc.Node,
        txtUserName:cc.Label,
        // txtHotLine:cc.Label,
        txtMoney:cc.Label,
        txtLevel:cc.RichText,
        btnMore:cc.Node,
        mcMore:cc.Node,
        avatar:Avatar,
        processBarVip:cc.ProgressBar
    },

    show:function () {
        this.groupTop.active = true;
    },

    hide:function () {
        this.groupTop.active = false;
    },

    // use this for initialization
    onLoad: function () {
        TopMenuSceneMediator.getInstance.init(this);
        this.show();
        this.isShowMoreSetting = false;
        this.listMoreSetting = [];

        for(var i = 0; i<this.mcMore.children.length;i++){
            this.listMoreSetting.push(this.mcMore.children[i]);
        }
        this.hideAllListMoreSetting();
        // this.txtHotLine.string = GameConfig.HOT_LINE;
    },

    hideAllListMoreSetting: function () {
        // for (var i = 0; i < this.listMoreSetting.length; i++) {
        //     var btn = this.listMoreSetting[i];
        //     btn.opacity = 0;
        //     btn.pos = new cc.Vec2(btn.x, btn.y);
        //     btn.y = this.btnMore.parent.y + this.btnMore.y;
        // }
        this.isShowMoreSetting = false;
        // this.spaceY = this.listMoreSetting[0].pos.y - this.listMoreSetting[1].pos.y;
        this.mcMore.active = false;
    },

    onHanlerBackClick: function () {
        this.activeBack();
    },

    onUpdateUserInfo: function (mySelf) {
        this.txtUserName.string = mySelf.displayName;
        this.txtMoney.string = Utility.formatCurrency(mySelf.money);
        // this.txtLevel.string = mySelf.levelInfo.levelName;
        this.avatar.updateImg(mySelf.avatar);
        this.processBarVip.progress = mySelf.levelInfo.point / mySelf.levelInfo.targetPoint;
    },

    onUpdateChangeAvatar:function (mySelf) {
        this.avatar.updateImg(mySelf.avatar);
    },

    onHandlerShowGiftForm: function () {
        this.activeShowGift();
    },

    onHandlerShowGiftCodeForm: function () {
        this.activeShowGiftCode();
    },

    onHandlerShowShopForm: function () {
        this.activeShowShop();
    },

    onHandlerShowTransferForm: function () {
        this.activeShowShop({tabId: 1});
    },

    onHandlerShowSupportMailForm: function () {
        this.isShowMoreSetting = false;
        this.mcMore.active = false;
        this.activeShowSupportMail();
    },

    onHandlerShowRechargeForm: function () {
        this.activeShowRecharge();
    },

    onHandlerShowSettingForm: function () {
        this.isShowMoreSetting = false;
        this.mcMore.active = false;
        this.activeShowSetting();
    },

    onHandlerShowMailForm: function () {
        this.activeShowMail();
    },

    onHandlerShowUserProfileForm: function () {
        this.activeShowUserProfile();
    },

    onHandlerShowHelpForm: function () {
        this.isShowMoreSetting = false;
        this.mcMore.active = false;
        this.activeShowHelp();
    },

    onHandlerShowEventForm: function () {
        this.activeShowEvent();
    },

    onHandlerShowAgentForm: function () {
        this.activeShowAgent();
    },

    onHandlerShowSecurityForm: function () {
        this.isShowMoreSetting = false;
        this.mcMore.active = false;
        this.activeShowSecurity();
    },

    onHandlerShowHistoryForm: function () {
        this.isShowMoreSetting = false;
        this.mcMore.active = false;
        this.activeShowHistory();
    },

    onHandlerShowVipForm: function () {
        this.activeShowVip();
    },

    onHandlerDailyEvent: function () {
        this.activeDailyEvent();
    },

    onHandlerDailyMission: function () {
        this.activeDailyMission();
    },

    onHandlerFanpage: function () {
        this.activeShowFanpage();
    },

    onHandlerHotLine: function () {
    },

    onHandlerEvent: function () {
    },

    onHandlerMoreSetting: function () {
        // var index = 0;
        // for (var i = 0; i < this.listMoreSetting.length; i++) {
        //     var btn = this.listMoreSetting[i];
        //     btn.opacity = 255;
        //     if (this.isShowMoreSetting === false) {
        //         if (btn.active === true) {
        //             // TweenLite.to(btn, 0.1 * (i + 1), {y: this.listMoreSetting[0].pos.y - (index * this.spaceY)});
        //             index++;
        //         }
        //     } else {
        //         var posY = this.btnMore.parent.y + this.btnMore.y;
        //         // TweenLite.to(btn, 0.1 * (i + 1), {
        //         //     y: posY, onComplete: function (btn) {
        //         //         btn.opacity = 0;
        //         //     }.bind(this), onCompleteParams: [btn]
        //         // });
        //     }
        // }
        this.isShowMoreSetting = !this.isShowMoreSetting;
        this.mcMore.active = this.isShowMoreSetting;
    }
});

