var BaseMediator = require('BaseMediator');
var Constants = require('Constants');
var MiniGameMessage = require('MiniGameMessage');
var CustomAction = require('CustomAction');

class TaiXiuSceneMediator extends BaseMediator {
  static get NAME() {
    return 'TaiXiuSceneMediator';
  }

  static get getInstance() {
    if(this.instance === undefined){
        this.instance = new TaiXiuSceneMediator();
    }
    return this.instance;
  }

  onRegister() {
      puremvc.Mediator.call(this, this.constructor.NAME);
      this.mySelf =  this.facade.retrieveProxy('UserProxy').mySelf;
  }
  /** @override */
  listNotificationInterests() {
      return [
        MiniGameMessage.SHOW_MINIGAME_TAI_XIU,
        MiniGameMessage.HIDE_MINIGAME_TAI_XIU,
        MiniGameMessage.ON_UPDATE_PLAYING_INFO_TAIXIU,
        MiniGameMessage.ON_UPDATE_BOARD_BET_TAIXIU,
        MiniGameMessage.ON_UPDATE_COUNTDOWN_REPAY_TAIXIU,
        MiniGameMessage.ON_UPDATE_COUNTDOWN_WAITING_TAIXIU,
        MiniGameMessage.ON_UPDATE_GAME_STATE_TAIXIU,
        MiniGameMessage.ON_GAME_OVER_TAIXIU,
        MiniGameMessage.ON_REPAY_TAIXIU,
        MiniGameMessage.ON_UPDATE_MONEY_TAIXIU,
        MiniGameMessage.ON_BET_TAIXIU,
        MiniGameMessage.ON_UPDATE_CHAT_TAIXIU,
        MiniGameMessage.ON_UPDATE_CHANGE_MONEY_TYPE_TAIXIU,
        MiniGameMessage.ON_UPDATE_TOP_TAI_XIU,
        MiniGameMessage.ON_UPDATE_HISTORY_TAI_XIU,
        MiniGameMessage.ON_UPDATE_DETAIL_SESSION_TAI_XIU,
        MiniGameMessage.ON_UPDATE_EVENT_TAIXIU,
        MiniGameMessage.ON_UPDATE_MY_DETAIL_SESSION_TAI_XIU,
        MiniGameMessage.SHOW_MESSAGE_TAIXIU,
        MiniGameMessage.DISCONNECT_ALL_MINIGAME
      ]
  }

  /** @override */
  handleNotification(notification) {
      BaseMediator.prototype.handleNotification.call(this);
      this.userProxy = this.facade.retrieveProxy('UserProxy');

      var data = notification.getBody();
      this.view.buildUI(this.userProxy.mySelf);
      switch (notification.getName()) {
          case MiniGameMessage.SHOW_MINIGAME_TAI_XIU:
              this.view.show();
              break;
          case MiniGameMessage.HIDE_MINIGAME_TAI_XIU:
              this.view.hide();
              break;
          case MiniGameMessage.DISCONNECT_ALL_MINIGAME:
              this.view.hide();
              this.activeDisconnect();
              break;
          case MiniGameMessage.ON_UPDATE_PLAYING_INFO_TAIXIU:
              this.view.onUpdatePlayingInfo(data);
              break;
          case MiniGameMessage.ON_UPDATE_BOARD_BET_TAIXIU:
              this.view.onUpdateBoardBet(data.totalTai, data.totalXiu, data.numTai, data.numXiu, data.time);
              break;
          case MiniGameMessage.ON_UPDATE_COUNTDOWN_REPAY_TAIXIU:
              this.view.onUpdateCountdownRepay(data);
              break;
          case MiniGameMessage.ON_UPDATE_COUNTDOWN_WAITING_TAIXIU:
              this.view.onUpdateCountdownWaiting(data);
              break;
          case MiniGameMessage.ON_UPDATE_GAME_STATE_TAIXIU:
              this.view.onUpdateGameState(data.gameState, data.session);
              break;
          case MiniGameMessage.ON_GAME_OVER_TAIXIU:
              this.view.onShowResultDice(data);
              break;
          case MiniGameMessage.ON_REPAY_TAIXIU:
              this.view.onRepay(data);
              break;
          case MiniGameMessage.ON_UPDATE_MONEY_TAIXIU:
              this.view.onUpdateMoney(data);
              break;
          case MiniGameMessage.ON_BET_TAIXIU:
              this.view.onBetTaiXiu(data);
              break;
          case MiniGameMessage.ON_UPDATE_CHANGE_MONEY_TYPE_TAIXIU:
              this.view.onUpdateChangeMoneyType(data);
              break;
          case MiniGameMessage.ON_UPDATE_MY_DETAIL_SESSION_TAI_XIU:
              this.view.onUpdateDetailSession(data);
              break;
          case MiniGameMessage.ON_UPDATE_TOP_TAI_XIU:
              this.view.onUpdateRank(data);
              break;
          case MiniGameMessage.ON_UPDATE_HISTORY_TAI_XIU:
              this.view.onUpdateHistory(data);
              break;
          case MiniGameMessage.ON_UPDATE_EVENT_TAIXIU:
              this.view.onUpdateEvent(data);
              break;
          case MiniGameMessage.ON_UPDATE_CHAT_TAIXIU:
              this.view.onUpdateChat(data);
              break;
          case MiniGameMessage.SHOW_MESSAGE_TAIXIU:
              this.view.onShowMessage(data.msg);
              break;
          default:
              break;
      }
  }

  addHanlers() {
    BaseMediator.prototype.addHanlers.call(this);
    this.view.activeSendBetTaiXiu = this.activeSendBetTaiXiu.bind(this);
    this.view.activeSendChangeMoneyType = this.activeSendChangeMoneyType.bind(this);
    this.view.activeRank = this.activeRank.bind(this);
    this.view.activeHistory = this.activeHistory.bind(this);
    this.view.activeBtnGuide = this.activeBtnGuide.bind(this);
    this.view.activeDetailSession = this.activeDetailSession.bind(this);
    this.view.activeShowEventTaiXiu = this.activeShowEventTaiXiu.bind(this);
    this.view.activeSendChat = this.activeSendChat.bind(this);
    this.view.activeDisconnect = this.activeDisconnect.bind(this);
  }

  activeSendBetTaiXiu(params) {
      this.sendNotification(MiniGameMessage.SEND_BET_TAIXIU , params);
  }

  activeSendChangeMoneyType(params) {
      this.sendNotification(MiniGameMessage.SEND_CHANGE_MONEY_TYPE_TAIXIU, params);
  }

  activeRank(params) {
      this.sendNotification(MiniGameMessage.SEND_GET_RANK_TAIXIU, params);
  }

  activeHistory(params) {
      this.sendNotification(MiniGameMessage.SEND_GET_HISTORY_TAIXIU, params);
  }

  activeBtnGuide() {
      this.sendNotification(MiniGameMessage.SEND_SHOW_GUIDE_TAIXIU);
  }

  activeDetailSession(params) {
      this.sendNotification(MiniGameMessage.SEND_GET_DETAIL_SESSION_TAI_XIU, params);
  }

  activeShowEventTaiXiu() {
      this.sendNotification(MiniGameMessage.SHOW_EVENT_TAI_XIU);
  }

  activeSendChat(params) {
      this.sendNotification(MiniGameMessage.SEND_CHAT_TAIXIU, params);
  }

  activeDisconnect() {
      this.sendNotification(MiniGameMessage.SEND_DISCONNECT_MINIGAME, Constants.MINIGAME_TAI_XIU);
  }
}

module.exports = TaiXiuSceneMediator;
