var BaseCommand = require('BaseCommand');
var Utility = require('Utility');
var MiniGameMessage = require('MiniGameMessage');
var TaiXiuVO = require('TaiXiuVO');

export default class ReceiveGetPlayingInfoTaiXiuCommand extends BaseCommand {
  execute(notification) {
    BaseCommand.prototype.execute.call(this, notification);
    var params = notification.getBody().data;
    var session = params.session;
    var gameState = params.gameState;
    var totalTai = params.totalTai;
    var totalXiu = params.totalXiu;
    var betSide = params.betSide;
    var myBetTai = params.tai ? params.tai : 0;
    var myBetXiu = params.xiu ? params.xiu : 0;
    var totalBet = params.totalBet;
    var history = Utility.convertSFSArrayToArray(params.history);
    var result = params.result;
    var dice1 = params.dice1;
    var dice2 = params.dice2;
    var dice3 = params.dice3;

    this.sendNotification(MiniGameMessage.ON_UPDATE_PLAYING_INFO_TAIXIU, {
        session: session,
        gameState: gameState,
        totalTai: totalTai,
        totalXiu: totalXiu,
        betSide: betSide,
        totalBet: totalBet,
        myBetTai:myBetTai,
        myBetXiu:myBetXiu,
        history: history,
        result: result,
        dice1: dice1,
        dice2: dice2,
        dice3: dice3
    });
  }
}
