var LobbyEvent = require("LobbyEvent");
var Utility = require("Utility");
cc.Class({
    extends: cc.Component,

    properties: {
        content: cc.Node,
        txtNumPage: cc.Label
    },

    // use this for initialization
    onLoad: function () {
        this.currentPage = 1;
        this.totalPages = 0;
        this.item = this.content.getChildByName("item");
        this.content.removeAllChildren();
    },

    onUpdateListCardPayBack: function (payBackVO) {
        if(!this.item) {
            this.onLoad();
        }
        this.content.removeAllChildren();
        var listCard = payBackVO.dataList;
        var vo, txtSTT, txtProvider, back, txtSerial, txtCardValue, txtdateSend, btnAccept;

        for (var i = 0; i < listCard.length; i++) {
            if (this.item === undefined) continue;
            vo = cc.instantiate(this.item);
            back = vo.getChildByName("back");
            txtSTT = vo.getChildByName("txtSTT").getComponent(cc.Label);
            txtProvider = vo.getChildByName("txtProvider").getComponent(cc.Label);
            txtSerial = vo.getChildByName("txtSerial").getComponent(cc.Label);
            txtCardValue = vo.getChildByName("txtCardValue").getComponent(cc.Label);
            txtdateSend = vo.getChildByName("txtdateSend").getComponent(cc.Label);
            btnAccept = vo.getChildByName("btnAceept");

            txtSTT.string = listCard[i].rowIndex;
            txtProvider.string = listCard[i].telco;
            txtSerial.string = listCard[i].serial;
            txtCardValue.string = listCard[i].value;
            txtdateSend.string = listCard[i].date;

            btnAccept.on(cc.Node.EventType.TOUCH_START, this.onGetCardPayBack.bind(this, listCard[i], this));

            this.content.addChild(vo);
        }
        this.currentPage = payBackVO.currentPage;
        this.totalPages = payBackVO.totalPages;
        this.txtNumPage.string = this.currentPage + "/" + this.totalPages;
    },

    onNextPage: function () {
        if (this.currentPage < this.totalPages) {
            this.node.emit(LobbyEvent.GET_LIST_CARD_PAY_BACK_ON_PAGE, {
                page: parseInt(this.currentPage) + 1
            });
        }
    },

    onPrevPage: function () {
        if (this.currentPage > 1) {
            this.node.emit(LobbyEvent.GET_LIST_CARD_PAY_BACK_ON_PAGE, {
                page: parseInt(this.currentPage) - 1
            });
        }
    },

    onGetCardPayBack: function (card) {
        this.node.emit(LobbyEvent.GET_INFO_CARD_PAY_BACK, {
            data: card
        });
    }
});
