cc.Class({
    extends: cc.Component,

    properties: {
        time:50,
        isToRight:false,
        isAutoStart:false,
        angle:10
    },

    // use this for initialization
    onLoad: function () {
        this.timer = null;
        this.node.opacity = this.opacityMax;
        if(this.isAutoStart){
            this.onStart();
        }
    },

    onStart: function() {
        if(this.timer) return;
        this.timer = setInterval(this.onTimer.bind(this), this.time);
    },

    onStop:function(){
        clearInterval(this.timer);
        this.timer = null;
    },

    onTimer: function () {
        console.log("timer zzz");
        var i = this.isToRight ? 1 : -1;
        this.node.angle = this.node.angle + i * this.angle;
    },

    // called every frame, uncomment this function to activate update callback
    // update: function (dt) {

    // },
});
