var LobbyEvent = require('LobbyEvent');
var Utility = require('Utility');
cc.Class({
    extends: cc.Component,

    properties: {
        txtUserReceiveMoney:  cc.EditBox,
        txtMoneyValue: cc.EditBox,
        txtReason: cc.EditBox,
        txtOTP: cc.EditBox,
        txtMoney: cc.Label,
        txtMoneyTrans: cc.Label,
        txtAccMess: cc.Label,
        txtOtpDes:cc.RichText,
        txtRules:cc.RichText
    },

    // use this for initialization
    onLoad: function () {
        this.txtAccMess.string = "";
        this.mySelf = null;
        this.transferdesVO = null;
        this.txtOtpDes.string = "";
        this.txtMoneyTrans.string = 0;
    },

    updateMySelf:function (mySelf) {
        this.mySelf = mySelf;
        this.txtMoney.string = Utility.formatCurrency(mySelf.money);
    },

    updateTranferdes:function (transferdesVO) {
        this.transferdesVO = transferdesVO;
        this.txtRules.string = Utility.convertToRtf(transferdesVO.transferDescription);
        this.txtOtpDes.string = Utility.convertToRtf(transferdesVO.otpDes);
    },

    updateCheckAccountTransferdes:function (params) {
        if (params.isAgent === 1)
            this.txtAccMess.node.color = new cc.Color(0, 255, 0, 0);
        else
            this.txtAccMess.node.color = new cc.Color(255, 0, 0, 0);

        this.txtAccMess.string = params.message;
    },

    updateAccountReceive:function (name) {
        this.txtUserReceiveMoney.string = name;
        // this.txtUserReceiveMoney.focus();
        this.onHandlerCheckAccountTransferdes();
    },
    
    onHandlerCheckAccountTransferdes:function () {
        if(this.txtUserReceiveMoney.string === "") {
            this.txtAccMess.string = '';
            return;
        }
        this.node.emit(LobbyEvent.CHECK_ACCOUNT_TRANSFERDES, {toDisplayName:this.txtUserReceiveMoney.string});
    },
    
    onHanderChangeReceiveMoney:function () {
        this.txtMoneyValue.string = (parseInt(this.txtMoneyValue.string) < 0)?0:this.txtMoneyValue.string;
        this.txtMoneyValue.string = (parseInt(this.txtMoneyValue.string) > this.mySelf.money)?this.mySelf.money:this.txtMoneyValue.string;
        if(this.txtMoneyValue.string === "") this.txtMoneyTrans.string = 0;
        else if(this.transferdesVO && this.transferdesVO.isAgent === 1)
            this.txtMoneyTrans.string = parseInt(this.txtMoneyValue.string) * (100 + this.transferdesVO.ratioForAgent) / 100;
        else if (this.transferdesVO && this.transferdesVO.isAgent === 0)
            this.txtMoneyTrans.string = parseInt(this.txtMoneyValue.string) * (100 + this.transferdesVO.ratioForUser) / 100;
        else this.txtMoneyTrans.string = 0;

        this.txtMoneyTrans.string = Utility.formatCurrency(parseInt(this.txtMoneyTrans.string));
    },

    onHandlerSendTransferdes:function () {
        var data  = {
            toDisplayName:this.txtUserReceiveMoney.string,
            amount:this.txtMoneyValue.string,
            note:this.txtReason.string,
            otp:this.txtOTP.string
        };

        this.node.emit(LobbyEvent.SEND_TRANSFERDES, data);
    },

    resetInput: function () {
        this.txtAccMess.string = '';
        this.txtUserReceiveMoney.string = '';
        this.txtMoneyValue.string = '';
        this.txtMoneyTrans.string = 0;
        this.txtReason.string = '';
        this.txtOTP.string = '';
    }
});
