var BaseCommand = require('BaseCommand');
var LobbyMessage = require('LobbyMessage');
var Utility = require('Utility');
var MiniGameMessage = require('MiniGameMessage');

export default class ReceiveGameOverBauCuaCommand extends BaseCommand {
    execute(notification) {
        BaseCommand.prototype.execute.call(this, notification);
        var params = notification.getBody().data;
        var result = params.result.substr(1,7);
        result = result.split(", ");

        this.sendNotification(MiniGameMessage.ON_GAME_OVER_BAU_CUA, {
            result: result,
        });
    }
}