var BaseCommand = require('BaseCommand');
var i18n = require('i18n');
var LobbyMessage = require('LobbyMessage');

export default class SendConfirmOtpCommand extends BaseCommand {
    execute(notification) {
        BaseCommand.prototype.execute.call(this, notification);
        var data = notification.getBody();

        if (this.isError(data.codeOtp)) return;

        var recharge = this.facade.retrieveProxy('RechargeProxy').recharge;
        var http = this.facade.retrieveProxy('HttpRequestProxy');
        var mySelf = this.facade.retrieveProxy('UserProxy').mySelf;

        var sendData = {
            displayName: mySelf.displayName,
            token: mySelf.token,
            requestId: recharge.resultOtpVO.requestId,
            transId: recharge.resultOtpVO.transId,
            phone: recharge.resultOtpVO.phone,
            amount: recharge.resultOtpVO.amount,
            otp: data.codeOtp
        };

        http.sendConfirmOtp(LobbyMessage.RECEIVE_CONFIRM_OTP, sendData);
    }

    isError(codeOtp) {
        if (codeOtp === "") {
            this.sendNotification(LobbyMessage.SHOW_ALERT, { content: i18n.t("C0016") });
            return true;
        }
        return false;
    }
}