var BaseScene = require('BaseScene');
var LocalStorage = require('LocalStorage');
var LoginSceneMediator = require('LoginSceneMediator');

cc.Class({
    extends: BaseScene,

    properties: {
        popupContent: cc.Node,
        loginPopup: cc.Node,
        registerPopup: cc.Node,
        txtUserName: cc.Node,
        txtPassWord: cc.Node,
        togRememberPass: cc.Toggle
    },

    // use this for initialization
    onLoad: function () {
        LoginSceneMediator.getInstance.init(this);
        this.checkAutoLogin();
        this.setInputControl();

        // if (cc.sys.platform === cc.sys.DESKTOP_BROWSER) {
        //     this.txtUserName.getChildByName("txtInput").getComponent(cc.EditBox).stayOnTop = true;
        //     this.txtPassWord.getChildByName("txtInput").getComponent(cc.EditBox).stayOnTop = true;
        // }

    },

    onShowLogin: function () {
        if (!this.popupContent.active) {
            this.popupContent.active = true;
            this.popupContent.scaleX = this.popupContent.scaleY = 1;
            this.popupContent.opacity = 255;
            TweenLite.from(this.popupContent, 0.3, {scaleX:0, scaleY:0});
            TweenLite.from(this.popupContent, 0.3, {opacity: 0});
        }
        this.loginPopup.active = true;
        this.registerPopup.active = false;

        if (this.togRememberPass.isChecked && LocalStorage.getUserName()) {
            this.txtUserName.getChildByName("txtInput").getComponent(cc.EditBox).string = LocalStorage.getUserName();
            this.txtPassWord.getChildByName("txtInput").getComponent(cc.EditBox).string = LocalStorage.getPassword();
        }
        else {
            this.txtUserName.getChildByName("txtInput").getComponent(cc.EditBox).string = '';
            this.txtPassWord.getChildByName("txtInput").getComponent(cc.EditBox).string = '';
        }

    },

    onShowRegister: function () {
        if (!this.popupContent.active) {
            this.popupContent.active = true;
            this.popupContent.scaleX = this.popupContent.scaleY = 1;
            this.popupContent.opacity = 255;
            TweenLite.from(this.popupContent, 0.3, {scaleX:0, scaleY:0});
            TweenLite.from(this.popupContent, 0.3, {opacity: 0});
        }
        this.loginPopup.active = false;
        this.registerPopup.active = true;
        this.registerPopup.getComponent('RegisterScene').refreshCaptcha();
    },

    onHideLoginPopup: function () {
        this.popupContent.active = false;
    },

    hanlerEventLogin: function () {
        var userName = this.txtUserName.getChildByName("txtInput").getComponent(cc.EditBox).string;
        var password = this.txtPassWord.getChildByName("txtInput").getComponent(cc.EditBox).string;
        this.activeLogin({userName: userName, password: password});

        this.updateLocalStorage(userName, password);


    },

    handlerEventRegister: function () {
        // if (this.toggleTerm.isChecked === false) {
        //         //     this.showAlert(i18n.t("C0037")).bind(this);
        //         //     return;
        //         // }

        // var data = {
        //     userName: this.regUserName.string,
        //     password: this.regPassword.string,
        //     repassword: this.regRePassword.string,
        //     displayName: this.txtDisplayName.string
        // };
        //
        // this.activeRegister(data);
    },

    hanlerExitGame:function () {
        this.sendNotification(LobbyMessage.SHOW_ALERT_WITH_CONFIRM, {
            content: message,
            acceptLabel: i18n.t("C0054"),
            callback: function () {
                cc.game.end();
            }
        });
    },

    setUserName: function (username) {
        var txtUsername = this.txtUserName.getChildByName("txtInput").getComponent(cc.EditBox);
        txtUsername.string = username;
    },

    setPassword: function (password) {
        var txtUsername = this.txtPassWord.getChildByName("txtInput").getComponent(cc.EditBox);
        txtUsername.string = password;
    },

    updateLocalStorage: function (userName, password) {
        LocalStorage.setUserName(userName);
        LocalStorage.setPassword(password);
    },

    updateData: function (userName, password) {
        // this.setUserName(userName);
        // this.setPassword(password);
        // this.updateLocalStorage(userName, password);
    },

    checkAutoLogin: function () {
        var isSocial = LocalStorage.getIsSocial();
        if (isSocial === "1") {
            this.hanlerEventLoginFB();
        }
        if (isSocial === "0") {
            var userName = LocalStorage.getUserName();
            var password = LocalStorage.getPassword();
            if (userName) this.setUserName(userName);
            if (password) this.setPassword(password);
            if (userName && password && userName !== "" && password !== "") {
                // console.log("123: " + LocalStorage.getAutoLoginNormal());
                // if (LocalStorage.getAutoLoginNormal() === "1")
                    this.hanlerEventLogin();
            }
        }
    },

    setInputControl: function () {
        var self = this;
        // add keyboard event listener
        cc.eventManager.addListener({
            event: cc.EventListener.KEYBOARD,
            onKeyPressed: function (keyCode, event) {
                switch (keyCode) {
                    case cc.KEY.enter:
                        self.hanlerEventLogin();
                        break;
                }
            },
            onKeyReleased: function (keyCode, event) {
                switch (keyCode) {
                    default:
                        break;
                }
            }
        }, self.node);
    },


    hanlerEventChangeRememberPass: function () {
    },

    hanlerEventRegister: function () {
        this.activeShowRegister();
    },

    hanlerEventLoginFB: function () {
        this.activeLoginFB();
    },

    hanlerEventForGetPassWord: function () {
        this.activeShowForgetPassword();
    },

    hanlerEventPlayNow: function () {

    },
});

