var BaseGameCommand = require('BaseGameCommand');
var MiniGameMessage = require('MiniGameMessage');
var SlotBTProxy = require('SlotBTProxy');
var SlotBTSceneMediator = require('SlotBTSceneMediator');

export default class SetupSlotBTCommand extends BaseGameCommand {
  execute(notification) {
    BaseGameCommand.prototype.execute.call(this, notification);
    this.gameProxy = new SlotBTProxy();

    this.facade.registerCommand(MiniGameMessage.INIT_SLOT_BT, require('InitSlotBTCommand'));
    this.facade.registerCommand(MiniGameMessage.SEND_SPIN_SLOT_BT, require('SendSpinSlotBTCommand'));
    this.facade.registerCommand(MiniGameMessage.SEND_LEAVE_ROOM_SLOT_BT, require('SendLeaveRoomSlotBTCommand'));
    this.facade.registerCommand(MiniGameMessage.SEND_GET_RANK_SLOT_BT, require('SendGetRankSlotBTCommand'));
    this.facade.registerCommand(MiniGameMessage.SEND_GET_HISTORY_SLOT_BT, require('SendGetHistorySlotBTCommand'));

    this.facade.registerCommand(MiniGameMessage.RECEIVE_SPIN_SLOT_BT, require('ReceiveSpinSlotBTCommand'));
    this.facade.registerCommand(MiniGameMessage.RECEIVE_UPDATE_JACKPOT_SLOT_BT, require('ReceiveUpdateJackPotSlotBTCommand'));
    this.facade.registerCommand(MiniGameMessage.RECEIVE_GET_RANK_SLOT_BT, require('ReceiveGetRankSlotBTCommand'));
    this.facade.registerCommand(MiniGameMessage.RECEIVE_GET_HISTORY_SLOT_BT, require('ReceiveGetHistorySlotBTCommand'));

    //register proxy
    this.facade.registerProxy(this.gameProxy);

    //register mediator
    this.facade.registerMediator(require('SlotBTSceneMediator').getInstance);
  }
}
