var LobbyEvent = require('LobbyEvent');
var Utility = require('Utility');
cc.Class({
    extends: cc.Component,

    properties: {
        setPhonePanel:  cc.Node,
        txtPhoneInput: cc.EditBox,
        activePhonePanel: cc.Node,
        inactivedPanel: cc.Node,
        activedPanel: cc.Node,
        txtUserName: cc.Label,
        txtPhone: cc.Label,
        txtOTP: cc.EditBox,
        resetPhonePanel: cc.Node,
        txtNewPhone: cc.EditBox,
        txtOTPReset: cc.EditBox,
        btnEdit: cc.Node
    },

    // use this for initialization
    onLoad: function () {
    },

    onHandlerSetPhone: function () {
        this.node.emit('SEND_SET_PHONE_NUMBER', {type: 1, phone: this.txtPhoneInput.string});
    },

    onHandlerChangePhone: function () {
        this.setPhonePanel.active = true;
        this.activePhonePanel.active = false;
    },

    onHandlerActivePhone: function () {
        this.node.emit('SEND_SET_PHONE_NUMBER', {type: 2, OTP: this.txtOTP.string});
    },

    onHandlerResetPhone: function () {
        this.node.emit('SEND_SET_PHONE_NUMBER', {type: 3, phone: this.txtNewPhone.string, OTP: this.txtOTPReset.string});

    },

    showInfoPhoneNumber: function (mySelf) {
        if (mySelf.phone !== ''){
            this.setPhonePanel.active = false;
            this.activePhonePanel.active = true;
            this.txtUserName.string = mySelf.fbId;
            this.txtPhone.string = mySelf.phone;
            if (mySelf.isActivePhone) {
                this.inactivedPanel.active = false;
                this.activedPanel.active = true;
                this.hideResetPhonePanel();
            }
            else {
                this.txtOTP.string = '';
                this.inactivedPanel.active = true;
                this.activedPanel.active = false;
            }
        }
        else {
            this.txtPhoneInput.string = '';
            this.setPhonePanel.active = true;
            this.activePhonePanel.active = false;
        }
    },

    showResetPhonePanel: function () {
        this.btnEdit.active = false;
        this.resetPhonePanel.active = true;
    },

    hideResetPhonePanel: function () {
        this.btnEdit.active = true;
        this.resetPhonePanel.active = false;
        this.txtNewPhone.string = '';
        this.txtOTPReset.string = '';
    },

    updateSetPhoneNumber: function (mySelf) {
        this.setPhonePanel.active = false;
        this.activePhonePanel.active = true;
        this.txtUserName.string = mySelf.fbId;
        this.txtPhone.string = this.txtPhoneInput.string;
    },

    updateActivePhoneNumber: function () {
        this.inactivedPanel.active = false;
        this.activedPanel.active = true;
    },

    updateResetPhoneNumber: function () {
        this.btnEdit.active = true;
        this.resetPhonePanel.active = false;
        this.txtPhone.string = this.txtNewPhone.string;
    },

});
