var BasePopup = require('BasePopup');
var SettingSceneMediator = require('SettingSceneMediator');
var GameConfig = require('GameConfig');
var LocalStorage = require('LocalStorage');

cc.Class({
    extends: BasePopup,

    properties: {
        btn_back:cc.Node,
        toggle_sound:cc.Toggle,
        toggle_music:cc.Toggle
    },

    // use this for initialization
    onLoad: function () {
        SettingSceneMediator.getInstance.init(this);
        this.addEventListeners();
        this.hide();
        this.btn_back.active = false;
        this.musicOn = LocalStorage.getMusic();
    },

    addEventListeners: function () {

    },

    removeEventListeners: function () {

    },

    onHandleSoundOnOffClick: function () {
        LocalStorage.setSound(!this.toggle_sound.isChecked);
    },

    onHandleMusicOnOffClick: function () {
        LocalStorage.setMusic(!this.toggle_music.isChecked);

        if(this.musicOn === "true"){
            if(this.toggle_music.isChecked)
                cc.audioEngine.pauseAll();
            else
                cc.audioEngine.resumeAll();
        }
        else{
            this.musicOn = "true";
            this.activePlayBackgroundSound();
        }
    },

    onHandleBackClick: function () {
        this.btn_back.active = false;
    },

    show:function () {
        BasePopup.prototype.show.call(this);

        var soundOn = LocalStorage.getSound();
        var musicOn = LocalStorage.getMusic();

        this.toggle_sound.isChecked = (soundOn === "false")?true:false;
        this.toggle_music.isChecked = (musicOn === "false")?true:false;
    },
});