var LobbyEvent = require('LobbyEvent');
var DropListMainTemplate = require('DropListMainTemplate');
cc.Class({
    extends: cc.Component,

    properties: {
        txtNumCard: cc.EditBox,
        txtNumSerial: cc.EditBox,
        listPartner: DropListMainTemplate,
        cardRateListContent: cc.Node
    },


    // use this for initialization
    onLoad: function () {
        this.itemRate = this.cardRateListContent.children[0];
        this.cardRateListContent.removeAllChildren();
    },

    updateMySelf: function (mySelf) {
        this.mySelf = mySelf;
    },

    updateCardTelcoList: function (cardTelcoList) {
        this.listPartner.setupData(cardTelcoList);
    },

    updateCardRateList: function (cardRateList) {
        var data = cardRateList;
        this.cardRateListContent.removeAllChildren();
        for (var i = 0; i < data.length; i++) {
            if (this.itemRate === undefined) return;
            var item = cc.instantiate(this.itemRate);
            var txtPrice = item.getChildByName("txtPrice").getComponent(cc.Label);
            var txtSale = item.getChildByName("txtSale").getComponent(cc.Label);
            var txtMoney = item.getChildByName("txtMoney").getComponent(cc.Label);

            txtPrice.string = data[i].name;
            txtMoney.string = data[i].value.replace('GOLD', '');
            txtSale.string = (data[i].promotion) ? data[i].promotion : "0%";
            this.cardRateListContent.addChild(item);
        }
    },

    onHandlerRecharge: function () {
        var numCard = this.txtNumCard.string;
        var numSerial = this.txtNumSerial.string;
        var partnerSelect = this.listPartner.getItemSelect();

        this.node.emit(LobbyEvent.MOBILE_CARD_RECHARGE, {
            numCard: numCard,
            numSerial: numSerial,
            partnerCode: (partnerSelect) ? partnerSelect.value : null
        });
    },

    reset: function () {
        this.txtNumCard.string = "";
        this.txtNumSerial.string = "";
    }

});
