import { SFSData } from "../../../../network/sfs/constans/SFSData";
import { SFSEvent } from "../../../../network/sfs/constans/SFSEvent";

var SFSProxy = require('SFSProxy');
// var SFSEvent = require('SFSEvent');
// var SFSData = require('SFSData');
var GameConfig = require('GameConfig');

export default class MinigameProxy extends SFSProxy {
    onRegister() {
        SFSProxy.prototype.onRegister.call(this);
        this.dataUser = this.facade.retrieveProxy('UserProxy');
        this.isLoadDone = false;
        this.isLoadProcess = false;
    }

    //network reponse
    onMessage(event) {

    }

    //override
    onRoomJoined(evtParams) {
        var data = {
            cmd: SFSEvent.USER_JOIN_MINIGAME,
            body: evtParams
        };
        this.onMessage(data);
        this.startPingToServer();
    }

    //override
    onExtensionResponse(evtParams) {
        if (!this.isBlackListReponseLog(evtParams.cmd)) {
            this.reponseExtensionRequestLog(evtParams);
        }
        this.onMessage(evtParams);
    }

    //override
    onPublicMessage() {
        // evtParams.data.putUtfString(this.SFSData.CMD, SFSEvent.PUBLIC_CHAT);
        // var vo = Utility.convertSFSObjectToObject(evtParams.data);
        // this.onMessage(vo);
    }

    sendExtensionRequest(command, sfo) {
        if (this.sfs === null)
            return;
        sfo.putUtfString(SFSData.USER_NAME, this.userName);
        var room = this.sfs.lastJoinedRoom;
        var request = new SFS2X.ExtensionRequest(command, sfo, room);
        this.send(request);
    }

    /////////////////////////////////////////////////////////////////
    ////////////////////SEND TO SERVER//////////////////////////////
    ////////////////////////////////////////////////////////////////
    sendJoinGame(params) {
        cc.log("" + GameConfig.SOCKET_IP + "" + GameConfig.SOCKET_PORT);
        this.joinZone(GameConfig.SOCKET_IP, GameConfig.SOCKET_PORT, params.userJoin, "", params.zoneName, this.dataUser.mySelf.token);
        // this.sfsProxy.joinZone("192.168.0.135", 8080, params.data.userJoin, "", "minipoker", this.dataUser.mySelf.token);
    }

    sendDisconnect() {
        this.onDisconnect();
    }

    sendGetRank(params) {
        // params = params.params;
        // var page = params.page;
        // var type = 1;
        var sfsob = new SFS2X.SFSObject();
        sfsob.putInt(SFSData.GID, params.gid);

        // sfsob.putInt(SFSData.PAGE, page);
        // sfsob.putInt(SFSData.MONEY_TYPE, type);
        this.sendExtensionRequest(SFSEvent.GET_RANK, sfsob);
    }

    sendGetHistory(params) {
        // var page = params.page;
        // var type = 1;
        var sfsob = new SFS2X.SFSObject();
        sfsob.putInt(SFSData.GID, params.gid);

        // sfsob.putInt(SFSData.PAGE, page);
        // sfsob.putInt(SFSData.MONEY_TYPE, type);
        this.sendExtensionRequest(SFSEvent.GET_USER_HISTORY, sfsob);
    }

    /////////////////////////////////////////////////////////////////
    /////////////////////////////////////////////////////////////////
    /////////////////////////////////////////////////////////////////
}
