cc.Class({
    extends: cc.Component,

    properties: {
        scale: 1.15,
        timeScale: 0.1,
        spriteFrames: [cc.SpriteFrame]
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        this.node.on(cc.Node.EventType.TOUCH_START, this.onHanlerButtonStart, this);
        this.node.on(cc.Node.EventType.TOUCH_END, this.onHanlerButtonEnd, this);
    },

    onHanlerButtonStart: function (evt) {
        if (this.node.getComponent(cc.Button).interactable)
            this.node.runAction(cc.scaleTo(this.timeScale, this.scale));
    },

    onHanlerButtonEnd: function () {
        this.node.runAction(cc.scaleTo(this.timeScale, 1));
    },

    setEnableButton: function (isEnable) {
        this.node.getComponent(cc.Button).interactable = isEnable;
        if (isEnable)
            this.node.getComponent(cc.Sprite).spriteFrame = this.spriteFrames[1];
        else
            this.node.getComponent(cc.Sprite).spriteFrame = this.spriteFrames[0];
    }


});
