var BaseCommand = require('BaseCommand');
var SlotMessage = require('SlotMessage');
var UserProxy = require('UserProxy');
var LobbyMessage = require('LobbyMessage');
export default class ReceivePlayX2MCCommand extends BaseCommand {
    execute(notification) {
        BaseCommand.prototype.execute.call(this, notification);
        var params = notification.getBody();
        var mySelf = this.facade.retrieveProxy('UserProxy').mySelf;
        mySelf.money = params.data.balance;
        this.sendNotification(LobbyMessage.ON_UPDATE_MY_INFO);

        if (!params.success) return;

        this.sendNotification(SlotMessage.ON_RESULT_GAME_X2_MASTER_CHEF, params.data);
    }
}
