  updateData(data) {
    this.reset();
    // this.session = data.data.id;
    if (data.success === false) {
      this.typeResult = SlotKNVO.ERROR;
      this.errorMessage = data.m;
    } else {
      ///////////fake/////////////////
      // data.d.mg =  {
      //   vb:1000,
      //   v:500000,
      //   s:'x1x2x3x3x5x5x0x5'
      // },
      // data.d.fs = 10;

      // ///////////////////////////////
      if (this.freeSpin > 0) {
        this.isFreeSpin = true;
      } else {
        this.isFreeSpin = false;
      }

      // this.bet = data.data.gid;
      this.money = data.data.money;
      this.jackpot = data.data.jackpot;
      this.wins = this.getClientReward(data.data);//data.d.res;
      this.items = this.getClientItem(data.data);//data.d.items;

      if (this.jackpot > 0) this.typeResult = SlotKNVO.JACKPOT_WIN;
      else if (this.prize >= this.listBet[this.bet - 1] * 70) this.typeResult = SlotKNVO.BIG_WIN;
      else if (this.prize > 0) this.typeResult = SlotKNVO.NORMAL_WIN;
      else this.typeResult = 0;

      this.typeResult = SlotKNVO.JACKPOT_WIN;

      this.checkShowBlurCol(this.items);
    }
  }

  getClientReward(data) {
    var result = [];
    for (var i = 0; i < data.steps.length; i++) {
      var rewardLine = data.steps[i].rewardLine;
      var ls = [];
      var isBigWin = false;
      var prize = 0;
      var fs = 0;
      for (var j = 0; j < rewardLine.length; j++) {
        var o = rewardLine[j];
        var freeSpin = this.getFreeSpinByRewardLine(o.rewardId);
        var jp = this.getJackpotByRewardLine(o.rewardId, o.value);
        prize += o.value;
        fs += freeSpin;
        if (!isBigWin && (o.value >= this.listBet[this.bet - 1] * 70)) isBigWin = true;
        var iw = {
          id: o.lineId,
          i: o.rewardId,
          v: o.value,
          jp: jp,
        };
        ls.push(iw);
      }

      var remove = [];
      if (i !== data.steps.length - 1) remove = data.steps[i + 1].remove;
      if (ls.length === 0 && remove.length === 0) continue;
      result[i] = { ls, remove, isBigWin, prize, fs };
    }

    return result;
  }

  getJackpotByRewardLine(rewardId, value) {
    if (rewardId === 1) return value;
    return 0;
  }

  getFreeSpinByRewardLine(rewardId) {
    if (rewardId === 25) return 5;
    if (rewardId === 26) return 10;
    if (rewardId === 27) return 15;
    return 0;
  }

  getClientItem(data) {
    var arrItem = [];
    for (var i = data.steps[0].slots.length - 1; i >= 0; i--) {
      var itemId = data.steps[0].slots[i];
      var col = i % 5;
      if (arrItem[col] === undefined) arrItem[col] = [];
      arrItem[col].unshift(itemId + 1);
    }

    for (var i = 1; i < data.steps.length; i++) {
      for (var j = data.steps[i].news.length - 1; j >= 0; j--) {
        var itemId = data.steps[i].news[j];
        if (itemId === -1) continue;
        var col = j % 5;
        arrItem[col].unshift(itemId + 1);
      }
    }

    return arrItem;

  }

