var BasePopup = require('BasePopup');
var Utility = require('Utility');

cc.Class({
    extends: BasePopup,

    properties: {
        content: cc.Node,
        itemTemp:cc.Node,
    },

    onLoad () {
    },

    show: function () {
        BasePopup.prototype.show.call(this);
        this.content.removeAllChildren();
        TweenLite.delayedCall(0.2, function () {
            this.node.emit('ACTIVE_HISTORY_SHOW_PAGE');
        }.bind(this));
    },

    hide: function () {
        BasePopup.prototype.hide.call(this);
        this.content.removeAllChildren();
    },

    updateHistory: function (data) {
        this.content.removeAllChildren();
        for(var i=0 ; i<data.length ; i++){
            var item = cc.instantiate(this.itemTemp);
            // item.getChildByName("bgLine").active = (i % 2 === 0);
            item.getChildByName("txtSession").getComponent(cc.Label).string = '#' + data[i].id;
            item.getChildByName("txtTime").getComponent(cc.Label).string = Utility.js_yyyy_mm_dd_hh_mm_ss(data[i].date);
            item.getChildByName("txtBetValue").getComponent(cc.Label).string = Utility.formatCurrency(data[i].bet);
            // item.getChildByName("txtLineWin").getComponent(cc.Label).string = data[i].pay_line.length;
            item.getChildByName("txtPrize").getComponent(cc.Label).string = Utility.formatCurrency(data[i].pay);
            this.content.addChild(item);
        }
    }
});
