var BaseCommand = require('BaseCommand');
var LobbyMessage = require('LobbyMessage');
var Utility = require('Utility');
var MiniGameMessage = require('MiniGameMessage');

export default class ReceiveGameOverTaiXiuCommand extends BaseCommand {
  execute(notification) {
    BaseCommand.prototype.execute.call(this, notification);
    var params = notification.getBody().data;
    var result = params.result;
    var dice1 = params.dice1;
    var dice2 = params.dice2;
    var dice3 = params.dice3;

    this.sendNotification(MiniGameMessage.ON_GAME_OVER_TAIXIU, {
        result: result,
        dice1: dice1,
        dice2: dice2,
        dice3: dice3
    });
  }
}
