var BaseScene = require('BaseScene');
var LobbySceneMediator = require('LobbySceneMediator');

cc.Class({
    extends: BaseScene,

    properties: {
        sceneList: [cc.Prefab]
    },

    ctor: function () {
        LobbySceneMediator.getInstance.init(this);
    },

    show: function () {
        this.node.x = this.rootX;
        BaseScene.prototype.show.call(this);
    },

    // use this for initialization
    onLoad: function () {
        this.rootX = this.node.x;
        this.initialize();
    },

    initialize: function () {
        for (var i = 0; i < this.sceneList.length; i++) {
            if(!this.sceneList[i]) continue;
            var node = cc.instantiate(this.sceneList[i]);
            if(!node) continue;
            this.node.addChild(node);
        }

       // this.resizeScene();
    },

    // resizeScene:function () {
    //     if(cc.sys.isBrowser){
    //         this.node.scaleX =  1280/window.innerWidth;
    //         this.node.scaleY =  720/window.innerHeight;
    //     }
    // },

    update: function () {

    }
});

