var BaseMediator = require('BaseMediator');
var LobbyMessage = require('LobbyMessage');
var ListJackpotProxy = require('ListJackpotProxy');


class ListJackpotSceneMediator extends BaseMediator {
    static get NAME() {
        return 'ListJackpotSceneMediator';
    }

    static get getInstance() {
        if (this.instance === undefined) {
            this.instance = new ListJackpotSceneMediator();
        }
        return this.instance;
    }

    onRegister() {
        this.logic = this.facade.retrieveProxy(ListJackpotProxy.NAME).getLogic();
    }

    listNotificationInterests() {
        return [
            LobbyMessage.SHOW_JACKPOT_SCENE,
            LobbyMessage.HIDE_JACKPOT_SCENE,
            LobbyMessage.SEND_SHOW_HIDE_JACKPOT,
            LobbyMessage.ON_UPDATE_LIST_JACKPOT
        ];
    }

    /** @override */
    handleNotification(notification) {
        var params = notification.getBody();
        switch (notification.getName()) {
            case LobbyMessage.SHOW_JACKPOT_SCENE:
                this.view.show();
                this.onGetChatData();
                break;
            case LobbyMessage.HIDE_JACKPOT_SCENE:
                this.view.hide();
                break;
            case LobbyMessage.SEND_SHOW_HIDE_JACKPOT:
                this.view.showHide();
                break;
            case LobbyMessage.ON_UPDATE_LIST_JACKPOT:
                var listJackpotProxy = this.facade.retrieveProxy(ListJackpotProxy.NAME);
                this.view.logic = listJackpotProxy.getLogic();
                this.view.updateListJackpot();
                break;
            default:
                break;
        }
    }

    addHanlers() {
        BaseMediator.prototype.addHanlers.call(this);
        this.view.onSendGetListJackpot = this.onSendGetListJackpot.bind(this);
        this.view.onClickBtnRoom = this.onClickBtnRoom.bind(this);
    }

    onClickBtnRoom() {
        this.sendNotification(LobbyMessage.SEND_GET_LIST_JACKPOT);
    }

    onSendGetListJackpot() {
        this.sendNotification(LobbyMessage.SEND_GET_LIST_JACKPOT);
    }
}

module.exports = ListJackpotSceneMediator;