var DropListMainTemplate = require("DropListMainTemplate");
var Utility = require("Utility");
var LobbyEvent = require("LobbyEvent");
var i18n = require("i18n");
cc.Class({
    extends: cc.Component,

    properties: {
        dropListProvider: DropListMainTemplate,
        dropListCardValue: DropListMainTemplate,
        txtQuantity:cc.EditBox,
        txtPrice: cc.Label,
        txtOtp:cc.EditBox,
        txtDeception:cc.RichText,
        txtMoney:cc.Label,
        contentRate: cc.Node
    },

    // use this for initialization
    onLoad: function () {
        this.itemRate = this.contentRate.children[0];
        this.dropListProvider.oldIndex = this.dropListProvider.node.zIndex;
        this.dropListCardValue.oldIndex = this.dropListCardValue.node.zIndex;
    },

    updateMySelf:function (mySelf) {
        this.txtMoney.string = Utility.formatCurrency(mySelf.money);
    },

    updateOutCardInfo:function (shopVO) {
        this.dropListProvider.setupData(shopVO.providerList);
        this.dropListCardValue.setupData(shopVO.cardValueList);
        this.txtDeception.string = Utility.convertToRtf(shopVO.otpDes);
        this.ratioBuyCard = shopVO.ratioBuyCard;
        this.updateRate(shopVO.outCardRateList);
        this.onHandlerChangeCardValue();
    },

    updateRate: function (data) {
        if(!this.itemRate) {
            this.onLoad();
        }
        this.contentRate.removeAllChildren();
        var item, txtMoney, txtCardValue;

        for (var i = 0; i < data.length; i++) {
            if (this.itemRate === undefined) continue;
            item = cc.instantiate(this.itemRate);
            txtMoney = item.getChildByName("txtMoney").getComponent(cc.Label);
            txtCardValue = item.getChildByName("txtCardValue").getComponent(cc.Label);

            txtMoney.string = data[i].value;
            txtCardValue.string = data[i].name;
            this.contentRate.addChild(item);
        }
    },

    onHandlerDropListProvideClick:function () {
        this.dropListProvider.node.setSiblingIndex(this.node.children.length-1);
        this.dropListProvider.node.zIndex = this.node.children.length - 1;

        this.dropListCardValue.node.setSiblingIndex(this.dropListCardValue.oldIndex);
        this.dropListCardValue.node.zIndex = this.dropListCardValue.oldIndex;
    },

    onHandlerDropListCardValueClick:function () {
        if (this.dropListCardValue.node.zIndex === this.node.children.length - 1) {
            this.dropListCardValue.node.setSiblingIndex(this.dropListCardValue.oldIndex);
            this.dropListCardValue.node.zIndex = this.dropListCardValue.oldIndex;
        }
        else {
            this.dropListCardValue.node.setSiblingIndex(this.node.children.length - 1);
            this.dropListCardValue.node.zIndex = this.node.children.length - 1;
        }

        this.dropListProvider.node.setSiblingIndex(this.dropListProvider.oldIndex);
        this.dropListProvider.node.zIndex = this.dropListProvider.oldIndex;
    },

    onHandlerChangeCardValue:function () {
        this.dropListCardValue.node.setSiblingIndex(this.dropListCardValue.oldIndex);
        this.dropListCardValue.node.zIndex = this.dropListCardValue.oldIndex;
        if (isNaN(this.txtQuantity.string)) {
            this.txtQuantity.string = '';
            this.txtPrice.string = '0';
        }
        this.txtPrice.string = parseInt(this.dropListCardValue.getItemSelect().value) * this.ratioBuyCard * parseInt(this.txtQuantity.string);
        this.txtPrice.string = Utility.formatCurrency(parseInt(this.txtPrice.string));
    },

    onHandlerChangeQuantity:function () {

    },

    onHandlerSend:function () {
        var data  = {
            provider:this.dropListProvider.getItemSelect().value,
            cardValue:this.dropListCardValue.getItemSelect().value,
            quantity:this.txtQuantity.string,
            otp:this.txtOtp.string
        };

        this.node.emit(LobbyEvent.BUY_ITEM_SHOP, data);
    },

    resetInput: function() {
        this.txtQuantity.string = '';
        this.txtPrice.string = '';
        this.txtOtp.string = '';
    }
});

