var BaseVO = require("BaseVO");
var GameJackpot = require("GameJackpot");
var puremvc = BaseVO.puremvc;

module.exports = puremvc.define
(
    // CLASS INFO
    {
        constructor: function () {
            this.listJackpot = null;
            this.mucCuoc = 100;
        }
    },

    // INSTANCE MEMBERS
    {
        update:function (data) {
            this.listJackpot = [];
            var kk = data.dataList;

            for (var key in kk) {
                if (kk.hasOwnProperty(key)) {
                    var item = new GameJackpot();
                    item.update(kk[key]);
                    this.listJackpot.push(item);
                }
            }
        },

        reset:function () {
            this.giftList = null;
        },

        getMucCuoc: function(){
            return this.mucCuoc;
        }
    },
    // STATIC MEMBERS
    {

    }
);