var ReceiveUpdateCurrentTurnCommand = require('ReceiveUpdateCurrentTurnCommand');
var GameMessage = require('GameMessage');

export default class ReceiveUpdateCurrentTurnSamCommand extends ReceiveUpdateCurrentTurnCommand {
    execute(notification) {
        ReceiveUpdateCurrentTurnCommand.prototype.execute.call(this, notification);
        var tableVO = this.gameProxy.getTable();
        if (!tableVO && tableVO.id < 1) return this.showWarning(this.WARNING.THE_TABLE_DOES_NOT_EXIST);
        if (tableVO.isFirstTurn) {
            tableVO.isFirstTurn = false;
            var seatBaoSam = tableVO.getSeatByUserId(tableVO.curTurn);
            if (seatBaoSam) {
                if (seatBaoSam.sam === 1) {
                    tableVO.seatIdBaoSam = seatBaoSam.id;
                    tableVO.userIdBaoSam = seatBaoSam.user.id;
                }
                this.sendNotification(GameMessage.ON_BAO_SAM, { seatId: seatBaoSam.id, sam: sam });
            }
        }
    }
}