var BasePopup = require('BasePopup');
var Utility = require('Utility');

cc.Class({
    extends: BasePopup,

    properties: {
      content: cc.Node,
      itemTemp:cc.Node,
    },

    onLoad () {
      this.curPage = 1;
      this.totalPages = 20;
    },

    show: function () {
      this.curPage = 1;
      BasePopup.prototype.show.call(this);
      this.content.removeAllChildren();
      TweenLite.delayedCall(0.2, function () {
        this.node.emit('ACTIVE_HISTORY_SHOW_PAGE');
      }.bind(this));
    },

    hide: function () {
        BasePopup.prototype.hide.call(this);
        this.content.removeAllChildren();
    },

    updateHistory: function (data) {
      // this.lbCurPage.string = "" + this.curPage + " / " + this.totalPages;
      // this.btnGroup.active = true;
      this.content.removeAllChildren();
      for(var i=0 ; i<data.length ; i++){
        var item = cc.instantiate(this.itemTemp);
        item.data = data[i];
        item.getChildByName("bgLine").active = (i % 2 !== 0);
        item.getChildByName("lbPhien").getComponent(cc.Label).string = '#' + data[i].id;
        item.getChildByName("lbDate").getComponent(cc.Label).string = Utility.js_yyyy_mm_dd_hh_mm_ss(data[i].date);
        item.getChildByName("lbPhong").getComponent(cc.Label).string = Utility.formatCurrency(data[i].bet);
        item.getChildByName("lbNBetLine").getComponent(cc.Label).string = data[i].bet_line.length;
        item.getChildByName("lbNWinLine").getComponent(cc.Label).string = data[i].pay_line.length;
        item.getChildByName("lbPrize").getComponent(cc.Label).string = Utility.formatCurrency(data[i].pay);
        this.content.addChild(item);
      }
    },

    onHandlerNextPage: function() {
      this.curPage = (this.curPage < this.totalPages) ? this.curPage + 1: this.totalPages;
      this.node.emit('ACTIVE_HISTORY_SHOW_PAGE', {curPage: this.curPage});
    },

    onHandlerPrevPage: function() {
      this.curPage = (this.curPage > 1) ? this.curPage - 1: 1;
      this.node.emit('ACTIVE_HISTORY_SHOW_PAGE', {curPage: this.curPage});
    },
});
