var BasePopup = require('BasePopup');
var Captcha = require('Captcha');
var GiftCodeSceneMediator = require('GiftCodeSceneMediator');

cc.Class({
    extends: BasePopup,

    properties: {
        txtGiftCode: cc.Node,
        txtCaptcha: cc.Node,
        ctlCaptcha: Captcha
    },

    // use this for initialization
    onLoad: function () {
        GiftCodeSceneMediator.getInstance.init(this);
        this.hide();

    },

    show: function () {
        BasePopup.prototype.show.call(this);
        this.ctlCaptcha.refeshCaptcha();
    },

    onHandlerUseGiftCode: function () {
        var giftCode = this.txtGiftCode.getChildByName("txtInput").getComponent(cc.EditBox);
        var captcha = this.txtCaptcha.getChildByName("txtInput").getComponent(cc.EditBox);

        this.activeUseGiftCode({giftCode: giftCode.string, captcha: captcha.string});

        giftCode.string = "";
        captcha.string = "";
        this.ctlCaptcha.refeshCaptcha();

    }
});

