var BaseCommand = require('BaseCommand');
var MiniGameMessage = require('MiniGameMessage');

export default class InitSlotBTCommand extends BaseCommand {
  execute(notification) {
    BaseCommand.prototype.execute.call(this, notification);
    this.sendNotification(MiniGameMessage.ON_INIT_GAME_SLOT_BT);
    this.sendNotification(MiniGameMessage.SHOW_MINIGAME_SLOT_BT);
  }
}
