var BaseCommand = require('BaseCommand');
var Constants = require('Constants');
var SlotMCProxy = require('SlotMCProxy');
export default class SendGetRankMCCommand extends BaseCommand {
  execute(notification) {
    BaseCommand.prototype.execute.call(this, notification);
    var params = notification.getBody();
    var page = (params.page !== undefined) ? params.page : 1;
    var sendData = {
      type: params.type,
      gid: params.bet
    };
    this.gameProxy = SlotMCProxy.proxy;
    this.gameProxy.sendGetRank(sendData);
  }
}
