var BaseCommand = require('BaseCommand');
var LobbyMessage = require('LobbyMessage');

export default class ReceiveCheckAccountTransferdesCommand extends BaseCommand {
    execute(notification) {
        BaseCommand.prototype.execute.call(this, notification);
        if (notification.getBody().data.errCode === "01") {
            notification.getBody().data.errCode = "00";
            notification.getBody().data.isAgent = -1;
        }

        if (this.isError(notification.getBody().data)) return;

        var params = notification.getBody().data;
        var shopProxy = this.facade.retrieveProxy('ShopProxy');
        shopProxy.updateIsAgent(params.isAgent);

        this.sendNotification(LobbyMessage.ON_UPDATE_CHECK_ACCOUNT_TRANSFERDES, params);
    }
}