var BasePopup = require('BasePopup');
var GamePanelBauCua = require('GamePanelBauCua');
var RankingPanelBauCua = require('RankingPanelBauCua');
var HistoryPanelBauCua = require('HistoryPanelBauCua');
var DragBowlBauCua = require('DragBowlBauCua');

var BauCuaSceneMediator = require('BauCuaSceneMediator');

cc.Class({
    extends: BasePopup,

    properties: {
        gamePanel: GamePanelBauCua,
        rankPanel: RankingPanelBauCua,
        historyPanel: HistoryPanelBauCua,
        guidePanel: BasePopup,
        bowl: DragBowlBauCua

    },

    // use this for initialization
    onLoad: function () {
        BauCuaSceneMediator.getInstance.init(this);
        this.addEventListeners();
    },

    buildUI() {
        // this.gamePanel.buildUI(this.gameVO);
    },

    show() {
        BasePopup.prototype.show.call(this);
        this.node.setSiblingIndex(this.node.parent.childrenCount - 1);
    },

    addEventListeners() {
        this.gamePanel.node.on('ACTIVE_SEND_BET', this.onActiveSendBet, this);
        this.gamePanel.node.on('ACTIVE_SEND_SOI_CAU', this.onActiveSendSoiCau, this);
        this.gamePanel.node.on('ACTIVE_RANK', this.onActiveRank, this);
        this.gamePanel.node.on('ACTIVE_HISTORY', this.onActiveHistory, this);
        this.gamePanel.node.on('ACTIVE_GUIDE', this.onActiveGuide, this);

        this.rankPanel.node.on('ACTIVE_RANKING_SHOW_PAGE', this.onActiveRankShowPage, this);
        this.historyPanel.node.on('ACTIVE_HISTORY_SHOW_PAGE', this.onActiveHistoryShowPage, this);

        this.bowl.node.on('ACTIVE_EVENT_DRAG_BOWL', this.onActiveEventDragBowl, this);
    },

    onActiveSendBet(params){
        this.activeSendBetBauCua(params);
    },

    onActiveSendSoiCau(){
        this.activeSendSoiCauBauCua();
    },

    onActiveRank(){
        this.rankPanel.show();
    },

    onActiveHistory(){
        this.historyPanel.show();
    },

    onActiveGuide(){
        this.guidePanel.show();
    },

    onActiveRankShowPage() {
        this.activeRank();
    },

    onActiveHistoryShowPage() {
        this.activeHistory();
    },

    onUpdatePlayingInfo: function (data) {
        this.gamePanel.onUpdatePlayingInfo(data);
    },

    onUpdateBoardBet: function (session, betting, time) {
        this.gamePanel.onUpdateBoardBet(session, betting, time);
    },

    onBetBauCua: function (data) {
        this.gamePanel.onBetBauCua(data);
    },

    onShowMessage: function (message) {
        this.gamePanel.onShowMessage(message);
    },

    onShowResultDice: function(result) {
        this.gamePanel.onShowResultDice(result.result);
    },

    onUpdateMoney: function (params) {
        this.gamePanel.updateMoney(params);
    },

    onUpdateSoiCau: function (data) {
        this.gamePanel.updateSoiCau(data);

    },

    onActiveEventDragBowl: function () {
        this.gamePanel.showEffectResult();
    },
});

