var SlotFBVO = require('SlotFBVO');
var Slot30IconFB = cc.Class({
    extends: cc.Node,
    ctor: function () {
    },

    initComponent: function (itemId, texture, itemPrefabs) {
        this.itemId = itemId;
        this.animItem = [];
        var node = cc.instantiate(itemPrefabs);

        this.item = node.getChildByName("item");
        this.sprite = this.item.getComponent(cc.Sprite);
        this.sprite.spriteFrame = texture;

        for (var i = 0; i < 7; i++) {
            this.animItem[i] = node.getChildByName("animItem" + i);
            // this.animItem[i].active = false;
        }
        this.addChild(node);
    },

    playAnimIdle(){
        this.item.active = false;
        for (var i = 0; i < 7; i++) {
            this.animItem[i].active = (i === this.itemId);
            var spine = this.animItem[i].getComponent(sp.Skeleton);
            // spine.setAnimation(0, 'active', false);
            spine.setAnimation(0, 'idle', true);
        }
    },

    playAnimActive(){
        this.item.active = false;
        for (var i = 0; i < 7; i++) {
            this.animItem[i].active = (i === this.itemId);
            var spine = this.animItem[i].getComponent(sp.Skeleton);
            spine.setAnimation(0, 'active', false);
            spine.addAnimation(0, 'idle', true, 0);
        }
    },

    stopAllAnim() {
        this.item.active = true;
        for (var i = 0; i < 7; i++) {
            this.animItem[i].active = false;
        }
    }

});

Slot30IconFB.create = function(cardId, texture, itemPrefabs){
  var component = new Slot30IconFB();
    component.initComponent(cardId, texture, itemPrefabs);
    return component;
};

module.exports = Slot30IconFB;
