var BaseCommand = require('BaseCommand');
var SlotMCProxy = require('SlotMCProxy');
var SlotMessage = require('SlotMessage');
var LobbyMessage = require('LobbyMessage');
var i18n = require('i18n');

export default class SendSpinMCCommand extends BaseCommand {
  execute(notification) {
    BaseCommand.prototype.execute.call(this, notification);
    var params = notification.getBody();
    var gameVO = SlotMCProxy.proxy.gameVO;

    var lines = params.data.lines.slice (1 , params.data.lines.length - 1).split(',');
    var money = lines.length * params.data.bet;
    var mySelf = this.facade.retrieveProxy('UserProxy').mySelf;

    if (money > mySelf.money) {
      this.sendNotification(LobbyMessage.SHOW_FLY_ALERT, { content: i18n.t("T0002") });
      return;
    }

    if(gameVO.isSpining === true) return this.sendNotification(SlotMessage.ON_SHOW_MESSAGE_MASTER_CHEF, {message: i18n.t("T0012")});

    gameVO.isSpining = true;
    gameVO.prevDataSpin = params.data;
    if(gameVO.autoSpin && gameVO.curNumAutoSpin === 0) {
      gameVO.autoSpin = false;
      this.sendNotification(SlotMessage.ON_SET_AUTO_SPIN_MASTER_CHEF, {isAuto: false});
    }
    this.sendNotification(SlotMessage.ON_HIDE_ALL_LINE_MASTER_CHEF);
    this.sendNotification(SlotMessage.ON_HIDE_WILD_MASTER_CHEF);

    if(gameVO.isChoiThu === false) {
      this.gameProxy = SlotMCProxy.proxy;
      // for(var i = 0; i < 100; i++) {
      //   this.gameProxy.sendSpin(params.data);
      // }
      var sendData = {
        lines: params.data.lines,
        bet: params.data.bet,
        gid: params.data.roomId
      }
      this.gameProxy.sendSpin(sendData);

    }else{
      gameVO.updateChoiThuData();
      this.sendNotification(SlotMessage.RECEIVE_SPIN_MASTER_CHEF, {data: gameVO} );
    }

  }
}
