import { SFSEvent } from "../../../../network/sfs/constans/SFSEvent";
import { SFSData } from "../../../../network/sfs/constans/SFSData";

var Utility = require('Utility');
var GameMessage = require('GameMessage');
var CoreGameProxy = require("CoreGameProxy");
var TableSamVO = require("TableSamVO");
var RulesSamVO = require("RulesSamVO");
var SeatSamVO = require("SeatSamVO");

export default class SamProxy extends CoreGameProxy {
    static get NAME() {
        return 'SamProxy';
    }

    onRegister() {
        CoreGameProxy.NAME = SamProxy.NAME;
        CoreGameProxy.prototype.onRegister.call(this);
    }

    initTable() {
        CoreGameProxy.prototype.initTable.call(this);
        this.tableVO = new TableSamVO();
        this.tableVO.TURN_TIME = 15;
        this.tableVO.TOTAL_PLAYER = 5;
        this.tableVO.RATE_MIN_BET = 5;

        this.tableVO.TIME_BAO_SAM = 15;
        this.tableVO.TIME_SHOW_RESULT = 3;
        this.tableVO.TIME_SHOW_RESULT_WIN_WHITE = 5;
        this.tableVO.TIME_DISPLAY_WIN_WITE_CARDS = 2;
        this.tableVO.TOTAL_CARDS = 10;
    }

    initRules() {
        CoreGameProxy.prototype.initRules.call(this);
        this.tableVO.rules = new RulesSamVO();
    }

    initSeats() {
        CoreGameProxy.prototype.initSeats.call(this);
        this.tableVO.seats = [];
        this.tableVO.seats.push(null);
        for (var i = 1; i <= this.tableVO.TOTAL_PLAYER; i++) {
            var seat = new SeatSamVO();
            seat.id = i;
            this.tableVO.seats.push(seat);
        }
    }
    
    /////////////////////////////////////////////////////////////////
    ////////////////////SEVER REPONSE//////////////////////////////
    ////////////////////////////////////////////////////////////////
    onMessage(event) {
        CoreGameProxy.prototype.onMessage.call(this, event);
        if (this.checkQueueMesseage(event)) return;
        var params = event.params;
        switch (event.cmd) {
            case SFSEvent.SAM:
                this.onBaoSam(params);
                break;
            case SFSEvent.WHITE_WIN:
                this.onWhiteWin(params);
                break;
            case SFSEvent.DISCARD:
                this.onDisCard(params);
                break;
            case SFSEvent.NEXT_TURN:
                this.onCancelCard(params);
                break;
            case SFSEvent.END_ROUND:
                this.onEndRound();
                break;
            case SFSEvent.GAME_OVER:
                this.onFinisGame(params);
                break;
        }
    }

    //overrride
    onJoinGame(evt) {
        var params = evt.params;
        var timePass = params.get(SFSData.TIME_PASS);
        var samPlayer = params.get(SFSData.SAM_PLAYER);
        samPlayer = (samPlayer) ? this.dataUser.getUserByUserName(samPlayer).uid : "-1";

        this.gameProxy.gameRoom.gameRoom.timePass = (timePass) ? timePass : 0;
        this.gameProxy.gameRoom.gameRoom.samPlayer = samPlayer;

        CoreGameProxy.prototype.onJoinGame.call(this, evt);
    }

    //override
    onDealCards(params) {
        var sfo = params;
        var arrCard = sfo.get(SFSData.CARDS);
        var curTurn = -1;
        var playerList = sfo.get(SFSData.PLAYER_LIST);
        var randomCardList = sfo.get(SFSData.RANDOM_CARD_LIST);
        var isWhiteWin = sfo.get(SFSData.IS_WHITE_WIN);
        var arrUser = [];
        var uid, randomCard, arrCardList = [];

        for (var i = 0; i < playerList.length; i++) {
            uid = this.dataUser.getUserByUserName(playerList[i]).uid;
            arrUser.push(uid)
        }

        // // mảng cac quân bài random để chia lượt đánh trước
        // for (i = 0; i < randomCardList.size(); i++) {
        //     var obj = Utility.convertSFSObjectToObject(randomCardList.getSFSObject(i));
        //     uid = this.dataUser.getUserByUserName(obj.userName).uid;
        //     randomCard = obj.randomCard;
        //     arrCardList.push({
        //         uid: uid,
        //         randomCard: randomCard
        //     });
        // }


        this.sendNotification(GameMessage.RECEIVE_DEAL_CARDS, {
            arrCard: arrCard,
            curTurn: curTurn,
            isWhiteWin: isWhiteWin,
            playerList: arrUser,
            // randomCardList: arrCardList
        });
    }

    onBaoSam(params) {
        var sfo = params;
        var sam = sfo.get(SFSData.IS_SAM);
        var userName = sfo.get(SFSData.USER_NAME);
        var user = this.dataUser.getUserByUserName(userName);
        this.sendNotification(GameMessage.RECEIVE_BAO_SAM, {
            sam: sam,
            userId: user.uid
        });
    }

    onWhiteWin(params) {
        var sfo = params;
        var arrUser = Utility.convertSFSArrayToArray(sfo.get(SFSData.PLAYER_LIST));
        var cards = sfo.get(SFSData.CARDS);
        var winners = sfo.get(SFSData.WINNERS);
        var winType = sfo.get(SFSData.WHITE_WIN_TYPE);
        for (var i = 0; i < arrUser.length; i++) {
            var o = arrUser[i];
            o.uid = this.dataUser.getUserByUserName(o.userName).uid;
        }

        for (i = 0; i < winners.length; i++) {
            o = winners[i];
            winners[i] = this.dataUser.getUserByUserName(winners[i]).uid;
        }

        this.sendNotification(GameMessage.RECEIVE_FINISH_GAME, { arrUser: arrUser, cards: cards, winType: winType });

    }

    onDisCard(params) {
        var sfo = params;
        var cards = sfo.get(SFSData.CARDS);
        var userName = sfo.get(SFSData.USER_NAME);
        var user = this.dataUser.getUserByUserName(userName);

        this.sendNotification(GameMessage.RECEIVE_PLAY_GAME, { cards: cards, userId: user.uid });
    }

    onCancelCard(params) {
        var sfo = params;
        var userName = sfo.get(SFSData.USER_NAME);
        var user = this.dataUser.getUserByUserName(userName);
        this.sendNotification(GameMessage.RECEIVE_PLAY_GAME, { cards: [], userId: user.uid });
    }

    onEndRound(params) {
        this.sendNotification(GameMessage.RECEIVE_ENROUND);
    }

    onFinisGame(params) {
        var sfo = params;
        var arrUser = Utility.convertSFSArrayToArray(sfo.get(SFSData.PLAYER_LIST));
        var winners = sfo.get(SFSData.WINNERS);
        for (var i = 0; i < arrUser.length; i++) {
            var o = arrUser[i];
            o.uid = this.dataUser.getUserByUserName(o.userName).uid;
        }

        for (i = 0; i < winners.length; i++) {
            o = winners[i];
            winners[i] = this.dataUser.getUserByUserName(winners[i]).uid;
        }

        this.sendNotification(GameMessage.RECEIVE_FINISH_GAME, { arrUser: arrUser, winners: winners });
    }


    /////////////////////////////////////////////////////////////////
    ////////////////////SEND TO SERVER//////////////////////////////
    ////////////////////////////////////////////////////////////////
    sendBaoSam(params) {
        var sam = params.sam;
        var sfo = new SFS2X.SFSObject();
        sfo.putUtfString(SFSData.COMMAND, SFSData.SAM);
        sfo.putInt(SFSData.IS_SAM, sam);

        this.sendExtensionRequest(SFSEvent.SAM, sfo);
    }

    sendPlayGame(params) {
        var cards = params.cards;
        var sfo = new SFS2X.SFSObject();
        sfo.putUtfString(SFSData.COMMAND, SFSData.DISCARD);
        sfo.putIntArray(SFSData.CARDS, cards);

        this.sendExtensionRequest(SFSEvent.DISCARD, sfo);
    }

    sendCancelTurn(params) {
        var userName = params.userName;
        var sfo = new SFS2X.SFSObject();
        sfo.putUtfString(SFSData.COMMAND, SFSData.NEXT_TURN);
        sfo.putUtfString(SFSData.USER_NAME, userName.toString());

        this.sendExtensionRequest(SFSEvent.NEXT_TURN, sfo);
    }

    sendFakeCard(params) {
        var arrCards = params.arrCards;
        var sfo = new SFS2X.SFSObject();
        sfo.putUtfString(SFSData.COMMAND, SFSData.START_GAME_1);
        sfo.putIntArray(SFSData.CARD_ARRAY_1, arrCards[0]);
        sfo.putIntArray(SFSData.CARD_ARRAY_2, arrCards[1]);
        sfo.putIntArray(SFSData.CARD_ARRAY_3, arrCards[2]);
        sfo.putIntArray(SFSData.CARD_ARRAY_4, arrCards[3]);
        sfo.putIntArray(SFSData.CARD_ARRAY_4, arrCards[4]);

        this.sendExtensionRequest(SFSEvent.START_GAME_1, sfo);
    }
}

