var BasePopup = require('BasePopup');
var Utility = require('Utility');

cc.Class({
    extends: BasePopup,

    properties: {
      icon_top: [cc.SpriteFrame]
    },

    onLoad () {
      this.content = this.node.getChildByName("content");
      this.itemTemp = this.content.getChildByName("item");
    },

    show: function () {
      BasePopup.prototype.show.call(this);
      this.content.removeAllChildren();
      this.node.emit('ACTIVE_RANKING_SHOW_PAGE');
    },

    updateRank: function (list) {
        list = Utility.convertSFSArrayToArray(list);
      this.content.removeAllChildren();
      for(var i=0 ; i<list.length ; i++){
        var item = cc.instantiate(this.itemTemp);
        item.data = list[i];
        item.getChildByName("bgLine").active = (i % 2 !== 0);
        if(i <= 2){
            item.getChildByName("lbSTT").active = false;
            item.getChildByName("iconTop").getComponent(cc.Sprite).spriteFrame = this.icon_top[i];
        }
        else{
            item.getChildByName("lbSTT").getComponent(cc.Label).string = "" + list[i].rank;
        }
        item.getChildByName("lbName").getComponent(cc.Label).string = list[i].displayname;
        item.getChildByName("lbWin").getComponent(cc.Label).string = Utility.formatCurrency(list[i].total_win);

        this.content.addChild(item);
      }
    }

});
