var LobbyEvent = require('LobbyEvent');
var Utility = require('Utility');
cc.Class({
    extends: cc.Component,

    properties: {
        txtOldPass:  cc.EditBox,
        txtNewPass: cc.EditBox,
        txtReNewPass: cc.EditBox,
        txtError: cc.Label
    },

    // use this for initialization
    onLoad: function () {
        this.txtError.string = '';
    },

    onHandlerChangePass: function (event) {
        var params = {};
        params.currentPassword = this.txtOldPass.string;
        params.newPassword = this.txtNewPass.string;
        params.reNewPassword = this.txtNewPass.string;

        this.node.emit(LobbyEvent.CHANGE_PASSWORD, params);
    },

    onUpdateChangePass: function () {
        this.txtOldPass.string = '';
        this.txtNewPass.string = '';
        this.txtReNewPass.string = '';
    },



});
