var BaseCommand = require('BaseCommand');
var SlotMessage = require('SlotMessage');
var LobbyMessage = require('LobbyMessage');
var SoundGameMessage = require('SoundGameMessage');

export default class InitSlotMCCommand extends BaseCommand {
  execute(notification) {
    BaseCommand.prototype.execute.call(this, notification);
    this.sendNotification(SlotMessage.ON_INIT_GAME_MASTER_CHEF);
    // this.sendNotification(SlotMessage.SHOW_MINIGAME_MASTER_CHEF);
    this.sendNotification(LobbyMessage.STOP_BACKGROUND_MUSIC);
  }
}
