var BaseCommand = require('BaseCommand');
var LobbyMessage = require('LobbyMessage');
var MiniGameMessage = require('MiniGameMessage');
var i18n = require('i18n');

export default class ReceiveBetBauCuaCommand extends BaseCommand {
    execute(notification) {
        BaseCommand.prototype.execute.call(this, notification);
        var params = notification.getBody().data;
        if(!params.success) {
            this.isErrorBauCua(parseInt(params.error));
            return;
        }

        var value = parseInt(params.bet);
        var index = parseInt(params.index);

        var mySelf =  this.facade.retrieveProxy('UserProxy').mySelf;
        mySelf.money = parseInt(params.money);

        this.sendNotification(MiniGameMessage.ON_BET_BAU_CUA, {
            index: index,
            value: value
        });
        this.onShowMessageBauCua(i18n.t("T0001"));
        this.sendNotification(LobbyMessage.ON_UPDATE_MY_INFO);
    }

    isErrorBauCua (errorCode) {
        switch (errorCode) {
            case 1001:
                this.onShowMessageBauCua(i18n.t("T0002"));
                break;
            case 10008:
                this.onShowMessageBauCua(i18n.t("T0004"));
                break;
            default:
                this.onShowMessageBauCua(i18n.t("T0059"));
                break;
        }
    }

    onShowMessageBauCua (msg) {
        this.sendNotification(MiniGameMessage.SHOW_MESSAGE_BAU_CUA, {
            msg:msg
        });
    }
}
