var GameConfig = require('GameConfig');
var Utility = require('Utility');
cc.Class({
    extends: cc.Component,

    properties: {
        txtGiftCode:cc.EditBox,
        txtCaptcha:cc.EditBox,
        imgCaptCha:cc.Sprite
    },

    // use this for initialization
    onLoad: function () {
        this.userName = "";

    },

    setupCaptcha:function (userName) {
        this.userName = userName;
        var api = GameConfig.WEB_SERVICE + "/newapi/getcaptcha?userName=" + userName;
        Utility.loadUrlImage(api, function (imgCaptCha, texture) {
            imgCaptCha.spriteFrame = new cc.SpriteFrame(texture);
        },[this.imgCaptCha]);
    },

    onHandlerRefeshCaptcha:function () {
        this.setupCaptcha(this.userName);
    }
});
