var MiniGameConfig = cc.Class({
    extends: cc.Component,

    properties: {
        zoneBauCua:"bcc",
        zoneMiniPoker:"",
        zoneSlot3x3:"Pokego",
        zoneSlotBT: "slotbom",
        zoneTaiXiu:"taixiu_32",
        zoneToNho:"",
        zoneVongQuay:""

    },

    statics: {
        ZONE_BAU_CUA:"",
        ZONE_MINIPOKER:"",
        ZONE_SLOT3X3:"",
        ZONE_SLOT_BT: "",
        ZONE_TAIXIU:"",
        ZONE_TO_NHO:"",
        ZONE_VONG_QUAY:""
    },

    // use this for initialization
    onLoad: function () {
        MiniGameConfig.ZONE_BAU_CUA = this.zoneBauCua;
        MiniGameConfig.ZONE_MINIPOKER = this.zoneMiniPoker;
        MiniGameConfig.ZONE_SLOT3X3 = this.zoneSlot3x3;
        MiniGameConfig.ZONE_SLOT_BT = this.zoneSlotBT;
        MiniGameConfig.ZONE_TAIXIU = this.zoneTaiXiu;
        MiniGameConfig.ZONE_TO_NHO = this.zoneToNho;
        MiniGameConfig.ZONE_VONG_QUAY = this.zoneVongQuay;
    }
});
