var BasePopup = require('BasePopup');
var VipPointSceneMediator = require('VipPointSceneMediator');

cc.Class({
    extends: BasePopup,

    properties: {
        txtLevel:cc.Label,
        txtVipPoint:cc.Label,
        txtVipPointTrace:cc.Label,
    },

    // use this for initialization
    onLoad: function () {
        VipPointSceneMediator.getInstance.init(this);
        this.hide();
    },

    show: function () {
        BasePopup.prototype.show.call(this);
    },
});

