import { SFSData } from "../../../../network/sfs/constans/SFSData";
import { SFSEvent } from "../../../../network/sfs/constans/SFSEvent";
var BaseProxy = require('BaseProxy');
var GameConfig = require('GameConfig');
var LobbyMessage = require('LobbyMessage');

export default class ChatProxy extends BaseProxy {
    static get NAME() {
        return 'ChatProxy';
    }

    static errData = {

    }

    onRegister() {
        BaseProxy.prototype.onRegister.call(this);
        this.dataUser = this.facade.retrieveProxy('UserProxy');
    }


    /////////////////////////////////////////////////////////////////
    ////////////////////SEVER REPONSE//////////////////////////////
    ////////////////////////////////////////////////////////////////
    onMessage(event) {
        var params = event.params;
        switch (event.cmd) {
            case SFSEvent.PUBLIC_CHAT:
                this.onUpdateChat(params);
                break;

        }
    }

    onUpdateChat(params) {
        if (params.userName === "") return;
        this.sendNotification(LobbyMessage.RECEIVE_CHAT, params);
    }

    /////////////////////////////////////////////////////////////////
    ////////////////////SEND TO SERVER//////////////////////////////
    ////////////////////////////////////////////////////////////////
    sendExtensionRequest(command, sfo) {
        sfo.putUtfString(SFSData.USER_NAME, this.dataUser.mySelf.userName);
        var room = this.sfs.lastJoinedRoom;
        var request = new SFS2X.ExtensionRequest(command, sfo, room);
        this.sfsProxy.send(request);
    }

    onJoinChatZone() {
        var mySelf = this.dataUser.mySelf;
        this.sfsProxy.joinZone(GameConfig.SOCKET_IP, GameConfig.SOCKET_PORT, "chatall_" + mySelf.uid, mySelf.password, "chatall", mySelf.token);
    }

    sendChat(params) {
        var sfo = new SFS2X.SFSObject();
        sfo.putUtfString(SFSData.COMMAND, SFSData.PUBLIC_CHAT);
        sfo.putUtfString(SFSData.MESSAGE, params.message);
        sfo.putUtfString(SFSData.DISPLAY_NAME, params.displayName);
        sfo.putUtfString(SFSData.USER_NAME, this.dataUser.mySelf.uid);

        var room = this.sfsProxy.sfs.lastJoinedRoom;
        var request = new SFS2X.PublicMessageRequest(SFSEvent.PUBLIC_CHAT, sfo, room);
        this.sfsProxy.send(request);
    }

    sendDisconnect() {
        //this.sfsProxy.onDisconnect();
    }

}
