var BaseCommand = require('BaseCommand');
var SFSMessage = require('SFSMessage');
var SFSSubMesseage = require('SFSSubMesseage');

export default class SendGetDetailSessionTaiXiuCommand extends BaseCommand {
  execute(notification) {
    BaseCommand.prototype.execute.call(this, notification);
    var params = notification.getBody();
    var sendData = {
        session: params.session,
        page: params.page,
        moneyType: 0
    };

    this.gameProxy = this.facade.retrieveProxy("TaiXiuProxy");
    this.gameProxy.sendGetDetailSession(sendData);
  }
}
