var BaseGameCommand = require('BaseGameCommand');
var SlotMessage = require('SlotMessage');
var SlotMCProxy = require('SlotMCProxy');

export default class SetupSlotMCCommand extends BaseGameCommand {
  execute(notification) {
    BaseGameCommand.prototype.execute.call(this, notification);
    this.gameMediator =  notification.getBody().gameMediator;
    this.gameProxy = notification.getBody().gameProxy;
    SlotMCProxy.proxy = this.gameProxy;

    this.facade.registerCommand(SlotMessage.INIT_SLOT_MASTER_CHEF, require('InitSlotMCCommand'));
    this.facade.registerCommand(SlotMessage.SEND_SPIN_MASTER_CHEF, require('SendSpinMCCommand'));
    this.facade.registerCommand(SlotMessage.SEND_GET_RANK_MASTER_CHEF, require('SendGetRankMCCommand'));
    this.facade.registerCommand(SlotMessage.SEND_GET_MY_HISTORY_MASTER_CHEF, require('SendGetMyHistoryMCCommand'));
    this.facade.registerCommand(SlotMessage.SEND_LEAVE_ROOM_MASTER_CHEF, require('SendLeaveRoomMCCommand'));
    this.facade.registerCommand(SlotMessage.SEND_ORDER_SPIN_MASTER_CHEF, require('SendOrderSpinMCCommand'));
    this.facade.registerCommand(SlotMessage.SEND_PLAY_GAME_X2_MASTER_CHEF, require('SendPlayX2MCCommand'));

    this.facade.registerCommand(SlotMessage.RECEIVE_SPIN_MASTER_CHEF, require('ReceiveSpinMCCommand'));
    this.facade.registerCommand(SlotMessage.RECEIVE_UPDATE_JACKPOT_MASTER_CHEF, require('ReceiveUpdateJackPotMCCommand'));
    this.facade.registerCommand(SlotMessage.RECEIVE_GET_RANK_MASTER_CHEF, require('ReceiveGetRankMCCommand'));
    this.facade.registerCommand(SlotMessage.RECEIVE_GET_MY_HISTORY_MASTER_CHEF, require('ReceiveGetMyHistoryMCCommand'));
    this.facade.registerCommand(SlotMessage.RECEIVE_PLAY_GAME_X2_MASTER_CHEF, require('ReceivePlayX2MCCommand'));
    this.facade.registerMediator(this.gameMediator);
  }
}
