var UserVO = require('UserVO');
var BaseCommand = require('BaseCommand');
var LobbyMessage = require('LobbyMessage');
var SoundGameMessage = require('SoundGameMessage');
var LocalStorage = require('LocalStorage');
var Constants = require('Constants');

export default class ReceiveLoginCommand extends BaseCommand {
    execute(notification) {
        BaseCommand.prototype.execute.call(this, notification);
        if (this.isError(notification.getBody().data)) return;
        this.resetSetAllData();
        var params = notification.getBody().data;
        var sendData = notification.getBody().sendData;
        var userProxy = this.facade.retrieveProxy('UserProxy');
        var configProxy = this.facade.retrieveProxy('ConfigProxy');
        var userVO = new UserVO();
        userVO.onRegister();
        LocalStorage.setIsSocial(params.isSocial);
        // if (params.isSocial === 0)
        //     LocalStorage.setAutoLoginNormal("1");

        userProxy.reset();
        configProxy.reset();

        userVO.updateData(params);
        userProxy.addMyself(userVO);

        if (params.displayName === "") {
            this.sendNotification(LobbyMessage.SHOW_FACEBOOK_VALIDATE_SCENE);
        } else {
            LocalStorage.setUserName(sendData.userName);
            LocalStorage.setPassword(sendData.password);
            this.sendNotification(LobbyMessage.SEND_GET_IN_GAME_CONFIG);
        }

        this.sendNotification(LobbyMessage.HIDE_LOGIN_SCENE);
        this.sendNotification(LobbyMessage.SHOW_SELECT_GAME_SCENE);
        this.sendNotification(LobbyMessage.SHOW_TOP_MENU);
        this.sendNotification(LobbyMessage.SHOW_BOTTOM_MENU);
        this.sendNotification(LobbyMessage.SHOW_EVENT_BANNER_SCENE);
        this.sendNotification(LobbyMessage.PLAY_BACKGROUND_SOUND, SoundGameMessage.LOBBY_SOUND);
        this.checkGameReconnect();

        // var oneSignal = this.facade.retrieveProxy('OneSignalProxy');
        // oneSignal.setUserInfo(userProxy.mySelf.id, userProxy.mySelf.email, cc.sys.os);
    }

    checkGameReconnect() {
        var reconnectVO = LocalStorage.getIsReconnect();
        if (!reconnectVO) return;
        reconnectVO = JSON.parse(reconnectVO);
        Constants.CURRENT_GAME = reconnectVO.channelId;
        this.sendNotification(LobbyMessage.SEND_GET_CHANNEL_INFO, { channelId: reconnectVO.channelId });
        this.sendNotification(LobbyMessage.HIDE_STARTGAME_POPUP_SCENE);
        this.sendNotification(LobbyMessage.SHOW_LOADING);
        LocalStorage.setIsReconnect(false);
    }

    resetSetAllData() {
        var giftProxy = this.facade.retrieveProxy('GiftProxy');
        var mailProxy = this.facade.retrieveProxy('MailProxy');
        var rechargeProxy = this.facade.retrieveProxy('RechargeProxy');
        var shopProxy = this.facade.retrieveProxy('ShopProxy');
        var userProxy = this.facade.retrieveProxy('UserProxy');


        giftProxy.reset();
        mailProxy.reset();
        rechargeProxy.reset();
        shopProxy.reset();
        userProxy.reset();
    }
}