var Utility = require('Utility');

cc.Class({
    extends: cc.Component,

    properties: {
        roomJackpot: [cc.Label]
    },

    onLoad () {
    },

    buildUI(gameVO) {
        this.gameVO = gameVO;
    },

    show() {
        this.node.active = true;
    },

    hide() {
        this.node.active = false;
    },

    selectRoom(event) {
        switch (event.target.name) {
            case 'btnRoom1':
                this.gameVO.bet = 1;
                break;
            case  'btnRoom2':
                this.gameVO.bet = 2;
                break;
            case  'btnRoom3':
                this.gameVO.bet = 4;
                break;
            case  'btnChoiThu':
                this.gameVO.bet = 0;
                break;
        }
        this.gameVO.isChoiThu = (event.target.name === "btnChoiThu");
        this.node.emit('ACTIVE_SET_ROOM_BET', this.gameVO.bet);
        this.hide();
    },

    onUpdateJackpot() {
        Utility.tweenRunNumber(this.roomJackpot[0].node, this.gameVO.roomJackpot1, 1);
        Utility.tweenRunNumber(this.roomJackpot[1].node, this.gameVO.roomJackpot2, 1);
        // Utility.tweenRunNumber(this.roomJackpot[2].node, this.gameVO.roomJackpot3, 1);
        Utility.tweenRunNumber(this.roomJackpot[3].node, this.gameVO.roomJackpot4, 1);
    },

    clickBtnBack() {
        this.node.emit('ACTIVE_LEAVE_GAME');
    }

});
