var BaseVO = require("BaseVO");
var puremvc = BaseVO.puremvc;

module.exports = puremvc.define
(
    // CLASS INFO
    {
        constructor: function () {
            this.gameName = "";
            this.jackpot = 0;
            this.act = "";
            this.code = "";
        }
    },

    // INSTANCE MEMBERS
    {
        update:function (data) {
            this.gameName = data.code;
            // this.jackpot = data.value;
            if (this.gameName === 'songoku' || this.gameName === 'aicap' || this.gameName === 'candy' ) {
                this.jp100 = data.data[0].jackpot;
                this.jp1000 = data.data[1].jackpot;
                this.jp10000 = data.data[3].jackpot;
            }
            else {
                this.jp100 = data.data[0].jackpot;
                this.jp1000 = data.data[1].jackpot;
                this.jp10000 = data.data[2].jackpot;
            }
            // this.act = data.act;
            this.code = data.code;
        },

        reset:function () {
            this.gameName = "";
            this.jackpot = 0;
            this.act = "";
            this.code = "";
        }
    },
    // STATIC MEMBERS
    {
        NAME: "GameJackpot"
    }
);