var BaseGameCommand = require('BaseGameCommand');
var GameMessage = require('GameMessage');

export default class DestroyGameCommand extends BaseGameCommand {
    execute(notification) {
        BaseGameCommand.prototype.execute.call(this, notification);
        this.sendNotification(GameMessage.ON_DESTROY_GAME);
        this.gameProxy.isLoadDone = false;
        this.gameProxy.isInitGameDone = false;
        this.gameProxy.removeMessage();
        
        this.removeAllCommand();
        this.removeAllMediator();
        this.removeAllProxy();
        this.facade.removeCommand(GameMessage.DESTROY_GAME);
    }
}