var BaseMediator = require('BaseMediator');
var LobbyMessage = require('LobbyMessage');
var CustomAction = require('CustomAction');


class RechargeSceneMediator extends BaseMediator {
    static get NAME() {
        return 'RechargeSceneMediator';
    }

    static get getInstance() {
        if (this.instance === undefined) {
            this.instance = new RechargeSceneMediator();
        }
        return this.instance;
    }

    onRegister() {
        this.rechargeProxy = this.facade.retrieveProxy('RechargeProxy');
        this.dataUser = this.facade.retrieveProxy('UserProxy');
    }

    /** @override */
    listNotificationInterests() {
        return [
            LobbyMessage.SHOW_RECHARGE_SCENE,
            LobbyMessage.HIDE_RECHARGE_SCENE,
            LobbyMessage.SHOW_TAB_IN_RECHARGE_SCENE,
            LobbyMessage.ON_RESET_MOBILE_CARD_RECHARGE,
            LobbyMessage.SHOW_CONFIRM_OTP,
            LobbyMessage.ON_CONFIRM_OTP_FAIL,
            LobbyMessage.ON_CONFIRM_OTP_SUCESS,
            LobbyMessage.ON_UPDATE_RECHARGE_INFO,
            LobbyMessage.ON_UPDATE_MY_INFO
        ];
    }

    /** @override */
    handleNotification(notification) {
        var params = notification.getBody();
        switch (notification.getName()) {
            case LobbyMessage.SHOW_RECHARGE_SCENE:
                if (!this.isLogin("C0052")) {
                    return;
                }
                this.view.show();
                this.sendNotification(LobbyMessage.SEND_GET_RECHARGE_INFO);
                this.view.onUpdateMySelf(this.dataUser.mySelf);
                this.view.onShowTab(CustomAction.ACTION_RECHARGE_ROY);
                break;
            case LobbyMessage.HIDE_RECHARGE_SCENE:
                this.view.hide();
                break;
            case LobbyMessage.ON_UPDATE_RECHARGE_INFO:
                this.view.onUpdateRechargeInfo(this.rechargeProxy.recharge);
                break;
            case LobbyMessage.SHOW_TAB_IN_RECHARGE_SCENE:
                this.sendNotification(LobbyMessage.SHOW_RECHARGE_SCENE);
                this.view.onShowTab(params.tabId);
                break;
            case LobbyMessage.ON_RESET_MOBILE_CARD_RECHARGE:
                this.view.onResetMobileCardRecharge();
                break;
            case LobbyMessage.SHOW_CONFIRM_OTP:
                this.view.onShowConfirmOTP();
                break;
            case LobbyMessage.ON_CONFIRM_OTP_FAIL:
                this.view.onComfirmOTPFail();
                break;
            case LobbyMessage.ON_CONFIRM_OTP_SUCESS:
                this.view.onComfirmOTPSucess();
                break;
            case LobbyMessage.ON_UPDATE_MY_INFO:
                this.view.onUpdateMySelf(this.dataUser.mySelf);
                break;
            default:
                break;
        }
    }

    addHanlers() {
        this.view.activeMobileCardRecharge = this.activeMobileCardRecharge.bind(this);
        this.view.activeOTPRecharge = this.activeOTPRecharge.bind(this);
        this.view.activeOTPConfirm = this.activeOTPConfirm.bind(this);
        this.view.activeBankRecharge = this.activeBankRecharge.bind(this);
        this.view.activeTransferMoneyToChip = this.activeTransferMoneyToChip.bind(this);
        this.view.activeShowInfo = this.activeShowInfo.bind(this);
    }

    activeMobileCardRecharge(params) {
        this.sendNotification(LobbyMessage.SEND_MOBILE_CARD_RECHARGE, params);
    }

    activeOTPRecharge(params) {
        this.sendNotification(LobbyMessage.SEND_REQUEST_OTP, params);
    }

    activeOTPConfirm(params) {
        this.sendNotification(LobbyMessage.SEND_CONFIRM_OTP, params);
    }

    activeBankRecharge(params) {
        this.sendNotification(LobbyMessage.SEND_GET_URL_BANK, params);
    }

    activeTransferMoneyToChip(params) {
        this.sendNotification(LobbyMessage.SEND_TRANSFER_MONEY_TO_CHIP, params);
    }

    activeShowInfo() {
        this.sendNotification(LobbyMessage.SHOW_USER_PROFILE_SCENE);
        this.sendNotification(LobbyMessage.HIDE_RECHARGE_SCENE);
    }
}

module.exports = RechargeSceneMediator;