  updateSelectRoomId(selectRoomId) {
    this.selectRoomId = selectRoomId;
  }

  updatejackpot(data) {
    this.roomJackpot1 = data[0].jackpot;
    this.roomJackpot2 = data[1].jackpot;
    this.roomJackpot3 = data[2].jackpot;
    this.roomJackpot4 = data[3].jackpot;
  }

  getCurrentJackpot() {
    var roomJackPot = 0;
    if (this.selectRoomId === 1) roomJackPot = this.roomJackpot1;
    if (this.selectRoomId === 2) roomJackPot = this.roomJackpot2;
    if (this.selectRoomId === 3) roomJackPot = this.roomJackpot3;
    if (this.selectRoomId === 4) roomJackPot = this.roomJackpot4;
    return roomJackPot;
  }

  getListRoomJackpot() {
    return [this.roomJackpot1, this.roomJackpot2, this.roomJackpot3, this.roomJackpot4];
  }

  getLinesBet() {
    var arr = this.linesBet.split('');
    var result = [];
    for (var i = 0; i < arr.length; i++) {
      if (arr[i] === "1") result.push(i + 1);
    }

    return result.toString()
  }

  // getLinesBet() {
  //   return ;
  // }

  setLinesBet(value) {
    this.linesBet = value;
  }

  setNextBet() {
    this.bet = (this.bet < this.listBet.length) ? this.bet + 1 : 1;
  }

  getCurBet() {
    return this.listBet[this.bet];
  }

  getNumBetLine() {
    var arr = this.linesBet.split("");
    var count = 0;
    for (var i = 0; i < arr.length; i++) {
      if (parseInt(arr[i]) === 1) count++;
    }

    return count;
  }

  getTotalBet() {
    return this.getCurBet() * this.getNumBetLine()
  }



  getGid(bet) {
    var gid = this.listBet.indexOf(bet);
    return gid;
  }

  getItemIdFromRewardId(rewardId){
    var mapId = [0,0,0,0,1,1,1,2,2,2,3,3,3,4,4,0];
    if (rewardId === -1)
      return 5;
    if (rewardId === -2)
      return 6;
    return mapId[rewardId];
  }
