import { SFSData } from "../../../../network/sfs/constans/SFSData";
import { SFSEvent } from "../../../../network/sfs/constans/SFSEvent";

var BaseProxy = require("BaseProxy");
var TableVO = require("TableVO");
var SeatVO = require("SeatVO");
var RulesVO = require("RulesVO");
var UserProxy = require("UserProxy");
var Utility = require('Utility');
var UserVO = require('UserVO');
var GameMessage = require('GameMessage');

export default class CoreGameProxy extends BaseProxy {
    initialize() {
        this.isLoadDone = false;
        this.gameRoom = null;
        this.tableVO = null;
        this.queueMsg = [];

        this.commandList = [];
        this.proxyList = [];
        this.mediatorList = [];

        this.initTable();
        this.initRules();
        this.initSeats();
    }

    onRegister() {
        this.dataUser = this.facade.retrieveProxy(UserProxy.NAME);
        this.gameProxy = this.facade.retrieveProxy(CoreGameProxy.NAME);
        this.tableVO = this.gameProxy.tableVO;

        this.sfsProxy = this.facade.retrieveProxy('SFSGameProxy');
        this.sfsProxy.messages.push(this.onMessage.bind(this));
    }

    initTable() {
        this.tableVO = new TableVO();

        this.tableVO.TURN_TIME = 15;
        this.tableVO.TOTAL_PLAYER = 4;
        this.tableVO.RATE_MIN_BET = 5;
    }

    initRules() {
        this.tableVO.rules = new RulesVO();
    }

    initSeats() {
        this.tableVO.seats = [];
        this.tableVO.seats.push(null);
        for (var i = 1; i <= this.tableVO.TOTAL_PLAYER; i++) {
            var seat = new SeatVO();
            seat.id = i;
            this.tableVO.seats.push(seat);
        }
    }

    getTable() {
        return this.tableVO;
    }

    reset() {
        this.onRegister();
    }

    /////////////////////////////////////////////////////////////////
    ////////////////////SEVER REPONSE//////////////////////////////
    ////////////////////////////////////////////////////////////////
    onMessage(event) {
        var params = event.params;
        switch (event.cmd) {
            case SFSEvent.UPDATE_ROOM_MASTER:
                this.onUpdateOwnerId(params);
                break;
            case SFSEvent.UPDATE_CURRENT_TURN:
                this.onUpdateCurTurn(params);
                break;
            case SFSEvent.GET_PLAYING_INFO: //done
                this.onJoinGame(event);
                break;
            case SFSEvent.USER_JOIN_ROOM: //thằng khác vào game
                this.onUserJoinGame(params);
                break;
            case SFSEvent.UPDATE_GAME_ROOM_DATA:
                this.onUpdateGameRoomData(params);
                break;
            case SFSEvent.UPDATE_USER_LIST:
                this.onUpdateUserList(params.user);
                break;
            case SFSEvent.USER_LEAVE_GAME:
                this.onUserLeaveGame(params);
                break;
            case SFSEvent.COUNT_DOWN:
                this.onCountDownStartGame(params);
                break;
            case SFSEvent.START_GAME:
                this.onStartGame(params);
                break;
            case SFSEvent.DEAL_CARD:
                this.onStartGame(params);
                this.onDealCards(params);
                break;
            case SFSEvent.UPDATE_USER_INFO:
                this.onUpdateUserInfo(params);
                break;
            case SFSEvent.UPDATE_MONEY:
                this.onUpdateMoney(params);
                break;
            case SFSEvent.SIT_DOWN:
                this.onSitDown(params);
                break;
            case SFSEvent.PUBLIC_CHAT:
                this.onUpdateChat(params);
                break;
            case SFSEvent.REGISTER_QUIT:
                this.onRegisterQuit(params);
                break;
            case SFSEvent.CANCEL_REGISTER_QUIT:
                this.onCancelRegisterQuit(params);
                break;
            case SFSEvent.READY:
                this.onReadyGame(params);
                break;
        }
    }

    onReadyGame(params) {
        var sfo = params;
        var userId = sfo.get(SFSData.USER_NAME);
        this.sendNotification(GameMessage.RECEIVE_READY_GAME, { userId: userId });
    }

    onRegisterQuit(params) {
        var sfo = params;
        var userName = sfo.get(SFSData.USER_NAME);
        var user = this.dataUser.getUserByUserName(userName);
        this.sendNotification(GameMessage.RECEIVE_REGISTER_QUIT, { userId: user.uid, isQuit: true });
    }

    onCancelRegisterQuit(params) {
        var sfo = params;
        var userName = sfo.get(SFSData.USER_NAME);
        var user = this.dataUser.getUserByUserName(userName);
        this.sendNotification(GameMessage.RECEIVE_REGISTER_QUIT, { userId: user.uid, isQuit: false });
    }

    onUpdateChat(evtParams) {
        var seat = this.tableVO.getSeatByUserId(evtParams.userName);
        var data = {
            seatId: seat.id,
            chatContent: evtParams.message,
            chatType: evtParams.type
        };

        var dataHistoryChat = {
            displayName: evtParams.displayName,
            message: evtParams.message
        };
        this.sendNotification(GameMessage.ON_SHOW_CHAT_GAME_CONTENT, data);
        this.sendNotification(LobbyMessage.ON_UPDATE_CHAT_GAME, dataHistoryChat);
    }

    onUpdateGameRoomData(params) {
        var room = params.room;
        var gameRoom = {};
        gameRoom.id = room.id;
        gameRoom.name = room.name;
        gameRoom.password = (room.isPasswordProtected) ? '1' : '0';
        gameRoom.stake = (room.getVariable(SFSData.ROOM_BET)) ? room.getVariable(SFSData.ROOM_BET).value : gameRoom.stake;
        gameRoom.stake = (room.getVariable(SFSData.RB)) ? room.getVariable(SFSData.RB).value : gameRoom.stake;
        gameRoom.maxPlayer = room.maxUsers;
        gameRoom.isSystem = room.getVariable(SFSData.IS_SYSTEM);

        var vtPlayer = [];
        var playerList = room.getUserList();
        for (var key in playerList) {
            var sfsUserInfo = playerList[key].getVariable(SFSData.USER_INFO).value;
            var playerInfo = Utility.convertSFSObjectToObject(sfsUserInfo);
            var user = this.dataUser.getUserById(playerInfo.uid);
            if (!user) {
                user = new UserVO();
                user.onRegister();
                user.updateData(playerInfo);
                this.dataUser.addUser(user);
            } else {
                user.updateData(playerInfo);
            }
            this.dataUser.adPrefixUser(user);
            vtPlayer.push(user);
        }

        var data = {
            gameRoom: gameRoom,
            vtPlayer: vtPlayer,
            curTurn: -1
        };
        this.gameProxy.gameRoom = data;
    }

    onJoinGame(event) {
        var sfo = event.params;
        var sfoRoom = event.room;

        var playerList = sfo.get(SFSData.PLAYER_LIST);
        var status = sfo.get(SFSData.GAME_STATE);
        var vtPlayer = this.gameProxy.gameRoom.vtPlayer;
        if (playerList) {
            for (var i = 0; i < playerList.size(); i++) {
                var playerInfo = Utility.convertSFSObjectToObject(playerList.getSFSObject(i));
                var user = this.dataUser.getUserByUserName(playerInfo.userName);
                if (!user) {
                    user = new UserVO();
                    user.onRegister();
                    this.dataUser.addUser(user);
                }
                playerInfo.seatId = playerInfo.position + 1;
                user.updateData(playerInfo);
                user.status = status;
                user.position = (playerInfo.position === undefined || playerInfo.position === null) ? -1 : playerInfo.position;
                user.cards = this.convertCardInCurrentGame(playerInfo.cards);
                user.isViewer = (status !== SeatVO.WAITING) ? user.isViewer : false;
                this.dataUser.adPrefixUser(user);
                var sfoUser = sfoRoom.getUserByName(playerInfo.userName);
                if (sfoUser) {
                    user.money = parseInt(sfoUser.getVariable(SFSData.MONEY).value);
                }
                var config = this.facade.retrieveProxy('ConfigProxy').config;
                user.deviceId = config.deviceId;

                if (!this.checkHasIdVtPlayer(user.uid, vtPlayer)) {
                    vtPlayer.push(user);
                }
            }
        }

        this.updateMoreInfoJoinGame(event);

        var timePass = sfo.get(SFSData.TIME_PASS);
        var currentTurn = sfo.get(SFSData.CURRENT_TURN);
        currentTurn = (currentTurn === undefined || currentTurn === null || currentTurn === "") ? -1 : this.dataUser.getUserByUserName(currentTurn).uid;

        this.gameProxy.gameRoom.gameRoom.isPlaying = (status !== SeatVO.WAITING) ? true : false;
        this.gameProxy.gameRoom.gameRoom.cardsPlace = sfo.get(SFSData.CURRENT_CARD_GROUP);
        this.gameProxy.gameRoom.gameRoom.gameState = status;
        this.gameProxy.gameRoom.curTurn = currentTurn;
        this.gameProxy.gameRoom.timePass = (timePass) ? timePass : 0;
        this.gameProxy.gameRoom.regQuit = sfo.get(SFSData.REG_QUIT);
        this.gameProxy.gameRoom.ownerId = sfo.get(SFSData.GAME_MASTER);
        this.sendNotification(GameMessage.RECEIVE_JOIN_GAME, this.gameProxy.gameRoom);

        // var ownerId = sfo.get(SFSData.GAME_MASTER);
        // this.sendNotification(GameMessage.RECEIVE_CHANGE_OWNER, { ownerId: ownerId });
    }

    updateMoreInfoJoinGame(params) {

    }

    convertCardInCurrentGame(cards) {
        return cards;
    }

    onUpdateUserList(params) {
        var playerInfo = Utility.convertSFSObjectToObject(params.getVariable(SFSData.USER_INFO).value);
        var user = this.dataUser.getUserById(playerInfo.uid);
        playerInfo.money = params.getVariable(SFSData.MONEY).value;
        playerInfo.deviceId = params.getVariable(SFSData.DEVICE_ID).value;
        if (!user) {
            user = new UserVO();
            user.onRegister();
            user.updateData(playerInfo);
            this.dataUser.addUser(user);
            this.dataUser.adPrefixUser(user);
        } else {
            user.updateData(playerInfo);
            this.dataUser.adPrefixUser(user);
        }
    }

    onUserJoinGame(params) {
        var sfo = params;
        var userName = sfo.get(SFSData.USER_NAME);
        var user = this.dataUser.getUserByUserName(userName);
        if (user.uid === this.dataUser.mySelf.uid) {
            return;
        }

        var position = sfo.get(SFSData.POSITION);

        var vtPlayer = this.gameProxy.gameRoom.vtPlayer;
        var hasUser = false;
        for (var i = 0; i < vtPlayer.length; i++) {
            if (vtPlayer[i].uid === user.uid) {
                vtPlayer[i] = user;
                hasUser = true;
            }
        }

        if (!hasUser) vtPlayer.push(user);
        user.position = position;

        this.sendNotification(GameMessage.RECEIVE_USER_JOIN_GAME, { user: user });
    }

    onUpdateOwnerId(params) {
        var ownerId = params.get(SFSData.GAME_MASTER);
        this.sendNotification(GameMessage.RECEIVE_CHANGE_OWNER, { ownerId: ownerId });
    }

    onUserLeaveGame(params) {
        var userName = params.get(SFSData.USER_NAME);
        var user = this.dataUser.getUserByUserName(userName);
        if (user.uid === this.dataUser.mySelf.uid) {
            this.sfsProxy.stopPingToServer();
            //this.onLeaveRoom();
        }

        this.sendNotification(GameMessage.RECEIVE_LEAVE_GAME, { userIdLeave: user.uid });


    }

    onCountDownStartGame(params) {
        var timeLeft = params.get(SFSData.TIME_LEFT);
        this.sendNotification(GameMessage.RECEIVE_COUNT_DOWN_START_GAME, { timeLeft: timeLeft });
    }

    onStartGame(params) {
        this.sendNotification(GameMessage.RECEIVE_START_GAME);
    }

    onDealCards(params) {
        var sfo = params;
        var arrCard = sfo.get(SFSData.PLAYER_CARDS);
        var curTurn = sfo.get(SFSData.CURRENT_TURN);
        var playerList = sfo.get(SFSData.PLAYER_LIST);
        var randomCardList = sfo.get(SFSData.RANDOM_CARD_LIST);
        var userIdList = [];
        var uid, randomCard, arrCardList = [];
        curTurn = this.dataUser.getUserByUserName(curTurn).uid;
        for (var i = 0; i < playerList.length; i++) {
            uid = this.dataUser.getUserByUserName(playerList[i]).uid;
            userIdList.push(uid);
        }

        // mảng cac quân bài random để chia lượt đánh trước
        // for (i = 0; i < randomCardList.size(); i++) {
        //     var obj = Utility.convertSFSObjectToObject(randomCardList.getSFSObject(i));
        //     uid = this.dataUser.getUserByUserName(obj.userName).uid;
        //     randomCard = obj.randomCard;
        //     arrCardList.push({
        //         uid: uid,
        //         randomCard: randomCard
        //     });
        // }

        this.sendNotification(GameMessage.RECEIVE_DEAL_CARDS, {
            arrCard: arrCard,
            curTurn: curTurn,
            playerList: userIdList,
            // randomCardList: arrCardList
        });
    }

    onUpdateCurTurn(params) {
        var curTurn = params.get(SFSData.CURRENT_TURN);
        curTurn = this.dataUser.getUserByUserName(curTurn).uid;
        this.sendNotification(GameMessage.RECEIVE_UPDATE_CURRENT_TURN, { curTurn: curTurn });
    }

    onUpdateUserInfo(params) {
        var sfsUser = params.user.getVariable(SFSData.USER_INFO);
        var sfsMoney = params.user.getVariable(SFSData.MONEY);
        var playerInfo = Utility.convertSFSObjectToObject(sfsUser.value);
        playerInfo.money = (sfsMoney) ? parseInt(sfsMoney.value) : playerInfo.money;
        var user = this.dataUser.getUserByUserName(playerInfo.userName);
        if (!user) {
            user = new UserVO();
            user.onRegister();
            this.dataUser.addUser(user);
        }
        user.updateData(playerInfo);

        this.sendNotification(GameMessage.RECEIVE_UPDATE_USER_INFO, { user: user });
    }

    onSitDown(params) {

    }

    /**
     *
     * @param params
     * @returns {boolean}
     * true: -> queue
     * false-> not queue
     */
    checkQueueMesseage(params) {
        var gameProxy = this.facade.retrieveProxy(CoreGameProxy.NAME);
        switch (params.cmd) {
            case SFSEvent.UPDATE_GAME_ROOM_DATA:
            case SFSEvent.GET_PLAYING_INFO:
            case SFSEvent.GET_PLAYING_INFO_XD:
                return false;
                break;
        }

        if (gameProxy && gameProxy.isInitGameDone) {
            return false;
        } else {
            gameProxy.queueMsg.push(params);
            return true;
        }
    }

    onUpdateMoney(params) {
        var sfo = params;
        var plusName = sfo.get(SFSData.PLUS_NAME);
        var subName = sfo.get(SFSData.SUB_NAME);
        var data = {
            plusName: this.dataUser.getUserByUserName(plusName).uid,
            plusMoney: sfo.get(SFSData.PLUS_MONEY),
            subName: this.dataUser.getUserByUserName(subName).uid,
            subMoney: sfo.get(SFSData.SUB_MONEY)
        };

        this.sendNotification(GameMessage.RECEIVE_UPDATE_MONEY, data);
    }

    checkHasIdVtPlayer(uid, vtPlayer) {
        for (var i = 0; i < vtPlayer.length; i++) {
            if (vtPlayer[i].uid === uid) return true;
        }
        return false;
    }

    /////////////////////////////////////////////////////////////////
    ////////////////////SEND TO SERVER//////////////////////////////
    ////////////////////////////////////////////////////////////////

    sendExtensionRequest(command, sfo) {
        sfo.putUtfString(SFSData.USER_NAME, this.dataUser.mySelf.userName);

        var room = this.sfsProxy.sfs.lastJoinedRoom;
        var request = new SFS2X.ExtensionRequest(command, sfo, room);
        this.sfsProxy.send(request);
    }

    sendFakeCard(params) {

    }

    sendReadyGame(params) {
        var sfo = new SFS2X.SFSObject();
        this.sendExtensionRequest(SFSEvent.READY, sfo);
    }

    sendStartGame(params) {
        var sfo = new SFS2X.SFSObject();
        this.sendExtensionRequest(SFSEvent.START_GAMME, sfo);
    }

    sendLeaveGame() {
        var sfo = new SFS2X.SFSObject();
        // this.send(new SFS2X.LeaveRoomRequest(this.sfs.lastJoinedRoom));
        this.sendExtensionRequest(SFSEvent.REGISTER_QUIT, sfo);
    }

    sendCancelRegisterQuit() {
        var sfo = new SFS2X.SFSObject();
        this.sendExtensionRequest(SFSEvent.CANCEL_REGISTER_QUIT, sfo);
    }

    sendSitDown(params) {

    }

    sendBuyMasterGame() {

    }

    sendChat(params) {
        var sfo = new SFS2X.SFSObject();
        sfo.putUtfString(SFSData.COMMAND, SFSData.PUBLIC_CHAT);
        sfo.putUtfString(SFSData.MESSAGE, params.message.content);
        sfo.putInt(SFSData.TYPE, params.message.type);
        sfo.putUtfString(SFSData.DISPLAY_NAME, params.displayName);
        sfo.putUtfString(SFSData.USER_NAME, this.dataUser.mySelf.uid);

        var room = this.sfsProxy.sfs.lastJoinedRoom;
        var request = new SFS2X.PublicMessageRequest(SFSEvent.PUBLIC_CHAT, sfo, room);
        this.sfsProxy.send(request);
    }
}
