var BaseCommand = require('BaseCommand');
var LobbyMessage = require('LobbyMessage');

export default class ReceiveRegisterCommand extends BaseCommand {
    execute(notification) {
        BaseCommand.prototype.execute.call(this, notification);
        if (this.isError(notification.getBody().data)) return;

        var data = notification.getBody().data;
        var sendData = notification.body.sendData;

        // this.sendNotification(LobbyMessage.SHOW_LOGIN_SCENE);
        this.sendNotification(LobbyMessage.SHOW_SELECT_GAME_SCENE);
        this.sendNotification(LobbyMessage.SHOW_EVENT_BANNER_SCENE);
        this.sendNotification(LobbyMessage.UPDATE_LOGIN_SCENE, { userName: sendData.userName, password: sendData.password });
        this.sendNotification(LobbyMessage.HIDE_REGISTER_SCENE);
        // this.sendNotification(LobbyMessage.SHOW_ALERT, { content: data.message });
        this.sendNotification(LobbyMessage.SEND_LOGIN, { userName: sendData.userName, password: sendData.password });
    }
}