var BaseCommand = require('BaseCommand');
var LobbyMessage = require('LobbyMessage');

export default class ReceiveActivePhoneCommand extends BaseCommand {
    execute(notification) {
        BaseCommand.prototype.execute.call(this, notification);
        this.sendNotification(LobbyMessage.HIDE_LOADING);
        if (this.isError(notification.getBody().data))
            return;
        else
            this.sendNotification(LobbyMessage.SHOW_ALERT, { content: notification.getBody().data.message });

        var mySelf = this.facade.retrieveProxy('UserProxy').mySelf;

        var params = notification.getBody().data;
        if (params.money) {
            mySelf += params.money;
            this.sendNotification(LobbyMessage.ON_UPDATE_USER_INFO);
        }

        this.sendNotification(LobbyMessage.ON_UPDATE_ACTIVE_PHONE);
    }
}