var DropListMainTemplate = require('DropListMainTemplate');
var Utility = require('Utility');

cc.Class({
    extends: cc.Component,

    properties: {
        smsRateListContent: cc.Node,
        listPartner:DropListMainTemplate,
        listPrice:DropListMainTemplate,
        txtSMS: cc.Label,
        // txtNote:cc.RichText
    },

    // use this for initialization
    onLoad: function () {
        this.command = null;
        this.itemSMS = this.smsRateListContent.children[0];
        this.smsRateListContent.removeAllChildren();
    },

    updateTextNote:function(note){
        // this.txtNote.string = Utility.convertToRtf(note);
        // this.txtNote.node.active = (note === "")?false:true;
    },

    updateMySelf: function (mySelf) {
        this.mySelf = mySelf;
    },

    updateSms9029RateList:function (sms9029RateList) {
        var data = sms9029RateList;
        this.smsRateListContent.removeAllChildren();
        for (var i = 0; i < data.length; i++) {
            if(this.itemSMS === undefined) return;
            var item = cc.instantiate(this.itemSMS);
            var txtPrice = item.getChildByName("txtPrice").getComponent(cc.Label);
            var txtSale = item.getChildByName("txtSale").getComponent(cc.Label);
            var txtMoney = item.getChildByName("txtMoney").getComponent(cc.Label);

            txtPrice.string = data[i].name;
            txtMoney.string = data[i].value;
            txtSale.string = (data[i].promotion)?data[i].promotion:"0%";
            this.smsRateListContent.addChild(item);
        }
    },

    updateSms9029ValueList:function (sms9029ValueList) {
        this.listPrice.setupData(sms9029ValueList);
        this.listPrice.setSelectIndex(sms9029ValueList.length-1);
    },

    updateSmsTelcoList:function (smsTelcoList) {
        this.listPartner.setupData(smsTelcoList);
        this.listPartner.setSelectIndex(1);
    },

    updateSmsCommand:function (command) {
        this.command = command;
        this.updateTextSMS();
    },

    updateTextSMS: function () {
        if(this.command === null) return;
        if(this.listPrice.getItemSelect() === undefined) return;

        TweenLite.delayedCall(0.2, function () {
            var telco =  this.listPartner.getItemSelect().value;
            var value = this.listPrice.getItemSelect().value;

            this.txtSMS.string = this.command[telco][value] + this.mySelf.displayName;
        }.bind(this));
    }
});
