import { SFSData } from "../../../../network/sfs/constans/SFSData";
import { SFSEvent } from "../../../../network/sfs/constans/SFSEvent";

var MinigameProxy = require("MinigameProxy");
var Constants = require("Constants");
var MiniGameMessage = require("MiniGameMessage");
var SlotBTVO = require("SlotBTVO");
var Utility = require('Utility');

export default class SlotBTProxy extends MinigameProxy {
    static get NAME() {
      return 'SlotBTProxy';
    }

    static get Cmd(){
        return {
          ENTER_ROOM: 10,
          LEAVE_ROOM: 11,
          SPIN:101,
          UPDATE_JACKPOT:100,
        }
    }

    onRegister() {
      MinigameProxy.prototype.onRegister.call(this);
      this.gameVO = new SlotBTVO();
      this.gameVO.onRegister();
      this.moduleId = Constants.MINIGAME_SLOT_BT;
    }

    // addEventListener(socket) {
    //   SCProxy.prototype.addEventListener.call(this, socket);
    //   if(cc.sys.isBrowser){
    //     this.socket.addEventListener('message', this.onMessage.bind(this));
    //   }else{
    //     socket.onmessage = function(event) {
    //       this.onMessage(event);
    //     }.bind(this);
    //   }
    // }

    onMessage(event) {
      MinigameProxy.prototype.onMessage.call(this, event);
      var data = null;
      if (typeof event.params === "object") {
        data = Utility.convertSFSObjectToObject(event.params);
      }
      switch (event.cmd) {
        case SFSEvent.USER_JOIN_MINIGAME:
          this.sendNotification(MiniGameMessage.RECEIVE_JOIN_MINIGAME, { curMiniGame: Constants.MINIGAME_SLOT_BT, data: data });
          break;
        case SFSEvent.GET_USER_DATA:
          this.receiveGetUserData(data);
          break;
        case SFSEvent.SPIN:
          this.receiveSpin(data);
          break;
        case SFSEvent.GET_RANK:
          this.receiveRank(data);
          break;
        case SFSEvent.GET_USER_HISTORY:
          this.receiveUserHistory(data);
           break;
        case SFSEvent.UPDATE_JACKPOT:
          this.receiveUpdateJackPot(data);
          break;
        default:
      }
    }

 

    //////////////////////////////////////////////////////////////////////////
    //////////////////////////////////////////////////////////////////////////
    //////////////////////////////////////////////////////////////////////////

    receiveJoinMinigame(params) {
      this.sendNotification(MiniGameMessage.RECEIVE_JOIN_MINIGAME, {curMiniGame: Constants.MINIGAME_SLOT_BT});
    }

    receiveGetUserData(params) {
        params = JSON.parse(params.json);
        this.gameVO.updatejackpot(params.data.boards);
        this.sendNotification(MiniGameMessage.RECEIVE_UPDATE_JACKPOT_SLOT_BT, {data: this.gameVO} );
    }

    receiveSpin(params) {
      params = JSON.parse(params.json);
      this.gameVO.updateData(params);
      this.sendNotification(MiniGameMessage.RECEIVE_SPIN_SLOT_BT, {data: this.gameVO} );
    }

    receiveRank(params) {
      params = JSON.parse(params.json);
      this.sendNotification(MiniGameMessage.RECEIVE_GET_RANK_SLOT_BT, params );
    }

    receiveUserHistory(params) {
      params = JSON.parse(params.json);
      this.sendNotification(MiniGameMessage.RECEIVE_GET_HISTORY_SLOT_BT, params );
    }

    receiveUpdateJackPot(params) {
      params = JSON.parse(params.json);
      this.gameVO.updatejackpot(params.data);
      this.sendNotification(MiniGameMessage.RECEIVE_UPDATE_JACKPOT_SLOT_BT, {data: this.gameVO} );
    }

    //////////////////////////////////////////////////////////////////////////
    //////////////////////////////////////////////////////////////////////////
    //////////////////////////////////////////////////////////////////////////

    sendSpin(params) {
      var sfo = new SFS2X.SFSObject();
      sfo.putInt(SFSData.GID, params.gid);
      sfo.putInt(SFSData.BET, params.bet);
      sfo.putUtfString(SFSData.LINE_DATA, "["+params.lines+"]");
      this.sendExtensionRequest(SFSEvent.SPIN, sfo); 
    }

    leaveRoom(params) {
      //roi ban
    }

    sendExtensionRequest(command, sfo) {
      if (this.sfs === null)
        return;
      sfo.putUtfString(SFSData.USER_NAME, this.userName);
      var room = null;
      var request = new SFS2X.ExtensionRequest(command, sfo, room);
      this.send(request);
    }
}
