var BasePopup = require('BasePopup');
var Utility = require('Utility');
var AssetVO = require('AssetVO');

cc.Class({
    extends: BasePopup,

    properties: {
      content: cc.Node,
      itemTemp:cc.Node,
    },

    onLoad () {
    },

    show: function () {
      BasePopup.prototype.show.call(this);
      this.content.removeAllChildren();
        TweenLite.delayedCall(0.2, function () {
            this.node.emit('ACTIVE_HISTORY_SHOW_PAGE');
        }.bind(this));
    },

    hide: function () {
        BasePopup.prototype.hide.call(this);
        this.content.removeAllChildren();
    },

    updateHistory: function (data) {
      this.content.removeAllChildren();
      data = Utility.convertSFSArrayToArray(data.list);

      for(var i=0 ; i<data.length ; i++){
        var item = cc.instantiate(this.itemTemp);
        item.data = data[i];
        var arrayCards = data[i].cards;
        item.getChildByName("bgLine").active = (i % 2 !== 0);
        item.getChildByName("lbName").getComponent(cc.Label).string = data[i].displayName;
        item.getChildByName("lbTime").getComponent(cc.Label).string = Utility.js_yyyy_mm_dd_hh_mm_ss(parseInt(data[i].time));
        item.getChildByName("lbBetValue").getComponent(cc.Label).string = Utility.formatCurrency(data[i].bet);
        item.getChildByName("lbWin").getComponent(cc.Label).string = Utility.formatCurrency(data[i].money);
        for(var j=0 ; j<5 ; j++){
            var cardName = (arrayCards[j] < 10) ? "cards_0" + arrayCards[j] : "cards_" + arrayCards[j];
            var card = item.getChildByName("card" + j);
            card.getComponent(cc.Sprite).spriteFrame = AssetVO.getTextureGameAtlas(cardName);
        }
        this.content.addChild(item);
      }
    }

});
