var BaseCommand = require('BaseCommand');
var SlotMCProxy = require('SlotMCProxy');
var SlotMessage = require('SlotMessage');

export default class ReceiveUpdateJackPotMCCommand extends BaseCommand {
  execute(notification) {
    BaseCommand.prototype.execute.call(this, notification);
    var params = notification.getBody();
    var gameVO = SlotMCProxy.proxy.gameVO;
    this.sendNotification(SlotMessage.ON_UPDATE_JACKPOT_MASTER_CHEF, {roomJackPot: gameVO.getCurrentJackpot(), listRoomJackPot: gameVO.getListRoomJackpot()});
  }
}
