var BaseCommand = require('BaseCommand');
var LobbyMessage = require('LobbyMessage');
var i18n = require('i18n');

export default class SendTransferdesCommand extends BaseCommand {
    execute(notification) {
        BaseCommand.prototype.execute.call(this, notification);
        if (this.isError(notification.getBody())) return;

        var data = notification.getBody();
        var userProxy = this.facade.retrieveProxy('UserProxy');
        var http = this.facade.retrieveProxy('HttpRequestProxy');

        var sendData = {
            displayName: userProxy.mySelf.displayName,
            token: userProxy.mySelf.token,
            toDisplayName: data.toDisplayName,
            amount: data.amount,
            note: data.note,
            otp: data.otp
        };

        http.sendTransferdes(LobbyMessage.RECEIVE_TRANSFERDES, sendData);
    }

    isError(data) {
        if (data.toDisplayName === "" || data.amount === "" || data.otp === "") {
            this.sendNotification(LobbyMessage.SHOW_ALERT, { content: i18n.t("C0005") });
            return true;
        }

        return false;
    }
}