var BaseMediator = require('BaseMediator');
var LobbyMessage = require('LobbyMessage');

class SlotSceneLayerMediator extends BaseMediator {
    static get NAME() {
        return 'SlotSceneLayerMediator';
    }

    static get getInstance() {
        if (this.instance === undefined) {
            this.instance = new SlotSceneLayerMediator();
        }
        return this.instance;
    }

    onRegister() {
    }

    /** @override */
    listNotificationInterests() {
        return [
            LobbyMessage.ON_LOAD_SLOT
        ];
   }

    /** @override */
    handleNotification(notification) {
        BaseMediator.prototype.handleNotification.call(this);
        var data = notification.getBody();
        switch (notification.getName()) {
            case LobbyMessage.ON_LOAD_SLOT:
                this.view.onLoadSlotmachine(data.name, data.target, data.onComplete);
                break;
            default:
                break;
        }
    }
}

module.exports = SlotSceneLayerMediator;
