var BaseCommand = require('BaseCommand');
var MiniGameMessage = require('MiniGameMessage');

export default class ReceiveChangeMoneyTypeTaiXiuCommand extends BaseCommand {
  execute(notification) {
    BaseCommand.prototype.execute.call(this, notification);
    var params = notification.getBody();

    var totalTai = params.totalTai;
    var totalXiu = params.totalXiu;
    var numTai = params.numTai;
    var numXiu = params.numXiu;
    var betSide = params.betSide;
    var totalBet = params.totalBet;
    var moneyType = params.moneyType;

    this.sendNotification(MiniGameMessage.ON_UPDATE_CHANGE_MONEY_TYPE_TAIXIU, {
        totalTai: totalTai,
        totalXiu: totalXiu,
        betSide: betSide,
        totalBet: totalBet,
        numTai: numTai,
        numXiu: numXiu,
        moneyType: moneyType
    });
  }
}
