var BaseVO = require("BaseVO");

export default class MiniPokerVO extends BaseVO {
    static get ERROR() { return 0 }
    static get NORMAL_WIN() { return 1 }
    static get BIG_WIN() { return 2 }
    static get JACKPOT_WIN() { return 3 }

    onRegister() {
        this.bet = 0;
        this.money = 0;
        this.prize = 0;
        this.jackpot = 0;
        this.items = [];
        this.session = 0;
        this.isSpining = false;
        this.autoSpin = false;
        this.errorMessage = "";
        this.typeResult = -1;
        this.currentBetIndex = 1;
        this.listBets = [0, 100, 1000, 10000];
        this.winType = 0;
        this.listJackpot = [];
        this.spinType = 1;
    }

    reset() {
        this.bet = 0;
        this.money = 0;
        this.prize = 0;
        this.freeSpin = "";
        this.jackpot = 0;
        this.wins = [];
        this.items = [];
        this.errorMessage = "";
        this.typeResult = -1;
    }

    updateData(data) {
        this.reset();
        if (!data.success) {
            this.typeResult = MiniPokerVO.ERROR;
            this.errorMessage = "spin error";
        }else{
            this.session++;
            this.money = data.result.money;
            this.items = data.result.cards;
            this.prize = parseInt(data.result.prize) + this.getCurrentBet();
            this.jackpot = (data.result.type !== 0) ? 0 : parseInt(data.result.prize);
            this.winType = data.result.type;
            this.typeResult = MiniPokerVO.NORMAL_WIN;
        }


        // if (data.m !== "") {
        //     this.typeResult = MiniPokerVO.ERROR;
        //     this.errorMessage = data.m;
        // } else {
        //     this.money = data.d.b;
        //     this.prize = data.d.v;
        //     this.jackpot = data.d.jp;
        //     this.winType = data.d.t;
        //     this.items = [];
        //     var rs = data.d.c.split(',');
        //     for (var i = 0; i < rs.length; i++) {
        //         this.items.push(this.mapCardId.indexOf(rs[i]));
        //     }

        //     this.typeResult = MiniPokerVO.NORMAL_WIN;
        // }

    }

    updateDataX3(data) {
        this.reset();
        if (!data.success) {
            this.typeResult = MiniPokerVO.ERROR;
            this.errorMessage = "spin error";
        }else{
            // this.money = data.result.money;
            this.items = [data.result1.cards, data.result2.cards, data.result3.cards];
            this.prize = [parseInt(data.result1.prize), parseInt(data.result2.prize), parseInt(data.result3.prize)];
            this.jackpot = (data.result1.type !== 0) ? 0 : parseInt(data.result1.prize);
            this.winType = [data.result1.type, data.result2.type, data.result3.type];
            this.typeResult = MiniPokerVO.NORMAL_WIN;
        }
    }

    updateCurrentBetIndex(currentBetIndex) {
        this.currentBetIndex = currentBetIndex;
    }

    updatejackpot(pots) {
        this.listJackpot = pots;
    }

    getCurrentBet() {
        return this.listBets[this.currentBetIndex];
    }

    getCurrentJackpot() {
        return this.listJackpot[this.currentBetIndex - 1];
    }

    getCurrentWinType() {
        return 0;
    }
}
