var BaseMediator = require('BaseMediator');
var Constants = require('Constants');
var SlotMessage = require('SlotMessage');
var CustomAction = require('CustomAction');
var SlotMCProxy = require('SlotMCProxy');
var LobbyMessage = require('LobbyMessage');
var SoundGameMessage = require('SoundGameMessage');
var UserProxy = require('UserProxy');

class SlotMCSceneMediator extends BaseMediator {
  static get NAME() {
    return 'SlotMCSceneMediator';
  }

  static get getInstance() {
    if(this.instance === undefined){
        this.instance = new SlotMCSceneMediator();
    }
    return this.instance;
  }

  onRegister() {
      puremvc.Mediator.call(this, this.constructor.NAME);
  }

  listNotificationInterests() {
      return [
        SlotMessage.SHOW_MINIGAME_MASTER_CHEF,
        SlotMessage.HIDE_MINIGAME_MASTER_CHEF,
        SlotMessage.ON_INIT_GAME_MASTER_CHEF,
        SlotMessage.ON_SPIN_AND_RESULT_MASTER_CHEF,
        SlotMessage.ON_SHOW_EFFECT_WIN_MASTER_CHEF,
        SlotMessage.ON_HIDE_EFFECT_WIN_MASTER_CHEF,
        SlotMessage.ON_SHOW_LINE_WIN_MASTER_CHEF,
        SlotMessage.ON_SHOW_MESSAGE_MASTER_CHEF,
        SlotMessage.ON_RESET_NEW_GAME_MASTER_CHEF,
        SlotMessage.ON_HIDE_ALL_LINE_MASTER_CHEF,
        SlotMessage.ON_SET_AUTO_SPIN_MASTER_CHEF,
        SlotMessage.ON_UPDATE_JACKPOT_MASTER_CHEF,
        SlotMessage.ON_SHOW_ITEM_WIN_MASTER_CHEF,
        SlotMessage.ON_SHOW_EFFECT_ITEM_WIN_MASTER_CHEF,
        SlotMessage.ON_HIDE_ALL_EFFECT_ITEM_WIN_MASTER_CHEF,
        SlotMessage.ON_SET_NUM_AUTO_SPIN_MASTER_CHEF,
        SlotMessage.ON_SHOW_MINIGAME,
        SlotMessage.ON_SHOW_EFFECT_FREE_SPIN_MASTER_CHEF,
        SlotMessage.ON_SHOW_FREE_SPIN_MASTER_CHEF,
        SlotMessage.ON_PLAY_ANIM_BONUS_MASTER_CHEF,
        SlotMessage.ON_PLAY_ANIM_SCATTER_MASTER_CHEF,
        SlotMessage.ON_PLAY_ANIM_JACKPOT_MASTER_CHEF,
        SlotMessage.ON_UPDATE_RANK_MASTER_CHEF,
        SlotMessage.ON_UPDATE_MY_HISTORY_MASTER_CHEF,
        SlotMessage.ON_UPDATE_MULTIPLIER_MASTER_CHEF,
        SlotMessage.ON_UPDATE_TICH_LUY_MASTER_CHEF,
        SlotMessage.ON_SHOW_WILD_MASTER_CHEF,
        SlotMessage.ON_HIDE_WILD_MASTER_CHEF,
        SlotMessage.SHOW_INFO_TOURNAMENT_MASTER_CHEF,
        SlotMessage.HIDE_INFO_TOURNAMENT_MASTER_CHEF,
        SlotMessage.ON_RESET_BTN_SPIN_MASTER_CHEF,
        SlotMessage.ON_CHECK_GAME_X2_MASTER_CHEF,
        SlotMessage.ON_RESULT_GAME_X2_MASTER_CHEF,
        SlotMessage.ON_SHOW_EFFECT_TPOINT_MASTER_CHEF,
        SlotMessage.ON_UPDATE_MONEY_PLAY_TRY_MASTER_CHEF,
        LobbyMessage.ON_UPDATE_MY_INFO,
        LobbyMessage.ON_UPDATE_TOP_TOURNAMENT

      ]
  }

  /** @override */
  handleNotification(notification) {
      BaseMediator.prototype.handleNotification.call(this);
      this.dataUser = this.facade.retrieveProxy(UserProxy.NAME);
      var data = notification.getBody();
      switch (notification.getName()) {
          case LobbyMessage.ON_UPDATE_MY_INFO:
            this.view.onUpdateUserInfo(this.dataUser.mySelf);
            break;
          case SlotMessage.ON_INIT_GAME_MASTER_CHEF:
            this.view.buildUI(SlotMCProxy.proxy.gameVO);
            this.activePlayMusicGame();
            break;
          case SlotMessage.SHOW_MINIGAME_MASTER_CHEF:
            this.view.show();
            break;
          case SlotMessage.HIDE_MINIGAME_MASTER_CHEF:
            this.view.hide();
            break;
          case SlotMessage.ON_SPIN_AND_RESULT_MASTER_CHEF:
            this.view.onSpinAndResult(data);
            break;
          case SlotMessage.ON_SHOW_EFFECT_WIN_MASTER_CHEF:
            this.view.onShowEffectWin(data.gameVO);
            break;
          case SlotMessage.ON_HIDE_EFFECT_WIN_MASTER_CHEF:
            this.view.onHideEffectWin();
            break;
          case SlotMessage.ON_SHOW_LINE_WIN_MASTER_CHEF:
            this.view.onShowLineWin(data.wins);
            break;
          case SlotMessage.ON_SHOW_EFFECT_ITEM_WIN_MASTER_CHEF:
            this.view.onShowEffectItemWin(data.wins);
            break;
          case SlotMessage.ON_HIDE_ALL_EFFECT_ITEM_WIN_MASTER_CHEF:
            this.view.onHideAllEffectItemWin();
            break;
          case SlotMessage.ON_SHOW_MESSAGE_MASTER_CHEF:
            this.view.onShowMessage(data.message);
            // this.sendNotification(Lob byMessage.SHOW_FLY_ALERT,{content:data.message});
            break;
          case SlotMessage.ON_RESET_GAME_MASTER_CHEF:
            this.view.onResetGame();
            break;
          case SlotMessage.ON_SET_AUTO_SPIN_MASTER_CHEF:
            this.view.onSetAutoSpin(data.autoSpin);
            break;
          case SlotMessage.ON_HIDE_ALL_LINE_MASTER_CHEF:
            this.view.onHideAllLine();
            break;
          case SlotMessage.ON_UPDATE_JACKPOT_MASTER_CHEF:
            this.view.onUpdateJackpot(data);
            break;
          case SlotMessage.ON_SET_NUM_AUTO_SPIN_MASTER_CHEF:
            this.view.onSetNumAutoSpin(data);
            break;
          case SlotMessage.ON_SHOW_MINIGAME:
            this.view.onShowMinigame(data);
            break;
          case SlotMessage.ON_PLAY_ANIM_BONUS_MASTER_CHEF:
            this.view.onPlayAnimBonus();
            break;
          case SlotMessage.ON_PLAY_ANIM_SCATTER_MASTER_CHEF:
            this.view.onPlayAnimScatter();
            break;
          case SlotMessage.ON_PLAY_ANIM_JACKPOT_MASTER_CHEF:
            this.view.onPlayAnimJackpot();
            break;
          case SlotMessage.ON_SHOW_EFFECT_FREE_SPIN_MASTER_CHEF:
            this.view.onShowEffectFreeSpin(data);
            break;
          case SlotMessage.ON_SHOW_FREE_SPIN_MASTER_CHEF:
              this.view.onShowFreeSpin();
              break;
          case SlotMessage.ON_UPDATE_RANK_MASTER_CHEF:
            this.view.onUpdateRank(data);
            break;
          case SlotMessage.ON_UPDATE_MY_HISTORY_MASTER_CHEF:
            this.view.onUpdateHistory(data);
            break;
          case SlotMessage.ON_UPDATE_MULTIPLIER_MASTER_CHEF:
              this.view.onUpdateMultiplier(data);
              break;
          case SlotMessage.ON_UPDATE_TICH_LUY_MASTER_CHEF:
              this.view.onUpdateTichLuy(data);
              break;
          case SlotMessage.ON_SHOW_WILD_MASTER_CHEF:
              this.view.onShowWild(data);
              break;
          case SlotMessage.ON_HIDE_WILD_MASTER_CHEF:
              this.view.onHideWild();
              break;
          case SlotMessage.SHOW_INFO_TOURNAMENT_MASTER_CHEF:
              this.view.onShowInfoTournament();
              break;
          case SlotMessage.HIDE_INFO_TOURNAMENT_MASTER_CHEF:
              this.view.onHideInfoTournament();
              break;
          case SlotMessage.ON_RESET_BTN_SPIN_MASTER_CHEF:
              this.view.onResetBtnSpin();
              break;
          case SlotMessage.ON_CHECK_GAME_X2_MASTER_CHEF:
              this.view.onCheckGameX2();
              break;
          case SlotMessage.ON_RESULT_GAME_X2_MASTER_CHEF:
              this.view.onResultGameX2(data);
              break;
          case SlotMessage.ON_SHOW_EFFECT_TPOINT_MASTER_CHEF:
              this.view.onShowEffectTPoint(data);
              break;
          case SlotMessage.ON_UPDATE_MONEY_PLAY_TRY_MASTER_CHEF:
              this.view.onUpdatePlayTryMoney();
              break;
          case LobbyMessage.ON_UPDATE_TOP_TOURNAMENT:
              this.view.onUpdateTopTournament(data);
              break;
      }
  }

  addHanlers() {
    BaseMediator.prototype.addHanlers.call(this);
    this.view.activeSendSpin = this.activeSendSpin.bind(this);
    this.view.activeRank = this.activeRank.bind(this);
    this.view.activeMyHistory = this.activeMyHistory.bind(this);
    this.view.activeGuide = this.activeGuide.bind(this);
    this.view.activeLeaveGame = this.activeLeaveGame.bind(this);
    this.view.activeShowCashInForm = this.activeShowCashInForm.bind(this);
    this.view.activeShowTournament = this.activeShowTournament.bind(this);
    this.view.activePlayMusicGame = this.activePlayMusicGame.bind(this);
    this.view.activeStopMusicGame = this.activeStopMusicGame.bind(this);
    this.view.activePlaySoundEffect = this.activePlaySoundEffect.bind(this);
    this.view.activeOrderSpin = this.activeOrderSpin.bind(this);
    this.view.activePlayGameX2 = this.activePlayGameX2.bind(this);
  }

    activeStopMusicGame(params) {
        this.sendNotification(LobbyMessage.STOP_BACKGROUND_SOUND);
    }

    activePlayMusicGame(params) {
        this.sendNotification(LobbyMessage.PLAY_BACKGROUND_SOUND, {soundName: SoundGameMessage.BG_MASTER_CHEF_MUSIC, volume: 0.3});
    }

    activePlaySoundEffect(soundName) {
        this.sendNotification(LobbyMessage.PLAY_SOUND_EFFECT, soundName);
    }

  activeSendSpin(params){
      this.sendNotification(LobbyMessage.PLAY_SOUND_EFFECT, SoundGameMessage.BTN_CLICK_MASTER_CHEF);
      if (params.lines === '[]')  {
        this.sendNotification(LobbyMessage.SHOW_FLY_ALERT , {content: 'Bạn chưa chọn dòng đặt'});
        return;
    }
    this.sendNotification(SlotMessage.SEND_SPIN_MASTER_CHEF , {data: params});
  }

  activeShowCashInForm(params){
    this.sendNotification(LobbyMessage.SHOW_TAB_IN_SHOP_SCENE, {tabIndex:0});
  }

  activeRank(params){
    this.sendNotification(SlotMessage.SEND_GET_RANK_MASTER_CHEF, params);
  }

  activeMyHistory(params){
    this.sendNotification(SlotMessage.SEND_GET_MY_HISTORY_MASTER_CHEF, params);
  }

  activeGuide(params){
    this.sendNotification(SlotMessage.SEND_SHOW_GUIDE_MINIGAME, Constants.MINIGAME_SLOT20);
  }

  activeShowTournament(gameModule){
    this.sendNotification(LobbyMessage.SHOW_LIST_TOURNAMENT_SCENE, gameModule);
  }

  activeOrderSpin () {
    this.sendNotification(SlotMessage.SEND_ORDER_SPIN_MASTER_CHEF);
  }

  activePlayGameX2(params) {
    this.sendNotification(SlotMessage.SEND_PLAY_GAME_X2_MASTER_CHEF, params);
  }

  activeLeaveGame(params){
    this.sendNotification(LobbyMessage.SHOW_LOBBY);
    this.sendNotification(SlotMessage.SEND_LEAVE_ROOM_MASTER_CHEF);
    this.sendNotification(LobbyMessage.STOP_BACKGROUND_SOUND);
  }
}

module.exports = SlotMCSceneMediator;
