var BasePopup = require('BasePopup');
var Utility = require('Utility');
var AssetVO = require('AssetVO');

cc.Class({
    extends: BasePopup,

    properties: {
      content: cc.Node,
      itemTemp:cc.Node,
    },

    onLoad () {
    },

    show: function () {
      BasePopup.prototype.show.call(this);
      this.content.removeAllChildren();
        TweenLite.delayedCall(0.2, function () {
            this.node.emit('ACTIVE_RANKING_SHOW_PAGE');
        }.bind(this));
    },

    hide: function () {
        BasePopup.prototype.hide.call(this);
        this.content.removeAllChildren();
    },

    updateRank: function (data) {
      this.content.removeAllChildren();
      data = Utility.convertSFSArrayToArray(data.list);
      for(var i=0 ; i<data.length ; i++){
        var item = cc.instantiate(this.itemTemp);
        item.data = data[i];
        item.getChildByName("bgLine").active = (i % 2 !== 0);
        item.getChildByName("lbDate").getComponent(cc.Label).string = Utility.js_yyyy_mm_dd_hh_mm_ss(parseInt(data[i].time));
        item.getChildByName("lbNickName").getComponent(cc.Label).string = data[i].displayName;
        item.getChildByName("lbBetValue").getComponent(cc.Label).string = Utility.formatCurrency(data[i].bet);
        item.getChildByName("lbWin").getComponent(cc.Label).string = Utility.formatCurrency(data[i].money);
        if(data[i].type === 3) {
          item.getChildByName("lbResult").getComponent(cc.Label).string = "Nổ hũ";
        }
        else if(data[i].type === 2){
          item.getChildByName("lbResult").getComponent(cc.Label).string = "Thắng lớn";
        }else{
          item.getChildByName("lbResult").getComponent(cc.Label).string = "Thắng";
        }
        this.content.addChild(item);
      }
    }
});
