var BaseMediator = require('BaseMediator');
var Constants = require('Constants');
var MiniGameMessage = require('MiniGameMessage');
var CustomAction = require('CustomAction');

class BauCuaSceneMediator extends BaseMediator {
    static get NAME() {
        return 'BauCuaSceneMediator';
    }

    static get getInstance() {
        if(this.instance === undefined){
            this.instance = new BauCuaSceneMediator();
        }
        return this.instance;
    }

    onRegister() {
        puremvc.Mediator.call(this, this.constructor.NAME);
        this.mySelf =  this.facade.retrieveProxy('UserProxy').mySelf;
    }
    /** @override */
    listNotificationInterests() {
        return [
            MiniGameMessage.ON_INIT_GAME_BAU_CUA,
            MiniGameMessage.SHOW_MINIGAME_BAU_CUA,
            MiniGameMessage.HIDE_MINIGAME_BAU_CUA,
            MiniGameMessage.ON_UPDATE_PLAYING_INFO_BAU_CUA,
            MiniGameMessage.ON_UPDATE_BOARD_BET_BAU_CUA,
            MiniGameMessage.ON_UPDATE_COUNTDOWN_WAITING_BAU_CUA,
            MiniGameMessage.ON_UPDATE_GAME_STATE_BAU_CUA,
            MiniGameMessage.ON_GAME_OVER_BAU_CUA,
            MiniGameMessage.ON_UPDATE_MONEY_BAU_CUA,
            MiniGameMessage.ON_BET_BAU_CUA,
            MiniGameMessage.ON_UPDATE_SOI_CAU_BAU_CUA,
            MiniGameMessage.ON_UPDATE_TOP_BAU_CUA,
            MiniGameMessage.ON_UPDATE_HISTORY_BAU_CUA,
            MiniGameMessage.SHOW_MESSAGE_BAU_CUA,
            MiniGameMessage.DISCONNECT_ALL_MINIGAME
        ]
    }

    /** @override */
    handleNotification(notification) {
        BaseMediator.prototype.handleNotification.call(this);
        var data = notification.getBody();
        switch (notification.getName()) {
            case MiniGameMessage.ON_INIT_GAME_BAU_CUA:
                // this.view.gameVO = this.facade.retrieveProxy('BauCuaProxy').gameVO;
                this.view.buildUI();
                break;
            case MiniGameMessage.SHOW_MINIGAME_BAU_CUA:
                this.view.show();
                break;
            case MiniGameMessage.HIDE_MINIGAME_BAU_CUA:
                this.view.hide();
                break;
            case MiniGameMessage.DISCONNECT_ALL_MINIGAME:
                this.view.hide();
                this.activeDisconnect();
                break;
            case MiniGameMessage.ON_UPDATE_PLAYING_INFO_BAU_CUA:
                this.view.onUpdatePlayingInfo(data);
                break;
            case MiniGameMessage.ON_UPDATE_BOARD_BET_BAU_CUA:
                this.view.onUpdateBoardBet(data.session, data.betting, data.time);
                break;
            case MiniGameMessage.ON_UPDATE_COUNTDOWN_WAITING_BAU_CUA:
                this.view.onUpdateCountdownWaiting(data);
                break;
            case MiniGameMessage.ON_UPDATE_GAME_STATE_BAU_CUA:
                this.view.onUpdateGameState(data.gameState, data.session);
                break;
            case MiniGameMessage.ON_GAME_OVER_BAU_CUA:
                this.view.onShowResultDice(data);
                break;
            case MiniGameMessage.ON_UPDATE_MONEY_BAU_CUA:
                this.view.onUpdateMoney(data);
                break;
            case MiniGameMessage.ON_BET_BAU_CUA:
                this.view.onBetBauCua(data);
                break;
            case MiniGameMessage.ON_UPDATE_SOI_CAU_BAU_CUA:
                this.view.onUpdateSoiCau(data);
                break;
            case MiniGameMessage.ON_UPDATE_TOP_BAU_CUA:
                this.view.onUpdateRank(data);
                break;
            case MiniGameMessage.ON_UPDATE_HISTORY_BAU_CUA:
                this.view.onUpdateHistory(data);
                break;
            case MiniGameMessage.SHOW_MESSAGE_BAU_CUA:
                this.view.onShowMessage(data.msg);
                break;
            default:
                break;
        }
    }

    addHanlers() {
        BaseMediator.prototype.addHanlers.call(this);
        this.view.activeSendBetBauCua = this.activeSendBetBauCua.bind(this);
        this.view.activeSendSoiCauBauCua = this.activeSendSoiCauBauCua.bind(this);
        this.view.activeRank = this.activeRank.bind(this);
        this.view.activeHistory = this.activeHistory.bind(this);
        this.view.activeDisconnect = this.activeDisconnect.bind(this);
    }

    activeSendBetBauCua(params) {
        this.sendNotification(MiniGameMessage.SEND_BET_BAU_CUA , params);
    }

    activeSendSoiCauBauCua() {
        this.sendNotification(MiniGameMessage.SEND_SOI_CAU_BAU_CUA);
    }

    activeRank(params) {
        this.sendNotification(MiniGameMessage.SEND_GET_RANK_BAU_CUA, params);
    }

    activeHistory(params) {
        this.sendNotification(MiniGameMessage.SEND_GET_HISTORY_BAU_CUA, params);
    }

    activeDisconnect() {
        this.sendNotification(MiniGameMessage.SEND_DISCONNECT_MINIGAME, Constants.MINIGAME_BAU_CUA);
    }
}

module.exports = BauCuaSceneMediator;
