var BaseCommand = require('BaseCommand');
var LobbyMessage = require('LobbyMessage');
var Constants = require('Constants');

export default class SendGetHistoryCashInCommand extends BaseCommand {
    execute(notification) {
        BaseCommand.prototype.execute.call(this, notification);

        var mySelf = this.facade.retrieveProxy('UserProxy').mySelf;
        var http = this.facade.retrieveProxy('HttpRequestProxy');

        var sendData = {
            displayName: mySelf.displayName,
            token: mySelf.token,
        };

        http.getLogTransfer(LobbyMessage.RECEIVE_GET_HISTORY_TRANFER, sendData);
    }
}