var BaseCommand = require('BaseCommand');
var LobbyMessage = require('LobbyMessage');
var Utility = require('Utility');
var MiniGameMessage = require('MiniGameMessage');

export default class ReceiveUpdateBoardBetTaiXiuCommand extends BaseCommand {
    execute(notification) {
        BaseCommand.prototype.execute.call(this, notification);
        var params = notification.getBody().data;
        // switch (params.gameState){
        //     case "bet":
        //         this.updateBoardBet(params);
        //         break;
        //     case "repay":
        //         this.updateCountdown(params);
        //         break;
        //     case "gameOver":
        //         this.updateCountdownWaiting(params);
        //         break;
        //     case "waiting":
        //         this.updateCountdownWaiting(params);
        //         break;
        //     default:
        //         break;
        // }
        this.updateBoardBet(params);
    }

    updateBoardBet(params) {
        var betting = params.betting;
        var session = params.sessionId;
        var time = params.timeLeft;

        this.sendNotification(MiniGameMessage.ON_UPDATE_BOARD_BET_BAU_CUA, {
            session: session,
            betting: betting,
            time: time,

        });
    }

    updateCountdown(params) {
        var time = params.time;
        this.sendNotification(MiniGameMessage.ON_UPDATE_COUNTDOWN_REPAY_TAIXIU, {
            time: time
        });
    }
    updateCountdownWaiting(params) {
        var time = params.time;
        this.sendNotification(MiniGameMessage.ON_UPDATE_COUNTDOWN_WAITING_TAIXIU, {
            time: time
        });
    }
}
