// Learn life-cycle callbacks:
//  - [Chinese] https://docs.cocos.com/creator/manual/zh/scripting/life-cycle-callbacks.html
//  - [English] https://www.cocos2d-x.org/docs/creator/manual/en/scripting/life-cycle-callbacks.html

cc.Class({
    extends: cc.Component,

    properties: {
       resizeWidth:false,
       resizeHeight: false,
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        this.fameSize = new cc.Vec2(1000000, 1000000);
    },

    update (dt) {
        if (this.fameSize.width === cc.view.getFrameSize().width && this.fameSize.height === cc.view.getFrameSize().height) {
            return;
        }
        this.fameSize = cc.view.getFrameSize();
        // if (cc.sys.isMobile && this.resizeWidth){
            this.node.width = 1280*1.2;
        // }
        // if (cc.sys.isMobile && this.resizeHeight) {
            this.node.height = 720*1.2;
        // }

        console.log(cc.view.getFrameSize().width + '    ' + cc.view.getFrameSize().height + '  ' + (1/cc.view.getScaleX()* this.fameSize.width) + '  ' + cc.view.getScaleY());
    },
});
