var BaseCommand = require('BaseCommand');
var LobbyMessage = require('LobbyMessage');

export default class SendGetRechargeInfoCommand extends BaseCommand {
    execute(notification) {
        BaseCommand.prototype.execute.call(this, notification);
        var rechargeProxy = this.facade.retrieveProxy('RechargeProxy');
        if (rechargeProxy.recharge) {
            console.log("Recharge Already Exists");
            return;
        }

        var http = this.facade.retrieveProxy('HttpRequestProxy');
        var mySelf = this.facade.retrieveProxy('UserProxy').mySelf;

        var sendData = {
            displayName: mySelf.displayName,
            token: mySelf.token
        };

        http.getRechargeInfo(LobbyMessage.RECEIVE_GET_RECHARGE_INFO, sendData);
    }
}