import { SFSData } from "../../../../network/sfs/constans/SFSData";
import { SFSEvent } from "../../../../network/sfs/constans/SFSEvent";

var Slot20Proxy = require("Slot20Proxy");
var SlotAlaVO = require('SlotAlaVO');
var Constants = require("Constants");
var SlotAlaVO = require('SlotAlaVO');
var Utility = require('Utility');
var LobbyMessage = require('LobbyMessage');
var SlotMessage = require('SlotMessage');

export default class SlotAlaProxy extends Slot20Proxy {
  static get NAME() {
    return 'SlotAlaProxy';
  }

  onRegister() {
    this.gameVO = new SlotAlaVO();
    this.gameVO.onRegister();
    Slot20Proxy.prototype.onRegister.call(this);
  }


  /////////////////////////////////////////////////////////////////
  ////////////////////SEVER REPONSE//////////////////////////////
  ////////////////////////////////////////////////////////////////
  onMessage(event) {
    Slot20Proxy.prototype.onMessage.call(this, event);
    var data = null;
    if (typeof event.params === "object") {
      data = Utility.convertSFSObjectToObject(event.params);
    }

    switch (event.cmd) {
      case SFSEvent.USER_JOIN_SLOTMACHINE:
        this.sendNotification(LobbyMessage.RECEIVE_JOIN_SLOT20, { curSlot: Constants.SLOT25_ALADIN, data: data });
        break;
      case SFSEvent.GET_USER_DATA:
        this.receiveGetUserData(data);
        break;
      case SFSEvent.SPIN:
        this.receiveSpin(data);
        break;
      case SFSEvent.GET_RANK:
        this.receiveRank(data);
        break;
      case SFSEvent.GET_POT:
        this.receiveRank(data);
        break;
      case SFSEvent.GET_USER_HISTORY:
        this.receiveUserHistory(data);
        break;
      case SFSEvent.UPDATE_JACKPOT:
        this.receiveUpdateJackPot(data);
        break;
      default:
    }
  }

  receiveGetUserData(params) {
    params = JSON.parse(params.json);
    this.gameVO.updatejackpot(params.data.boards);
    this.gameVO.updateFreeSpin(params.data.udata);
    TweenLite.delayedCall(1, function () {
      this.sendNotification(SlotMessage.RECEIVE_UPDATE_JACKPOT_ALADIN, {data: this.gameVO} );
    }.bind(this));
  }

  receiveSpin(params) {
    params = JSON.parse(params.json);
    this.gameVO.updateData(params);
    this.sendNotification(SlotMessage.RECEIVE_SPIN_ALADIN, { data: this.gameVO });
  }

  receiveRank(params) {
    params = JSON.parse(params.json);
    this.sendNotification(SlotMessage.RECEIVE_GET_RANK_ALADIN, params );
  }

  receiveUserHistory(params) {
    params = JSON.parse(params.json);
    this.sendNotification(SlotMessage.RECEIVE_GET_MY_HISTORY_ALADIN, params );
  }

  receiveUpdateJackPot(params) {
    params = JSON.parse(params.json);
    this.gameVO.updatejackpot(params.data);
    this.sendNotification(SlotMessage.RECEIVE_UPDATE_JACKPOT_ALADIN, {data: this.gameVO} );
  }
  /////////////////////////////////////////////////////////////////
  ////////////////////SEND TO SERVER//////////////////////////////
  ////////////////////////////////////////////////////////////////
  sendSpin(params) {
    var sfo = new SFS2X.SFSObject();
    sfo.putInt(SFSData.BET, params.bet);
    sfo.putInt(SFSData.GID, params.gid);
    sfo.putUtfString(SFSData.LINE_DATA, params.lines);
    this.sendExtensionRequest(SFSEvent.SPIN, sfo);
  }

    sendOrderSpin(params) {
        var sfo = new SFS2X.SFSObject();
        sfo.putInt(SFSData.BET, 10000);
        sfo.putInt(SFSData.GID, 4);
        sfo.putUtfString(SFSData.LINE_DATA, params.lines);
        sfo.putUtfString('orderData', params.orderData);
        this.sendExtensionRequest('order', sfo);
    }
}
