var BaseCommand = require('BaseCommand');
var LobbyMessage = require('LobbyMessage');
var MiniGameMessage = require('MiniGameMessage');
var i18n = require('i18n');

export default class ReceiveBetTaiXiuCommand extends BaseCommand {
  execute(notification) {
    BaseCommand.prototype.execute.call(this, notification);
    var params = notification.getBody().data;
    if(!params.success) {
        this.isErrorTaiXiu(parseInt(params.error));
        return;
    }

    var value = parseInt(params.value);
    var betType = parseInt(params.type);
    var moneyType = parseInt(params.moneyType);

    var mySelf =  this.facade.retrieveProxy('UserProxy').mySelf;
    if(moneyType === 0)
        mySelf.money = params.money;
    else
        mySelf.chip = params.money;

    this.sendNotification(MiniGameMessage.ON_BET_TAIXIU, {
        betType:betType,
        moneyType:moneyType,
        value: value
    });
    this.onShowMessageTaiXiu(i18n.t("T0001"));
    this.sendNotification(LobbyMessage.ON_UPDATE_MY_INFO);
  }

  isErrorTaiXiu (errorCode) {
    switch (errorCode) {
        case 1001:
             this.onShowMessageTaiXiu(i18n.t("T0002"));
             break;
        case 10008:
             this.onShowMessageTaiXiu(i18n.t("T0004"));
             break;
        default:
             this.onShowMessageTaiXiu(i18n.t("T0059"));
             break;
    }
  }

  onShowMessageTaiXiu (msg) {
    this.sendNotification(MiniGameMessage.SHOW_MESSAGE_TAIXIU, {
        msg:msg
    });
  }
}
