var BaseCommand = require('BaseCommand');
var LobbyMessage = require('LobbyMessage');
var i18n = require('i18n');

export default class ReceiveConfirmOtpCommand extends BaseCommand {
    execute(notification) {
        BaseCommand.prototype.execute.call(this, notification);
        if (this.isError(notification.getBody().data)) {
            this.sendNotification(LobbyMessage.SHOW_ALERT, { content: i18n.t("C0018") });
            return;
        }
        var params = notification.getBody();
        var mySelf = this.facade.retrieveProxy('UserProxy').mySelf;
        mySelf.money += params.money;
        mySelf.chip += params.chip;

        this.sendNotification(LobbyMessage.ON_CONFIRM_OTP_SUCESS);
        this.sendNotification(LobbyMessage.ON_UPDATE_MY_INFO);
        this.sendNotification(LobbyMessage.SHOW_ALERT, { content: i18n.t("C0019") });
    }
}