var BasePopup = require('BasePopup');
var BaseScene = require('BaseScene');
var UserProfileSceneMediator = require('UserProfileSceneMediator');
var i18n = require("i18n");
var DropListMainTemplate = require("DropListMainTemplate");
var Avatar = require("Avatar");
var TabMain = require("TabMain");
var TabUserProfile = require("TabUserProfile");
var TabChangePassword = require("TabChangePassword");
var TabMail = require("TabMail");
var TabUpdatePhone = require("TabUpdatePhone");
var LobbyMessage = require("LobbyMessage");
var LobbyEvent = require("LobbyEvent");
var Utility = require("Utility");

cc.Class({
    extends: BasePopup,

    properties: {
        tabMain: TabMain,
        tabUserProfile: TabUserProfile,
        tabChangePassword: TabChangePassword,
        tabMail: TabMail,
        tabUpdatePhone: TabUpdatePhone,
        avatar: Avatar,
    },

    // use this for initialization
    onLoad: function () {
        UserProfileSceneMediator.getInstance.init(this);
        this.addEventListeners();
        this.hide();

        this.isRequestUserInfo = false;
        this.isRequestMail = false;
    },

    addEventListeners: function () {
        this.tabChangePassword.node.on(LobbyEvent.CHANGE_PASSWORD, this.onChangPassword, this);
        this.tabMail.node.on('SEND_GET_MAIL_DETAIL', this.onSendGetMailDetail, this);
        this.tabMail.node.on('SEND_DELETE_MAIL', this.onDeleteMail, this);
        this.tabUpdatePhone.node.on('SEND_SET_PHONE_NUMBER', this.onSendSetPhoneNumber, this);
    },


    show:function () {
        BasePopup.prototype.show.call(this);
        this.tabMain.openTabIndex(0);
        this.tabMail.closeMailDetail();
        this.avatar.updateImg(this.dataUser.mySelf.avatar);
    },

    hide:function () {
        BasePopup.prototype.hide.call(this);
        this.isRequestUserInfo = false;
        this.isRequestMail = false;
    },

    onShowTab (tabId) {
        BasePopup.prototype.show.call(this);
        this.tabMain.openTabIndex(tabId);
    },

    onShowUserInfoTab() {
        if (this.isRequestUserInfo) return;
        this.activeGetUserInfoDetail();
    },

    onShowMailTab() {
        if (this.isRequestMail) return;
        this.activeGetMailList();
    },

    onShowValidateScene: function () {
        this.activeShowSecurity(LobbyMessage.VALIDATE_TAB_INDEX);
        this.hide();
    },

    onShowChangePassScene: function () {
        this.tabMain.openTabIndex(1);
    },

    onShowRechargeScene: function () {
        this.activeShowRecharge();
        this.hide();
    },

    onShowShopScene: function () {
        this.activeShowShop();
        this.hide();
    },

    onShowChangeAvatarScene: function () {
        this.activeShowChangeAvatar();
        this.hide();
    },

    onUpdateUserInfoDetail: function (data) {
        this.isRequestUserInfo = true;
        this.tabUserProfile.updateUserInfoDetail(data);
    },

    onUpdateMailList: function (params) {
        this.isRequestMail = true;
        this.tabMail.updateMailList(params.dataList);
    },

    onSendGetMailDetail: function (operatorId) {
        this.activeGetMailDetail({operatorId: operatorId});
    },

    onShowMailDetail: function (params) {
        this.tabMail.showMailDetail(params.content);
    },

    onDeleteMail: function (operatorId) {
        this.activeSendDeleteMail({operatorId: operatorId});
    },

    onChangPassword: function () {
        this.activeChangePass();
    },

    onUpdateChangePass: function () {
        this.tabChangePassword.onUpdateChangePass();
    },

    onSendSetPhoneNumber: function (params) {
        this.activeSetPhoneNumber(params);
    },

    onOpenTabPhoneNumber: function () {
        this.tabMain.openTabIndex(3);
    },

    onShowTabPhoneNumber: function () {
        this.tabUpdatePhone.showInfoPhoneNumber(this.dataUser.mySelf);
    },

    onUpdateSetPhoneNumber: function () {
        this.isRequestUserInfo = false;
        this.tabUpdatePhone.updateSetPhoneNumber(this.dataUser.mySelf);
    },

    onUpdateActivePhoneNumber: function () {
        this.isRequestUserInfo = false;
        this.tabUpdatePhone.updateActivePhoneNumber();
    },

    onUpdateResetPhoneNumber: function () {
        this.isRequestUserInfo = false;
        this.tabUpdatePhone.updateResetPhoneNumber();
    }
});