var BaseCommand = require('BaseCommand');
var LobbyMessage = require('LobbyMessage');
var i18n = require('i18n');

export default class InitMiniPokerCommand extends BaseCommand {
    execute(notification) {
        BaseCommand.prototype.execute.call(this, notification);
        var data = notification.getBody();
        var phoneNumber = data.phoneNumber;
        var amount = data.amount;

        if (this.isError(phoneNumber, amount)) return;

        var http = this.facade.retrieveProxy('HttpRequestProxy');
        var mySelf = this.facade.retrieveProxy('UserProxy').mySelf;

        var sendData = {
            displayName: mySelf.displayName,
            token: mySelf.token,
            phone: phoneNumber,
            amount: amount
        };

        http.sendRequestOtp(LobbyMessage.RECEIVE_REQUEST_OTP, sendData);
    }

    isError(phoneNumber, amount) {
        if (phoneNumber === "") {
            this.sendNotification(LobbyMessage.SHOW_ALERT, { content: i18n.t("C0015") });
            return true;
        }
        if (amount === null) {
            this.sendNotification(LobbyMessage.SHOW_ALERT, { content: i18n.t("C0051") });
            return true;
        }
        return false;
    }
}