var BaseMediator = require('BaseMediator');
var LobbyMessage = require('LobbyMessage');


class GiftSceneMediator extends BaseMediator {
    static get NAME() {
        return 'GiftSceneMediator';
    }

    static get getInstance() {
        if (this.instance === undefined) {
            this.instance = new GiftSceneMediator();
        }
        return this.instance;
    }

    onRegister() {
        this.giftProxy = this.facade.retrieveProxy('GiftProxy');
        this.dataUser = this.facade.retrieveProxy('UserProxy');
    }

    /** @override */
    listNotificationInterests() {
        return [
            LobbyMessage.SHOW_GIFT_SCENE,
            LobbyMessage.HIDE_GIFT_SCENE,
            LobbyMessage.ON_UPDATE_CAPTCHA,
            LobbyMessage.ON_UPDATE_GIFT_LIST
        ];
    }

    /** @override */
    handleNotification(notification) {
        switch (notification.getName()) {
            case LobbyMessage.SHOW_GIFT_SCENE:
                this.view.show();
                this.view.onUpdateCaptcha(this.dataUser.mySelf.accountName);
                this.sendNotification(LobbyMessage.SEND_GET_CAPTCHA);
                this.sendNotification(LobbyMessage.SEND_GET_GIFT_LIST);
                break;
            case LobbyMessage.HIDE_GIFT_SCENE:
                this.view.hide();
                break;
            case LobbyMessage.ON_UPDATE_CAPTCHA:
                //this.view.onUpdateCaptcha();
                break;
            case LobbyMessage.ON_UPDATE_GIFT_LIST:
                this.view.onUpdateGiftList(this.giftProxy.getGiftList());
                break;
            default:
                break;
        }
    }

    addHanlers() {
        this.view.activeUseGift = this.activeUseGift.bind(this);
        this.view.activeUseGiftCode = this.activeUseGiftCode.bind(this);
    }

    activeUseGift(giftItemVO) {
        this.sendNotification(LobbyMessage.SEND_USE_GIFT, giftItemVO);
    }

    activeUseGiftCode(giftCodeVO) {
        this.sendNotification(LobbyMessage.SEND_USE_GIFT_CODE, giftCodeVO);
    }

}

module.exports = GiftSceneMediator;