var BaseScene = require('BaseScene');
var ChooseLinePanelMC = require('ChooseLinePanelMC');
var EffectPanelMC = require('EffectPanelMC');
var SelectRoomPanelMC = require('SelectRoomPanelMC');
var GamePanelMC = require('GamePanelMC');
var HelpPanelMC = require('HelpPanelMC');
var LinePanelMC = require('LinePanelMC');
var MessagePanelMC = require('MessagePanelMC');
var MinigamePanelMC = require('MinigamePanelMC');
var RankPanelMC = require('RankPanelMC');
var HistoryPanelMC = require('HistoryPanelMC');
var TopPanelMC = require('TopPanelMC');
var X2PanelMC = require('X2PanelMC');
var Constants = require('Constants');
var SlotMCSceneMediator = require('SlotMCSceneMediator');

cc.Class({
    extends: BaseScene,

    properties: {
      chooseLinePanel: ChooseLinePanelMC,
      effectPanel: EffectPanelMC,
      selectRoomPanel: SelectRoomPanelMC,
      gamePanel: GamePanelMC,
      helpPanel: HelpPanelMC,
      linePanel: LinePanelMC,
      topPanel: TopPanelMC,
      rankPanel: RankPanelMC,
      historyPanel: HistoryPanelMC,
      minigamePanel: MinigamePanelMC,
      gameX2Panel: X2PanelMC,
      messagePanel: MessagePanelMC,
    },

    onLoad () {
      SlotMCSceneMediator.getInstance.init(this);
      this.isShowFirtRank = true;
      this.selectRoomPanel.node.on('ACTIVE_SET_ROOM_BET', this.onActiveSelectRoom, this);
      this.selectRoomPanel.node.on('ACTIVE_LEAVE_GAME', this.onActiveLeaveGame, this);

      this.gamePanel.node.on('ACTIVE_SEND_SPIN', this.onActiveSendSpin, this);
      this.gamePanel.node.on('ACTIVE_SHOW_CHOOSE_LINE_PANEL', this.onActiveShowChoosePanel, this);
      this.gamePanel.node.on('ACTIVE_SHOW_MESSAGE', this.onActiveShowMesage, this);
      this.gamePanel.node.on('ACTIVE_SHOW_SELECT_ROOM_PANEL', this.onActiveShowSelectRoom, this);
      this.gamePanel.node.on('ACTIVE_SELECT_ROOM', this.onActiveSelectRoom, this);
      this.gamePanel.node.on('ACTIVE_SHOW_TOURNAMENT', this.onActiveShowTournament, this);
      this.gamePanel.node.on('ACTIVE_SEND_ORDER_SPIN', this.onActiveOrderSpin, this);
      this.gamePanel.node.on('ACTIVE_PLAY_EFFECT_SOUND', this.onActivePlayEffectSound, this);
      this.gamePanel.node.on('ACTIVE_SHOW_GAME_X2', this.onActiveShowGameX2, this);

      this.chooseLinePanel.node.on('ACTIVE_UPDATE_COUNT_LINE', this.onUpdateCountLine, this);
      this.topPanel.node.on('ACTIVE_BACK_TO_LOBBY', this.onActiveBackToLobby, this);
      this.topPanel.node.on('ACTIVE_SHOW_HELP_PANEL', this.onShowHelpPanel, this);
      this.topPanel.node.on('ACTIVE_SHOW_CASH_IN_FORM', this.onActiveShowCashInForm, this);
      this.topPanel.node.on('ACTIVE_RANK_PANEL', this.onActiveRank, this);
      this.topPanel.node.on('ACTIVE_HISTORY_PANEL', this.onActiveHistory, this);
      this.topPanel.node.on('ACTIVE_PLAY_BACKGROUND_MUSIC_GAME', this.onPlayMusicGame, this);
      this.topPanel.node.on('ACTIVE_STOP_BACKGROUND_MUSIC_GAME', this.onStopMusicGame, this);

      this.gameX2Panel.node.on('ACTIVE_PLAY_GAME_X2', this.onPlayGameX2, this);

      this.rankPanel.node.on('ACTIVE_RANK_SHOW_PAGE', this.onShowRankPanel, this);
      this.historyPanel.node.on('ACTIVE_HISTORY_SHOW_PAGE', this.onShowHistoryPanel, this);
      // this.topPanel.node.on('ACTIVE_PLAY_SOUND_EFFECT', this.onPlaySoundEffect, this);

      this.selectRoomPanel.node.active = true;
    },

    buildUI(gameVO) {
      this.gameVO = gameVO;
      this.selectRoomPanel.buildUI(gameVO);
      this.gamePanel.buildUI(gameVO);
      this.topPanel.buildUI(gameVO);
      this.chooseLinePanel.buildUI(gameVO);
      this.gameX2Panel.buildUI(gameVO);
      this.show();
    },

    onStopMusicGame() {
      this.activeStopMusicGame();
    },

    onPlayMusicGame() {
      this.activePlayMusicGame();
    },

    onActivePlayEffectSound(soundName) {
        this.activePlaySoundEffect(soundName);
    },

    onActiveShowSelectRoom() {
      this.gamePanel.hide();
    },

    onActiveSelectRoom(roomId) {
        if (roomId === 0) {
            this.gameVO.PLAY_TRY_JACKPOT = this.gameVO.PLAY_TRY_MONEY = 50000000;
            this.topPanel.onUpdatePlayTryJackpot(this.gameVO.PLAY_TRY_JACKPOT);
            this.topPanel.onUpdatePlayTryMoney(this.gameVO.PLAY_TRY_MONEY);
        }
        else {
            this.gameVO.updateSelectRoomId(parseInt(roomId));
            this.topPanel.onUpdateJackpot(this.gameVO.getCurrentJackpot());
            this.gamePanel.checkFreeSpin();
        }

        this.topPanel.showMoneyType(this.gameVO.isChoiThu);
        this.chooseLinePanel.onHandlerTatCa();
        this.gamePanel.setRoomBet();
        this.gamePanel.show();
    },

    onActiveShowCashInForm() {
      this.activeShowCashInForm();
    },

    onActiveSendSpin(params) {
      var data = {
        roomId: this.gameVO.selectRoomId,
        bet: this.gameVO.getCurBet(),
        lines: '[' + this.gameVO.getLinesBet() + ']'
      };
      this.activeSendSpin(data);
      this.effectPanel.hide();
    },


    onActiveShowChoosePanel(params) {
      this.chooseLinePanel.show();
    },

    onActiveRank(params) {
      this.rankPanel.show();
    },

    onActiveHistory(params) {
      this.historyPanel.show();
    },

    onActiveGuide() {
      this.activeGuide();
    },

    onShowRankPanel(type) {
      var data = {
          type: type,
          bet: this.gamePanel.getRoomBet(),
      };
      this.activeRank(data);
    },

    onShowHistoryPanel() {
      var data = {
          bet: this.gamePanel.getRoomBet(),
      };
      this.activeMyHistory(data);
    },

    onActiveShowGameX2() {
      this.gameX2Panel.show();
      this.gameX2Panel.updatePrize(this.gameVO.prize);
    },

    onPlayGameX2() {
        var data = {
            bet: this.gamePanel.getRoomBet(),
        };
      this.activePlayGameX2(data);
    },

    onCheckGameX2() {
      if (this.gameVO.isChoiThu) return;
      this.gamePanel.checkGameX2();
    },

    onResultGameX2(data) {
      this.gameX2Panel.onResultPlayX2(data);
    },

    onUpdateCountLine() {
      this.gamePanel.onUpdateCountLine();
    },

    onSpinAndResult(data) {
      this.gamePanel.onSpinAndResult(data);
    },

    onShowLineWin(wins) {
      this.linePanel.onShowLineWin(wins);
    },

    onPlayAnimBonus() {
      this.gamePanel.playAnimBonus();
    },

    onPlayAnimScatter() {
      this.gamePanel.playAnimScatter();
    },

    onPlayAnimJackpot() {
      this.gamePanel.playAnimJackpot();
    },

    onShowEffectItemWin(wins) {
      var itemPos = this.linePanel.getItemPos(wins[0]);
      this.gamePanel.onShowEffectItemWin(itemPos, wins[0].itemId);
    },

    onHideAllEffectItemWin() {
      this.gamePanel.onHideAllEffectItemWin();
    },

    onShowEffectWin(data) {
      this.effectPanel.onShowEffectWin(data);
      this.gamePanel.onShowWinMoney(data);
    },

    onShowEffectFreeSpin(data) {
      this.effectPanel.onShowEffectFreeSpin(data);
    },

    onShowFreeSpin() {
        this.gamePanel.checkFreeSpin();
    },

    onUpdateRank(data) {
      this.rankPanel.updateRank(data);
    },

    onUpdateHistory(data) {
      this.historyPanel.updateHistory(data);
    },

    onUpdateMultiplier(data) {
        this.gamePanel.updateMultiplier(data);
    },

    onUpdateTichLuy(data) {
        this.gamePanel.updateTichLuy(data);
    },

    onShowWild(pos) {
        this.effectPanel.showWild(pos);
    },

    onHideWild() {
        this.effectPanel.hideWild();
    },

    onShowMinigame(data) {
      this.effectPanel.onShowMinigame(data.gameVO);
      TweenLite.delayedCall(2, function(){
        this.minigamePanel.onStartGame(data.gameVO.minigame, data.playComplete);
        this.effectPanel.hide();
      }.bind(this))
    },

    onActiveShowTournament() {
        this.activeShowTournament(Constants.SLOT20_MASTER_CHEF);
    },

    onHideEffectWin(data) {
      this.effectPanel.hide();
    },

    onShowMessage(message) {
      this.messagePanel.onShowMessage(message);
    },

    onHideAllLine() {
      // this.effectPanel.hide();
      this.linePanel.resetLineWin();
    },

    onSetAutoSpin(autoSpin) {
      this.gamePanel.onSetAutoSpin(autoSpin);
    },

    onActiveShowMesage(params) {
      this.messagePanel.onShowMessage(params.message);
    },

    onUpdateJackpot(data) {
      this.selectRoomPanel.onUpdateJackpot();
      this.topPanel.onUpdateJackpot(data.roomJackPot);
    },

    onUpdateUserInfo(mySelf) {
      this.topPanel.onUpdateMyMoney(mySelf.money);
      this.topPanel.onUpdateMyAvatar(mySelf.avatar);
    },

    onActiveBackToLobby() {
        this.selectRoomPanel.show();
    },

    onSetNumAutoSpin(data) {
      this.gamePanel.onSetNumAutoSpin(data.num);
    },

    onShowHelpPanel() {
      this.helpPanel.show();
    },

    onShowInfoTournament() {
      this.gamePanel.showInfoTournament();
    },

    onHideInfoTournament() {
        this.gamePanel.hideInfoTournament();
    },

    onUpdateTopTournament(data) {
        this.gamePanel.updateInfoTournament(data);
    },

    onResetBtnSpin() {
        this.gamePanel.resetBtnSpin();
    },

    onShowEffectTPoint(wins) {
        var itemPos = [];
        for (var i = 0; i < wins.length; i++) {
            var arr = this.linePanel.getItemPos(wins[i]);
            itemPos = itemPos.concat(arr);
        }
        itemPos = Array.from(new Set(itemPos));
        this.gamePanel.showEffectGetTPoint(itemPos);
    },

    onActiveOrderSpin() {
        this.activeOrderSpin();
    },

    onUpdatePlayTryMoney() {
        this.topPanel.onUpdatePlayTryJackpot(this.gameVO.PLAY_TRY_JACKPOT);
        this.topPanel.onUpdatePlayTryMoney(this.gameVO.PLAY_TRY_MONEY);
    },

    onActiveLeaveGame() {
      this.activeLeaveGame();
    }
});
