var BaseMediator = require('BaseMediator');
var LobbyMessage = require('LobbyMessage');
var UserProxy = require('UserProxy');


class UserProfileSceneMediator extends BaseMediator {
    static get NAME() {
        return 'UserProfileSceneMediator';
    }

    static get getInstance() {
        if (this.instance === undefined) {
            this.instance = new UserProfileSceneMediator();
        }
        return this.instance;
    }

    onRegister() {
        this.userProxy = this.facade.retrieveProxy('UserProxy');
    }

    /** @override */
    listNotificationInterests() {
        return [
            LobbyMessage.SHOW_USER_PROFILE_SCENE,
            LobbyMessage.HIDE_USER_PROFILE_SCENE,
            LobbyMessage.SHOW_TAB_IN_USER_INFO_SCENE,
            LobbyMessage.ON_UPDATE_USER_INFO_DETAIL,
            LobbyMessage.ON_UPDATE_BIRTHDAY,
            LobbyMessage.ON_UPDATE_CHANGE_PASS,
            LobbyMessage.ON_UPDATE_MAIL_LIST,
            LobbyMessage.ON_UPDATE_MAIL_DETAIL,
            LobbyMessage.ON_UPDATE_SET_PHONE,
            LobbyMessage.ON_UPDATE_ACTIVE_PHONE,
            LobbyMessage.ON_UPDATE_RESET_PHONE,
        ];
    }

    /** @override */
    handleNotification(notification) {
        BaseMediator.prototype.handleNotification.call(this);
        var params = notification.getBody();
        switch (notification.getName()) {
            case LobbyMessage.SHOW_USER_PROFILE_SCENE:
                this.view.dataUser = this.facade.retrieveProxy(UserProxy.NAME);
                this.view.show();
                if (params)
                    this.view.onShowTab(params.tabId);
                else
                    this.view.onShowTab(0);
                break;
            case LobbyMessage.HIDE_USER_PROFILE_SCENE:
                this.view.hide();
                break;
            case LobbyMessage.SHOW_TAB_IN_USER_INFO_SCENE:
                this.view.onShowTab(params.tabId);
                break;
            case LobbyMessage.ON_UPDATE_USER_INFO_DETAIL:
                this.view.onUpdateUserInfoDetail(this.userProxy.mySelf);
                break;
            case LobbyMessage.ON_UPDATE_BIRTHDAY:
                this.view.onUpdateSetBirthday();
                break;
            case LobbyMessage.ON_UPDATE_CHANGE_PASS:
                this.view.onUpdateChangePass();
                break;
            case LobbyMessage.ON_UPDATE_MAIL_LIST:
                this.view.onUpdateMailList(params);
                break;
            case LobbyMessage.ON_UPDATE_MAIL_DETAIL:
                this.view.onShowMailDetail(params);
                break;
            case LobbyMessage.ON_UPDATE_SET_PHONE:
                this.view.onUpdateSetPhoneNumber();
                break;
            case LobbyMessage.ON_UPDATE_ACTIVE_PHONE:
                this.view.onUpdateActivePhoneNumber();
                break;
            case LobbyMessage.ON_UPDATE_RESET_PHONE:
                this.view.onUpdateResetPhoneNumber();
                break;
            default:
                break;
        }
    }

    addHanlers() {
        this.view.activeShowMail = this.activeShowMail.bind(this);
        this.view.activeShowChangeAvatar = this.activeShowChangeAvatar.bind(this);
        this.view.activeShowShop = this.activeShowShop.bind(this);
        this.view.activeShowRecharge = this.activeShowRecharge.bind(this);
        this.view.activeShowSecurity = this.activeShowSecurity.bind(this);

        this.view.activeGetUserInfoDetail = this.activeGetUserInfoDetail.bind(this);
        this.view.activeSetBirthday = this.activeSetBirthday.bind(this);
        this.view.activeChangePass = this.activeChangePass.bind(this);
        this.view.activeGetMailList = this.activeGetMailList.bind(this);
        this.view.activeGetMailDetail = this.activeGetMailDetail.bind(this);
        this.view.activeSendDeleteMail = this.activeSendDeleteMail.bind(this);
        this.view.activeSetPhoneNumber = this.activeSetPhoneNumber.bind(this);
    }

    activeSetBirthday(params) {
        this.sendNotification(LobbyMessage.SEND_SET_BIRTHDAY, params);
    }

    activeGetUserInfoDetail(params) {
        this.sendNotification(LobbyMessage.SEND_GET_USER_INFO_DETAIL, params);
    }

    activeShowChangeAvatar() {
        this.sendNotification(LobbyMessage.SHOW_CHANGE_AVATAR_SCENE);
    }

    activeShowMail() {
        this.sendNotification(LobbyMessage.SHOW_MAIL_SCENE);
    }

    activeShowShop() {
        this.sendNotification(LobbyMessage.SHOW_SHOP_SCENE);
    }

    activeShowSecurity(params) {
        this.sendNotification(LobbyMessage.SHOW_SECURITY_SCENE, params);
    }

    activeShowRecharge() {
        this.sendNotification(LobbyMessage.SHOW_RECHARGE_SCENE);
    }

    activeChangePass(params) {
        this.sendNotification(LobbyMessage.SEND_CHANGE_PASS, params);
    }

    activeGetMailList() {
        this.sendNotification(LobbyMessage.SEND_GET_MAIL_LIST);
    }
    activeGetMailDetail(params) {
        this.sendNotification(LobbyMessage.SEND_GET_MAIL_DETAIL, params);
    }

    activeSendDeleteMail(params) {
        this.sendNotification(LobbyMessage.SEND_DELETE_MAIL, params);
    }
    activeSetPhoneNumber(params) {
        this.sendNotification(LobbyMessage.SEND_SET_PHONE, params);
    }
}

module.exports = UserProfileSceneMediator;
