var BaseCommand = require('BaseCommand');
var LobbyMessage = require('LobbyMessage');

export default class SendSetPhoneCommand extends BaseCommand {
    execute(notification) {
        BaseCommand.prototype.execute.call(this, notification);
        var data = notification.getBody();

        var http = this.facade.retrieveProxy('HttpRequestProxy');
        var mySelf = this.facade.retrieveProxy('UserProxy').mySelf;



        // if (data.phone === '') {
        //     this.sendNotification(LobbyMessage.SHOW_FLY_ALERT, {content: 'Bạn chưa nhập SĐT'});
        //     return;
        // }
        // else if (data.phone.length !== 10 || data.phone[0] !== '0') {
        //     this.sendNotification(LobbyMessage.SHOW_FLY_ALERT, {content: 'Số điện thoại không hợp lệ'});
        //     return;
        // }

        // var sendData = {
        //     displayName: mySelf.displayName,
        //     token: mySelf.token,
        //     phone: data.phone,
        //     OTP: data.OTP
        // };

        // this.sendNotification(LobbyMessage.SHOW_LOADING);
        // if (mySelf.phone !== '' && mySelf.isActivePhone === 1)
        //     http.resetPhone(LobbyMessage.RECEIVE_SET_PHONE, sendData);
        // else
        //     http.setPhone(LobbyMessage.RECEIVE_SET_PHONE, sendData);

        switch (data.type) {
            case 1:
                if (data.phone === '') {
                    this.sendNotification(LobbyMessage.SHOW_FLY_ALERT, {content: 'Bạn chưa nhập SĐT'});
                    return;
                }
                else if (data.phone.length !== 10 || data.phone[0] !== '0') {
                    this.sendNotification(LobbyMessage.SHOW_FLY_ALERT, {content: 'Số điện thoại không hợp lệ'});
                    return;
                }
                var sendData = {
                    displayName: mySelf.displayName,
                    token: mySelf.token,
                    phone: data.phone,
                };
                http.setPhone(LobbyMessage.RECEIVE_SET_PHONE, sendData);
                break;
            case 2:
                if (data.OTP === '') {
                    this.sendNotification(LobbyMessage.SHOW_FLY_ALERT, {content: 'Bạn chưa nhập OTP'});
                    return;
                }
                var sendData = {
                    displayName: mySelf.displayName,
                    token: mySelf.token,
                    OTP: data.OTP
                };
                http.activePhone(LobbyMessage.RECEIVE_ACTIVE_PHONE, sendData);
                break;
            case 3:
                if (data.OTP === '') {
                    this.sendNotification(LobbyMessage.SHOW_FLY_ALERT, {content: 'Bạn chưa nhập OTP'});
                    return;
                }
                var sendData = {
                    displayName: mySelf.displayName,
                    token: mySelf.token,
                    phone: data.phone,
                    OTP: data.OTP
                };
                http.resetPhone(LobbyMessage.RECEIVE_RESET_PHONE, sendData);
                break;

        }
    }
}