var BaseMediator = require('BaseMediator');
var LobbyMessage = require('LobbyMessage');


class PopupSceneMediator extends BaseMediator {
    static get NAME() {
        return 'PopupSceneMediator';
    }

    static get getInstance() {
        if (this.instance === undefined) {
            this.instance = new PopupSceneMediator();
        }
        return this.instance;
    }

    onRegister() {
    }

    /** @override */
    listNotificationInterests() {
        return [LobbyMessage.HIDE_ALL_POPUP];
    }

    /** @override */
    handleNotification(notification) {
        switch (notification.getName()) {
            case LobbyMessage.HIDE_ALL_POPUP:
                this.view.hideAll();
                break;
            default:
                break;
        }
    }

    addHanlers() {

    }
}

module.exports = PopupSceneMediator;
