var GameConfig = require("GameConfig");
var Base64 = require("Base64");
var Utility = require("Utility");
cc.Class({
    extends: cc.Component,

    properties: {
        mcImage:cc.Sprite
    },

    // use this for initialization
    onLoad: function () {
        //this.refeshCaptcha();
    },

    refeshCaptcha:function () {
        var displayName = GameConfig.DISPLAY_NAME;
        var xhr = new XMLHttpRequest();
        var api = GameConfig.WEB_SERVICE + "cmapi/getcaptcha";
        var method = "POST";
        var params = {displayName:displayName};

        xhr.open(method, api);
        xhr.onreadystatechange = function () {
            if (xhr.readyState == 4 && ( xhr.status >= 200 && xhr.status <= 207 )) {
                var jsonData = Base64.decode(xhr.responseText);
                jsonData = JSON.parse(jsonData);
                if (GameConfig.IS_DEBUG) {
                    console.log("%c[HTTP RECEIVE][" + api + "]: " + new Date().toLocaleString(), "color:orange");
                    console.log(this.JSONstringify(jsonData));
                }
                this.updateCaptcha(jsonData.path);
            }
            if (xhr.status < 200 || xhr.status > 207) {
                console.error("Lỗi kết nối vui lòng thử lại.");
            }
        }.bind(this);

        if (GameConfig.IS_DEBUG) {
            console.log("%c[HTTP SEND][" + api + "]: " + new Date().toLocaleString(), "color:orange");
            console.log(this.JSONstringify(params));

        }
        xhr.setRequestHeader("Content-Type", "application/x-www-form-urlencoded");
        xhr.setRequestHeader("Accept", "application/json");
        var urlParams = Object.keys(params).map(function (key) {
            return params[key];
        }).join('|');
        urlParams = "dt=" + Base64.encode(urlParams);
        xhr.send(urlParams);
    },

    updateCaptcha:function (path) {
        Utility.loadUrlImage(path, function (mcImage, texture) {
            mcImage.spriteFrame = new cc.SpriteFrame(texture);
        },[this.mcImage]);
    },

    JSONstringify: function (json) {
        if (typeof json != 'string') {
            json = JSON.stringify(json, undefined, '\t');
        }

        var
            arr = [],
            _string = 'color:green',
            _number = 'color:darkorange',
            _boolean = 'color:blue',
            _null = 'color:magenta',
            _key = 'color:white';

        json = json.replace(/("(\\u[a-zA-Z0-9]{4}|\\[^u]|[^\\"])*"(\s*:)?|\b(true|false|null)\b|-?\d+(?:\.\d*)?(?:[eE][+\-]?\d+)?)/g, function (match) {
            var style = _number;
            if (/^"/.test(match)) {
                if (/:$/.test(match)) {
                    style = _key;
                } else {
                    style = _string;
                }
            } else if (/true|false/.test(match)) {
                style = _boolean;
            } else if (/null/.test(match)) {
                style = _null;
            }
            arr.push(style);
            arr.push('');
            return '%c' + match + '%c';
        });

        arr.unshift(json);

        console.log.apply(console, arr);
    }
});
