var BaseCommand = require('BaseCommand');
var MiniGameMessage = require('MiniGameMessage');

export default class InitBauCuaCommand extends BaseCommand {
    execute(notification) {
        BaseCommand.prototype.execute.call(this, notification);
        this.sendNotification(MiniGameMessage.ON_INIT_GAME_BAU_CUA);
        this.sendNotification(MiniGameMessage.SHOW_MINIGAME_BAU_CUA);
        this.gameProxy = this.facade.retrieveProxy("BauCuaProxy");
        this.gameProxy.sendGetSoiCau();
    }
}
