var BaseMediator = require('BaseMediator');
var LobbyMessage = require('LobbyMessage');


class VipPointSceneMediator extends BaseMediator {
    static get NAME() {
        return 'VipPointSceneMediator';
    }

    static get getInstance() {
        if (this.instance === undefined) {
            this.instance = new VipPointSceneMediator();
        }
        return this.instance;
    }

    onRegister() {
    }

    /** @override */
    listNotificationInterests() {
        return [
            LobbyMessage.SHOW_VIP_POINT_SCENE,
            LobbyMessage.HIDE_VIP_POINT_SCENE
        ];
    }

    /** @override */
    handleNotification(notification) {
        BaseMediator.prototype.handleNotification.call(this);
        switch (notification.getName()) {

            case LobbyMessage.SHOW_VIP_POINT_SCENE:
                this.view.show();
                break;
            case LobbyMessage.HIDE_VIP_POINT_SCENE:
                this.view.hide();
                break;
            default:
                break;
        }
    }

    // addHanlers() {
    //     BaseMediator.prototype.addHanlers.call(this);
    //     this.view.activeUseGiftCode = this.activeUseGiftCode.bind(this);
    // }
    //
    // activeUseGiftCode(giftCode) {
    //     this.sendNotification(LobbyMessage.SEND_USE_GIFT_CODE, giftCode);
    // }
}

module.exports = VipPointSceneMediator;
