var BasePopup = require('BasePopup');
var Utility = require('Utility');

cc.Class({
    extends: BasePopup,

    properties: {
        togJackpot: cc.Toggle,
        content: cc.Node,
        itemTemp:cc.Node,
    },

    onLoad () {
    },

    show: function () {
        BasePopup.prototype.show.call(this);
        this.content.removeAllChildren();
        var type = 0;
        this.togJackpot.isChecked = true;
        TweenLite.delayedCall(0.2, function () {
            this.node.emit('ACTIVE_RANK_SHOW_PAGE', type);
        }.bind(this));
    },

    hide: function () {
        BasePopup.prototype.hide.call(this);
        this.content.removeAllChildren();
    },

    onHandlerListJackpot() {
        var type = 0;
        this.node.emit('ACTIVE_RANK_SHOW_PAGE', type);
    },

    onHandlerListBigWin() {
        var type = 1;
        this.node.emit('ACTIVE_RANK_SHOW_PAGE', type);
    },

    updateRank: function (data) {
        this.content.removeAllChildren();
        var type = data.command;
        data = data.data;
        for(var i=0 ; i<data.length ; i++){
            var item = cc.instantiate(this.itemTemp);
            item.data = data[i];
            // item.getChildByName("bgLine").active = (i % 2 === 0);
            item.getChildByName("txtSession").getComponent(cc.Label).string = '#' + data[i].lid;
            item.getChildByName("txtTime").getComponent(cc.Label).string = Utility.js_yyyy_mm_dd_hh_mm_ss(data[i].date);
            item.getChildByName("txtDisplayName").getComponent(cc.Label).string = data[i].username;
            item.getChildByName("txtBet").getComponent(cc.Label).string = Utility.formatCurrency(data[i].bet);
            // item.getChildByName("txtJackpot").getComponent(cc.Label).string = data[i].jackpot;
            item.getChildByName("txtPrize").getComponent(cc.Label).string = Utility.formatCurrency(data[i].pay);
            if (type === 'getLogBigWin')
                item.getChildByName("txtDetail").getComponent(cc.Label).string = 'THẮNG LỚN';
            else
                item.getChildByName("txtDetail").getComponent(cc.Label).string = 'NỔ HŨ';

            this.content.addChild(item);
        }
    }
});
