var BaseCommand = require('BaseCommand');
var LobbyMessage = require('LobbyMessage');


export default class ReceiveGetOutCardInfoCommand extends BaseCommand {
    execute(notification) {
        BaseCommand.prototype.execute.call(this, notification);
        if (this.isError(notification.getBody().data)) return;
        var data = notification.getBody().data;
        var shopProxy = this.facade.retrieveProxy('ShopProxy');
        shopProxy.updateShop(data.outCardData);
        shopProxy.updateTransferdes(data.transferData);

        this.sendNotification(LobbyMessage.ON_UPDATE_OUT_CARD_INFO);
        this.sendNotification(LobbyMessage.ON_UPDATE_TRANSFERDES);
    }
}