var Utility = require('Utility');
var LobbyEvent = require('LobbyEvent');
cc.Class({
    extends: cc.Component,

    properties: {
        txtMoney:cc.EditBox,
        txtUserName:cc.Label,
        txtRules:cc.RichText
    },

    // use this for initialization
    onLoad: function () {
        this.txtMoney.string = "";
    },

    onHandlerChooseMoney:function (params,customEventData) {
        this.txtMoney.string = customEventData;
    },

    onHandlerAccept:function () {
        this.node.emit(LobbyEvent.BANK_RECHARGE, {
            money: this.txtMoney.string
        });
    },

    updateMySelf:function (mySelf) {
        this.txtUserName.string = mySelf.displayName;
    },

    updateBankDescription: function (bankDescription) {
        this.txtRules.string = Utility.convertToRtf(bankDescription);
    }

});
