var LobbyEvent = require('LobbyEvent');
var Utility = require('Utility');
var i18n = require('i18n');
cc.Class({
    extends: cc.Component,

    properties: {
        txtGold:cc.Label,
        txtDisplayName:cc.Label,
        txtUserName:cc.Label,
        txtLevel:cc.Label,
        txtPhone:cc.Label,
        txtVip:cc.Label,
        txtJoinDate:cc.Label,
        txtStatus:cc.Label,
        btnUpdatePhone: cc.Node
    },

    // use this for initialization
    onLoad: function () {
        this.txtGold.string = '';
        this.txtDisplayName.string = '';
        this.txtUserName.string = '';
        this.txtLevel.string = '';
        this.txtPhone.string = '';
        this.txtVip.string = '';
    },

    updateAvatar: function () {
        this.avatar.updateImg(this.dataUser.mySelf.avatar);
    },

    updateUserInfoDetail: function (data) {
        this.txtGold.string = Utility.formatCurrency(data.money);
        this.txtDisplayName.string = data.displayName;
        this.txtUserName.string = data.fbId;
        this.txtLevel.string = data.levelInfo.currentLevel;
        this.txtVip.string = data.levelInfo.point;
        this.txtJoinDate.string = data.joinDate.substr(0,10);
        this.txtPhone.string = data.phone;

        if (data.phone !== ''){
            this.btnUpdatePhone.active = false;
            if (data.isActivePhone) {
                this.txtStatus.node.color = new cc.Color(0,255,0);
                this.txtStatus.string = i18n.t("D0002");
            }
            else {
                this.txtStatus.string = i18n.t("D0001");
                this.txtStatus.node.color = new cc.Color(255,0,0);
            }
        }
        else {
            this.txtStatus.string = '';
            this.btnUpdatePhone.active = true;
        }
    },

});
