var BaseScene = require('BaseScene');

cc.Class({
    extends: BaseScene,

    properties: {
        isShowBackPopup: true
    },

    ctor: function () {
        this.TIME_SHOW = 0.2;
    },

    // use this for initialization
    onLoad: function () {

    },

    show:function () {
        // if(this.node.active && this.component) return;
        if (this.node.popupScene && this.node.getChildByName('component').active) return;
        if(!this.node.popupScene && this.node.active) return;

        BaseScene.prototype.show.call(this);
        if(this.node.popupScene) {
            this.node.popupScene.show();
            if (this.isShowBackPopup){
                this.node.popupScene.node.getChildByName('background').active = true;
            }

            if (this.node.getChildByName('component'))
                this.node.getChildByName('component').active = true;
        }
        this.getTweenNode().scaleX = this.getTweenNode().scaleY = 0;
        this.getTweenNode().opacity = 0;
        // TweenLite.from(this.getTweenNode(), 0.2, {scaleX:0, scaleY:0});
        // TweenLite.from(this.getTweenNode(), 0.2, {opacity: 0});
        this.node.runAction(cc.spawn(cc.fadeIn(this.TIME_SHOW), cc.scaleTo(this.TIME_SHOW,1).easing(cc.easeBackOut())));
    },

    getTweenNode:function () {
        return this.node;
    },

    hide:function () {
        if(this.node === undefined) return;
        if(this.node.active === false) return;
        if (this.node.popupScene  && this.node.getChildByName('component') && this.node.getChildByName('component').active === false) return;

        // TweenLite.to(this.getTweenNode(), 0.2, {opacity: 0});
        // TweenLite.to(this.getTweenNode(), 0.2, {scaleX:0, scaleY:0, onComplete:function () {
        //         BaseScene.prototype.hide.call(this);
        //         if(this.node.popupScene) {
        //             this.node.popupScene.hide();
        //             if (this.node.getChildByName('component'))
        //                 this.node.getChildByName('component').active = false;
        //         }
        //     }.bind(this)});

        this.node.runAction(cc.sequence(
            cc.spawn(cc.fadeOut(this.TIME_SHOW), cc.scaleTo(this.TIME_SHOW,0).easing(cc.easeBackIn())),
            cc.callFunc(function () {
                        BaseScene.prototype.hide.call(this);
                        if(this.node.popupScene) {
                            this.node.popupScene.hide();
                            if (this.node.getChildByName('component'))
                                this.node.getChildByName('component').active = false;
                        }
                    }.bind(this))
        ));

    }

});

