var Utility = require('Utility');
cc.Class({
    extends: cc.Component,

    properties: {
        txtPrize: cc.Label,
        txtWinMoney: cc.Label,
        txtTime: cc.Label,
        txtMulti: cc.Label,
        mcTotalWin: cc.Node,
        mcBallGroup: cc.Node,
    },

    onLoad () {
        this.isLoad = true;
        this.totalCountTime = 15;
        this.timerCount = this.totalCountTime;
        this.isPlaying = false;
        this.isAuto = false;
        this.curPos = new cc.Vec2(this.node.x, this.node.y);
        this.node.active = false;
        this.reset();
    },

    onStartGame(data, onComplete) {
        this.show();
        setTimeout(function(){
            this.reset();
            this.show();
            this.onComplete = onComplete;
            this.isPlaying = true;
            this.dataResult = data;
            this.prize = this.dataResult.money;
            this.timerCount = this.totalCountTime;
            this.txtMulti.string = this.dataResult.multi;
            if(this.timer) clearInterval(this.timer);
            this.timer = setInterval(this.updateTimer.bind(this), 1000);
        }.bind(this), 1000);
    },

    show() {
        // if(this.curPos) {
        //   this.node.x = this.curPos.x;
        //   this.node.y = this.curPos.y;
        // }
        this.node.active = true;
        TweenLite.to(this.node, 0.5, { y: this.curPos.y });
    },

    hide() {
        this.node.active = false;
        TweenLite.to(this.node, 0.5, { y: -800 });
    },

    onHandlerAutoChoose() {
        if(this.isAuto) return;
        this.isAuto = true;
        clearInterval(this.timer);
        for(var i = 0; i < 12; i++) {
            TweenLite.delayedCall(i * 1, function (data) {
                if(!this.isPlaying) return;
                var btChocolate = this.getRandomChocolate();
                this.onChooseBall(btChocolate);
            }.bind(this));
        }
    },

    updateTimer() {
        if(!this.isPlaying) return;
        this.timerCount--;
        this.txtTime.string = this.timerCount;
        if(this.timerCount === 0) {
            var btChocolate = this.getRandomChocolate();
            this.onChooseBall(btChocolate);
            this.timerCount = this.totalCountTime;
        }
    },

    getRandomChocolate() {
        var num = Utility.randomNumber(0, 3);
        for(var i = num; i < 4; i++) {
            var bt = this.mcBallGroup.children[i];
            if(bt && bt.active === true) return bt;
            if(i === 24) i = 0;
        }
    },

    onChooseBall(mcTarget) {
        if(!this.isPlaying) return;
        this.isPlaying = false;
        mcTarget = mcTarget.target;
        mcTarget.getComponent(cc.Button).interactable = false;

        var spine = mcTarget.getChildByName('icon_ball').getComponent(sp.Skeleton);

        if(this.prize > 0) {
            spine.setAnimation(0, 'active-win', false);
            spine.addAnimation(0, 'idle-win', true, 0);

            Utility.tweenRunNumber(this.txtPrize.node, this.prize, 1);
            var resultMoney = mcTarget.getChildByName('resultMoney');
            resultMoney.active = true;
            resultMoney.getChildByName('txtMoney').getComponent(cc.Label).string = Utility.formatCurrency(this.prize);
        }
        else {
            spine.setAnimation(0, 'active-lose', false);
            spine.addAnimation(0, 'idle-lose', true, 0);
        }


        TweenLite.delayedCall(2, function () {
            this.mcTotalWin.active = true;
            this.txtWinMoney.string = 0;
            TweenLite.from(this.mcTotalWin, 0.2, {opacity:0});
            Utility.tweenRunNumber(this.txtWinMoney.node, this.prize, 1);
            // Utility.tweenRunNumber(this.txtPrize.node, this.dataResult.v, 1.5);
            setTimeout(function(session){
                this.onHandlerCloseGame();
            }.bind(this), 4000);
        }.bind(this))
    },

    onHandlerCloseGame() {
        this.hide();
        TweenLite.delayedCall(1, function(){
            if(this.onComplete) this.onComplete.call();
            this.onComplete = null;
            this.reset();
        }.bind(this));
    },

    handlerChooseBall(event) {
        var mcTarget = event.currentTarget;
        this.onChooseBall(mcTarget);
    },

    reset() {
        this.txtTime.string = this.totalCountTime;
        this.curResult = 0;
        this.mcTotalWin.active = false;
        this.isAuto = false;
        this.onComplete = null;
        this.txtPrize.string = "0";

        for(let i=0; i<this.mcBallGroup.children.length; i++) {
            if(this.mcBallGroup.children[i]) {
                var spineBall = this.mcBallGroup.children[i].getChildByName('icon_ball').getComponent(sp.Skeleton);
                spineBall.setAnimation(0, 'idle', true);
                this.mcBallGroup.children[i].getComponent(cc.Button).interactable = true;
                var resultMoney = this.mcBallGroup.children[i].getChildByName('resultMoney');
                resultMoney.active = false;
            }
        }
    },

    activeTurn(mcTurn, isActive) {
        var mcOn = mcTurn.getChildByName('LuotChon');
        var mcOff = mcTurn.getChildByName('LuotChonD');

        mcOn.active = isActive;
        mcOff.active = !isActive;
    }
});
