var LobbyEvent = require('LobbyEvent');
var Utility = require('Utility');
var DropListMainTemplate = require('DropListMainTemplate');
cc.Class({
    extends: cc.Component,

    properties: {
        smsRateListContent: cc.Node,
        listPrice: DropListMainTemplate,
        ctnOTP: cc.Node,
        ctnConfirmOTP: cc.Node,
        txtNote: cc.RichText
    },

    // use this for initialization
    onLoad: function () {
        this.txtPhoneNumber = this.ctnOTP.getChildByName("txtPhoneNumber").getComponent(cc.EditBox);
        this.txtOTP = this.ctnConfirmOTP.getChildByName("txtOTP").getComponent(cc.EditBox);
        this.itemOTP = this.smsRateListContent.children[0];
        this.smsRateListContent.removeAllChildren();

        this.ctnOTP.active = true;
        this.ctnConfirmOTP.active = false;
    },

    updateMySelf: function (mySelf) {
        this.mySelf = mySelf;
    },

    updateTextNote: function (note) {
        this.txtNote.string = Utility.convertToRtf(note);
        this.txtNote.node.active = (note === "") ? false : true;
    },

    updateOtpValueList: function (otpValueList) {
        this.listPrice.setupData(otpValueList);
    },

    updateSmsOtpRateList: function (smsOtpRateList) {
        var data = smsOtpRateList;
        this.smsRateListContent.removeAllChildren();
        for (var i = 0; i < data.length; i++) {
            if (this.itemOTP === undefined) return;
            var item = cc.instantiate(this.itemOTP);
            var txtPrice = item.getChildByName("txtPrice").getComponent(cc.Label);
            var txtSale = item.getChildByName("txtSale").getComponent(cc.Label);
            var txtMoney = item.getChildByName("txtMoney").getComponent(cc.Label);

            txtPrice.string = data[i].name;
            txtMoney.string = data[i].value;
            txtSale.string = (data[i].promotion) ? data[i].promotion : "0%";
            this.smsRateListContent.addChild(item);
        }
    },

    onHandlerSendOTP: function () {
        var phoneNumber = this.txtPhoneNumber.string;
        var price = this.listPrice.getItemSelect();
        this.node.emit(LobbyEvent.OTP_RECHARGE, {
            phoneNumber: phoneNumber,
            amount: (price) ? price.value : null
        });
    },

    onHandlerSendConfirmOTP: function () {
        var codeOtp = this.txtOTP.string;
        this.node.emit(LobbyEvent.OTP_CONFIRM, {
            codeOtp: codeOtp
        });
    },

    getIndexItemSelect: function () {
        for (var i = 0; i < this.smsRateListContent.children.length; i++) {
            var item = this.smsRateListContent.children[i];
            var toggle = item.getComponent(cc.Toggle);
            if (toggle.isChecked) return i;
        }
        return 0;
    },

    onShowConfirmOTP: function () {
        this.ctnOTP.active = false;
        this.ctnConfirmOTP.active = true;
    },

    onComfirmOTPFail: function () {
        //nothing...
    },

    onComfirmOTPSucess: function () {
        this.ctnConfirmOTP.active = false;
        this.ctnOTP.active = true;

        TweenLite.delayedCall(5, function () {
            this.txtPhoneNumber.string = "";
        }.bind(this));
    }
});
