var BaseMediator = require('BaseMediator');
var Constants = require('Constants');
var SlotMessage = require('SlotMessage');
var CustomAction = require('CustomAction');
var SlotFBProxy = require('SlotFBProxy');
var LobbyMessage = require('LobbyMessage');
var UserProxy = require('UserProxy');
var SoundGameMessage = require('SoundGameMessage');

class SlotFBSceneMediator extends BaseMediator {
  static get NAME() {
    return 'SlotFBSceneMediator';
  }

  static get getInstance() {
    if(this.instance === undefined){
        this.instance = new SlotFBSceneMediator();
    }
    return this.instance;
  }

  onRegister() {
      puremvc.Mediator.call(this, this.constructor.NAME);
  }

  listNotificationInterests() {
      return [
        SlotMessage.SHOW_MINIGAME_FOOTBALL,
        SlotMessage.HIDE_MINIGAME_FOOTBALL,
        SlotMessage.ON_INIT_GAME_FOOTBALL,
        SlotMessage.ON_SPIN_AND_RESULT_FOOTBALL,
        SlotMessage.ON_SHOW_EFFECT_WIN_FOOTBALL,
        SlotMessage.ON_HIDE_EFFECT_WIN_FOOTBALL,
        SlotMessage.ON_SHOW_LINE_WIN_FOOTBALL,
        SlotMessage.ON_SHOW_MESSAGE_FOOTBALL,
        SlotMessage.ON_RESET_NEW_GAME_FOOTBALL,
        SlotMessage.ON_HIDE_ALL_LINE_FOOTBALL,
        SlotMessage.ON_SET_AUTO_SPIN_FOOTBALL,
        SlotMessage.ON_UPDATE_JACKPOT_FOOTBALL,
        SlotMessage.ON_SHOW_ITEM_WIN_FOOTBALL,
        SlotMessage.ON_SHOW_EFFECT_ITEM_WIN_FOOTBALL,
        SlotMessage.ON_HIDE_ALL_EFFECT_ITEM_WIN_FOOTBALL,
        SlotMessage.ON_SET_NUM_AUTO_SPIN_FOOTBALL,
        SlotMessage.ON_SHOW_MINIGAME,
        SlotMessage.ON_SHOW_EFFECT_FREE_SPIN_FOOTBALL,
        SlotMessage.ON_SHOW_FREE_SPIN_FOOTBALL,
        SlotMessage.ON_PLAY_ANIM_BONUS_FOOTBALL,
        SlotMessage.ON_PLAY_ANIM_SCATTER_FOOTBALL,
        SlotMessage.ON_PLAY_ANIM_JACKPOT_FOOTBALL,
        SlotMessage.ON_UPDATE_RANK_FOOTBALL,
        SlotMessage.ON_UPDATE_MY_HISTORY_FOOTBALL,
        SlotMessage.ON_UPDATE_MULTIPLIER_FOOTBALL,
        SlotMessage.ON_UPDATE_TICH_LUY_FOOTBALL,
        SlotMessage.ON_SHOW_WILD_FOOTBALL,
        SlotMessage.ON_HIDE_WILD_FOOTBALL,
        SlotMessage.SHOW_INFO_TOURNAMENT_FOOTBALL,
        SlotMessage.HIDE_INFO_TOURNAMENT_FOOTBALL,
        SlotMessage.ON_RESET_BUTTON_FOOTBALL,
        SlotMessage.ON_RESET_BTN_SPIN_FOOTBALL,
        SlotMessage.ON_UPDATE_MONEY_PLAY_TRY_FOOTBALL,
        LobbyMessage.ON_UPDATE_MY_INFO,
        LobbyMessage.ON_UPDATE_TOP_TOURNAMENT

      ]
  }

  /** @override */
  handleNotification(notification) {
      BaseMediator.prototype.handleNotification.call(this);
      this.dataUser = this.facade.retrieveProxy(UserProxy.NAME);
      var data = notification.getBody();
      switch (notification.getName()) {
          case LobbyMessage.ON_UPDATE_MY_INFO:
            this.view.onUpdateUserInfo(this.dataUser.mySelf);
            break;
          case SlotMessage.ON_INIT_GAME_FOOTBALL:
            this.view.buildUI(SlotFBProxy.proxy.gameVO);
            this.activePlayMusicGame();
            break;
          case SlotMessage.SHOW_MINIGAME_FOOTBALL:
            this.view.show();
            break;
          case SlotMessage.HIDE_MINIGAME_FOOTBALL:
            this.view.hide();
            break;
          case SlotMessage.ON_SPIN_AND_RESULT_FOOTBALL:
            this.view.onSpinAndResult(data);
            break;
          case SlotMessage.ON_SHOW_EFFECT_WIN_FOOTBALL:
            this.view.onShowEffectWin(data.gameVO);
            break;
          case SlotMessage.ON_HIDE_EFFECT_WIN_FOOTBALL:
            this.view.onHideEffectWin();
            break;
          case SlotMessage.ON_SHOW_LINE_WIN_FOOTBALL:
            this.view.onShowLineWin(data.wins);
            break;
          case SlotMessage.ON_SHOW_EFFECT_ITEM_WIN_FOOTBALL:
            this.view.onShowEffectItemWin(data.wins);
            break;
          case SlotMessage.ON_HIDE_ALL_EFFECT_ITEM_WIN_FOOTBALL:
            this.view.onHideAllEffectItemWin();
            break;
          case SlotMessage.ON_SHOW_MESSAGE_FOOTBALL:
            this.view.onShowMessage(data.message);
            // this.sendNotification(Lob byMessage.SHOW_FLY_ALERT,{content:data.message});
            break;
          case SlotMessage.ON_RESET_GAME_FOOTBALL:
            this.view.onResetGame();
            break;
          case SlotMessage.ON_SET_AUTO_SPIN_FOOTBALL:
            this.view.onSetAutoSpin(data.autoSpin);
            break;
          case SlotMessage.ON_HIDE_ALL_LINE_FOOTBALL:
            this.view.onHideAllLine();
            break;
          case SlotMessage.ON_UPDATE_JACKPOT_FOOTBALL:
            this.view.onUpdateJackpot(data);
            break;
          case SlotMessage.ON_SET_NUM_AUTO_SPIN_FOOTBALL:
            this.view.onSetNumAutoSpin(data);
            break;
          case SlotMessage.ON_SHOW_MINIGAME:
            this.view.onShowMinigame(data);
            break;
          case SlotMessage.ON_PLAY_ANIM_BONUS_FOOTBALL:
            this.view.onPlayAnimBonus();
            break;
          case SlotMessage.ON_PLAY_ANIM_SCATTER_FOOTBALL:
            this.view.onPlayAnimScatter();
            break;
          case SlotMessage.ON_PLAY_ANIM_JACKPOT_FOOTBALL:
            this.view.onPlayAnimJackpot();
            break;
          case SlotMessage.ON_SHOW_EFFECT_FREE_SPIN_FOOTBALL:
            this.view.onShowEffectFreeSpin(data);
            break;
          case SlotMessage.ON_SHOW_FREE_SPIN_FOOTBALL:
              this.view.onShowFreeSpin();
              break;
          case SlotMessage.ON_UPDATE_RANK_FOOTBALL:
            this.view.onUpdateRank(data);
            break;
          case SlotMessage.ON_UPDATE_MY_HISTORY_FOOTBALL:
            this.view.onUpdateHistory(data);
            break;
          case SlotMessage.ON_UPDATE_MULTIPLIER_FOOTBALL:
              this.view.onUpdateMultiplier(data);
              break;
          case SlotMessage.ON_UPDATE_TICH_LUY_FOOTBALL:
              this.view.onUpdateTichLuy(data);
              break;
          case SlotMessage.ON_SHOW_WILD_FOOTBALL:
              this.view.onShowWild(data);
              break;
          case SlotMessage.ON_HIDE_WILD_FOOTBALL:
              this.view.onHideWild();
              break;
          case SlotMessage.SHOW_INFO_TOURNAMENT_FOOTBALL:
              this.view.onShowInfoTournament();
              break;
          case SlotMessage.HIDE_INFO_TOURNAMENT_FOOTBALL:
              this.view.onHideInfoTournament();
              break;
          case SlotMessage.ON_UPDATE_MONEY_PLAY_TRY_FOOTBALL:
              this.view.onUpdatePlayTryMoney();
              break;
          case LobbyMessage.ON_UPDATE_TOP_TOURNAMENT:
              this.view.onUpdateTopTournament(data);
              break;
          case SlotMessage.ON_RESET_BUTTON_FOOTBALL:
              this.view.onResetButton();
              break;
          case SlotMessage.ON_RESET_BTN_SPIN_FOOTBALL:
              this.view.onResetBtnSpin();
              break;
      }
  }

  addHanlers() {
    BaseMediator.prototype.addHanlers.call(this);
    this.view.activeSendSpin = this.activeSendSpin.bind(this);
    this.view.activeRank = this.activeRank.bind(this);
    this.view.activeMyHistory = this.activeMyHistory.bind(this);
    this.view.activeGuide = this.activeGuide.bind(this);
    this.view.activeLeaveGame = this.activeLeaveGame.bind(this);
    this.view.activeShowCashInForm = this.activeShowCashInForm.bind(this);
    this.view.activeShowTournament = this.activeShowTournament.bind(this);
    this.view.activePlayMusicGame = this.activePlayMusicGame.bind(this);
    this.view.activeStopMusicGame = this.activeStopMusicGame.bind(this);
    this.view.activePlaySoundEffect = this.activePlaySoundEffect.bind(this);
    this.view.activeOrderSpin = this.activeOrderSpin.bind(this);
  }

  activeStopMusicGame(params) {
    this.sendNotification(LobbyMessage.STOP_BACKGROUND_SOUND);
  }

  activePlayMusicGame(params) {
    this.sendNotification(LobbyMessage.PLAY_BACKGROUND_SOUND, {soundName: SoundGameMessage.BG_FOOTBALL_MUSIC, volume: 1});
  }

  activePlaySoundEffect(soundName) {
    this.sendNotification(LobbyMessage.PLAY_SOUND_EFFECT, soundName);
  }

  activeSendSpin(params){
      this.sendNotification(LobbyMessage.PLAY_SOUND_EFFECT, SoundGameMessage.BTN_CLICK_FOOTBALL);

      if (params.lines === '[]')  {
          this.sendNotification(LobbyMessage.SHOW_FLY_ALERT , {content: 'Bạn chưa chọn dòng đặt'});
          this.view.onResetButton();
          this.view.onResetBtnSpin();
          return;
      }
    this.sendNotification(SlotMessage.SEND_SPIN_FOOTBALL , {data: params});
  }

  activeShowCashInForm(params){
    this.sendNotification(LobbyMessage.SHOW_TAB_IN_SHOP_SCENE, {tabIndex:0});
  }

  activeRank(params){
    this.sendNotification(SlotMessage.SEND_GET_RANK_FOOTBALL, params);
  }

  activeMyHistory(params){
    this.sendNotification(SlotMessage.SEND_GET_MY_HISTORY_FOOTBALL, params);
  }

  activeGuide(params){
    this.sendNotification(SlotMessage.SEND_SHOW_GUIDE_MINIGAME, Constants.MINIGAME_SLOT20);
  }

  activeShowTournament(gameModule){
    this.sendNotification(LobbyMessage.SHOW_LIST_TOURNAMENT_SCENE, gameModule);
  }

  activeOrderSpin () {
    this.sendNotification(SlotMessage.SEND_ORDER_SPIN_FOOTBALL);
  }

  activeLeaveGame(params){
    this.sendNotification(LobbyMessage.SHOW_LOBBY);
    this.sendNotification(SlotMessage.SEND_LEAVE_ROOM_FOOTBALL);
    // this.sendNotification(LobbyMessage.PLAY_BACKGROUND_MUSIC, {soundName: SoundGameMessage.LOBBY_SOUND, volume: 1});
    this.sendNotification(LobbyMessage.STOP_BACKGROUND_SOUND);
  }
}

module.exports = SlotFBSceneMediator;
