var BasePopup = require('BasePopup');
var HelpAndRulesSceneMediator = require('HelpAndRulesSceneMediator');

cc.Class({
    extends: BasePopup,

    properties: {
        webView:cc.WebView
    },

    // use this for initialization
    onLoad: function () {
        HelpAndRulesSceneMediator.getInstance.init(this);
        this.hide()
    },
    show:function (url) {
        BasePopup.prototype.show.call(this);
        this.webView.url = url;
        window.document.addEventListener("testWebview", this.callback.bind(this));
    },

    callback:function () {

    }
});

