var BaseCommand = require('BaseCommand');
var MiniGameMessage = require('MiniGameMessage');
var LobbyMessage = require('LobbyMessage');
var SlotBTVO = require('SlotBTVO');
var i18n = require('i18n');

export default class ReceiveSpinSlotBTCommand extends BaseCommand {
  execute(notification) {
    console.log('----------ReceiveSpinSlotBTCommand---------');
    BaseCommand.prototype.execute.call(this, notification);
    var gameVO = notification.getBody().data;
    // gameVO.session++;
    if(gameVO.typeResult === SlotBTVO.ERROR) {
      gameVO.isSpining = false;
      gameVO.autoSpin = false;
      this.sendNotification(MiniGameMessage.ON_SHOW_MESSAGE_SLOT_BT, {message: gameVO.errorMessage});
      this.sendNotification(MiniGameMessage.ON_SET_AUTO_SPIN_SLOT_BT, {autoSpin: false});
      this.sendNotification(MiniGameMessage.ON_RESET_BTN_SPIN_SLOT_BT);
    }else{
      this.sendNotification(MiniGameMessage.ON_SPIN_AND_RESULT_SLOT_BT, {items:gameVO.items, onComplete: this.onComplete.bind(this)});
    }
  }

  onComplete() {
    var gameVO = this.facade.retrieveProxy('SlotBTProxy').gameVO;
    TweenLite.delayedCall(1*gameVO.timeSpin, function(){
      gameVO.isSpining = false;
      this.sendNotification(MiniGameMessage.ON_RESET_BTN_SPIN_SLOT_BT);
      this.updateMyMoney(gameVO.money);
      if(gameVO.wins.length > 0){
        var wins = gameVO.wins;
        this.sendNotification(MiniGameMessage.ON_SHOW_LINE_WIN_SLOT_BT, {wins: wins});
        this.sendNotification(MiniGameMessage.ON_SHOW_EFFECT_WIN_SLOT_BT, {gameVO: gameVO});
        TweenLite.delayedCall(3*gameVO.timeSpin, function(session){
          if(gameVO.session !== session) return;
          var timeDeay = 1*gameVO.timeSpin;
          this.sendNotification(MiniGameMessage.ON_HIDE_EFFECT_WIN_SLOT_BT);
          if(gameVO.autoSpin) {
            this.checkAutoSpin();
          }else {
            this.displayOneLine(wins, timeDeay, session);
            TweenLite.delayedCall(wins.length * timeDeay, function() {
              this.sendNotification(MiniGameMessage.ON_HIDE_ALL_LINE_SLOT_BT);
              this.sendNotification(MiniGameMessage.ON_HIDE_ALL_EFFECT_ITEM_WIN_SLOT_BT);
            }.bind(this));
          }
        }.bind(this),[gameVO.session]);
      }else{
        TweenLite.delayedCall(1, function(){
          this.checkAutoSpin();
        }.bind(this));
      }
    }.bind(this))
  }

  checkAutoSpin() {
    var gameVO = this.facade.retrieveProxy('SlotBTProxy').gameVO;
    if(gameVO.autoSpin) {
      this.sendNotification(MiniGameMessage.SEND_SPIN_SLOT_BT , {data: gameVO.prevDataSpin});
      this.sendNotification(MiniGameMessage.ON_HIDE_ALL_LINE_SLOT_BT);
    }
  }

  displayOneLine(wins, timeDeay, session) {
    for (var i = 0; i < wins.length; i ++) {
      var delay = i*timeDeay;
      TweenLite.delayedCall(delay, function(data, session){
          var gameVO = this.facade.retrieveProxy('SlotBTProxy').gameVO;
          if(gameVO.session !== session) return;
          this.sendNotification(MiniGameMessage.ON_HIDE_ALL_EFFECT_ITEM_WIN_SLOT_BT);
          this.sendNotification(MiniGameMessage.ON_SHOW_LINE_WIN_SLOT_BT, {wins: [data]});
          this.sendNotification(MiniGameMessage.ON_SHOW_EFFECT_ITEM_WIN_SLOT_BT, {wins: [data]});
      }.bind(this),[wins[i], session]);
    }
  }



  updateMyMoney(money) {
    var mySelf = this.facade.retrieveProxy('UserProxy').mySelf;
    mySelf.money = money;
    this.sendNotification(LobbyMessage.ON_UPDATE_MY_INFO);
  }

}
