var BaseCommand = require('BaseCommand');
var LobbyMessage = require('LobbyMessage');

export default class ReceiveRequestOtpCommand extends BaseCommand {
    execute(notification) {
        BaseCommand.prototype.execute.call(this, notification);
        if (this.isError(notification.getBody().data)) return;

        var params = notification.getBody().data;
        var recharge = this.facade.retrieveProxy('RechargeProxy');
        recharge.updateResultOTP(params);

        if (params.vinaUrl !== null && params.vinaUrl !== '') {
            window.open(params.vinaUrl, "_blank");
        } else {
            this.sendNotification(LobbyMessage.SHOW_CONFIRM_OTP);
        }
    }
}