var BaseVO = require("BaseVO");
var Utility = require("Utility");

export default class SlotBTVO extends BaseVO{
    static get ERROR() { return 0 }
    static get NORMAL_WIN() { return 1 }
    static get BIG_WIN() { return 2 }
    static get JACKPOT_WIN() { return 3 }

    onRegister() {
      this.session = 0;
      this.bet = 0;
      this.money = 0;
      this.prize = 0;
      this.jackpot = 0;
      this.wins = [];
      this.items = [];
      this.timeSpin = 1;
      this.isSpining = false;
      this.prevDataSpin = false;
      this.autoSpin = false;
      this.errorMessage = "";
      this.typeResult = -1;
      this.selectRoomId = 1;
      this.freeSpin = 0;

      this.roomJackpot1 = 0;
      this.roomJackpot2 = 0;
      this.roomJackpot3 = 0;

      this.linesBet = '111111111111111111111111111';
      this.listBet = [0, 1000, 2000, 5000, 10000, 30000, 100000];
      this.listRoom = [0, 1, 2, 3, 4]
    }

    reset(){
      this.bet = 0;
      this.money = 0;
      this.prize = 0;
      this.jackpot = 0;
      this.wins = [];
      this.items = [];
      this.errorMessage = "";
      this.typeResult = -1;
    }

    updateData(data) {
      this.reset();
      if (data.m !== undefined) {
        this.typeResult = SlotBTVO.ERROR;
        this.errorMessage = data.m;
      }else{
        this.bet = data.gid;
        this.money = data.data.money;
        this.prize = data.data.prize;
        // this.jackpot = data.data.jackpot;
        this.wins = [];
        if (data.data.rewardLine) {
          for (var i = 0; i < data.data.rewardLine.length; i++) {
            var o = data.data.rewardLine[i];
            //jackpot
            if (o.rewardId === 1) this.jackpot = o.value;
            var iw = {
              line: o.lineId,
              itemId: o.rewardId,
              money: o.value
            }
            this.wins.push(iw);
          }
        }
        
        this.items = data.data.slots;
        this.items.splice(3,0, Utility.getRandomBetween(5, 7));
        this.items.splice(4, 0, Utility.getRandomBetween(5, 7));
        this.items.splice(8, 0, data.data.slotsX[0]);
        this.items.splice(9, 0, data.data.slotsX[1]);
        this.items.splice(13, 0,Utility.getRandomBetween(5, 7));
        this.items.splice(14, 0,Utility.getRandomBetween(5, 7));
        // if(this.jackpot > 0) this.typeResult = SlotBTVO.JACKPOT_WIN;
        // else if(this.prize >= this.listBet[this.bet] * 70) this.typeResult = SlotBTVO.BIG_WIN;
        // else
        this.typeResult = SlotBTVO.NORMAL_WIN;
      }

    }

    setLinesBet(value) {
      this.linesBet = value;
    }

    getCountBet() {
      var arr = this.linesBet.split('');
      var count = 0;
      for(var i=0;i<this.linesBet.length;i++){
          if(this.linesBet[i] === "1") count++;
      }
      return count;
    }

    getLinesBet() {
      var arr = this.linesBet.split('');
      var result = [];
      for (var i = 0; i < arr.length; i++) {
        if (arr[i] === "1") result.push(i + 1);
      }

      return result.toString()
    }

    getTotalBet() {
      var count = this.getCountBet();
      return count * this.listBet[this.selectRoomId];
    }

    getCurentBet() {
        return this.listBet[this.selectRoomId];
    }

    updateSelectRoomId(selectRoomId) {
      this.selectRoomId = selectRoomId;
    }

    updatejackpot(data) {
      this.roomJackpot1 = data[0].jackpot;
      this.roomJackpot2 = data[1].jackpot;
      this.roomJackpot3 = data[2].jackpot;
      this.roomJackpot4 = data[3].jackpot;
      this.roomJackpot5 = data[4].jackpot;
      this.roomJackpot6 = data[5].jackpot;
    }

    getCurrentJackpot() {
      var roomJackPot = 0;
      if(this.selectRoomId === 1) roomJackPot = this.roomJackpot1;
      if(this.selectRoomId === 2) roomJackPot = this.roomJackpot2;
      if(this.selectRoomId === 3) roomJackPot = this.roomJackpot3;
      if(this.selectRoomId === 4) roomJackPot = this.roomJackpot4;
      if(this.selectRoomId === 5) roomJackPot = this.roomJackpot5;
      if(this.selectRoomId === 6) roomJackPot = this.roomJackpot6;
      return roomJackPot;
    }
}
