var BaseCommand = require('BaseCommand');
var LobbyMessage = require('LobbyMessage');
var UserProxy = require('UserProxy');

export default class ReceiveChangeAvatarCommand extends BaseCommand {
    execute(notification) {
        BaseCommand.prototype.execute.call(this, notification);
        this.sendNotification(LobbyMessage.HIDE_LOADING);
        if (this.isError(notification.getBody().data)) return;

        var dataUser = this.facade.retrieveProxy(UserProxy.NAME);
        dataUser.mySelf.avatar = notification.getBody().sendData.link;
        this.sendNotification(LobbyMessage.ON_UPDATE_CHANGE_AVATAR);
        this.sendNotification(LobbyMessage.SHOW_ALERT, { content: notification.getBody().data.message });
    }
}