var BaseScene = require('BaseScene');
var GiftCode = require('GiftCode');
var GiftSceneMediator = require('GiftSceneMediator');

cc.Class({
    extends: BaseScene,

    properties: {
        giftListContent: cc.Node,
        giftCode:GiftCode
    },

    onLoad: function () {
        GiftSceneMediator.getInstance.init(this);
        this.itemGift = this.giftListContent.children[0];
        this.hide();
    },

    onUpdateCaptcha:function (userName) {
        this.giftCode.setupCaptcha(userName);
    },

    onUpdateGiftList: function (data) {
        this.giftListContent.removeAllChildren();
        var item, txtTitle, txtDes, txtMoney, btnOk;
        for (var i = 0; i < data.length; i++) {
            item = cc.instantiate(this.itemGift);
            txtTitle = item.getChildByName("txtTitle").getComponent(cc.Label);
            txtDes = item.getChildByName("txtDes").getComponent(cc.Label);
            txtMoney = item.getChildByName("txtMoney").getComponent(cc.Label);
            btnOk = item.getChildByName("btnOk");

            txtTitle.string = data[i].bcName;
            txtDes.string = data[i].bcShortDes;
            txtMoney.string = data[i].money;
            btnOk.active = (data[i].buttons[0].allow === 0) ? false : true;
            btnOk.on(cc.Node.EventType.TOUCH_START, this.onHandlerUseGift.bind(this, data[i], this));

            this.giftListContent.addChild(item);
        }
    },

    onHandlerUseGift:function (giftItemVO) {
        this.activeUseGift(giftItemVO);
    },

    onHandlerUseGiftCode:function () {
        var data = {
            giftCode:this.giftCode.txtGiftCode.string,
            captcha:this.giftCode.txtCaptcha.string
        };
        this.activeUseGiftCode(data);
    }
});

