var BasePopup = require('BasePopup');
var Constants = require('Constants');
var ShopSceneMediator = require('ShopSceneMediator');
var TabShop = require('TabShop');
var TabMain = require('TabMain');
var TabTransferdes = require('TabTransferdes');
var TabPayBack = require('TabPayBack');
var LobbyEvent = require('LobbyEvent');
var CustomAction = require('CustomAction');

cc.Class({
    extends: BasePopup,

    properties: {
        tabMain:TabMain,
        tabShop: TabShop,
        tabTransferdes: TabTransferdes,
        tabPayBack: TabPayBack,
        tabButtonPayBack: cc.Node,
        TRANSFERDES_TAB_INDEX:1
    },

    // use this for initialization
    onLoad: function () {
        ShopSceneMediator.getInstance.init(this);
        this.addEventListeners();
        this.hide();
    },

    show: function () {
        BasePopup.prototype.show.call(this);
        // this.tabMain.openTab(CustomAction.ACTION_SHOP_ROY);
        this.tabShop.resetInput();
        this.tabTransferdes.resetInput();

        if (Constants.CURRENT_SCENE === Constants.GAME_SELECT)this.onHideTabPayBack();
        else this.onShowTabPayBack();
    },

    addEventListeners: function () {
        this.tabShop.node.on(LobbyEvent.BUY_ITEM_SHOP, this.onBuyItemShop, this);
        this.tabTransferdes.node.on(LobbyEvent.CHECK_ACCOUNT_TRANSFERDES, this.onCheckAccountTransferdes, this);
        this.tabTransferdes.node.on(LobbyEvent.SEND_TRANSFERDES, this.onSendTransferdes, this);
        this.tabPayBack.node.on(LobbyEvent.GET_INFO_CARD_PAY_BACK, this.onGetInfoCardPayBack, this);
        this.tabPayBack.node.on(LobbyEvent.GET_LIST_CARD_PAY_BACK_ON_PAGE, this.onGetListCardPayBackOnPage, this);
    },

    removeEventListeners: function () {
        this.tabShop.node.off(LobbyEvent.BUY_ITEM_SHOP, this.onBuyItemShop, this);
        this.tabTransferdes.node.off(LobbyEvent.CHECK_ACCOUNT_TRANSFERDES, this.onCheckAccountTransferdes, this);
        this.tabTransferdes.node.off(LobbyEvent.SEND_TRANSFERDES, this.onSendTransferdes, this);
        this.tabPayBack.node.off(LobbyEvent.GET_INFO_CARD_PAY_BACK, this.onGetInfoCardPayBack, this);
        this.tabPayBack.node.off(LobbyEvent.GET_LIST_CARD_PAY_BACK_ON_PAGE, this.onGetListCardPayBackOnPage, this);
    },

    onShowTab:function (tabId) {
        this.tabMain.openTabIndex(tabId);
    },

    onUpdateAcountReceive: function (name) {
        this.tabTransferdes.updateAccountReceive(name);
    },

    onUpdateMySelf:function (mySelf) {
        this.tabShop.updateMySelf(mySelf);
        this.tabTransferdes.updateMySelf(mySelf);
    },

    onUpdateCheckAccountTransferdes:function (params) {
        this.tabTransferdes.updateCheckAccountTransferdes(params);
    },
    
    onUpdateOutCardInfo: function (shopVO) {
        this.tabShop.updateOutCardInfo(shopVO);
    },

    onBuyItemShop: function (params) {
        this.activeBuyItemShop(params);
    },

    onUpdateTranferdes: function (transferdesVO) {
        this.tabTransferdes.updateTranferdes(transferdesVO);
    },

    onCheckAccountTransferdes:function (params) {
        this.activeCheckAccountTransferdes(params);
    },

    onSendTransferdes:function (params) {
        this.activeSendTransferdes(params);
    },

    onUpdateListCardPayBack: function (payBackVO) {
        if (this.tabPayBack) this.tabPayBack.onUpdateListCardPayBack(payBackVO);
    },

    onGetInfoCardPayBack:function (params) {
        this.activeGetInfoCardPayBack(params.detail);
    },

    onGetListCardPayBackOnPage: function (params) {
        this.activeGetListCardPayBackOnPage(params.detail);
    },

    onHandlerShowInfo:function () {
        this.activeShowInfo();
    },

    onHideTabPayBack: function () {
        if(!this.tabButtonPayBack) return;
        this.tabButtonPayBack.active = false;
        this.tabPayBack.node.active = false;
    },

    onShowTabPayBack: function () {
        if(!this.tabButtonPayBack) return;
        this.tabButtonPayBack.active = true;
        this.tabPayBack.node.active = true;
    },

    showTransferdes:function (params) {
        this.tabMain.openTab(this.TRANSFERDES_TAB_INDEX);
        if(params&& params.displayName !== null){
            this.tabTransferdes.updateAcountReceive(params.displayName)
        }
    },

    onShowAgentScene: function () {
        this.activeShowAgentList();
    }
});

