var BaseCommand = require('BaseCommand');
var LobbyMessage = require('LobbyMessage');
var i18n = require('i18n');

export default class SendChangePassCommand extends BaseCommand {
    execute(notification) {
        BaseCommand.prototype.execute.call(this, notification);
        var data = notification.getBody();

        if (data.currentPassword === '' || data.newPassword === '' || data.reNewPassword === '') {
            this.sendNotification(LobbyMessage.SHOW_FLY_ALERT, {content: i18n.t('T0061')});
            return;
        }
        if (data.currentPassword === data.newPassword) {
            this.sendNotification(LobbyMessage.SHOW_FLY_ALERT, {content: i18n.t('T0062')});
            return;
        }
        if (data.reNewPassword !== data.newPassword) {
            this.sendNotification(LobbyMessage.SHOW_FLY_ALERT, {content: i18n.t('C0006')});
            return;
        }

        var http = this.facade.retrieveProxy('HttpRequestProxy');
        var mySelf = this.facade.retrieveProxy('UserProxy').mySelf;

        var sendData = {
            displayName: mySelf.displayName,
            token: mySelf.token,
            currentPassword: data.currentPassword,
            newPassword: data.newPassword,
            reNewPassword: data.reNewPassword
        };

        this.sendNotification(LobbyMessage.SHOW_LOADING);
        http.changePass(LobbyMessage.RECEIVE_CHANGE_PASS, sendData);
    }
}