var BasePopup = require('BasePopup');
var Utility = require('Utility');
var DetailSessionTaiXiuMediator = require('DetailSessionTaiXiuMediator');

cc.Class({
    extends: BasePopup,

    properties: {
        itemTaiXiu : cc.Node,
        dice: [cc.Sprite],
        iconDice: [cc.SpriteFrame]
    },

    // use this for initialization
    onLoad: function () {
        DetailSessionTaiXiuMediator.getInstance.init(this);
        this.hide();

        this.contentDetailTai = this.node.getChildByName("contentDetailTai");
        this.contentDetailXiu = this.node.getChildByName("contentDetailXiu");
        this.imgTai = this.node.getChildByName("img_tai");
        this.imgXiu = this.node.getChildByName("img_xiu");
        this.lbSesion = this.node.getChildByName("lbSesion").getComponent(cc.Label);
        this.lbScore = this.node.getChildByName("lbScore").getComponent(cc.Label);
        this.lbCurPage = this.node.getChildByName("lbCurPage").getComponent(cc.Label);
        this.lbTotalTai = this.node.getChildByName("lbTotalTai").getComponent(cc.Label);
        this.lbTotalRepayTai = this.node.getChildByName("lbTotalRepayTai").getComponent(cc.Label);
        this.lbTotalXiu = this.node.getChildByName("lbTotalXiu").getComponent(cc.Label);
        this.lbTotalRepayXiu = this.node.getChildByName("lbTotalRepayXiu").getComponent(cc.Label);

        this.currentPage = 1;
        this.session = 0;
        this.moneyType = 0;
        this.totalPages = 20;

    },

    show: function () {
        this.currentPage = 1;
        BasePopup.prototype.show.call(this);
    },

    onPrevSession: function () {
        var params = {};
        params.session = this.session - 1;
        params.page = 1;
        this.node.emit('ACTIVE_DETAIL_SESSION_UPDATE', params);

        this.currentPage = 1;
    },

    onNextSession: function () {
        var params = {};
        params.session = this.session + 1;
        params.page = 1;

        this.node.emit('ACTIVE_DETAIL_SESSION_UPDATE', params);
        this.currentPage = 1;
    },

    onPrevPage: function () {
        if (this.currentPage <= 1) return;

        var params = {};
        params.session = this.session;
        params.page = this.currentPage-1;
        this.currentPage -=1;
        this.node.emit('ACTIVE_DETAIL_SESSION_UPDATE', params);
    },

    onNextPage: function () {
        if (this.currentPage >= this.totalPages) return;

        var params = {};
        params.session = this.session;
        params.page = this.currentPage+1;
        this.currentPage +=1;
        this.node.emit('ACTIVE_DETAIL_SESSION_UPDATE', params);
    },

    updateDetailSession: function (data) {
        // var info = Utility.convertSFSObjectToObject(data.session);
        data = Utility.convertSFSObjectToObject(data);

        // if (data.r.length === 0)
        //     return;
        // var arrayHis = data.his;
        this.contentDetailTai.removeAllChildren();
        this.contentDetailXiu.removeAllChildren();
        this.imgTai.stopAllActions();
        this.imgXiu.stopAllActions();
        this.imgTai.opacity = 255;
        this.imgXiu.opacity = 255;
        // //
        this.session = data.sid;
        // this.totalPages = data.pt;
        this.lbSesion.string = "Phiên #" + this.session;
        // this.lbCurPage.string = "" + this.currentPage + " / " + this.totalPages;
        this.lbTotalTai.string = Utility.formatCurrency(data.tongDatTai);
        this.lbTotalRepayTai.string = Utility.formatCurrency(data.tongTraTai);
        this.lbTotalXiu.string = Utility.formatCurrency(data.tongDatXiu);
        this.lbTotalRepayXiu.string = Utility.formatCurrency(data.tongTraXiu);
        // //
        var dices = data.result.split(',');
        var result = parseInt(dices[0]) + parseInt(dices[1]) + parseInt(dices[2]);
        this.lbScore.string = ' = ' + result;
        // //
        this.dice[0].spriteFrame = this.iconDice[dices[0]-1];
        this.dice[1].spriteFrame = this.iconDice[dices[1]-1];
        this.dice[2].spriteFrame = this.iconDice[dices[2]-1];
        // //
        if(result >= 11)
            this.showEffect(this.imgTai);
        else
            this.showEffect(this.imgXiu);
        //
        // var transactions = Utility.convertSFSObjectToObject(data.transactions);
        var taiTransactions = Utility.convertSFSArrayToArray(data.TaiTransaction);
        var xiuTransactions = Utility.convertSFSArrayToArray(data.XiuTransaction);

        for(var i=0 ; i<taiTransactions.length ; i++){
            var itemTai = cc.instantiate(this.itemTaiXiu);
            itemTai.getChildByName("bgLine").active = (i % 2 !== 0);
            itemTai.getChildByName("lbTime").getComponent(cc.Label).string = Utility.js_hh_mm_ss(taiTransactions[i].date);
            itemTai.getChildByName("lbName").getComponent(cc.Label).string = taiTransactions[i].displayname;
            itemTai.getChildByName("lbBetValue").getComponent(cc.Label).string = Utility.formatCurrency(taiTransactions[i].bet);
            itemTai.getChildByName("lbRepay").getComponent(cc.Label).string = Utility.formatCurrency(taiTransactions[i].payback);

            this.contentDetailTai.addChild(itemTai);
        }

        for(var j=0 ; j<xiuTransactions.length ; j++){
            var itemXiu = cc.instantiate(this.itemTaiXiu);
            itemXiu.getChildByName("bgLine").active = (j % 2 !== 0);
            itemXiu.getChildByName("lbTime").getComponent(cc.Label).string = Utility.js_hh_mm_ss(xiuTransactions[j].date);
            itemXiu.getChildByName("lbName").getComponent(cc.Label).string = xiuTransactions[j].displayname;
            itemXiu.getChildByName("lbBetValue").getComponent(cc.Label).string = Utility.formatCurrency(xiuTransactions[j].bet);
            itemXiu.getChildByName("lbRepay").getComponent(cc.Label).string = Utility.formatCurrency(xiuTransactions[j].payback);
            this.contentDetailXiu.addChild(itemXiu);
        }
    },

    showEffect: function (node) {
        var fadeIn = cc.fadeIn(0.15);
        var fadeOut = cc.fadeOut(0.15);
        node.runAction(cc.sequence(fadeIn, fadeOut).repeatForever());
    }
});

