var BasePopup = require('BasePopup');
var Utility = require('Utility');

cc.Class({
    extends: BasePopup,

    properties: {
        txtMyMoney:cc.Label,
        txtMoneyWin:cc.Label,
        btnContinue:cc.Button,
        btnStop:cc.Button,
        resultPanel:cc.Node,
        btnSelectGroup:[cc.Node],
        spriteFrames: [cc.SpriteFrame],
    },

    onLoad () {
        // this.txtMyMoney.string =  Utility.formatCurrency(this.gameVO.prize);
        // this.txtMoneyWin.string = Utility.formatCurrency(this.gameVO.prize * 2);
        this.resetGame();
    },

    show: function() {
        BasePopup.prototype.show.call(this);
        this.resetGame();
    },

    buildUI: function(gameVO) {
        // this.gameVO = gameVO;
    },

    updatePrize: function(prize) {
        Utility.tweenRunNumber(this.txtMyMoney.node, prize, 1);
        Utility.tweenRunNumber(this.txtMoneyWin.node, prize * 2, 1);

        this.startMoney = prize;
        // this.resetGame();
    },

    onHandlerStopGameX2() {
        this.hide();
    },

    onHandlerContinueGameX2() {
        this.resetGame();
    },

    onHandlerSelectGameX2(event, eventData) {
        this.node.emit('ACTIVE_PLAY_GAME_X2');
        this.btnGetSelect = eventData;
    },

    onResultPlayX2(data) {
        this.btnSelectGroup[this.btnGetSelect-1].getChildByName('iconNap').active = false;
        if (data.pay > 0){
            this.btnSelectGroup[this.btnGetSelect-1].getChildByName('sprite').getComponent(cc.Sprite).spriteFrame = this.spriteFrames[1];
            this.btnSelectGroup[this.btnGetSelect-1].getChildByName('iconResult').getComponent(cc.Sprite).spriteFrame = this.spriteFrames[4];
            var result = this.btnSelectGroup[this.btnGetSelect-1].getChildByName('black_win');
            result.active = true;
            // result.getChildByName('txtPrize').getComponent(cc.Label).string = Utility.formatCurrency(this.startMoney);

            Utility.tweenRunNumber(result.getChildByName('txtPrize'), this.startMoney + data.pay, 1);

        } else {
            this.btnSelectGroup[this.btnGetSelect - 1].getChildByName('sprite').getComponent(cc.Sprite).spriteFrame = this.spriteFrames[0];
            this.btnSelectGroup[this.btnGetSelect-1].getChildByName('iconResult').getComponent(cc.Sprite).spriteFrame = this.spriteFrames[3];
            this.btnStop.interactable = false;
        }
        this.enableAllButton(false);
        TweenLite.delayedCall(1, function () {
            this.showResult(data);
        }.bind(this));
    },

    showResult: function(data) {
        if (data.pay > 0) {
            this.btnContinue.interactable = true;
            this.startMoney = this.startMoney + data.pay;
            this.updatePrize(this.startMoney);
        }
        else {
            this.resultPanel.active = true;
            this.resultPanel.scaleX = 0;
            this.resultPanel.runAction(cc.scaleTo(0.3,1).easing(cc.easeBackOut()));

            TweenLite.delayedCall(3, function () {
                this.onHandlerStopGameX2()
            }.bind(this));
        }
    },

    enableAllButton: function (enable) {
        for (var i = 0; i < 4; i++ ) {
            this.btnSelectGroup[i].getComponent(cc.Button).interactable = enable;
        }
    },

    resetGame() {
        this.resultPanel.active = false;
        for (var i = 0; i < 4; i++ ) {
            this.btnSelectGroup[i].getChildByName('iconNap').active = true;
            this.btnSelectGroup[i].getChildByName('black_win').active = false;
            this.btnSelectGroup[i].getChildByName('sprite').getComponent(cc.Sprite).spriteFrame = this.spriteFrames[2];
        }
        this.btnContinue.interactable = false;
        this.btnStop.interactable = true;
        this.enableAllButton(true);
    }

});
